import {BaseStyleType, ElementalBlockStyle, BlockStyleType, ParseEffects, RE_MARKDOWN_INDENT} from './elementalBlockStyle.js'
import {Block} from './block.js'
import * as ir from '../../stir/index.js'
import type {Line} from '../line.js'
import type {Stylesheet} from './stylesheet.js'

type ListParseEffects = ParseEffects & { irNewList: ir.Block | undefined }

export class ListStyle implements ElementalBlockStyle {
    name: string
    baseType: BlockStyleType = BaseStyleType.List

    public indent: 'yes' | 'no' | 'optional' = 'no'

    unorderedMarkers = ''
    orderedMarkers = ''

    orderedMarkerPattern = /$/

    honorStartNum = true

    // This is set to true, because sometimes a list can.
    // the actual parse logic may impose more restrictive logic
    // (e.g. only if the list item number is 1).
    canInterruptParagraph = true
    canInterruptParaWithinList = true
    canInterruptLazy = true

    readonly consumesBackChunk = false

    styleSheet: Stylesheet

    constructor (name: string, styleSheet: Stylesheet) {
        this.name = name
        this.styleSheet = styleSheet
    }

    /* one instance of continues func to be shared by all occurrences of this style*/
    public continues = listItemContinuesFunc()

    initAndValidate (): void {
        const match = RE_MARKDOWN_INDENT.exec(this.indent)
        if (match) {
            throw Error(`markdown mode indent is not supported for list items`)
        }
        if (this.canInterruptParagraph && !this.canInterruptParaWithinList) {
            throw Error(`if canInterruptParagraph is set true, canInterruptParaWithinList must also be true`)
        }
        this.orderedMarkerPattern = new RegExp('(\\d{1,9})([' + this.orderedMarkers + '])', 'y')
        return
    }


    parse (firstLine: Line, fromBackLine: Line | null, lookahead = false)
        : ListParseEffects | null {

        const src = firstLine.src

        // indent will be zero if there is slop (which would be already parsed out)
        const indent = firstLine.parseSpaces()
        if (indent === 0 && this.indent === 'yes') {
            return null
        }
        if (indent > 0 && this.indent === 'no') {
            return null
        }

        let listOrdered: boolean | undefined
        let bulletChar: string | undefined
        let delimiter: string | undefined
        let startNum: number | undefined

        let markerLen = 0
        let paddingLen = 0
        let lineContentBlank = false

        const checkpoint = firstLine.checkpoint()
        /////////// try unordered pattern ////////////
        // reBulletListMarker = /([*+-])([ ]+|$)/my
        for (const c of this.unorderedMarkers) {
            if (src[firstLine.ptr] === c) {
                bulletChar = c
                firstLine.ptr++
                break
            }
        }
        if (bulletChar) {
            markerLen = bulletChar.length
            if (firstLine.remainingIsBlank()) {
                // if first line of item is blank, it is normal for the line
                // to end on the marker with no trailing space, in which case
                // we assume padding of 1.
                // todo[spec, padding] Is "assume padding of 1" this a Markdown Mode
                //   only rule? Non-Markdown could parse ahead to the next
                //   line to determine the actual padding.
                lineContentBlank = true
                paddingLen = 1
                listOrdered = false
            } else {
                //todo[spec, markdown] Markdown rule: If the spaces is > 4, only parse one
                //  space as padding. This allows an Indented Code Block to occur
                //  on the first line of an item.
                //  .
                //  How to generalize?
                //  - Markdown Mode?
                //  - a max padding setting on the style declaration?
                //  - ??
                paddingLen = firstLine.parseSpaces(4, true)
                if (paddingLen === 0) {
                    //todo[perf] there's a bit of redundant re-parsing of the same
                    //  spaces as we make two calls to parseSpaces as well
                    //  as the earlier parsing to see if we have a blank line. Can
                    //  we combine this into a single parseSpaces,
                    //  e.g. with `min` and `checkForBlank` args?
                    paddingLen = firstLine.parseSpaces(1)
                    if (paddingLen === 1) {
                        listOrdered = false
                    }
                } else {
                    listOrdered = false
                }
            }
        }

        /////////// try ordered pattern ////////////
        // reOrderedListMarker = /(\d{1,9})([.)])([ ]+|$)/my
        if (listOrdered === undefined) {
            checkpoint.restore()
            this.orderedMarkerPattern.lastIndex = firstLine.ptr
            const match = this.orderedMarkerPattern.exec(src)
            if (match) {
                startNum = parseInt(match[1])
                delimiter = match[2]
                markerLen = match[1].length + delimiter.length
                firstLine.ptr += markerLen
                if (firstLine.remainingIsBlank()) {
                    lineContentBlank = true
                    paddingLen = 1
                    listOrdered = true
                } else {
                    paddingLen = firstLine.parseSpaces(4, true)
                    if (paddingLen === 0) {
                        paddingLen = firstLine.parseSpaces(1)
                        if (paddingLen === 1) {
                            listOrdered = true
                        }
                    } else {
                        listOrdered = true
                    }

                }
            }
        }

        if (listOrdered === undefined) {
            return null
        }

        //  CommonMark rules for list paragraph interruption:
        //  Only a list starting at 1 can interrupt a paragraph. This
        //  restriction only applies if the paragraph in same container
        //  (i.e. not one lazily continuing from another)
        // todo[spec, markdown] this should be decided by the stylesheet.
        //    Also, might be better handled/generalized by my adjacency control idea.
        if (fromBackLine !== null) {
            if ((startNum && startNum !== 1)
                || lineContentBlank) {
                return null
            }
        }

        console.debug(`parse ListItem: ${listOrdered ? 'ordered' : 'unordered'}`)
        console.group()

        // ==== MATCH CERTAINTY =====
        if (lookahead) {
            console.groupEnd()
            return {irNewList: undefined}
        }

        if (lineContentBlank) {
            console.debug('first line is blank')
        }

        //!  With this logic we are including slop rather than
        //   ignoring it as we normally do. This is because in this one
        //   case, CommonMark doesn't treat slop as slop, and actually
        //   goes for alignment (except not really, since when the list
        //   is within a sloppy blockquote, alignment goes out the window!
        const contentIndent = indent + firstLine.pContainerSlop + markerLen + paddingLen
        console.debug(`contentIndent:${contentIndent} offset:${firstLine.ptr}`)

        const ptListItem = new Block(this)
        ptListItem.style = this
        ptListItem.lineStart = firstLine.num
        ptListItem.lineEnd = firstLine.num
        ptListItem.indent = indent
        ptListItem.listContentIndent = contentIndent
        ptListItem.continues = this.continues

        //todo[spec, markdown] Markdown Mode rule that list item can only start with
        //  one blank line. This is a hack impl for now. See note on field
        if (lineContentBlank) {
            ptListItem.endItemIfNextLineBlank = true
        }

        const irListItem = new ir.Block('list_item')
        irListItem.source = ptListItem

        let newList: ir.Block | undefined
        const curList = firstLine.pContainer.openList
        if (!curList
            || listOrdered !== curList.listOrdered
            || delimiter !== (curList.source as Block).listDelimiter
            || bulletChar !== (curList.source as Block).listBulletChar) {

            const ptList = new Block(this)
            ptList.listBulletChar = bulletChar
            ptList.listDelimiter = delimiter
            ptList.lineStart = firstLine.num
            ptList.lineEnd = firstLine.num

            newList = new ir.Block('list')
            newList.source = ptList
            newList.listOrdered = listOrdered
            if (listOrdered) {
                newList.listStyle = 'decimal'
                if (this.honorStartNum) {
                    newList.listStartNum = startNum
                }
            }

            //todo[spec, markdown] HACK this and the code that sets it to false elsewhere is
            //  CommonMark specific. Can it be moved into a stylesheet setting?
            //  A property expression?
            newList.hints['commonmark.list.tight'] = true

            console.debug(`starting new List: ordered:${(listOrdered)} bullet:${(bulletChar)} delim:${(delimiter)} start:${(startNum)}`)
        }

        console.groupEnd()
        return {irBlock: irListItem, ptNamedValue: undefined, endLine: firstLine, irNewList: newList}
    }
}

function listItemContinuesFunc (): (line: Line) => boolean {
    return function (this: Block, line: Line): boolean {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const ptBlock = this
        if (line.remainingIsBlank()) {
            return !ptBlock.endItemIfNextLineBlank
        } else {
            const cp = line.checkpoint()
            const contentIndent = ptBlock.listContentIndent as number
            const indent = line.parseSpaces(contentIndent)
            if (indent !== contentIndent) {
                cp.restore()
                return false
            }
            return true
        }
    }
}
