import * as ir from '../../../stir/index.js'

export type AbstractOut = {
    buf: string
}

type BlockWriter<T extends AbstractOut> = (b: ir.Block, out: T, flags?: Record<string, unknown>) => void
type InlineWriter<T extends AbstractOut> = (i: ir.Inline, out: T) => void

export abstract class AbstractWriter<T extends AbstractOut> {

    bWriters: { [p: string]: BlockWriter<T> } = {}
    iWriters: { [p: string]: InlineWriter<T> } = {}

    useInheritance: boolean

    protected constructor (useInheritance = true) {
        this.useInheritance = useInheritance
    }

    protected initWriters (blockWriters: { [p: string]: BlockWriter<T> },
                           inlineWriters: { [p: string]: InlineWriter<T> }) {
        this.bWriters = blockWriters
        this.iWriters = inlineWriters
    }

    abstract writeText (t: string, out: T): void

    writeSequence (s: ir.BlockSequence | ir.InlineSequence, out: T): void {
        if (s === undefined) {
            //todo[stir] Should we be silently ignoring `undefined`?
            //   See note under writeInlineSequence
        } else if (typeof s === 'string') {
            this.writeText(s, out)
        } else {
            if (ir.isBlockSequence(s)) {
                this.writeBlockSequence(s as ir.BlockSequence, out)
            } else {
                this.writeInlineSequence(s as ir.InlineSequence, out)
            }
        }
    }

    //! writeBlock must remain a shared implementation as it
    //  implements STIR rules for inheritance
    writeBlock (b: ir.Block, out: T): void {
        const w = this.bWriters[b.type]
        if (w) {
            w(b, out)
        } else {
            this.perWriteBlockPerExtendedType(b, out)
        }
    }

    private perWriteBlockPerExtendedType (b: ir.Block, out: T) {
        const extended = b.typeExtends as string
        if (extended) {
            //todo![fallback inheritance] TEMP HACK: hardcoding the single case
            // of type extension we have at the moment. Punting until we have more
            // examples to validate our plan. See [Fallback Inheritance.md].
            // -
            // 🌶🌶🌶 See this in action by disabling the 'CaseBlock2HtmlPlugin'
            // plugin, regenerate the site, and visit one of the stylesheet/
            // case pages. 🌶🌶🌶

            //! this would be the `extends` option, and the following construction
            //  of ``body` would be per the style meta info:
            /*
            const w = this.bWriters[extended]
            if (w) {
                b.body = b.input + '.\n.' + b.expected
                b.info = `${b.class}${b.parameters ? b.parameters : ''}`
                w(b, this)
            } else {
                throw new Error(`STIR block type ${b.type} extends an unknown type: ${extended}`)
            }
            */

            //! this would be the `composition` option where instead of
            //  extending a single type, we compose a fallback rendering
            //  in terms of any number of types, also as described in the
            //  style meta info:
            const input = new ir.Block(`code_block`)
            input.body = b.input as string
            const expected = new ir.Block(`code_block`)
            expected.body = b.expected as string

            const table = new ir.Block('table')
            const col1 = new ir.Block('cell') as ir.Cell
            col1.colSpan = 1
            col1.rowSpan = 1
            col1.body = 'input'
            const col2 = new ir.Block('cell') as ir.Cell
            col2.colSpan = 1
            col2.rowSpan = 1
            col2.body = 'expected'
            const body1 = new ir.Block('cell') as ir.Cell
            body1.colSpan = 1
            body1.rowSpan = 1
            body1.body =  [input]
            const body2 = new ir.Block('cell') as ir.Cell
            body2.colSpan = 1
            body2.rowSpan = 1
            body2.body = [expected]

            table.head = [[col1, col2]]
            table.body = [[body1, body2]]

            this.writeBlock(table, out)
        } else {
            throw new Error(`STIR block type ${b.type} is not recognized nor extends a known type.`)
        }
    }

    //! writeInline must remain a shared implementation as it
    //  implements STIR rules for inheritance
    writeInline (i: ir.Inline, out: T): void {
        const w = this.iWriters[i.type]
        if (w) {
            w(i, out)
        } else {
            if (this.useInheritance) {
                //todo[stir] implement STIR node inheritance
                throw new Error('unrecognized STIR inline type: ' + i.type)
            }
            // else ignore
        }
    }

    writeBlockSequence (bSeq: ir.BlockSequence, out: T): void {
        if (bSeq === undefined) return
            //todo[stir] Should we be silently ignoring `undefined`?
            //   See note under writeInlineSequence
        for (const b of bSeq) {
            this.writeBlock(b, out)
        }
    }

    writeInlineSequence (iSeq: ir.InlineSequence, out: T): void {
        if (iSeq === undefined) {
            // if writeInlineSequence is being for a property of a node, it
            // means that a value was expected for that property. This is an
            // ERROR condition.
            //
            // todo[stir] how should this error condition be handled?
            //   - with the hack below? Seems wrong for error message to
            //     silently be inserted into rendered output.
            //   - fail hard and throw an Error?
            //   - write an empty string and issue a WARNING?
            //   - change our mind and treat a missing property as something
            //     each renderer should resolve silently with some default
            //     value?
            //   .
            //   ALSO, should this happen earlier, in a common location, rather
            //   than repeated in each Renderer? Perhaps an IR validation check?
            //
            //todo[perf] ONCE the above questions are addressed, can we nix this
            //  check? can we assume well-formed STIR?
            this.writeText('❌ ERROR: expected IR node InlineSequence property is undefined ❌', out)
        } else if (typeof iSeq === 'string') {
            this.writeText(iSeq, out)
        } else {
            for (const j of iSeq) {
                if (typeof j === 'string') {
                    this.writeText(j, out)
                } else {
                    this.writeInline(j, out)
                }
            }
        }
    }


}
