import * as ptss from '../ptss/index.js'
import {caseBlockStyle} from './utilStyles.js'
import {newGracedownSheet} from './gracedown.ptss.js'

export function newRubyRhodStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = newGracedownSheet(options)
    stylesheet.name = 'Ruby Rhod evolving talking documentation style sheet'

    // todo![RepoDoc] Sections must be native to RepoDoc so that entire
    //   sections can be annotated. But it looks like I never actually
    //   implemented this

    const atx = stylesheet.get('ATX Heading') as ptss.ChunkStyle
    // todo[doc] Need to document both the mechanics/how-to of sections AND the power
    //  of it being so simple a change. **Only two changes were needed to convert
    //  flat headings to sections**:
    //    - (1) setting the irNode.type to 'section'
    //    - (2) writing the heading text to irNode.title instead of irNode.body
    //
    //   Explain how the body of a section IR node will *automatically* become an
    //   IR container, and them populated according to "the algorithm".
    //
    //   The mechanics and how-to go in the users manual.
    //   The sales pitch goes in, well, the sales pitch.
    //
    // todo[spec] Is this how we want to express that a chunk represents
    //   implicit sectioning? Here are the options to consider for the
    //   end solution:
    //     - A) simply setting the ir node type (as below) declares sectioning
    //        semantics.
    //     - B) add an explicit ChunkStyle.startsNewSection field.
    //   The pieces need for sectioning:
    //     - boolean: does the chunk starts a new section?
    //     - string: section title (is there a use case for title-free sections?)
    //     - number: level of section. determines whether the new section is
    //         a child, sibling, "aunt", "great-aunt" etc to the cur section.
    //
    // 🌶NOTE:
    //  - *In the plain text*"
    //    - it's a text chunk, not a container ✅
    //    - the heading is just the *implicit* **boundary** of a container. It
    //       has no container syntax (borders, indentation, etc).
    //    - It triggers the logic (whether human eye or parser) that perceives
    //      section containers.
    //    - It is named "section heading"
    //  - *In the STIR*:
    //    - sections ARE containers. ✅
    //    - The heading from the plain text is the *title* of the section/container.
    //  - 🌶This is an example of PTSS and IR being independent.
    //  - differing names makes sense. ✅
    //
    // todo[spec] this is the first case where the ptss.Block is a leaf but the ir.Block is
    //   a container. Do we need to do something special here? Or is this some magic
    //   property of irNode.type = 'section'?  BlockTreeParser already has special logic for
    //   irNode.type = 'list'. Is this all kosher?
    //    - if we keep it such, it also implies something new. Previously we said that a custom
    //      STIR type inherits a default rendering from its baseType. But we may now also be saying
    //      that it also has parse-time implications. i.e. setting a custom type to have
    //      'section' as its baseType will mean that it will be parsed according to 'section'
    //      logic. Is this how custom subtyping should work?
    //    - if this is a bad idea, then we need to make an explicit PTSS construct or property
    //      to deal with this. DO NOT create a new PTSS type: *this IS a chunk. But it is a chunk
    //      that behaves like a container top border, with special container termination rules.
    //      - IS THIS ITS OWN THING? or do we need to generalize, with other forms of chunks
    //        imparting structure. (I THINK IT'S ITS OWN THING)
    //      - THE ALTERNATIVE is to call this a container, and then define new container parse
    //        logic. I DON'T LIKE THIS AT ALL. In the plain text it is JUST A CHUNK! Keep PTSS
    //        and STIR models independent and focused on describing their own realms.
    atx.irNode.type = new ptss.Expression('section')
    atx.bodyPattern = /(?:[ \t]+(🟨|🟧|🟥|🏈|❌)?[ \t]*(.*?))??(?:[ \t]+#+)?[ \t]*$/ym
    atx.irNode.title = new ptss.Expression(['parseInlines', ['first', ['body', 2], '']])
    delete atx.irNode.body
    atx.irNode.class = new ptss.Expression(
        ['map', ['body', 1], {
            '🟨': 'tentative',
            '🟧': 'unsettled blocker',
            '🟥': 'unsettled critical',
            '🏈': 'unsettled punt', //todo[RubyDoc] "punt" is too American maybe? "non-blocker" instead?
            '❌': 'rejected'
        }])
    atx.ptNamedValue = {
        type: 'element',
        name: new ptss.Expression(['replace', ['trim', ['first', ['body', 2], '']], /[ \t\r\n]+/, ' ']),
        value: 'self'
    }

    const setext = stylesheet.get('Setext Heading') as ptss.ChunkStyle
    setext.irNode.type = new ptss.Expression('section')
    setext.bodyPattern = /[ \t]*(🟨|🟧|🟥|🏈|❌)?[ \t]*(.*?)[ \t]*$/ym
    setext.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['body', 2]]])
    delete setext.irNode.body
    setext.irNode.class = new ptss.Expression(
        ['map', ['body', 1], {
            '🟨': 'tentative',
            '🟧': 'unsettled blocker',
            '🟥': 'unsettled critical',
            '🏈': 'unsettled punt', //todo[RubyDoc] "punt" is to American maybe? "non-blocker" instead?
            '❌': 'rejected'
        }])
    setext.ptNamedValue = {
        type: 'element',
        name: new ptss.Expression(['replace', ['trim', ['first', ['body', 2], '']], /[ \t\r\n]+/, ' ']),
        value: 'self'
    }

    const devTodo = new ptss.ContainerStyle('Dev Todo', stylesheet)
    devTodo.canInterruptParagraph = true
    devTodo.canInterruptLazy = true
    devTodo.hangingIndent = 'auto' //don't force the full length of the hang, but also allow it
    devTodo.marker = /\[\/\/]:\s+#\s+["'(]todo!?\[([^"]+?)]/y
    devTodo.indent = 'no'
    devTodo.body = 'BLOCKS'
    devTodo.irNode.type = new ptss.Expression('interjection')
    devTodo.irNode.subType = new ptss.Expression('todo')
    devTodo.irNode.icon = new ptss.Expression('🪣')
    devTodo.irNode.title = new ptss.Expression(['parseInlines', ['marker', 1]])

    // place at higher precedence than inherited styles to preempt them
    stylesheet.blockStyles.unshift(devTodo)
    stylesheet.blockStyles.unshift(caseBlockStyle(stylesheet))
    return stylesheet
}
