import type {Stylesheet} from './stylesheet.js'
import {BaseStyleType, ElementalBlockStyle, BlockStyleType, ParseEffects, RE_MARKDOWN_INDENT} from './elementalBlockStyle.js'
import {Block} from './block.js'
import type {Line} from '../line.js'
import * as ir from '../../stir/index.js'


const TAG_NAME = '[A-Za-z][A-Za-z0-9-]*'
const ATTRIBUTE_NAME = '[a-zA-Z_:][a-zA-Z0-9:._-]*'
const UNQUOTED_VALUE = '[^"\'=<>`\\x00-\\x20]+'
const SINGLE_QUOTED_VALUE = '\'[^\'\n]*\''
const DOUBLE_QUOTED_VALUE = '"[^"\n]*"'
const ATTRIBUTE_VALUE = '(?:' + UNQUOTED_VALUE + '|' + SINGLE_QUOTED_VALUE + '|' + DOUBLE_QUOTED_VALUE + ')'
const ATTRIBUTE_VALUE_SPEC = '(?:[ \\t]*=[ \\t]*' + ATTRIBUTE_VALUE + ')'
const ATTRIBUTE = '(?:[ \\t]+' + ATTRIBUTE_NAME + ATTRIBUTE_VALUE_SPEC + '?)'
const OPEN_TAG = '<' + TAG_NAME + ATTRIBUTE + '*' + '[ \\t]*/?>'
const CLOSE_TAG = '</' + TAG_NAME + '[ \\t]*[>]'

//todo[markdown] shouldn't all of these start with ^ ???
export const reHtmlBlockOpen = [
    /./, // dummy for 0
    /(?:script|pre|style|textarea)(?:\s|>)/iy,
    /!--/y,
    /[?]/y,
    /![A-Za-z]/y,
    /!\[CDATA\[/y,
    /[/]?(?:address|article|aside|base|basefont|blockquote|body|caption|center|col|colgroup|dd|details|dialog|dir|div|dl|dt|fieldset|figcaption|figure|footer|form|frame|frameset|h[123456]|head|header|hr|html|iframe|legend|li|link|main|menu|menuitem|nav|noframes|ol|optgroup|option|p|param|section|source|summary|table|tbody|td|tfoot|th|thead|title|tr|track|ul)(?:\s|[/]?[>]|$)/iy,
    new RegExp('(?:' + OPEN_TAG.slice(1) + '|' + CLOSE_TAG.slice(1) + ')[ \\t]*\n', 'y')
]
export const reHtmlBlockClose = [
    /./, // dummy for 0
    /^.*<\/(?:script|pre|style|textarea)>/iym,
    /^.*-->/ym,
    /^.*\?>/ym,
    /^.*>/ym,
    /^.*]]>/ym
]

//todo[markdown, arch, spec] HTML blocks aren't a "style". It's a Markdown Mode
//   special case that cannot be customized, and doesn't follow
//   any normal rules that deserve being generalized. It would
//   be better to special case this as something else, and just
//   use Typescript's union type support to handle including it
//
//todo[spec] Consider alternate rules for embedded HTML blocks,
//   e.g. HTML that doesn't necessarily consume whole lines,
//        that act as if they dont' exist for parsing purposes,
//        just inserted in-site in the output. Or does this
//        already exist with inline HTML?
export class HTMLBlockStyle implements ElementalBlockStyle {
    baseType: BlockStyleType = BaseStyleType.TEMP_HTML_HACK
    name: string

    public indent: 'yes' | 'no' | 'optional' = 'no'

    canInterruptParagraph = true
    canInterruptLazy = true

    readonly consumesBackChunk = false

    styleSheet: Stylesheet

    constructor (name: string, styleSheet: Stylesheet) {
        this.name = name
        this.styleSheet = styleSheet
    }

    initAndValidate (): void {
        const match = RE_MARKDOWN_INDENT.exec(this.indent)
        if (match) {
            throw new Error(`markdown mode indent is not supported for HTML blocks`)
        }
        return
    }

    parse (firstLine: Line, fromBackLine: Line | null, lookahead = false): ParseEffects | null {
        const src = firstLine.src

        // indent will be zero if there is slop (which would be already parsed out)
        const indent = firstLine.parseSpaces()
        if (indent === 0 && this.indent === 'yes') {
            return null
        }
        if (indent > 0 && this.indent === 'no') {
            return null
        }

        let startOffset = firstLine.ptr

        if (src.charAt(startOffset) !== '<') {
            return null
        }
        startOffset++


        // type 7 may not interrupt a paragraph
        const allowedTypes = fromBackLine === null ? 7 : 6

        let blockType
        for (blockType = 1; blockType <= 7; blockType++) {
            const regExp = reHtmlBlockOpen[blockType]
            regExp.lastIndex = startOffset
            if (blockType <= allowedTypes && regExp.test(src)) {
                console.debug('parse HTML Block:  blockType', blockType)
                console.group()

                // ==== MATCH CERTAINTY =====
                if (lookahead) {
                    console.groupEnd()
                    return {}
                }

                // todo[spec, markdown] the ENTIRE line is added even if the start tag occurs midline.
                //   This differs from Markdown 1.0.2, Pandoc and others.
                //   The text ends up as body text in the output, but is not combined
                //   with any continuation text on the following line into a single
                //   paragraph.
                firstLine.ptr = firstLine.pContainerSOL

                // JUST LIKE CODE BLOCKS, HTML blocks cannot nest other blocks. So we
                // can parse the whole block right here.
                let blockContent, lastLine
                if (blockType <= 5) {
                    // closing tag terminated HTML block
                    [blockContent, lastLine] =
                        firstLine.fwdBlock(false, reHtmlBlockClose[blockType])
                } else {
                    // blank line terminated HTML block
                    [blockContent, lastLine] = firstLine.fwdBlock(true)
                }

                const ptBlock = new Block(this)
                ptBlock._htmlBlockType = blockType
                ptBlock.lineStart = firstLine.num
                ptBlock.lineEnd = lastLine.num

                const irHtmlBlock = new ir.Block('html_block')
                irHtmlBlock.source = ptBlock
                //! Remove all trailing NEWLINES and SPACES from the block content
                irHtmlBlock.body = blockContent.replace(/(\n *)+$/, '')

                console.groupEnd()
                return {irBlock: irHtmlBlock, endLine: lastLine}
            }
        }

        return null
    }


}
