/*  Used by case file tools  */

import * as ptss from '../ptss/index.js'
import {newCommonMarkSheet} from './commonmark.ptss.js'
import {caseBlockStyle, catchAllAsLiteral} from './utilStyles.js'

export function newCaseFileManipulationSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('gen-case-expectations')
    stylesheet.sloppyMargin = 0

    stylesheet.escapeSyntax.escapeChar = '\\'

    const caseBlocks = caseBlockStyle(stylesheet)
    stylesheet.blockStyles.push(caseBlocks)

    // preserved all whitespace in indented regions
    const indented = new ptss.ContainerStyle('Indented Text To Preserve', stylesheet)
    indented.irNode.type = new ptss.Expression('code_block')
    indented.indent = 'markdown(1,4)'
    indented.body = 'RAW_NL'
    indented.canInterruptParagraph = false
    indented.canInterruptLazy = false
    stylesheet.blockStyles.push(indented)

    // preserved all whitespace in regular CommonMark code blocks,
    const cm = newCommonMarkSheet(options)

    const backtick = cm.get('Backtick Fenced Code Block') as ptss.ContainerStyle
    backtick.styleSheet = stylesheet
    stylesheet.blockStyles.push(backtick)

    const tilde = cm.get('Tilde Fenced Code Block')
    tilde.styleSheet = stylesheet
    stylesheet.blockStyles.push(tilde)

    // match the rest as lit chunks so we can pass them thru to the output as-is
    //   (otherwise they'd be converted to paragraphs)
    //  Does not preserve extra whitespace between chunks hence the code_block
    //  styles.
    stylesheet.defaultBlockStyle = catchAllAsLiteral(stylesheet)

    return stylesheet
}
