import * as ptss from './ptss/index.js'

/*
todo[debug] can we leverage Javascript tagged templates to automatically handle
formatting output for complex object, e.g. STIR nodes?

And example from an article on tagged templates:
```
// Let's say we want to write a way to log arbitrary strings containing a number, but format the number.
// We can use tagged templates for that.
function formatNumbers(strings: TemplateStringsArray, number: number): string {
  return strings[0] + number.toFixed(2) + strings[1];
}
console.log(formatNumbers`This is the value: ${0}, it's important.`); // This is the value: 0.00, it's important.

// Or if we wanted to "translate" (change to lowercase here) translation keys within strings.
function translateKey(key: string): string {
  return key.toLocaleLowerCase();
}
function translate(strings: TemplateStringsArray, ...expressions: string[]): string {
  return strings.reduce((accumulator, currentValue, index) => accumulator + currentValue + translateKey(expressions[index] ?? ''), '');
}
console.log(translate`Hello, this is ${'NAME'} to say ${'MESSAGE'}.`); // Hello, this is name to say message.
```
*/

export function stringifyTruncate (s: string, max = 50): string {
    if (s.length > max) {
        return JSON.stringify(s.slice(0, max)).slice(0, max + 1) + '... (TRUNCATED)'
    }
    return JSON.stringify(s)
}


/**
 * Constructs a one-time use replacer (uses a closure to
 * maintain an index of seen items to handle circularity)
 *
 * For use with JSON.stringify. Handles circularity
 * and tries to remove noise.
 *
 * todo![debug] maybe replace with a debugStringify
 *  to make call sites cleaner and also avoid the
 *  frequent mistake of forgetting to add `()` to
 *  call this method for the replacer.
 *
 * todo![debug] is this redundant with serialization.ts?
 */
export const stringifyReplacerForDebugLogging = () => {
    const seen = new WeakSet()
    return (k: string, v: unknown) => {
        if (!v) {
            return v
        }

        if (k === 'source') {
            return undefined
        }

        if (v instanceof ptss.Stylesheet) {
            return {stylesheet: v.name}
        }

        if (v instanceof RegExp) {
            return v.source
        }

        if (typeof v === 'object') {
            if (seen.has(v)) {
                return `🏷`
            }
            seen.add(v)
        }

        return v
    }
}
