import * as ptss from '../ptss/index.js'

export function newPlainJaneStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('PlainJane')

    //todo[spec] just copied CommonMark's. Haven't even thought about natural escapes.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    const para = stylesheet.get('Paragraph') as ptss.ChunkStyle
    para.irNode.body = new ptss.Expression(
        [
            'parseInlines',
            ['trim', ['body']],
            'newlineIsHardBreak'
        ]
    )

    const setext = new ptss.ChunkStyle('Setext Section Heading', stylesheet)
    setext.irNode.type = new ptss.Expression('section')
    setext.tailPattern = /((=+|-+)[ \t]*$)/ym
    setext.maxLines = 5
    setext.indent = 'optional'
    setext.canInterruptParagraph = false
    setext.irNode.level = new ptss.Expression(['map', ['at', ['tail', 2], 0], {'=': 1, '-': 2}])
    setext.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['body']], 'newlineIsHardBreak'])

    const indentedBlockquote = new ptss.ContainerStyle('Indented Block Quotation', stylesheet)
    indentedBlockquote.irNode.type = new ptss.Expression('block_quote')
    indentedBlockquote.indent = 'yes'

    const interjectionHanging = new ptss.ContainerStyle('Interjection Hanging', stylesheet)
    interjectionHanging.indent = 'optional'
    interjectionHanging.hangingIndent = 'auto'
    interjectionHanging.hangingIndentRequired = true
    //todo: use ES9 *Unicode property escapes in regular expressions*
    // https://pawelgrzybek.com/whats-new-in-ecmascript-2018/#unicode-property-escapes-in-regular-expressions-by-mathias-bynens
    interjectionHanging.marker = /‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|❓|💡|✅|❌/y
    interjectionHanging.canInterruptParagraph = true
    interjectionHanging.irNode.type = new ptss.Expression('interjection')
    // todo[spec] see relevant notes under Gracedown 'Interjection Paragraph'
    interjectionHanging.irNode.subType = new ptss.Expression(
        ['map', ['marker', 0], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '❓': 'question',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    // todo![composition] title should be set from first heading?
    // interjectionPara.irNode.title =
    interjectionHanging.irNode.icon = new ptss.Expression(['marker', 0])

    const interjectionPara = new ptss.ChunkStyle('Interjection Paragraph', stylesheet)
    //todo: use ES9 *Unicode property escapes in regular expressions*
    // https://pawelgrzybek.com/whats-new-in-ecmascript-2018/#unicode-property-escapes-in-regular-expressions-by-mathias-bynens
    interjectionPara.headPattern = /‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|❓|💡|✅|❌/y
    // todo[RepoDoc] hack: this regex doesn't ensure the emphasis/strong markers are properly paired
    // todo![ptss composition] ideally we use the plan where this style  in terms of the other style
    //   elements, e.g. ATX heading.
    interjectionPara.bodyPattern = / *(?:([^\n]+)(?:\\| {2})\s*\n|[*_]{1,3}([^\n]+?)[*_]{1,3} *\n)?([\s\S]*)/y
    interjectionPara.canInterruptParagraph = false
    interjectionPara.canInterruptLazy = false
    interjectionPara.irNode.type = new ptss.Expression('interjection')
    // todo[stir] settle on first cut of interjection subtypes supported natively by the IR
    //    OR:
    // todo![stir] decide whether the IR is neutral and just passes the subtype through? Would that
    //   work for other output formats if they don't have a notion like classes ???
    //   **Should there instead be a predefined *base* set of interjection types that all writers
    //   can rely on without worrying about whether a particular implementation supports it?**
    // See also https://rdmd.readme.io/docs/callouts
    // Also my post: on talk.commonmark.org about github admonitions + my post on the github announcement
    // DocBook: danger, error, hint, important, note, tip, warning
    interjectionPara.irNode.subType = new ptss.Expression(
        ['map', ['head', 0], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '❓': 'question',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    interjectionPara.irNode.icon = new ptss.Expression(['head', 0])
    interjectionPara.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['first', ['body', 1], ['body', 2]]]])
    interjectionPara.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body', 3]]])

    stylesheet.blockStyles.push(
        setext,
        indentedBlockquote,
        interjectionHanging,
        interjectionPara
    )

    stylesheet.inlineStyles = [
        'autolink',
        'backticks',
        'emphasis',
        'entity',
        'escape',
        'html_inline',
        'image',
        'reference',
        'newline',
        'html'
    ]

    return stylesheet
}
