import {unescapeString, escapeText} from '../../orphans/parseUtil.js'
import {BlockTreeParser} from '../blockTreeParser.js'
import {NamedValues} from '../namedValues.js'
import type {Stylesheet} from './stylesheet.js'
import {stringifyReplacerForDebugLogging} from '../debugUtil.js'

function expressionError (msg: string): never {
    throw new Error(`Expression Error: ${msg}`)
}

type ExpressionValue = string | number | undefined | unknown

/* S-Expression Notes

https://igor.io/2012/12/06/sexpr.html
https://igor.io/2012/12/14/sexpr-special-forms.html

iammerrick's json-lisp does something similar to what i'm doing here, but
by implementing Lisp's model:
  - https://www.merrickchristensen.com/articles/json-lisp/
  - https://github.com/iammerrick/json-lisp/blob/master/index.ts

*/

// todo[perf] we evaluate expressions even when their input (e.g. the "info" righter
//  for fenced code blocks) doesn't occur. Is it worth optimizing that out?

// todo[perf] The reason that the 'parseInlines' expression defers execution is to
//  to wait until after block parsing has identified all the linkable targets
//  in the source, so that inline link references an be resolved.
//  .
//  An alternate and probably far more efficient approach: rather than defer
//  the entire inline parse (as links are relatively rare), just defer the
//  link resolution. So instead of flagging the entire inline string as we
//  do here, we do the flagging at a deeper level, at the nodes that contain
//  inline link. We could flag them, or more efficiently, push pointers to those
//  IR nodes onto a stack, thus avoiding walking a potentially very large node
//  tree. If the deferred link matches a link target, an IR link node is
//  substituted. If not, a plain text node with the original plain text is
//  substituted.
export const INLINE_PARSE_FLAG = '🅿️arse🚩'

function evaluateExpression (expression: unknown,
                             headMatch: string[] | null,
                             tailMatch: string[] | null,
                             bodyMatch: string[] | null,
                             markerMatch: string[] | null,
                             righterMatch: string[] | null): ExpressionValue {
    // if is terminal
    // todo[spec] TEMP HACK: this assumes an array is an expression, thus
    //            disallowing arrays to be values. A proper DSL will of course
    //            solve this.
    if (!Array.isArray(expression)) {
        return expression as string
    }

    console.debug(JSON.stringify(expression, stringifyReplacerForDebugLogging()))
    console.group()

    //! unlike lisp, we don't treat the first item as an expression that could
    //  evaluate to a function. If there is a good reason to do that, we could
    //  do that here, just as we do for each of the other args below.
    const func = expression[0]

    //! as in Lisp, the `if` operator is a *special form*, with lazily evaluated branch args,
    // (known as *normal order* evaluation) so it is special-cased here.
    if (func === 'if') {
        if (expression.length < 4) {
            expressionError(`'if' expression requires 3 args. There are ${expression.length - 1}.`)
        }
        let value: ExpressionValue
        if (evaluateExpression(expression[1], headMatch, tailMatch, bodyMatch, markerMatch, righterMatch)) {
            value = evaluateExpression(expression[2], headMatch, tailMatch, bodyMatch, markerMatch, righterMatch)
        } else {
            value = evaluateExpression(expression[3], headMatch, tailMatch, bodyMatch, markerMatch, righterMatch)
        }
        console.debug(`${JSON.stringify(value, stringifyReplacerForDebugLogging())}`)
        console.groupEnd()
        return value
    }

    //! For the normal functions, arguments are evaluated in *Applicative order*:

    const args: ExpressionValue[] = []
    for (let i = 1; i < expression.length; i++) {
        args.push(evaluateExpression(expression[i], headMatch, tailMatch, bodyMatch, markerMatch, righterMatch))
    }

    let value: ExpressionValue = undefined

    //todo[spec] should we do more argument validation, e.g. error on unexpected args?
    //todo[spec] as implemented, a literal value cannot happen to be a func name!
    //   when we implement the proper S-expression syntax, we need to disambiguate values from func names.
    switch (func) {
        // todo![spec, expressions] consider having the 'variables' such as
        //  head, tail, etc be distinguished from functions with '$'. But not
        //  sure about this. Which is more elegant. What do other S-expression
        //  syntaxes do? What do functional languages do?
        case 'head':
            if (headMatch) {
                if (expression[1]) {
                    value = headMatch[expression[1]]
                } else {
                    value = headMatch[0]
                }
            } else {
                value = undefined
            }
            break
        case 'tail':
            if (tailMatch) {
                if (expression[1]) {
                    value = tailMatch[expression[1]]
                } else {
                    value = tailMatch[0]
                }
            } else {
                value = undefined
            }
            break
        case 'body':
            if (bodyMatch) {
                if (expression[1]) {
                    value = bodyMatch[expression[1]]
                } else {
                    value = bodyMatch[0]
                }
            } else {
                value = undefined
            }
            break
        case 'marker':
            if (markerMatch) {
                if (expression[1]) {
                    value = markerMatch[expression[1]]
                } else {
                    value = markerMatch[0]
                }
            } else {
                value = undefined
            }
            break
        case 'righter':
            if (righterMatch) {
                if (expression[1]) {
                    value = righterMatch[expression[1]]
                } else {
                    value = righterMatch[0]
                }
            } else {
                value = undefined
            }
            break
        case 'length':
            value = (args[0] as string | Array<unknown>).length
            break
        case 'at':
            value = (args[0] as string | Array<unknown>).at(args[1] as number)
            break
        case 'trim':
            value = (args[0] as string)?.trim()
            break
        case 'map': //todo[spec] rename to lookup? map implies []-to-[]?
            value = (args[1] as Record<string, unknown>)[args[0] as string]
            break
        case 'match':
            value = (args[0] as string).match(args[1] as string | RegExp)
            break
        case 'replace':
            value = (args[0] as string).replace(args[1] as string | RegExp, args[2] as string)
            break
        case 'escape':
            value = escapeText(args[0] as string)
            break
        case 'unescape':
            value = unescapeString(args[0] as string)
            break
        case 'first':
            for (let i = 0; i < args.length; i++) {
                if (args[i] !== undefined) {
                    value = args[i]
                    break
                }
            }
            break
        case 'concat':
            value = ''
            for (let i = 0; i < args.length; i++) {
                value += args[i] as string
            }
            break
        case 'parseInlines':
            // first arg must have evaluated to a string,
            // else there is nothing to parse
            if (args[0] !== undefined) {
                value = [INLINE_PARSE_FLAG, ...args]
            } else {
                value = undefined
            }
            break
        case 'parseBlocks':
            if (args[0] !== undefined) {
                const stylesheet = args[1] as Stylesheet
                const rootType = args.length > 2 ? args[2] as string : 'dummy'
                const btp = new BlockTreeParser(args[0] as string, stylesheet, rootType, new NamedValues())
                const root = btp.parse()
                value = rootType === 'dummy' ? root.body : root
            } else {
                value = undefined
            }
            break
       default:
            // if no args, treat as literal
            if (args.length === 0) {
                value = func
            } else {
                expressionError(`unrecognized expression function: ${func}`)
            }
    }

    console.debug(`${JSON.stringify(value, stringifyReplacerForDebugLogging())}`)
    console.groupEnd()
    return value
}

export class Expression {
    private readonly exp: unknown

    constructor (exp: unknown) {
        this.exp = exp
    }

    //todo![arch, spec, expressions] if we need to add ANY more to this list,
    //  switch to a map to allow arbitrary named variables to be passed in
    //  and referenced by expressions.
    evaluate (headMatch: string[] | null,
              tailMatch: string[] | null,
              bodyMatch: string[] | null,
              markerMatch: string[] | null,
              righterMatch: string[] | null): ExpressionValue {
        return evaluateExpression(this.exp, headMatch, tailMatch, bodyMatch, markerMatch, righterMatch)
    }

    toString (): string {
        return JSON.stringify(this.exp, stringifyReplacerForDebugLogging())
    }
}

