import type {RenderSTIR, RenderSTIRProvider} from '../../../renderSTIR.js'
import {Config, newConfigFromSimpleOptions} from '../../../config.js'
import * as ir from '../../../../stir/index.js'
import type {STIR} from '../../../stir.js'
import {registerSTIRRenderer, lookupPlugin} from '../../../registry.js'
import {escapeText, escapeAttrib} from '../../../../orphans/parseUtil.js'
import encode from 'mdurl/encode.js'


