/*
todo[html] ALTERNATE RENDERINGS, and standard conformance
  To support standards conformance, e.g. html5Out,
  we can supply ALTERNATE RENDERINGS simply by registering
  a modified or entirely different set of default Block and Inline Writers.
  .
  WE DO NOT NEED MULTIPLE stir2html classes.
  .
  One way to factor is:
   --Html4Out
   --Html5Out *HtmlLivingStandardOut)
     --HtmlAccessibleOut    outputs arias, and all other attributes needed to comply with accessibility standards
   --XHtmlOut
   --Bootstrap5Out
  .
  Another way is to have one HtmlOut, but simply register different combinations
  of individual writers (this makes sense if most are the same).
  .
  Another way is to do it as "quirks", as we do for Pandoc and GFM.
  .
  I don't know if there is an easy way to do Mixins, e.g. "HTML5 + accessible + boostrap".
 */


// todo![now] DECIDED: toc is produced by stir2html.
//  1. how ID's are generated for HTML is an internal renderer matter.
//  2. so unless we want to establish cross-pipeline rules for the HTML
//     rendering, it doesn't make sense for anything other than ONE
//     component to generate the IDs.
//  3. We *could* generate a generic TOC and hand that off to stir2html
//     to insert the proper IDs, but why add ALL THAT COMPLEXITY just so
//     that VERY SIMPLE code is re-usable?
//  4. If we want to establish a standard form for the TOC structure, it's
//     as simple as making that part of the Template API spec, and even
//     supply Typescript typings.


export type HTMLOut = AbstractOut & {
    renderPlainText: (s: string) => string
    // todo![site scheme, footnotes, template api] margin note vs footnote:
    //  - CANNOT be handled by CSS-only, requires specific HTML structure.
    //  - Raw ideas for ways to give user this option:
    //    - CSS control: NOT POSSIBLE (need to double verify)
    //    - A Stir2Html switch. This is easiest, but does not give either
    //      the site scheme or the stylesheet control.
    //    - Site Scheme control: all notes look the same in the source plaintext
    //      and the choice of Site Scheme (or a switch within a configurable Site
    //      Scheme) determines whether notes are margin or foot.
    //    - Author, PTSS & STIR control: Ideal world:
    //      - a style sheet can offer syntax for both foot and margin notes.
    //      - the author chooses for *each* note, via syntax choice.
    //      - notes of STIR MARGIN NOTE type get rendered in the margin, or at least
    //        adjacent to the reference in some way (e.g. tooltip, or
    //        `[bracketed note]` for print renderings). (maybe we use a more
    //        generic term, e.g. STICKY NOTE, for all notes that get rendered
    //        adjacent, and leave the form of adjacency to the Site Scheme)
    //      - notes of STIR FOOTNOTE type get rendered as footnotes. (maybe
    //        we use a more generic term, e.g. NON-STICKY for lack of better
    //        tern, for all notes that get rendered in a separate section, and
    //        leave the location (foot vs end) to the Site Scheme)
    //      - notes of generic type get rendered either as footnotes, margin
    //        notes, or endnotes, as deemed fit by the Site Scheme OR
    //        renderer config setting, i.e. one of the other options above.
    //  search repo for "tuft" for more notes on this.
    // todo![now, references] move the above comment to the right place, and
    //   put a link to it here instead, if such a link makes sense.
    phase: 'main' | 'footnote'
    readonly takenHtmlIDs: { [key: string]: boolean }
    /**
     * This is necessary because we don't have an idempotent function that
     * maps STIR IDs to HTML IDs uniquely, that is to say a one-to-one mapping,
     * because many chars are stripped or replaced with the same char. The function
     * guarantees uniqueness by appending a number, and calling that number
     * successively *for the exact same STIR ID* will yield different values.
     * Thus we need to track the HTML ID generated the first time we convert each
     * STIR ID.
     */
    readonly stirID2htmlID: { [stirID: string]: string }
    // for numbering, and also to determine unreferenced notes (for warning)
    readonly notesInReferencedOrder: ir.Block[]
    // map each note to note_refs (note.stirID to note_ref.htmlID)
    readonly noteBackRefs: { [stirID: string]: string[] }
}

export type BlockWriter = (b: ir.Block, out: HTMLOut, flags?: Record<string, unknown>) => void
export type InlineWriter = (i: ir.Inline, out: HTMLOut) => void

type AttrList = [string, string][]

type ElemID4Node = (node: ir.Block | ir.Inline, out: HTMLOut) => string | undefined

/**
 * TextPlain's default HTML render. It outputs the IR
 * nodes in a straight-forward way, expecting the user
 * to supply the CSS for it to render correctly.
 *
 * todo[html] the Stir2Template could have a default
 * template that embeds a stylesheet with nice but generic styling
 * that covers all the elements and styles produced by Stir2Html.
 */
export class Stir2Html extends AbstractWriter<HTMLOut> {

    readonly safe: boolean

    readonly createAttrList: (ire: ir.Block) => AttrList

    readonly elemID4Node: ElemID4Node
    readonly idStyle: 'gfm' | 'pandoc' | 'default'

    // todo[html] CommonMark quirks, things we do just for CM test
    //  conformance, should also be made explicit and optional in the same way.
    //  If the quirks are extensive, see ALTERNATE RENDERINGS above.
    //  -
    //  That Pandoc doesn't output empty ALT attribs for images
    //  seems correct, not a quirk; thus the logic for rending image links
    //  looks backwards.
    readonly quirks: 'none' | 'gfm' | 'pandoc' = 'none'
    readonly notEscaped: string
    readonly checkboxUnchecked: string = '<input type="checkbox" />'
    readonly checkboxChecked: string = '<input type="checkbox" checked />'

    // todo[arch] We support generic Record for options for simple use cases
    //    such as Textplain Live. But should we? Does it make sense to always
    //    require the caller create a Config, which could take the caller's
    //    context for any necessary path resolution?
    constructor (config?: Config | Record<string, unknown>) {
        super()
        this.initWriters(this.initBlockWriters(), this.initInlineWriters())

        if (config === undefined || !(config instanceof Config)) {
            config = newConfigFromSimpleOptions(config)
        }
        const options = config.data()

        // todo![plugin api] are we going to mandate that all RenderProviders implement a Plugin interface?
        //  - or should this be internal to their Provider.configure method, where we pass in their
        //    piece of the config and let them determine both config structure and what they support?
        //  - or is there advantage to enforcing consistency (a plugin can have a noop for this).

        // expected config schema:
        //     plugins: [
        //         {
        //             provider: string
        //             config?: Record<string, unknown>
        //         }
        //     ]
        const plugins = config?.subConfigs('plugins')
        if (plugins !== undefined) {
            for (const pluginConfig of plugins) {
                const providerName = pluginConfig.requiredValue('provider')
                console.group(`configure Plugin: ${providerName}`)
                const plugin = lookupPlugin(providerName)
                if (plugin === undefined || plugin.install === undefined) {
                    throw new Error(`invalid plugin: ${providerName}`)
                }
                plugin.install(this, pluginConfig.subConfig('config')?.data())
                console.groupEnd()
            }
        }

        this.safe = options.safe === true   //todo: should we default to true instead?
        this.createAttrList = options.sourcepos === true ? createAttrListWithSourceInfo : createEmptyAttrList

        switch (options.idStyle || 'default') {
            case 'gfm':
                this.idStyle = 'gfm'
                this.elemID4Node = elemID4Node_GFM
                break
            case 'pandoc':
                this.idStyle = 'pandoc'
                this.elemID4Node = elemID4Node_Pandoc
                break
            case 'default':
                this.idStyle = 'default'
                this.elemID4Node = elemID4Node_Default
                break
            default:
                throw new Error(`invalid idStyle: ${options.idStyle}}`)
        }

        switch (options.quirks) {
            case 'gfm':
                this.quirks = 'gfm'
                this.notEscaped = encode.defaultChars.replace('$', '') + '%'
                this.checkboxUnchecked = '<input type="checkbox" disabled />'
                this.checkboxChecked = '<input type="checkbox" checked disabled />'
                break
            case 'pandoc':
                this.quirks = 'pandoc'
                this.notEscaped = encode.defaultChars + '%\\'
                this.checkboxUnchecked = '<input type="checkbox" />'
                this.checkboxChecked = '<input type="checkbox" checked="" />'
                break
            case undefined:
            case null:
            case '':
                this.quirks = 'none'
                this.notEscaped = encode.defaultChars //todo![spec] is this right? what about `%`?
                break
            default:
                throw new Error('unrecognized quirks option')
        }
    }

    // todo[api] Overwrites the default writer; no possibility of `super.write()`
    //   - i might register a BlockWriter that only handles the case for
    //     a subset of blocks of a given type, e.g. those with a `caption` value.
    //   - i should be able to defer back to the original BlockWriter, either
    //     to say "this one isn't mine" or to prepend/append/wrap the default output
    //     with some custom stuff.
    // todo![api, stir customization, stir templates] given the complexity of some of the
    //   block writer implementations, i'm not sure this is the way to support
    //   adding custom rendering. See my notes *STIR Element-level Templates*.
    registerBlockWriter (type: string, bw: BlockWriter): void {
        this.bWriters[type] = bw.bind(this)
    }

    registerInlineWriter (type: string, iw: InlineWriter): void {
        this.iWriters[type] = iw.bind(this)
    }

    render (stir: STIR): string {
        const out: HTMLOut = {
            buf: '',
            renderPlainText: escapeText,
            phase: 'main',
            takenHtmlIDs: {},
            stirID2htmlID: {},
            notesInReferencedOrder: [],
            noteBackRefs: {}
        }
        //todo[arch] the rest of this is identical for all renderers. DRY!
        if (ir.isBlock(stir)) {
            this.writeBlock(stir, out)
        } else {
            this.writeInlineSequence(stir, out)
        }
        if (out.notesInReferencedOrder.length > 0) {
            out.phase = 'footnote'
            if (this.quirks === 'gfm') {
                out.buf += '<section class="footnotes" data-footnotes="">\n<ol>'
            } else if (this.quirks === 'pandoc') {
                out.buf += '<aside class="footnotes footnotes-end-of-document" id="footnotes" role="doc-endnotes">\n<hr>\n<ol>'
            } else {
                out.buf += '<aside class="footnotes" role="doc-endnotes" epub:type="endnotes"><ol>'
            }
            for (const note of out.notesInReferencedOrder) {
                console.debug(`footnote ${note.id}`)
                this.writeBlock(note, out)
            }
            if (this.quirks === 'gfm') {
                out.buf += '</ol></section>'
            } else {
                out.buf += '</ol></aside>'
            }
        }
        return out.buf
    }

    writeText (t: string, out: HTMLOut): void {
        out.buf += out.renderPlainText(t)
    }

    // Helper function to produce an HTML tag.
    tag (name: string, attrs: AttrList = [], selfClosing: boolean, out: HTMLOut): void {
        out.buf += '<'
        out.buf += name
        if (attrs && attrs.length > 0) {
            let i = 0
            let attrib
            while ((attrib = attrs[i]) !== undefined) {
                out.buf += ' '
                out.buf += attrib[0]
                out.buf += '="'
                out.buf += escapeAttrib(attrib[1])
                out.buf += '"'
                i++
            }
        }
        if (selfClosing) {
            out.buf += ' />'
        } else {
            out.buf += '>'
        }
    }

    encodeHrefAttributeValue (href: string): string {
        try {
            return encode(href, this.notEscaped)
        } catch (err) {
            return href
        }
    }

    /**
     * todo[api, perf, arch] I need to revisit how i'm attaching BlockWriters and
     * InlineWriters to instances of Stir2Html and other AbstractWriter classes.
     *
     * - the register methods use bind to bind `this` to *this* instance of
     *   Stir2Html. But bind doesn't work for arrow functions. It happens to
     *   work the few cases I've used register so far because:
     *   - the functions don't reference `this`
     *   - the arrow function binds to the Stir2Bootstrap instance, which is
     *     fine because it's `super` is *this* instance of Stir2Html.
     *   - in `genCaseExpectedVals.ts` I use a regular function, for which
     *     bind does work (but it didn't work until I added `bind` to the
     *     register method of Stir2Markdown)... a bug I had to figure out.
     *
     *   In other words, I don't like the fragility of it all.
     *
     * - Also, is there a performance or code complexity costs to how I define
     *   those writers (type of function, closure scope, etc, use of bind)?
     *
     *   What is the maximally lightweight way for the desired function code to
     *   be executed? Am I overthinking this? Though according to
     *   https://delicious-insights.com/en/posts/call-and-apply-in-javascript/:
     *
     *   > arrow functions at call time do not define extra entries in the Function
     *   > Environment Record mentioned earlier.
     *
     *   and later it says that the lexically bound references in arrow functions
     *   are fixed, so if it weren't for the need to bind `this`, would arrow
     *   functions be best? What about not using `this` at all and making
     *   the AbstractWriter an explicit parameter?
     *
     * - I THINK THE RIGHT THING TO DO IS USE THE SAME METHOD to setup the
     *   built-ins and the custom writers currently added via the register
     *   methods.
     *
     * - I'm currently using arrow functions for the built-ins (i.e. see below).
     *   This means distinct instances of the functions for each instantiation
     *   of Stir2Html, but (1) we don't make many instances and (2) i have a
     *   feeling the JS engine detects this and shares the Function object.
     *   There are getting lexically tied to the `this` of the init___ methods,
     *   which of course is *this* Stir2Html.
     *
     * @returns {{[p: string]: BlockWriter}}
     */
    initBlockWriters (): { [index: string]: BlockWriter } {
        const writers: { [index: string]: BlockWriter } = {}

        writers['structured_text_intermediate_representation'] = (b: ir.Block, out: HTMLOut) => {
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
        }

        writers['paragraph'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            this.tag('p', attrs, false, out)
            this.writeInlineSequence(b.body as ir.InlineSequence, out)
            out.buf += '</p>\n'
        }

        writers['section'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            const elemID = this.elemID4Node(b, out)
            if (elemID) {
                attrs.push(['id', elemID])
            }
            //todo![stir] can't a class be attached to any stir node?
            // What about `subtype` which is currently used by interjection?
            // We are adding these things to the content model as we need
            // them for the style sheets I'm writing, but the whole point is
            // that users can write new stylesheets without writing any code,
            // certainly not have to update this class! EVEN if we switch to
            // a template-based stir2html, most stylesheets shouldn't have to
            // modify the default templates for the predefined stir types, and
            // this writer or the template-based writer should still be able
            // to provide a reasonable rendering for new subtypes for which
            // custom templates aren't supplied.
            //   1. We probably need to support `class` on all stir nodes, which
            //      any stylesheet can use however it wants.
            //   2. `subtype` on interjection should just be replaced with `class`,
            //      unless we decide there is a semantic difference. Then we must
            //      decide if `subtype` is an interjection-specific content model
            //      element, of if we should support it for others, perhaps all.
            if (b.class) {
                attrs.push(['class', b.class as string])
            }
            this.tag('section', attrs, false, out)
            out.buf += '\n<h'
            out.buf += b.level
            out.buf += '>'
            this.writeInlineSequence(b.title as ir.InlineSequence, out)
            out.buf += '</h'
            out.buf += b.level
            out.buf += '>\n'
            this.writeBlockSequence(b.body as ir.BlockSequence, out)
            out.buf += '</section>\n'
        }

        writers['heading'] = (b: ir.Block, out: HTMLOut) => {
            const tag = 'h' + b.level
            const attrs = this.createAttrList(b)
            const elemID = this.elemID4Node(b, out)
            if (elemID) {
                attrs.push(['id', elemID])
            }
            this.tag(tag, attrs, false, out)
            this.writeInlineSequence(b.body as ir.InlineSequence, out)
            this.tag('/' + tag, [], false, out)
            out.buf += '\n'
        }

        // todo[ptss] This is the default rendering of the base type, Block.
        //   - all conforming implementations must render every one of the
        //     the built-in types, even Block. Why? because someone might
        //     base a custom type off of it (because none of the other more
        //     specific styles make sense. in which case, this default Block
        //     rendering will be activated.
        //   - ONCE WE DEFINE THE RULES, we'll implement them here and for all
        //     blocks. specifically: (1) all properties should be rendered, e.g.
        //     name-value pairs written as HTML attributes.
        writers['block'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            if (b.class) {
                attrs.push(['class', b.class as string])
            }
            this.tag('div', attrs, false, out)
            out.buf += '\n'
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.buf += '\n</div>\n'
        }

        /**
         * We CANNOT name this type 'literal_block' because that would imply simple
         * text content, not formatted inlines.
         *
         * The choices are:
         *  - 'pre': well established HTML name and semantics
         *  - 'literallayout': DocBook's name
         *     https://tdg.docbook.org/tdg/4.5/literallayout-x.html
         *  - see STIR Spec doc for other ideas.
         */
        writers['pre'] = (b: ir.Block, out: HTMLOut) => {
            if (this.quirks === 'pandoc') {
                const attrs = this.createAttrList(b)
                attrs.push(['class', 'line-block'])
                this.tag('div', attrs, false, out)
                if (b.body) {
                    const save = out.renderPlainText
                    out.renderPlainText = pandocRenderPlainText
                    this.writeInlineSequence(b.body as ir.InlineSequence, out)
                    out.renderPlainText = save
                }
                out.buf += '</div>\n'
                return
            }
            const attrs = this.createAttrList(b)
            this.tag('pre', attrs, false, out)
            if (b.body) {
                this.writeInlineSequence(b.body as ir.InlineSequence, out)
            }
            out.buf += '</pre>\n'
        }

        writers['code_block'] = (b: ir.Block, out: HTMLOut) => {
            const infoWords = b.info ? b.info.split(/\s+/) : []
            const attrs = this.createAttrList(b)
            // todo[gfm] GitHub's spec tests are now out of sync with what it
            //  actually does (per GitHub Markdown REST API, which genCaseExpectedVals.ts
            //  now can get). For now adhering to spec. If we want to adhere to actual
            //  GitHub behavior, do one or both of the following:
            //   1. let genCaseExpectedVals.ts update the gfm case files, but commit
            //      the changes into a distinct commit above the base stylesheet/
            //      commit, as I've done with the DISABLED cases. Basically put what
            //      I wrote above in the commit message.
            //   2. Submit a bug with GitHub against their spec repo: asking them
            //      to clarify whether the spec is still their spec, or whether it
            //      is no longer maintained.
            /*
            if (this.quirks === 'gfm') {
                if (infoWords.length > 0 && infoWords[0].length > 0) {
                    attrs.push(['lang', escapeAttrib(infoWords[0])])
                }
                this.tag('pre', attrs, false)
                out.buffer += '<code>'
            } else
            */
            if (this.quirks === 'pandoc') {
                if (infoWords.length > 0 && infoWords[0].length > 0) {
                    attrs.push(['class', infoWords[0]])
                }
                this.tag('pre', attrs, false, out)
                out.buf += '<code>'
            } else {
                // default render of language info per
                // https://talk.commonmark.org/t/fenced-code-blocks-should-add-class-to-code-rather-than-pre-matching-the-html-best-practice/136
                if (infoWords.length > 0) {
                    if (infoWords[0].length > 0) {
                        attrs.push(['class', 'language-' + infoWords[0]])
                    }
                    //! most implementations ignore everything other than the first word
                    if (infoWords.length > 1) {
                        //
                    }
                }
                out.buf += '<pre>'
                this.tag('code', attrs, false, out)
            }
            if (b.body) {
                out.buf += escapeText(b.body as string)
            }
            out.buf += '</code></pre>\n'
        }

        // todo[stir] TBD whether `verse` should be a built-in STIR type.
        //  See notes in STIR docs.
        //  -
        //  Currently it renders exactly like `pre`, and if that doesn't change,
        //  there is no point in having it.
        //  -
        //  I'm don't think `verse` is *semantically* a sub-type of `pre`, even
        //  if *presentationally* it may be. Which may be an argument for making
        //  it a built-in (or at least coming up with a more generic thing that
        //  `verse` is a subclass of).
        writers['verse'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            this.tag('pre', attrs, false, out)
            if (b.body) {
                this.writeInlineSequence(b.body as ir.InlineSequence, out)
            }
            out.buf += '</pre>\n'
        }

        writers['thematic_break'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            this.tag('hr', attrs, true, out)
            out.buf += '\n'
        }

        writers['block_quote'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            this.tag('blockquote', attrs, false, out)
            out.buf += '\n'
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.buf += '</blockquote>\n'
        }

        writers['interjection'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            attrs.push(['class', 'interjection ' + b.subType as string])
            // todo[html] TEMP HACK? I haven't thought through whether `icon`
            //  is a standard part of the STIR interjection type, or just a
            //  Gracedown/RepoDoc/RubyRhod customization. If it is the latter,
            //  then it does not belong in HTMLOut, since this class should be
            //  the *reference* and generic implementation of STIR to HTML. If
            //  that is the case, there are two proper ways to go for rendering
            //  Gracedown/RepoDoc/RubyRhod customizations:
            //   1. Extend this class and plug it into stir2html
            //   2. Do it ALL in CSS. (i.e. not write the custom `icon` field).
            if (b.icon && typeof b.icon === 'string') {
                attrs.push(['icon', b.icon])
            }
            this.tag('div', attrs, false, out)
            out.buf += '\n'
            if (b.title) {
                // todo[HTML] My current understanding is that you don't put the
                //   title in an attribute of the div, e.g. According to MDN:
                //   > Text to be displayed in a tooltip when hovering over the element.
                //   and also i read somewhere the content belongs in the content,
                //   and that the heading tag is *the* way to represent headings.
                // todo[rollout] include this as an open question, and an invitation
                //   to an HTML expert to join the team.
                const titleAttrs = createEmptyAttrList()
                // todo[html] h3 is not thought through. quick hack
                this.tag('h3', titleAttrs, false, out)
                this.writeInlineSequence(b.title as ir.InlineSequence, out)
                this.tag('/h3', [], false, out)
            }
            if (b.body) {
                this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            }
            out.buf += '\n</div>\n'
        }

        writers['list'] = (b: ir.Block, out: HTMLOut) => {
            const tag = b.listOrdered ? 'ol' : 'ul'
            const attrs = this.createAttrList(b)
            const start = b.listStartNum
            if (start != undefined && start !== 1) {
                attrs.push(['start', start.toString()])
            }
            if (this.quirks === 'pandoc'
                // todo[quirks] TEMP HACK: I don't think we should rely on "source"
                //   for this in production code. BUT NOT SURE: ✅=pro, ❌=con
                //   - ❌ per spec, source is optional
                //   - ❌ we might run a mode where source is not written.
                //   - ✅ this has nothing to do with the spec, only Textplain's
                //     mimicry of Pandoc rendering to the T. So this is not necessarily
                //     a hack, though the above to issues still must be considered.
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore
                && b.source?.style?.styleSheet?.name === 'Pandoc Markdown') {
                if (b.listStyle === 'decimal') {
                    attrs.push(['type', '1'])
                }
                let allAllCheckboxes = true
                for (const listItem of b.body as ir.BlockSequence) {
                    const liBody = listItem.body as ir.BlockSequence
                    if (liBody.length > 0) {
                        const firstChild = liBody[0]
                        if (firstChild.type === 'paragraph') {
                            if (((firstChild.body as ir.InlineSequence)[0] as ir.Inline).type === 'checkbox') {
                                if (typeof (firstChild.body as ir.InlineSequence)[1] === 'string') {
                                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                                    // @ts-ignore
                                    (firstChild.body as ir.InlineSequence)[1] = (firstChild.body as ir.InlineSequence)[1].trimStart()
                                }
                            } else {
                                allAllCheckboxes = false
                            }
                        } else {
                            allAllCheckboxes = false
                            break
                        }
                    }
                }
                if (!b.listOrdered && allAllCheckboxes) {
                    attrs.push(['class', 'task-list'])
                }
            }
            this.tag(tag, attrs, false, out)
            out.buf += '\n'

            const itemWriter = this.bWriters.list_item
            const flags = {compact: b.hints['commonmark.list.tight'] === true}
            for (const item of (b.body as ir.BlockSequence)) {
                itemWriter(item, out, flags)
            }

            this.tag('/' + tag, [], false, out)
            out.buf += '\n'
        }

        writers['list_item'] = (b: ir.Block, out: HTMLOut, flags?) => {
            const attrs = this.createAttrList(b)
            this.tag('li', attrs, false, out)
            if (b.body === undefined) {
                // empty item
                out.buf += '</li>\n'
            } else if (ir.isBlockSequence(b.body)) {
                const bodySequence = b.body as ir.BlockSequence
                if (flags?.compact) {
                    if (bodySequence[0].type !== 'paragraph') {
                        out.buf += '\n'
                    }
                    for (let i = 0; i < bodySequence.length; i++) {
                        const bodyItem = bodySequence[i]
                        if (bodyItem.type === 'paragraph') {
                            this.writeInlineSequence(bodyItem.body as ir.InlineSequence, out)
                            if (i < bodySequence.length - 1) {
                                out.buf += '\n'
                            }
                        } else {
                            this.writeBlock(bodyItem, out)
                        }
                    }
                    out.buf += '</li>\n'
                } else {
                    out.buf += '\n'
                    this.writeBlockSequence(bodySequence, out)
                    out.buf += '</li>\n'
                }
            } else {
                throw new Error(`invalid list item body: ${b.body}`)
            }
        }

        writers['table'] = (b: ir.Block, out: HTMLOut) => {
            const attrs = this.createAttrList(b)
            this.tag('table', attrs, false, out)
            out.buf += '\n'

            const headRows = b.head as ir.BlockTable
            const bodyRows = b.body as ir.BlockTable
            const headMeta = b.tableMeta?.head as ir.ColumnMeta[]
            const bodyMeta = b.tableMeta?.body as ir.ColumnMeta[]

            if (headRows) {
                const attrByCol = genPerColumnCellAttribs(headMeta, this.quirks === 'gfm')
                out.buf += '<thead>\n'
                for (const headRow of headRows) {
                    out.buf += '<tr>'
                    for (let i = 0; i < headRow.length; i++) {
                        const cell = headRow[i]
                        if (attrByCol[i]) {
                            this.tag('th', attrByCol[i], false, out)
                        } else {
                            out.buf += '<th>'
                        }
                        this.writeBlock(cell, out)
                        out.buf += '</th>'
                    }
                    out.buf += '</tr>\n'
                }
                out.buf += '</thead>\n'
            }

            if (bodyRows !== undefined) {
                const attrByCol = genPerColumnCellAttribs(bodyMeta, this.quirks === 'gfm')
                if (bodyRows.length > 0 || this.quirks === 'pandoc') {
                    out.buf += '<tbody>\n'
                }
                for (const headRow of bodyRows) {
                    out.buf += '<tr>'
                    for (let i = 0; i < headRow.length; i++) {
                        const cell = headRow[i]
                        if (attrByCol[i]) {
                            this.tag('td', attrByCol[i], false, out)
                        } else {
                            out.buf += '<td>'
                        }
                        this.writeBlock(cell, out)
                        out.buf += '</td>'
                    }
                    out.buf += '</tr>\n'
                }
                if (bodyRows.length > 0 || this.quirks === 'pandoc') {
                    out.buf += '</tbody>\n'
                }
            }

            out.buf += '</table>\n'
        }

        writers['cell'] = (b: ir.Block, out: HTMLOut) => {
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
        }

        //todo![footnotes] these might be stir2html options:
        //  - whether footnotes have backlinks
        //    - whether backlink points back at the footnote reference
        //      or the containing element (e.g. the paragraph containing
        //      the footnote reference).
        writers['note'] = (b: ir.Block, out: HTMLOut) => {
            if (out.phase === 'main') {
                // do nothing
                // todo![now, references] we should track notes created
                //   but never references, and log error/warning.
                //   SHOULD ALSO do this for named values if we are not already.
            } else {
                const noteNum = out.notesInReferencedOrder.indexOf(b) + 1
                const attrs = this.createAttrList(b)
                attrs.push(['id', out.stirID2htmlID[b.id as string]])
                this.tag('li', attrs, false, out)
                out.buf += '\n'
                this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
                const backrefs = out.noteBackRefs[b.id as string]
                if (backrefs?.length > 0) {
                    // todo![now, references]  LET THIS HACK STAND?
                    //   - what's worse is that it depends on there being a newline as well.
                    const endsWithPara = out.buf.endsWith('</p>\n')
                    if (endsWithPara) {
                        out.buf = out.buf.slice(0, -5)
                    }
                    for (let i = 0; i < backrefs.length; i++){
                        const backref = backrefs[i]
                        if (this.quirks === 'pandoc') {
                            out.buf += `<a href="#${backref}" class="footnote-back" role="doc-backlink">↩︎</a>`
                        } else if (this.quirks === 'gfm') {
                            out.buf += ` <a href="#${backref}" class="footnote-backref" data-footnote-backref=""`
                            if (i===0) {
                                out.buf += ` aria-label="Back to reference ${noteNum}">↩</a>`
                            } else {
                                out.buf += ` aria-label="Back to reference ${noteNum}-${i+1}">↩<sup class="footnote-ref">${i+1}</sup></a>`
                            }
                        } else {
                            out.buf += ` <a href="#${backref}" class="footnote-backref" role="doc-backlink">↩︎</a>`
                        }
                    }
                    if (endsWithPara) {
                        out.buf += '</p>\n'
                    }
                }
                out.buf += '\n</li>\n'
            }
        }

        writers['html_block'] = (b: ir.Block, out: HTMLOut) => {
            if (this.safe) {
                out.buf += '<!-- raw HTML omitted -->'
            } else {
                out.buf += b.body as string
            }
            out.buf += '\n'
        }

        return writers
    }


    initInlineWriters (): { [index: string]: InlineWriter } {
        const writers: { [index: string]: InlineWriter } = {}

        // todo[stir]  not sure why we have this as an element
        writers['linebreak'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<br>\n'
        }

        writers['xref'] = (i: ir.Inline, out: HTMLOut) => {
            const attrs: AttrList = []
            // todo![now, references] this will fail if the reference occurs before the referent
            const href = typeof i.referent === 'object'
                ? '#' + this.elemID4Node(i.referent, out)
                : i.referent as string
            if (!(this.safe && potentiallyUnsafe(href))) {
                attrs.push(['href', this.encodeHrefAttributeValue(href)])
            }
            if (i.desc) {
                attrs.push(['title', i.desc])
            }
            this.tag('a', attrs, false, out)
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</a>'
        }

        writers['note_ref'] = (i: ir.Inline, out: HTMLOut) => {
            if (this.quirks === 'gfm') {
                out.buf += '<sup class="footnote-ref">'
            } else if (this.quirks === 'none') {
                if (out.buf.endsWith('</sup>')) out.buf += ' '
                out.buf += '<sup class="footnote-ref">'
            }
            const attrs: AttrList = []
            const referent = i.referent as ir.Block
            const noteID = this.elemID4Node(referent, out) as string
            const noteHREF = '#' + noteID
            const noteNum = out.notesInReferencedOrder.indexOf(referent) + 1

            let refID = noteID?.replace(/fn/, 'fnref')
            if (refID in out.takenHtmlIDs) {
                let newID
                // we start dupes with `-2` because having `some_heading` and `some_heading-1` is confusing
                for (let i = 2; i < Number.MAX_VALUE; i++) {
                    newID = `${refID}-${i}`
                    if (!(newID in out.takenHtmlIDs)) {
                        refID = newID
                        break
                    }
                }
            }
            out.takenHtmlIDs[refID] = true
            if (out.noteBackRefs[referent.id as string] === undefined) {
                out.noteBackRefs[referent.id as string] = [refID]
            } else {
                out.noteBackRefs[referent.id as string].push(refID)
            }

            attrs.push(['id', refID])
            // todo![now, references] do we need this potentiallyUnsafe check for note_ref?
            //   maybe we do as PTSS could allow a URL for referent instead of a local element
            if (!(this.safe && potentiallyUnsafe(noteHREF))) {
                attrs.push(['href', this.encodeHrefAttributeValue(noteHREF)])
            }
            if (this.quirks === 'pandoc') {
                attrs.push(['class', 'footnote-ref'])
                attrs.push(['role', 'doc-noteref'])
            } else if (this.quirks === 'gfm') {
                attrs.push(['data-footnote-ref', ''])
            } else {
                attrs.push(['epub:type', 'noteref'])
                attrs.push(['role', 'doc-noteref'])
            }
            this.tag('a', attrs, false, out)
            if (this.quirks === 'pandoc') {
                out.buf += `<sup>${noteNum}</sup>`
            } else {
                out.buf += noteNum
            }
            // todo![now, references] the space can't be right!!!! Either NO space, or after the sup?
            out.buf += '</a>'
            if (this.quirks !== 'pandoc') {
                out.buf += '</sup>'
            }
        }

        writers['image'] = (i: ir.Inline, out: HTMLOut) => {
            // todo![now, references] this will fail if the reference occurs before the referent
            const href = typeof i.referent === 'object'
                ? '#' + this.elemID4Node(i.referent, out)
                : i.referent as string
            if (this.safe && potentiallyUnsafe(href)) {
                out.buf += '<img src="'
            } else {
                out.buf += '<img src="'
                out.buf += this.encodeHrefAttributeValue(href)
            }
            if (i.body || !(this.quirks === 'pandoc')) {
                out.buf += '" alt="'
                this.writeInlineSequence(ir.inlineBodyText(i.body as ir.InlineSequence), out)
            }
            if (i.desc) {
                out.buf += '" title="'
                out.buf += i.desc
            }
            out.buf += '" />'
        }

        writers['emph'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<em>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</em>'
        }

        writers['strong'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<strong>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</strong>'
        }

        writers['strike'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<del>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</del>'
        }

        writers['subscript'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<sub>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</sub>'
        }

        writers['superscript'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<sup>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</sup>'
        }

        writers['highlight'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<mark>'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '</mark>'
        }

        writers['code'] = (i: ir.Inline, out: HTMLOut) => {
            out.buf += '<code>'
            out.buf += escapeText(i.body as string)
            out.buf += '</code>'
        }

        writers['checkbox'] = (i: ir.Inline, out: HTMLOut) => {
            if (i.checked === true) {
                out.buf += this.checkboxChecked
            } else {
                out.buf += this.checkboxUnchecked
            }
        }

        writers['html_inline'] = (i: ir.Inline, out: HTMLOut) => {
            if (this.safe) {
                out.buf += '<!-- raw HTML omitted -->'
            } else {
                out.buf += i.body as string
            }
        }

        return writers
    }

}


function createEmptyAttrList (/* ire: ir.Block */): AttrList {
    return []
}

function createAttrListWithSourceInfo (ire: ir.Block): AttrList {
    if (!ire.source) {
        return []
    }

    const att: AttrList = []
    const lineStart = ire.source.lineStart
    const lineEnd = ire.source.lineEnd
    if (lineStart != undefined) {
        att.push(['data-src-line', String(lineStart) + '-' + String(lineEnd)])
    }
    return att
}

function genPerColumnCellAttribs (columns: ir.ColumnMeta[] | undefined, gfmQuirk: boolean): (AttrList | undefined)[] {
    const perColAttribs: (AttrList | undefined)[] = []
    if (columns) {
        if (gfmQuirk) {
            // The `th` `align` attribute has been
            // [deprecated by current HTML spec](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/th#deprecated_attributes)
            // in favor of using the globally supported `style` attribute. GitHub has chosen
            // not to change its main spec and instead support HTML5 conforming output with
            // a parser switch: `cmark-gfm  -e table --table-prefer-style-attributes`.
            for (const c of columns) {
                switch (c.align) {
                    case 'L':
                        perColAttribs.push([['align', 'left']])
                        break
                    case 'R':
                        perColAttribs.push([['align', 'right']])
                        break
                    case 'C':
                        perColAttribs.push([['align', 'center']])
                        break
                    default:
                        perColAttribs.push(undefined)
                }
            }
        } else {
            for (const c of columns) {
                switch (c.align) {
                    case 'L':
                        perColAttribs.push([['style', 'text-align:left']])
                        break
                    case 'R':
                        perColAttribs.push([['style', 'text-align:right']])
                        break
                    case 'C':
                        perColAttribs.push([['style', 'text-align:center']])
                        break
                    default:
                        perColAttribs.push(undefined)
                }
            }
        }
    }
    return perColAttribs
}

const reTextSpecial = /[\n&<>]/g
export const pandocRenderPlainText = function (s: string): string {
    if (reTextSpecial.test(s)) {
        return s.replace(reTextSpecial, function (s: string): string {
            switch (s) {
                case '\n':
                    return '<br>'
                case '&':
                    return '&amp;'
                case '<':
                    return '&lt;'
                case '>':
                    return '&gt;'
                default:
                    return s
            }
        })
    } else {
        return s
    }
}


const reUnsafeProtocol = /^javascript:|vbscript:|file:|data:/i
const reSafeDataProtocol = /^data:image\/(?:png|gif|jpeg|webp)/i

const potentiallyUnsafe = function (url: string): boolean {
    return reUnsafeProtocol.test(url) &&
           !reSafeDataProtocol.test(url)
}


// todo![now] this duplicates a lot of code from elemID4Node_Default
function elemID4Node_Pandoc (node: ir.Block | ir.Inline,
                             out: HTMLOut): string | undefined {
    if (ir.isInline(node)) {
        throw Error('not implemented')
    }
    const stirID = node.id as string
    if (stirID === undefined) {
        return undefined
    }
    if (out.stirID2htmlID[stirID]) {
        return out.stirID2htmlID[stirID]
    }
    let htmlID
    if (node.type === 'note') {
        if (node.subType !== 'footnote') {
            throw Error('not implemented')
        }
        let n = out.notesInReferencedOrder.indexOf(node)
        if (n === -1) {
            out.notesInReferencedOrder.push(node)
            htmlID = 'fn' + out.notesInReferencedOrder.length
        } else {
            n++
            htmlID = 'fn' + n
        }
    } else {
        // https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/id
        htmlID = stirID.toLowerCase()
            .replace(/[^\p{L}\p{N}\s_./-]+/gu, '')
            .trim()
            .replace(/\s+/g, '-')
            .replace(/^[^\p{L}]+/gu, '')
            .replace(/^$/, 'section')
        if (htmlID in out.takenHtmlIDs) {
            let newID
            // Pandoc starts with `-1` for dupes
            for (let i = 1; i < Number.MAX_VALUE; i++) {
                newID = `${htmlID}-${i}`
                if (!(newID in out.takenHtmlIDs)) {
                    htmlID = newID
                    break
                }
            }
        }
    }

    out.stirID2htmlID[stirID] = htmlID
    out.takenHtmlIDs[htmlID] = true
    return htmlID
}

function elemID4Node_GFM (node: ir.Block | ir.Inline,
                          out: HTMLOut): string | undefined {
    if (ir.isInline(node)) {
        throw Error('not implemented')
    }
    const stirID = node.id as string
    if (stirID === undefined) {
        return undefined
    }
    if (out.stirID2htmlID[stirID]) {
        return out.stirID2htmlID[stirID]
    }
    let htmlID
    if (node.type === 'note') {
        if (node.subType !== 'footnote') {
            throw Error('not implemented')
        }
        htmlID = 'fn-' + stirID.slice(1)
        if (out.notesInReferencedOrder.indexOf(node) === -1) {
            out.notesInReferencedOrder.push(node)
        }
    } else {
        // https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/id
        htmlID = stirID.toLowerCase()
            .replace(/[^\p{L}\p{N}\s_./-]+/gu, '')
            .trim()
            .replace(/\s+/g, '-')
            .replace(/^$/, 'section')
        if (htmlID in out.takenHtmlIDs) {
            let newID
            // we start dupes with `-2` because having `some_heading` and `some_heading-1` is confusing
            for (let i = 2; i < Number.MAX_VALUE; i++) {
                newID = `${htmlID}-${i}`
                if (!(newID in out.takenHtmlIDs)) {
                    htmlID = newID
                    break
                }
            }
        }
    }

    out.stirID2htmlID[stirID] = htmlID
    out.takenHtmlIDs[htmlID] = true
    return htmlID
}

