export const provider: RenderSTIRProvider = {
    async configure (config?: Config): Promise<RenderSTIR> {
        const stir2Html = new Stir2Html(config)
        return function (stir: STIR) {
            return stir2Html.render(stir)
        }
    }
}

registerSTIRRenderer('Stir2Html', provider)

export default provider

