import type {Pipe, PipeProvider} from '../pipe.js'
import {registerPipe} from '../registry.js'
import type {STIR} from '../stir.js'
import * as ir from '../../stir/index.js'

/**
 * NOTE: HTML5 has abandoned this. See notes on "section hierarchy".
 *
 * Converts headings to sections by interpreting them as implicit sections using
 * the [HTML5 And The Document Outlining Algorithm](https://www.smashingmagazine.com/2011/08/html5-and-the-document-outlining-algorithm/)
 *
 * The algorithm should match the well respected: [HTML5 Outliner](https://gsnedders.html5.org/outliner/) tool.
 *
 * into more modern and accessible. See also
 *  - [Semantic HTML Tutorial](https://www.internetingishard.com/html-and-css/semantic-html/)
 *  - [Structural Semantics: The Importance Of HTML5 Sectioning Elements](https://www.smashingmagazine.com/2013/01/the-importance-of-sections/)
 *
 * ONLY THE DIRECT CHILDREN OF THE ROOT NODE PASSED IN WILL BE RESTRUCTURED
 * ------------------------------------------------------------------------
 * This is the correct behavior iff all of the following are true:
 *    - section hierarchies have a scope and do not cross scope boundaries.
 *      For example, the content within a block quote represents a separate
 *      "embedded" structured text context, with its own independent outline.
 *    - The node passed into this function represents the root of such as
 *      sectioning context or "sectioning root". e.g. the root `document`
 *      node, or a `blockquote` node.
 *    - There are in fact no nesting contexts (e.g. block quote, list item)
 *      currently which are not sectioning roots *other than* `section` itself.
 *    - The sectioning root passed has no sections, and is being passed in to
 *      generate them from "flat" headings.
 *
 * Given the above rules being met, this function simply needs to iterate
 * over the the direct `body` children of the passed in sectioning root,
 * and transform the flat structure into a hierarchical one based on
 * headings at the root and their relative heading level values.
 */
const transform: Pipe = (root: STIR) => {
    if (!ir.isBlock(root)) {
        return
    }
    if (root.hasSections()) {
        return
    }

    // todo![now, references] if we define a TS type for the root STIR node,
    //   we can avoid having to do type coercion all over the code.
    const exports: ir.Exports = root.exports as ir.Exports || {}

    // detach current body from root and it give a blank one
    const origBody = root.body
    if (origBody === undefined || !ir.isBlockSequence(origBody)) {
        return
    }
    root.body = undefined

    console.debug(`converting headings to sections...`)

    // we will treat the root itself as a section regardless of its type
    let curSection: ir.Block = root
    const sectionStack: ir.Block[] = []

    // we don't need recursion because all headings are a the root
    // todo[spec] is this *always* true? Will there never be a container that
    //   isn't a sectioning root that contains headings? Will there ever be
    //   a container besides `section` that isn't a sectioning root?
    for (const b of origBody) {
        if (b.type === 'heading') {
            // create section from heading
            const newSection = new ir.Block('section')
            // todo[spec]: Is copying over all props like this safe?
            //  - WE KNOW what fields *make* a `heading` (they're part of the
            //    built-in definition), so we know exactly what to exclude
            //  - all other fields at worst are extraneous. But if a user
            //    defined custom data for a heading, it most likely still
            //    applies to the `section`. Why wouldn't it?
            //    - The only possibility is that they defined a custom subtype
            //      of `heading`, with props that don't belong in `section`.
            //      - At worst it's extraneous.
            //      - MOST IMPORTANT: if they are doing a subclass of `heading`
            //        why would they use this pipe?!?! They should change their
            //        def to `section` from the get-go.
            //  - Positive Unintended Consequences: the Section has the Source
            //    for the heading, with line numbers as well as name of style of
            //    the source element (as opposed to a style element, since
            //    there was none. This is useful and accurate, since the source
            //    *was* an *ATX Heading* or *Setext Heading* on line 27.
            for (const k in b) {
                if (k !== 'type' && k !== 'body') {
                    newSection[k] = b[k]
                }
            }
            newSection.title = b.body as ir.InlineSequence
            newSection.level = b.level

            // todo![now, references] if `exports` were a dynamically generated
            //  property rather than a static one, always reflecting the current
            //  state of the STIR, we wouldn't need to remember to do the
            //  following when converting headings to sections. This also
            //  violates Codd's notion of "normal form"; even though this isn't
            //  an RDBMS the principle is arguably a good one.
            if (exports[b.id as string]) {
                exports[b.id as string] = {title: newSection.title}
            }

            if (curSection === root || (curSection.level as number) < (newSection.level as number)) {
                // is child of curSection
                curSection.appendToBody(newSection)
                sectionStack.push(curSection)
                curSection = newSection
            } else if ((curSection.level as number) === (newSection.level as number)) {
                // is sibling of curSection
                curSection = sectionStack.pop() as ir.Block
                curSection.appendToBody(newSection)
                sectionStack.push(curSection)
                curSection = newSection
            } else {
                // is sibling of ancestor of curSection
                do {
                    curSection = sectionStack.pop() as ir.Block
                } while (curSection !== root && (curSection.level as number) >= (newSection.level as number))
                curSection.appendToBody(newSection)
                sectionStack.push(curSection)
                curSection = newSection
            }
        } else {
            curSection.appendToBody(b)
        }

    }

    return
}

async function configure (): Promise<Pipe> {
    return transform
}

const provider: PipeProvider = {
    async configure (/*config?: Config*/): Promise<Pipe> {
        return configure()
    }
}

registerPipe('ConvertHeadingsToSections', provider)

export default provider

