import type {File} from './file.js'

import posix from 'path-browserify'

// see to-do on Node.title for proper determination
export function defaultFileTitle (file: File): string {
    if (file.isOpeningContent()) {
        if (file.parent.name === '/') {
            return '🏠' //language independent default
        } else {
            return niceNameFromFilename(file.parent.name?.slice(0, -1))
        }
    }
    return niceNameFromFilename(posix.parse(file.name).name)
}

function niceNameFromFilename (n: string) {
    // todo: any conversions we want to do (e.g. replace `_` with space)
    // should go here
    return n
}
