import type {Assembly} from './assembly.js'
import type {Subfolder} from './subfolder.js'
import type {File} from './file.js'
import type {Config} from '../config.js'
import type {AssemblyPipeline} from '../pipeline.js'


//todo[perf] some method implementations of of the various Node types
// use recursion. Can easily replace with non-recursive versions, no?


export type Node = Assembly | Subfolder | File
export type Folder = Assembly | Subfolder

export interface NodeInterface {
    /**
     * File/folder name.
     * The last element in the path to this node within the assembly.
     */
    name: string
    parent: Folder | undefined

    // todo[stir] TITLE and other metadata needs to come out of the STIR:
    // - It's needed for standalone file/input rendering, not just text assembly
    // - Title should be determined by the content, per the stylesheet's design
    // - When a format doesn't support such metadata, we will follow TextAssembly
    //   defined conventions/heuristics for determining a title (see TA spec)
    //   - This might be implemented as a pipe. See Textplain/textassembly
    //     Pipeline architecture doc for design discussion.
    //
    // todo[textassembly] there should be two kinds of titles:
    // - contextual title (aka short title): the title that makes sense
    //   in context, usually the simple name of the file, e.g.
    //   "System Architecture" or "Chapter Two".
    // - acontextual title (aka long or full title): e.g.
    //    "TextPlain System Architecture" or "The Good Lord Bird, Chapter Two".
    //    - this could be supplied by the content metadata
    //    - if not, it *might* be included in the conventions/heuristics defined
    //      by Text Assembly spec to construct a default one.
    //    - or we could leave it to the templates to construct one, as the
    //      template (1) has visibility to the context, (2) represents the page-
    //      specific design choices that would govern what a long title should
    //      look like or include and (3) has everything needed to construct such
    //      a title and insert it the HTML header or what-not.
    //    - In the case that OTHER pages need make reference to a page's long
    //      title, then we might want to include it in the TextAssembly spec,
    //      OR leave it to the user to insert a pipe that constructs a
    //      long title earlier in the pipeline so that it is accessible to other
    //      pages.
    //    - Remember, whatever solution is chosen, it should work reasonably
    //      for the ZERO CONFIG case.
    // For Folder, should come from opening content
    //    - should interface define this as a method call, so that for
    //      folder it retrieves it from opening content?
    title: string | undefined

    // whether a resource should appear in TOC or other indexes.
    // For example:
    //   - structured text file that renders to its own page in the
    //     output assembly
    //   - NOT a structured text file that is meant to be embedded
    //     in other files, not render as a standalone page
    //   - NOT an image that is embedded in in a page
    //   - an image in a folder that is is meant to render as an image
    //     gallery or slideshow, without need of any containing text
    //     container/document. Such an image behaves like a structured
    //     text file. There may even be images that support outbound links
    //     that need to be resolved by just like for text pages.
    // How a TextAssembly specifies this is TBD per the TextAssembly spec.
    //
    // todo[textassembly doc] For example, it could be that as follows:
    //  - structured text file: default TRUE
    //  - structured text file whose name is prefixed with an underscore: FALSE
    //  - image files: default FALSE
    //  - image files prefixed with +: TRUE
    isIndexable: boolean

    /**
     * This field exists purely for use by renderer implementations as they
     * see fit to map this Node to one or more corresponding output nodes, each
     * of which may or may not be its own output file node as this Node is.
     *
     * Or as put in the Pipeline Architecture docs:
     * > 🚩 **There is not necessarily a one-to-one mapping of input to output.**
     * > For example, 20 files on the input side might aggregate to a single
     * > file in the output. Or vice-versa, a single input file might multiplex
     * > to two or more output files. And both can occur: 20 files map to a
     * > single logical output, which is then multiplexed to multiple physical
     * > outputs.
     *
     * Current Implementation Plan
     * ===========================
     * **If the value is a string**, it means that this text assembly node has
     * a simple 1:1 mapping to an output node, and its value will be the name on
     * the output side. It likewise implies the same is true for all its parent
     * nodes up the line, such that the path structure of the output parallels
     * the path structure of the input.
     *
     * > Since such a 1:1 corresponding between Text Assembly input files and
     * > AssemblyRendering output files will be the most common scenario, this
     * > field is thus optimized for this case (by being a simple string needing
     * > no interpretation when used for output). It is optimized both in terms of
     * > simplicity and efficiency, in terms of machine memory and CPU, as well as
     * > the meat CPU (my head).
     *
     * **If the value is an `OutNode` data structure**, it means the Node does
     * not have a simple 1:1 mapping to the output. The data structure will
     * describe it.
     *
     * 'OutNode` data structure
     * ------------------------
     * if the value of `outNode` is not a `string` but an `OutNode` data structure,
     * it will mean that this node's output either does not follow the same path
     * structure as its input, and may not even have its own filesystem node, but
     * be aggregated into its parent's output node. The `OutNode` object will
     * contain all the necessary data for whatever the details are.
     *
     * - we will develop this data structure as we implement rendering scenarios.
     */
    //todo[pipeline] This field is not meant for use outside the renderer. i.e.
    // earlier stages in the pipeline CANNOT have any dependencies on this.
    // Is there a way to signify this? Perhaps move this field to a special
    // 'RenderNode' subtype that only the renderer sees?
    //todo[template api] should the Template API expose this?
    outNode: string /*| OutNode */ | undefined

    isFile (): this is File
    isFolder (): this is Folder
    isAssemblyRoot (): this is Assembly

    assemblyRoot (crossAssemblyBoundary?: boolean): Assembly

    /**
     * //! we don't need to construct assembly paths, but RESOLVE them
     *     when referenced in the source.
     *     WE ONLY NEED THIS FOR DEBUG, yeah?
     *
     * this node's path within local assembly
     */
    innerAssemblyPath (): string

    /**
     * //! we don't need to construct assembly paths, but RESOLVE them
     *    when referenced in the source.
     *    WE ONLY NEED THIS FOR DEBUG, yeah?
     *
     * This node's path from the outermost assembly root.
     */
    outerAssemblyPath (): string

    /**
     * //! we don't need to construct assembly paths, but RESOLVE them
     *    when referenced in the source.
     *    WE ONLY NEED THIS FOR DEBUG, yeah?
     *
     * This node's cross-assembly path, externally linkable.
     */
    crossAssemblyPath (): string

    /**
     * resolves the path to a Node in the assembly. Resolves both
     * absolute and relative paths within the (inner) assembly,
     * as well as cross-assembly paths.
     * @param {string} path
     */
    resolvePath (path: string): Node | undefined

    /**
     * searches up the tree for the first File with the given name.
     * Specifically, it searches the immediate content of each folder up the tree.
     * If `this` is a Folder, the search starts with its content.
     */
    searchUpForFile (name: string, crossAssemblyBoundary?: boolean): File | undefined

    /**
     * searches up the tree for the first node with a value for the named property
     */
    searchUpForProperty (property: string, crossAssemblyBoundary?: boolean): { node: Node, value: unknown } | undefined
}

export interface FolderInterface extends NodeInterface {
    //! initially undefined until opening content is determined
    // todo[api] update docs: "opening content' is better than "opening page"
    openingContent: File | undefined

    //! initially includes the opening content File until it is determined
    //  to be such and moved to openingContent property
    readonly content: Node[]
    hasIncludedContent: boolean

    sortSpec: string[] | undefined

    // a config can be defined at any folder node. Any specified settings
    // override those settings otherwise inherited from up tree.
    textplainConfig: Config | undefined

    // a parse/render configuration that applies to this folder
    //  on down, unless overridden further down.
    //
    // todo[arch] having this here is maybe a temp hack
    //   because of this we have assembly render specific
    //   data in what should be a pure representation of
    //   a source TextAssembly.
    //   The alternative is to move this into a separate
    //   index, like a Trie, so that we can match pipeline
    //   rigs by comparing their path to the path of the
    //   file being rendered, and choosing the pipeline
    //   rig at the longest matching path.
    //   BUT, THE ARGUMENT TO KEEP IT HERE is practical.
    //   I think this solution uses the least memory and
    //   CPU. Also this is internal, not part of an API.
    //   Finally loading a TextAssembly structure has but
    //   one purpose: rendering the assembly. So if this
    //   is internal, why is my nose so fussy?
    pipeline: AssemblyPipeline | undefined
}

/**
 * Resolves any assembly path.
 *
 * todo[perf] not using an index on each node, trading CPU for memory. I think
 * cross-refs are relatively rare, so a good tradeoff. An index on each Node
 * will be expensive and memory wasteful. AN ALTERNATIVE is to build a TRIE
 * index for each subassembly, or even a global TRIE index that somehow supports
 * subassembly and cross-assembly paths.
 *
 * todo[test] unit tests
 *
 * @internal
 */
// todo![links] how does this compare to my resolve method in MemFS?
export function resolvePath (path: string, context: Node): Node | undefined {
    path = path.trim()
    if (path === '') return undefined

    let from: Node | undefined
    if (path.startsWith('/')) {
        // if path is an absolute path
        if (path === '/') return context.assemblyRoot()
        from = context.assemblyRoot()
        path = path.slice(1)
    } else {
        // todo![perf] switch to precompiled regex
        const match = path.match(/^{(\w*)}\/(.*)/)
        if (match !== null) {
            // if path is a cross-assembly path
            from = context.assemblyRoot(true).assemblyForID(match[1])
            if (from === undefined) {
                return undefined
            }
            path = match[2]
            if (path === '') return from
        } else {
            // else a relative path, relative to context
            from = context
        }
    }

    let node = from.isFolder() ? from : from.parent
    const isExplicitlyFolderPath = path.endsWith('/')
    const segments = isExplicitlyFolderPath ?
        path.slice(0, -1).split('/') :
        path.slice(0).split('/')

    const last = segments.length - 1
    nextSegment:
        for (let i = 0; i <= last; i++) {
            const s = segments[i]
            if (s === '') {
                return undefined
            }
            if (s === '.') {
                continue
            }
            if (s === '..') {
                if (node.parent === undefined || node.isAssemblyRoot()) {
                    //let caller decide whether to report an error
                    return undefined
                }
                node = node.parent
                continue
            }
            // todo![links] WIP test cases for non-folders in the middle of the path]
            if (s === node.openingContent?.name) {
                return !isExplicitlyFolderPath && i === last ? node.openingContent : undefined
            }
            //todo![links] I REALLY DISLIKE THE LOGIC OF THIS METHOD. We're splitting
            //  on '/', but ta.Folder.name always ends in '/'. Two options:
            //  - redo the logic of this method
            //  - nix the notion of the trailing '/'
            //todo![test] this method really needs unit tests.
            for (const child of node.content) {
                if (s === (child.name.endsWith('/') ? child.name.slice(0, -1) : child.name)) {
                    if (child.isFile()) {
                        return !isExplicitlyFolderPath && i === last ? child : undefined
                    }
                    node = child
                    continue nextSegment
                }
            }
            return undefined
        }

    return node
}
