import {AbstractOut, AbstractWriter} from './abstractWriter.js'
import * as ir from '../../../stir/index.js'
import type {STIR} from '../../stir.js'

// todo: https://github.com/commonmark/commonmark.js/issues/83 might be useful for ideas

export type MarkdownOut = AbstractOut & {
    prefix: string
    isFirstInContainer: boolean
    blockFirstLinePrefix: string | undefined
}

type BlockWriter = (b: ir.Block, out: MarkdownOut, flags?: Record<string, unknown>) => void
type InlineWriter = (i: ir.Inline, out: MarkdownOut) => void

/**
 * todo: this is a quick non-production-ready implementation:
 *   - lacks tests. there would be failures if any existed as
 *   - doesn't handle many rules, such as for fenced blocks
 *     nested inside fenced blocks, doing escapes when necessary,
 *     etc.
 *   - This is an example of a renderer where the rendering of
 *     each node depends on more context. For example, Setext
 *     headings can't interrupt paragraphs, needing a leading
 *     blank line in such cases. But here we ALWAYS insert a
 *     blank line. In the case of Setext, that might be a good
 *     idea anyway. BUT there might be others where it isn't.
 */
export class Stir2Markdown extends AbstractWriter<MarkdownOut> {

    useATX = false

    constructor (options: Record<string, unknown> = {}) {
        super()
        this.useATX = options.useATX === true
        this.initWriters(this.initBlockWriters(), this.initInlineWriters())
    }

    registerBlockWriter (type: string, bw: BlockWriter): void {
        this.bWriters[type] = bw.bind(this)
    }

    registerInlineWriter (type: string, iw: InlineWriter): void {
        this.iWriters[type] = iw.bind(this)
    }

    render (stir: STIR): string {
        const out: MarkdownOut = {
            buf: '',
            prefix: '',
            isFirstInContainer: false,
            blockFirstLinePrefix: undefined
        }
        if (ir.isBlock(stir)) {
            this.writeBlock(stir, out)
        } else {
            this.writeInlineSequence(stir, out)
        }
        return out.buf
    }

    writeText (t: string, out: MarkdownOut): void {
        out.buf += t
    }

    writeBlankLine (out: MarkdownOut): void {
        out.buf += out.prefix.trimEnd()
        out.buf += '\n'
    }

    writeLeadingBlankIfNecessary (out: MarkdownOut): void {
        if (out.isFirstInContainer) {
            out.isFirstInContainer = false
        } else {
            this.writeBlankLine(out)
        }
    }

    writeBlockFirstLinePrefix (out: MarkdownOut): void {
        this.writeLeadingBlankIfNecessary(out)
        if (out.blockFirstLinePrefix) {
            out.buf += out.blockFirstLinePrefix
            out.blockFirstLinePrefix = undefined
        } else {
            out.buf += out.prefix
        }
    }

    writePrefixedTextBlock (textBlock: string, out: MarkdownOut): void {
        if (!textBlock) return

        for (const t of textBlock.split('\n')) {
            out.buf += out.prefix
            out.buf += t
            out.buf += '\n'
        }
    }

    // hack to remove blank line inside the end of a container
    //   that results from the current code's putting a blank
    //   line after every block in a block sequence. Obviously
    //   we can do better but I got higher priorities at the
    //   moment!
    hackRemoveTrailingPrefixedBlankLine (out: MarkdownOut): void {
        const toRemove = out.prefix + '\n'
        if (out.buf.endsWith(toRemove)) {
            out.buf = out.buf.slice(0, -toRemove.length)
        }
    }


    initBlockWriters (): { [index: string]: BlockWriter } {
        const writers: { [index: string]: BlockWriter } = {}

        writers['structured_text_intermediate_representation'] = (b: ir.Block, out: MarkdownOut) => {
            out.isFirstInContainer = true
            this.writeBlockSequence(b.body as ir.BlockSequence, out)
        }

        writers['paragraph'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeBlockFirstLinePrefix(out)
            this.writeInlineSequence(b.body as ir.InlineSequence, out)
            out.buf += '\n'
        }

        writers['section'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
        }

        writers['heading'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeBlockFirstLinePrefix(out)
            if (!this.useATX && (b.level as number) < 3) {
                const before = out.buf.length
                this.writeInlineSequence(b.body as ir.InlineSequence, out)
                const len = out.buf.length - before
                out.buf += '\n'
                out.buf += out.prefix
                const ch = b.level === 1 ? '=' : '-'
                out.buf += ch.repeat(len)
                out.buf += '\n'
            } else {
                out.buf += '#'.repeat(b.level as number)
                out.buf += ' '
                this.writeInlineSequence(b.body as ir.InlineSequence, out)
                out.buf += '\n'
            }
        }

        writers['block'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
            // this.writeIndentedLine(`BLOCK (class=${b.class}):`)
            // out.depth++
            // this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence)
            // out.depth--
        }

        writers['code_block'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeBlockFirstLinePrefix(out)
            out.buf += '```'
            if (b.info) {
                out.buf += b.info
            }
            out.buf += '\n'
            this.writePrefixedTextBlock(b.body as string, out)
            out.buf += out.prefix
            out.buf += '```\n'
        }

        writers['verse'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
        }

        writers['thematic_break'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeBlockFirstLinePrefix(out)
            out.buf += '---\n'
        }

        writers['block_quote'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeLeadingBlankIfNecessary(out)
            const basePrefix = out.prefix
            out.prefix += '> '
            out.isFirstInContainer = true
            this.writeBlockSequence(b.body as ir.BlockSequence, out)
            this.hackRemoveTrailingPrefixedBlankLine(out)
            out.prefix = basePrefix
        }


        //todo[stir] once we implement the IR's type hierarchy and support for
        //  default rendering of custom nodes, then implement the default
        //  stir2markdown rendering of such nodes. Could be one of these (TBD):
        //    - an "unsupported by Markdown" message embedded in the Markdown
        //    - throw an Error / render() returns an error object
        //    - render as embedded HTML
        //    - if two or of the above are legitimate solutions, then a
        //      switch on the renderer to let the user decide.
        writers['interjection'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
            // this.writeIndentedLine(`INTERJECTION (subtype=${b.subType}):`)
            // out.depth++
            // this.writeInlineSequence(b.title as ir.InlineSequence)
            // this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence)
            // out.depth--
        }

        writers['list'] = (b: ir.Block, out: MarkdownOut) => {
            this.writeLeadingBlankIfNecessary(out)
            const compact = b.hints['commonmark.list.tight'] === true
            const flags = {
                ordered: b.listOrdered,
                num: b.listStartNum ? b.listStartNum : 1,
                compact: compact
            }
            const itemWriter = this.bWriters.list_item
            for (let i = 0; i < (b.body as ir.BlockSequence).length; i++) {
                const item = (b.body as ir.BlockSequence)[i]
                if (i !== 0 && !compact) {
                    this.writeBlankLine(out)
                }
                itemWriter(item, out, flags)
                flags.num++ // ignored for unordered list, but keeping impl simple for now
            }
        }

        writers['list_item'] = (b: ir.Block, out: MarkdownOut, flags?) => {
            const marker = flags?.ordered ? flags.num + '. ' : '- '
            const basePrefix = out.prefix
            out.blockFirstLinePrefix = basePrefix + marker
            out.prefix = basePrefix + ' '.repeat(marker.length)

            if (b.body === undefined) {
                // empty item
                out.buf += '\n'
            } else if (ir.isBlockSequence(b.body)) {
                const bodySequence = b.body as ir.BlockSequence
                if (flags?.compact) {
                    for (let i = 0; i < bodySequence.length; i++) {
                        out.isFirstInContainer = true //todo[hack]
                        const bodyItem = bodySequence[i]
                        if (bodyItem.type === 'paragraph') {
                            this.writeBlockFirstLinePrefix(out)
                            this.writeInlineSequence(bodyItem.body as ir.InlineSequence, out)
                            out.buf += '\n'
                        } else {
                            this.writeBlock(bodyItem, out)
                        }
                    }
                } else {
                    out.isFirstInContainer = true
                    this.writeBlockSequence(bodySequence, out)
                }
            } else {
                throw new Error(`invalid list item body: ${b.body}`)
            }

            out.prefix = basePrefix
        }

        // todo[RepoDoc] Create stir2Gracedown: "stir2Gracedown will
        //   output standard CommonMark when it can. When it can't, such as when
        //   the input contains a table, it will output Gracedown's gracefully
        //   degrading form, which is 100% legit CommonMark, will render quite
        //   legibly on legacy tools, but can still be rendered in rich ways by
        //   modern tools."
        //   .
        //   E.g. for table it will render a graceful table (probably indented)
        writers['table'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
            // this.writeIndentedLine('TABLE:')
            // out.depth++
            // const table = b.body as ir.Table
            // this.writeIndentedLine(JSON.stringify(table.headRows))
            // this.writeIndentedLine(JSON.stringify(table.rows))
            // out.depth--
        }

        writers['cell'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
        }

        writers['html_block'] = (b: ir.Block, out: MarkdownOut) => {
            out.buf += `❌❌❌❌ NOT IMPLEMENTED: ${b.type} ❌❌❌❌\n`
            // this.writeIndentedLine('HTML_BLOCK:')
            // out.depth++
            // this.writeIndentedTextBlock(b.body as string)
            // out.depth--
        }

        return writers
    }


    initInlineWriters (): { [index: string]: InlineWriter } {
        const writers: { [index: string]: InlineWriter } = {}

        writers['linebreak'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += ' \\\n'
            out.buf += out.prefix
        }

        // todo: should detect multiple occurrences of the same link
        //  and generate link ref defs for those.
        writers['xref'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += '['
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += `](${i.referent})`
        }

        writers['image'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += '!['
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += `](${i.referent})`
        }

        writers['emph'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += '*'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '*'
        }

        writers['strong'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += '**'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '**'
        }

        writers['code'] = (i: ir.Inline, out: MarkdownOut) => {
            out.buf += '`'
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.buf += '`'
        }

        writers['html_inline'] = (i: ir.Inline, out: MarkdownOut) => {
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
        }

        return writers
    }

}
