import {registerPlugin} from '../../../registry.js'
import type {Stir2Html} from './stir2html.js'
import type {PluginProvider} from './pluginProvider.js'

//todo[PDF, LaTeX] For non-live renderings, e.g. output to LaTeX or PDF,
// we'd implement a Transformer, statically replacing all Mermaid IR nodes
// SVG image nodes.


// todo![plugin api] we need another provider method or install option for inserting items
//   into the HTML template.
//   Right now mermaid is hard-coded into it.

/**
 * todo[api] this method or whatever it evolves to becomes
 *  part of the renderer's plugin API
 */
function install (stir2html: Stir2Html, options: Record<string, unknown>) {
    //console.log('installing mermaid plugin')
    //mermaid.initialize(options?.mermaid as mermaidAPI.Config || {})

    // todo[api] we need a public API for customizing default block writers
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const defaultBlockWriter = stir2html.bWriters['code_block']
    stir2html.registerBlockWriter('code_block', (b, out) => {
        if (b.info?.startsWith('mermaid')) {
            out.buf += `<div class="mermaid">\n${b.body}\n</div>`
        } else {
            defaultBlockWriter(b, out)
        }
    })

}

const provider: PluginProvider = {
    install: (renderer: Stir2Html, options: Record<string, unknown> = {}) => {
        install(renderer, options)
    }
}

registerPlugin('Mermaid2HtmlPlugin', provider)

export default provider


