import * as ptss from '../ptss/index.js'
import {newPandocMarkdownStrictStylesheet} from './pandoc-markdown-strict.ptss.js'
import {newCommonMarkSheet} from './commonmark.ptss.js'

// todo[spec] Configurability (like Pandoc, disable any extension)
//   - This can be implemented most simply by accepting a config object. But however it
//     is done, it maybe should be done in a standard way for all plain text stylesheets.


export function newPandocMarkdownStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = newPandocMarkdownStrictStylesheet(options)
    stylesheet.name = 'Pandoc Markdown'

    // todo[hack] Pandoc-Markdown builds on Pandoc-Markdown-strict, adding
    //  some styles and properties defined from CommonMark that Pandoc-strict
    //  does not define/excludes. I don't think "mixins" like this makes sense
    //  for the PTSS spec. We probably should just redefine the styles here
    //  from scratch.
    const commonmark = newCommonMarkSheet(options)

    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~ \t'
    stylesheet.escapeSyntax.escapeMappings = [[' ', '\xa0'],['\t', '\xa0']] //mapped to non-breaking space

    const para = stylesheet.get('Paragraph') as ptss.ChunkStyle
    para.irNode.body = new ptss.Expression(
        [
            'parseInlines',
            ['trim', ['body']],
            'backslashNewlineIsHardBreak', 'backslashEOCIsHardBreak', 'twoSpaceNewlineIsHardBreak'
        ]
    )

    const atx = stylesheet.get('ATX Heading') as ptss.ChunkStyle
    atx.canInterruptParagraph = false  //pandoc ext: +blank_before_header
    atx.canInterruptLazy = false

    const blockquote = stylesheet.get('Block Quote') as ptss.ChunkStyle
    blockquote.canInterruptParagraph = false  //pandoc ext: +blank_before_blockquote

    const list = stylesheet.get('List') as ptss.ListStyle
    list.honorStartNum = true
    list.orderedMarkers = '.)' //apparently ')' only supported for pandoc ext: +fancy_lists

    const pipeTable = new ptss.TableStyle('Pipe Table', stylesheet)
    pipeTable.tableType = 'sv'
    pipeTable.canInterruptParagraph = false
    pipeTable.heading = 'delimited, required'
    pipeTable.headingDelim = '-'
    pipeTable.delimIntersection = '|+'
    pipeTable.sideBorder = '|'
    pipeTable.sideBorderLeftRule = 'lazy'
    pipeTable.sideBorderRightRule = 'lazy'
    pipeTable.columnsDelim = '|'
    pipeTable.columnsNumber = 'per head delim'
    pipeTable.columnsAlignIndicator = ':'
    pipeTable.bodyRowTooShort = 'add-empty'
    pipeTable.bodyRowTooLong = 'truncate'
    // placing at precedence below ATX and Setext but above the container
    // and code block styles.
    // todo![api] insertAbove doesn't guarantee the assumptions just stated
    stylesheet.insertAbove('ATX Heading', pipeTable)

    const footnote = new ptss.ContainerStyle('Footnote', stylesheet)
    footnote.hangingIndent = 4
    footnote.marker = /\[(\^(\S+))]:/y
    footnote.body = 'BLOCKS'
    footnote.irNode.type = new ptss.Expression('note')
    footnote.irNode.subType = new ptss.Expression('footnote')
    footnote.ptNamedValue = {
        type: 'element',
        // Pandoc has a segregated note namespace, where `^` prefixes all note names and references
        name: new ptss.Expression(['marker', 1]),
        value: 'self'
    }
    // without this precedence, all footnotes would be taken as Link Ref Definition
    stylesheet.insertAbove('Link Ref Definition', footnote)

    // todo[spec] Pandoc-Markdown's different behavior (does not conform to
    //  "literal-blocks_trailing-newline.md"). To observe the difference,
    //  replace the expression below which strips the body's trailing newline
    //  with the following pass-through expression to see the resulting
    //  Pandoc spec test failures:
    //  ```
    //  s.irNode.body = new ptss.Expression(['body'])
    const backTick = commonmark.get('Backtick Fenced Code Block') as ptss.ContainerStyle
    backTick.styleSheet = stylesheet
    backTick.irNode.body = new ptss.Expression(['replace', ['body'], /\n$/, ''])
    stylesheet.insertAbove('HTML Block', backTick)

    const tilde = commonmark.get('Tilde Fenced Code Block') as ptss.ContainerStyle
    tilde.styleSheet = stylesheet
    tilde.irNode.body = new ptss.Expression(['replace', ['body'], /\n$/, ''])
    stylesheet.insertAbove('HTML Block', tilde)

    const lineBlock = new ptss.ContainerStyle('Line Block', stylesheet)
    lineBlock.irNode.type = new ptss.Expression('pre')
    lineBlock.leftBorder = '|'
    lineBlock.leftPadding = 'yes'
    lineBlock.canInterruptParagraph = false
    lineBlock.indent = 'no'
    lineBlock.body = 'RAW'
    // todo[pandoc] This complex processing is to convert Line Block content
    //  into semantically equivalent STIR `pre` form. See test failures for
    //  remaining gaps. The issue isn't with generating the `pre` equiv,
    //  but parsing the Lines of Line Blocks independently, with no formatting
    //  crossing Line boundaries, and Lines have their own rules on Line
    //  continuation.
    lineBlock.irNode.body = new ptss.Expression(
        ['parseInlines',
            ['replace',
                ['replace',
                    ['replace',
                        ['replace',
                            ['body'],
                            / +$/gm,
                            ''
                        ],
                        /([^\n ]) +/gm,
                        '$1[SP]'
                    ],
                    / /gm,
                    '\u00a0'
                ],
                /\[SP]/gm,
                ' '
            ],
            'preserveNewlines',
            'preserveWhitespace']
    )
    stylesheet.blockStyles.push(lineBlock)

    stylesheet.inlineStyles.splice(stylesheet.inlineStyles.indexOf('reference'), 0, 'noteRef(segregated)')

    stylesheet.inlineStyles.push('strikethrough')
    stylesheet.inlineStyles.push('subscript')
    stylesheet.inlineStyles.push('superscript')
    stylesheet.inlineStyles.push('checkbox_text')

    return stylesheet
}
