import * as ptss from '../ptss/index.js'

export function newTestWritersPrerogativeSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('PTSS Test TWO Stylesheet')

    //todo[spec] just copied CommonMark's. Haven't even thought about natural escapes.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    // todo![now] re-order all declarations putting the irNode set at the end:
    //   - separates inputs (plain text lexical declarations) from outputs (STIR mappings)
    const markerWidthHanging = new ptss.ContainerStyle('Marker Width Hanging Indent as Interjection', stylesheet)
    markerWidthHanging.indent = 'optional'
    markerWidthHanging.hangingIndent = 'marker'
    markerWidthHanging.marker = /↔️/y
    markerWidthHanging.canInterruptParagraph = true
    markerWidthHanging.irNode.type = new ptss.Expression('interjection')
    markerWidthHanging.irNode.subType = new ptss.Expression('width')
    //! less efficient that the following, but we want to test marker extraction
    //      markerWidthHanging.irNode.icon = new ptss.Expression('↔️')
    markerWidthHanging.irNode.icon = new ptss.Expression(['marker', 0])

    const markedAutoWidthHanging = new ptss.ContainerStyle('Marked Auto Width Hanging Indent as Interjection', stylesheet)
    markedAutoWidthHanging.indent = 'optional'
    markedAutoWidthHanging.hangingIndent = 'auto'
    markedAutoWidthHanging.marker = /🚗/y
    markedAutoWidthHanging.canInterruptParagraph = true
    markedAutoWidthHanging.irNode.type = new ptss.Expression('interjection')
    markedAutoWidthHanging.irNode.subType = new ptss.Expression('auto')
    markedAutoWidthHanging.irNode.icon = new ptss.Expression(['marker', 0])

    const markedFixedHanging = new ptss.ContainerStyle('Marked Fixed Hanging Indent as Interjection', stylesheet)
    markedFixedHanging.indent = 'optional'
    markedFixedHanging.hangingIndent = 3
    markedFixedHanging.marker = /📌/y
    markedFixedHanging.canInterruptParagraph = true
    markedFixedHanging.irNode.type = new ptss.Expression('interjection')
    markedFixedHanging.irNode.subType = new ptss.Expression('pin')
    markedFixedHanging.irNode.icon = new ptss.Expression(['marker', 0])

    // the only way we can have two types of unmarked hanging indent containers,
    // to test both 'auto' and fixed width types, is to give the fixed width
    // version higher precedence, allowing those not matching that width to
    // fall through to the 'auto'. It also needs to be wide enough so that the
    // auto width tests don't accidentally match the fixed width.
    const unmarkedFixedIndentFixedHanging = new ptss.ContainerStyle('Unmarked Fixed Ident Fixed Hanging Indent as Interjection', stylesheet)
    unmarkedFixedIndentFixedHanging.indent = 'optional'
    unmarkedFixedIndentFixedHanging.hangingIndent = 6
    unmarkedFixedIndentFixedHanging.canInterruptParagraph = false
    unmarkedFixedIndentFixedHanging.irNode.type = new ptss.Expression('interjection')
    unmarkedFixedIndentFixedHanging.irNode.subType = new ptss.Expression('unmarked fixed')

    const unmarkedAutoHanging = new ptss.ContainerStyle('Unmarked Fixed Hanging Indent as Interjection', stylesheet)
    unmarkedAutoHanging.indent = 'optional'
    unmarkedAutoHanging.hangingIndent = 'auto'
    unmarkedAutoHanging.canInterruptParagraph = false
    unmarkedAutoHanging.irNode.type = new ptss.Expression('interjection')
    unmarkedAutoHanging.irNode.subType = new ptss.Expression('unmarked auto')

    const atx = new ptss.ChunkStyle('ATX Heading', stylesheet)
    atx.irNode.type = new ptss.Expression('heading')
    atx.canInterruptParagraph = true
    atx.canInterruptLazy = true
    atx.headPattern = /(#{1,6})(?=[ \t]|$)/ym
    atx.bodyPattern = /(?:[ \t]+(.*?))??(?:[ \t]+#+)?[ \t]*$/ym
    atx.maxLines = 1
    atx.indent = 'optional'
    atx.irNode.level = new ptss.Expression(['length', ['head', 1]])
    atx.irNode.body = new ptss.Expression(['parseInlines', ['first', ['body', 1], '']])

    const setext = new ptss.ChunkStyle('Setext Heading', stylesheet)
    setext.irNode.type = new ptss.Expression('heading')
    setext.tailPattern = /((=+|-+)[ \t]*$)/ym
    setext.maxLines = 3
    setext.indent = 'optional'
    setext.irNode.level = new ptss.Expression(['map', ['at', ['tail', 2], 0], {'=': 1, '-': 2}])
    setext.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body']]]) //todo[perf] trim may be redundant with latter handling. clean up.

    const blockquote = new ptss.ContainerStyle('Block Quote', stylesheet)
    blockquote.irNode.type = new ptss.Expression('block_quote')
    blockquote.indent = 'yes'

    stylesheet.blockStyles.push(
        // because this is an unmarked hanging indent, it must come before
        // any other block style so that said style can occur as the first
        // block within the hanging indent.
        markerWidthHanging,
        markedAutoWidthHanging,
        markedFixedHanging,
        unmarkedFixedIndentFixedHanging,
        unmarkedAutoHanging,
        setext,
        atx,
        blockquote
    )

    stylesheet.inlineStyles = [
        'autolink',
        'backticks',
        'emphasis',
        'entity',
        'escape',
        'html_inline',
        'image',
        'reference',
        'newline',
        'html'
    ]

    return stylesheet
}
