/* eslint-disable @typescript-eslint/no-explicit-any */

import type {Assembly} from './textAssembly/index.js'
import {VfsPath, Vpath} from '../vfs/index.js'
import JSON5 from 'json5'


/** if the path is relative, interpret as relative to Config location/context */
export type ResolveConfigResourcePath = (path: string) => VfsPath

//todo![api, release] consider extracting a generic superclass from this and releasing
//  as an independent package.

/**
 * A container of hierarchical config properties with the following features:
 *
 * - can resolve properties with relative paths values, relative to the Config's
 *   own physical or virtual source file location. See *Notes on path
 *   resolution* below.
 *
 * - any branch of the property hierarchy can be extracted via
 *   {@link subConfig} or {@link subConfigs}.
 *
 * - access to the owning TextAssembly if it represents one.
 *
 * - the option to trigger an Error if the requested property or subConfig
 *   does not exist.
 *
 * Notes on path resolution
 * ------------------------
 * The source file system is not necessarily the local file system. It may be
 * a real or virtualized cloud file system. It may be a virtualized container
 * within the local file system. It may be within a virtualized file system
 * with unique special path syntax (e.g. TextAssembly cross-assembly paths).
 * Thus it is necessary that all path values held by an instance of this Config
 * be resolved using methods of this class, e.g. {@link requiredResolvedPathValue} and
 * {@link requiredReadFileSyncAt}.
 *
 * todo[test] unit test
 */
export class Config {
    private readonly _name: string
    private readonly _resolvePath: ResolveConfigResourcePath
    private readonly _isSubconfig: boolean

    private readonly _data: Record<string, any> = {}

    private readonly _assembly: Assembly | undefined

    /**
     *
     * @param name         the name of this config. Will be the filename
     *                     when it represents the whole config file,
     *                     appended with the subsection path if it represents
     *                     and extracted subsection.
     * @param data         the config data
     * @param resolvePath  used to resolve path
     *                     values held by this Config per rules specified
     *                     {@link Config above}.
     * @param isSubconfig  whether the config values in this instance
     *                     are a branch extracted from the full Config.
     * @param textAssembly reference to the owning TextAssembly if
     *                     this Config represents one.
     */
    constructor (name: string,
                 data: Record<string, any>,
                 resolvePath: ResolveConfigResourcePath,
                 isSubconfig = false,
                 textAssembly?: Assembly) {
        this._name = name
        Object.assign(this._data, data)
        this._resolvePath = resolvePath
        this._isSubconfig = isSubconfig
        this._assembly = textAssembly
    }

    name (): string {
        return this._name
    }

    data (): Readonly<Record<string, any>> {
        return this._data
    }

    //! this is not identical to `value(key) !== undefined` because
    //  undefined is a possible value for key.
    hasValue (key: string): boolean {
        const keys = key.split('.')
        let d = this._data
        for (const k of keys) {
            if (!(k in d)) {
                return false
            }
            d = d[k]
        }
        return true
    }

    value (key: string): any | undefined {
        const keys = key.split('.')
        let d = this._data
        for (const k of keys) {
            if (!(k in d)) {
                return undefined
            }
            d = d[k]
        }
        return d
    }

    requiredValue (key: string): any {
        const keys = key.split('.')
        let d = this._data
        for (const k of keys) {
            if (k.length === 0) {
                throw new Error(`invalid key: "${key}"`)
            }
            if (!(k in d)) {
                throw new Error(`expected value for ${key} in ${this._name}`)
            }
            d = d[k]
        }
        return d
    }

    subConfig (key: string): Config | undefined {
        const d = this.value(key)
        if (d === undefined) return undefined
        const name = this._isSubconfig
            ? `${this._name}.${key}`
            : `${this._name}#${key}`
        return new Config(name, d, this._resolvePath, true, this._assembly)
    }

    requiredSubConfig (key: string): Config {
        const d = this.requiredValue(key)
        const name = this._isSubconfig
            ? `${this._name}.${key}`
            : `${this._name}#${key}`
        return new Config(name, d, this._resolvePath, true, this._assembly)
    }

    subConfigs (key: string): Config[] | undefined {
        const d = this.value(key)
        if (d === undefined || !Array.isArray(d)) return undefined
        return this._createSubConfigs(d, key)
    }

    requiredSubConfigs (key: string): Config[] {
        const d = this.requiredValue(key)
        if (!Array.isArray(d)) {
            throw new Error(`expected array of values for ${key} in ${this._name}`)
        }
        return this._createSubConfigs(d, key)
    }

    private _createSubConfigs (d: any, key: string): Config[] {
        const configs: Config[] = []
        for (let i = 0; i < d.length; i++) {
            const name = this._isSubconfig
                ? `${this._name}.${key}[$i]`
                : `${this._name}#${key}[$i]`
            configs.push(
                new Config(name, d[i], this._resolvePath, true, this._assembly)
            )
        }
        return configs
    }

    resolvedPathValue (key: string): VfsPath | undefined {
        const value = this.value(key)
        if (value === undefined || typeof value !== 'string') {
            return undefined
        }
        return this._resolvePath(value)
    }

    requiredResolvedPathValue (key: string): VfsPath {
        const value = this.requiredValue(key)
        if (typeof value !== 'string') {
            throw new Error(`invalid path value for ${key} in ${this._name}`)
        }
        return this._resolvePath(value)
    }

    readFileSyncAt (key: string): string | undefined {
        const path = this.resolvedPathValue(key)
        if (path === undefined) return undefined
        return path.vfs.readFileSync(path.path)
    }

    requiredReadFileSyncAt (key: string): string {
        const path = this.requiredResolvedPathValue(key)
        return path.vfs.readFileSync(path.path)
    }

    textAssembly (): Assembly | undefined {
        return this._assembly
    }
}

export function newConfigForVfsPath (configPath: VfsPath, assembly?: Assembly): Config {
    const vfs = configPath.vfs
    const configPathParts = Vpath.parse(configPath.path)
    const data = JSON5.parse(vfs.readFileSync(configPath.path))
    const resolvePath = (path: string): VfsPath => {
        if (path.startsWith('/')) {
            return {vfs: vfs, path: path}
        }
        path = Vpath.join(configPathParts.dir, path)
        return {vfs: vfs, path: path}
    }
    return new Config(configPathParts.base, data, resolvePath, false, assembly)
}

// todo![api] `newSimpleConfigWithCWDPathResolution` in bin/textplain.js
//  should be consolidated with this, probably when we implement the
//  BaseFS/SystemFS/WebFS plan described in the notes in WebFS.ts.
export function newConfigFromSimpleOptions (options?: Record<string, unknown>, assembly?: Assembly): Config {
    const resolvePath = () => {
        // todo![api] this could support resolvePath in a couple of ways:
        //  1. add a `basePath` param above, which will be used for resolvePath
        //  2. default `basePath` to `proc.cwd()` in Node and `/` in browser.
        //     See https://github.com/vfile/vfile#filecwd.
        //  Deciding factor: what are the use cases for programmatic construction
        //  of Config? Does it make sense or is it safe to assume proc.cwd()?
        throw new Error('internal application logic error: cannot resolve paths from simple config')
    }
    return new Config('options', options || {}, resolvePath, false, assembly)
}
