import {Node, NodeInterface, Folder, resolvePath} from './node.js'
import type {Assembly} from './assembly.js'
import type {STIR} from '../stir.js'
import {Vpath} from '../../vfs/vpath.js'

/**
 * todo[arch] Antora used [Vinyl](https://www.npmjs.com/package/vinyl)
 * "Vinyl is a very simple metadata object that describes a file. When you think
 * of a file, two attributes come to mind: path and contents. These are the main
 * attributes on a Vinyl object. A file does not necessarily represent something
 * on your computer’s file system. You have files on S3, FTP, Dropbox, Box,
 * CloudThingy.io and other services. Vinyl can be used to describe files from
 * all of these sources."
 *
 * Some notes on Vinyl
 * - see https://gulpjs.com/docs/en/api/src/
 * - uses `cwd` like I use `relativePathBase`.
 * - does not have a concept of absolute path root, or encapsulation boundary.
 *   - an absolute path will resolve to an absolute path on the host FS.
 * - it doesn't intake a tree, but rather files matching a glob pattern.
 *   - though i guess if yo give it the glob: `**` it will read the tree.
 *   - not sure whether it treats the tree as a first class part of the content,
 *     or if that's only the typical usage.
 *
 *  CONCLUSION: vinyl doesn't make sense for Textplain. Its soo much designed
 *  around Gulp's needs.
 *
 *  vFile may make more sense: https://github.com/vfile/vfile
 *
 */


export class File implements NodeInterface {
    readonly name: string
    readonly parent: Folder

    stir: STIR | undefined
    title: string | undefined

    isIndexable = false

    // todo[arch] haven't decided if this is how we'll use an index like this.
    // exported deep link targets, resolved to #anchor string 
    deepLinkTargets: { [name: string]: string } = {}

    // the render status
    // values with the same prefix shown in order of valid state transition:
    status: undefined | 'excluded'
        | 'resource: waiting' | 'resource: in pipeline' | 'resource: copied'
        | 'sText: waiting' | 'sText: in pipeline' | 'sText: awaiting dependency' | 'sText: rendered'

    outNode: string | undefined

    constructor (name = '[[GENERATED]]', parent: Folder) {
        this.name = name
        this.parent = parent
    }

    nameSansExt (): string {
        return Vpath.parseName(this.name)
    }

    extension (): string {
        return Vpath.parseExt(this.name)
    }

    isFile (): this is File {
        return true
    }

    isFolder (): this is Folder {
        return false
    }

    isAssemblyRoot (): this is Assembly {
        return false
    }

    assemblyRoot (crossAssemblyBoundary?: boolean): Assembly {
        return this.parent.assemblyRoot(crossAssemblyBoundary)
    }

    isOpeningContent (): boolean {
        return this === this.parent.openingContent
    }

    // todo[arch] this avoids two things: 
    //  - a name that is 'undefined'
    //  - an additional boolean field
    isGenerated (): boolean {
        return this.name === '[[GENERATED]]'
    }

    innerAssemblyPath (): string {
        return this.parent.innerAssemblyPath() + this.name
    }

    outerAssemblyPath (): string {
        return this.parent.outerAssemblyPath() + this.name
    }

    crossAssemblyPath (): string {
        return this.parent.crossAssemblyPath() + this.name
    }

    resolvePath (path: string): Node | undefined {
        return resolvePath(path, this)
    }

    searchUpForFile (name: string, crossAssemblyBoundary = false): File | undefined {
        if (this.name === name) {
            return this
        }
        if (this.parent === undefined) {
            return undefined
        }
        return this.parent.searchUpForFile(name, crossAssemblyBoundary)
    }

    searchUpForProperty (property: string, crossAssemblyBoundary = false): { node: Node, value: unknown } | undefined {
        if (property in this) {
            const value = (this as unknown as Record<string, unknown>)[property]
            if (value !== undefined) {
                return {node: this, value}
            }
        }
        return this.parent.searchUpForProperty(property, crossAssemblyBoundary)
    }

    readFileSync (): string {
        return this.parent.assemblyRoot()
            .srcFS()
            .readFileSync(this.innerAssemblyPath())
    }
}
