import type {STIR} from '../../stir.js'
import type {Assembly, File} from '../../textAssembly/index.js'
import type {Config} from '../../config.js'
import type {SandboxFS} from '../../../vfs/index.js'
import {lookupAssemblyRenderer} from '../../registry.js'

export type RenderStats = number | { [key: string]: RenderStats }

export interface AssemblyRenderer {
    prepare (assembly: Assembly): void
    render (stir: STIR): void
    copy (file: File): void
    finish (): void
    stats (): { [key: string]: RenderStats }
}

export interface AssemblyRendererProvider {
    configure: (dest: SandboxFS, config?: Config) => Promise<AssemblyRenderer>
}

export async function configAssemblyRendererProvider (dest: SandboxFS, config: Config): Promise<AssemblyRenderer> {
    const providerName = config.requiredValue('provider')
    console.group(`configure outlet: ${providerName}`)
    const provider = lookupAssemblyRenderer(providerName)
    if (provider === undefined || provider.configure === undefined) {
        throw new Error(`invalid outlet: ${providerName}`)
    }
    const providerConfig = config.subConfig('config')
    const assemblyRenderer = await provider.configure(dest, providerConfig)
    console.groupEnd()
    return assemblyRenderer
}
