import type {Stylesheet} from './stylesheet.js'
import {ValueType, NamedValue, normalizeName} from './namedValue.js'
import type {Expression} from './expression.js'
import type * as ir from '../../stir/index.js'
import type {Line} from '../line.js'
import {unescapeString} from '../../orphans/parseUtil.js'

// todo[arch] Do we need an enum that spans both Block and Inline
//   types, or should we instead have the two enums, BlockStyleType
//   and InlineStyleType?
export enum BaseStyleType {
    Segment,
    Chunk,
    Container,
    List,
    Table,
    TEMP_HTML_HACK
}

export type BlockStyleType =
    BaseStyleType.Chunk |
    BaseStyleType.Container |
    BaseStyleType.List |
    BaseStyleType.Table |
    BaseStyleType.TEMP_HTML_HACK

// todo[spec] will there be more than one kind of InlineStyle? If not,
//   shouldn't we just rename BaseStyleType.Segment to BaseStyleType.Inline?
export type InlineStyleType =
    BaseStyleType.Segment

export type ParseEffects = {
    irBlock?: ir.Block | undefined
    ptNamedValue?: NamedValue | undefined
    startLine?: Line //! we only need startLine here as long as we keep support for backward matching
    endLine?: Line
}

export type NamedValueExpressions = {
    type: ValueType,
    name: Expression;
    value: 'self' | Expression;
    description?: Expression
}

/**
 * The interface for the various elemental styled block parsers:
 * {@link ChunkStyle}, {@link ContainerStyle}, {@link ListStyle} and
 * {@link TableStyle}.
 *
 * Responsible for
 *  - recognizing instances of the associated style declaration in
 *    the plain text
 *  - parsing those instances
 *  - executing the declared style property {@link Expression}s.
 *  - construct the expected IR Block, PT NamedValue, or both
 */
export interface ElementalBlockStyle {
    baseType: BlockStyleType
    name: string

    styleSheet: Stylesheet

    /**
     * todo[RepoDoc] i should be able to point to the PTSS spec
     *   section describing these options.
     */
    indent: string // valid values defined within each base type

    //todo[doc] make sure all the rules in the impls of this method are
    //  represented in the user docs.
    initAndValidate: () => void

    // todo[spec] rethink canInterruptParagraph & canInterruptLazy
    //   - should this instead be canInterruptTextChunk?
    //   - These two should maybe be replaced by a better logical
    //     model, OR AT LEAST renamed? Look at the settings for
    //     Pandoc-markdown-strict List. Does it make sense when you read it?
    //   - the default value for this (perhaps here, perhaps per style type)
    //     should be such that new style authors shouldn't have to set them to get the
    //     most natural behavior, and certainly should have to set or even understand
    //     the complexities of lazy continuation if they are not using it.
    canInterruptParagraph: boolean
    canInterruptLazy: boolean

    //todo[spec] there probably should be some validation to prevent inconsistency
    // between this and the interruption controls above? In BlockTreeParser, we
    // don't even check the above if consumesBackChunk is true. So we can either
    // not validate and in the docs say that this has precedence over those, or
    // we do validation and force style defs to be logical. I lean toward the latter.
    consumesBackChunk: boolean

    irNode?: { [key: string]: Expression }

    ptNamedValue?: NamedValueExpressions

    // todo[arch] if chunk style is the only one that only ever uses fromBackLine,
    //  can i put it at the end and then remove it from this generic interface?
    //  i.e. i'm passing undefined in to ALL other style.parse methods. If it is
    //  at the end, then i can remove it from here and keep it as an optional
    //  param on ChunkStyle.
    /**
     * Returns null if doesn't match
     */
    parse: (fromLine: Line,
            fromBackLine: Line | null,
            lookahead: boolean) => ParseEffects | null
}

//! should NOT be executed every parse attempt. Instead, call
//  during initAndValidate and set any style properties from the match.
export const RE_MARKDOWN_INDENT = /markdown\((\d+),(\d+)\)/

export function evalIRBlockPropertyExpr (expressions: { [property: string]: Expression },
                                         irBlock: ir.Block,
                                         headMatch: string[] | null,
                                         tailMatch: string[] | null,
                                         bodyMatch: string[] | null,
                                         markerMatch: string[] | null,
                                         righterMatch: string[] | null): void {
    console.debug('EVALUATING IR PROPERTY EXPRESSIONS:')
    console.group()
    for (const property in expressions) {
        const exp = expressions[property]
        console.debug(`${property} <-- ${exp.toString()}`)
        console.group()
        irBlock[property] = exp.evaluate(headMatch, tailMatch, bodyMatch, markerMatch, righterMatch)
        console.groupEnd()
    }
    console.groupEnd()
}

export function evalPTNamedValueExpr (expressions: NamedValueExpressions,
                                      irNode: ir.Block | ir.Inline | undefined,
                                      headMatch: string[] | null,
                                      tailMatch: string[] | null,
                                      bodyMatch: string[] | null,
                                      markerMatch: string[] | null,
                                      righterMatch: string[] | null): NamedValue | undefined {
    console.debug('APPLYING PT REF-DEF PROPERTY EXPRESSIONS:')
    console.group()

    console.debug(`expression: name <-- ${JSON.stringify(expressions.name)}`)
    const name = expressions.name.evaluate(
        headMatch, tailMatch, bodyMatch, markerMatch, righterMatch) as string

    // todo![ptss spec] move any CommonMark-specific stuff out of this and into
    //  style sheet Expressions that are applied to produce the args to this.

    const normalizedName = normalizeName(name)
    // todo![now, references] any other name validation?
    if (normalizedName === '') {
        console.debug('  invalid NamedValue definition: invalid name:', JSON.stringify(name))
        return undefined
    }

    let nv: NamedValue, href: string, desc: string | undefined, content: ir.InlineSequence
    switch (expressions.type) {
        case 'element':
            // todo[now, references, spec, expressions] should `self` by an
            //   expression func that returns the current node? would allow
            //   for expressions such as `['parent' ['self']]` etc.
            if (expressions.value === 'self' && irNode !== undefined) {
                nv = {name: normalizedName, value: {type: 'element', element: irNode}}
            } else {
                throw new TypeError(`style sheet error: invalid referent-element Named Value declaration. value: ${expressions.value}, irNode:${irNode}}`)
            }
            break
        case 'href':
            console.debug(`expression: value <-- ${JSON.stringify(expressions.value)}`)
            href = (expressions.value as Expression).evaluate(
                headMatch, tailMatch, bodyMatch, markerMatch, righterMatch) as string
            href = unescapeString(href)

            console.debug(`expression: description <-- ${JSON.stringify(expressions.description)}`)
            desc = expressions.description?.evaluate(
                headMatch, tailMatch, bodyMatch, markerMatch, righterMatch) as string | undefined
            if (desc !== undefined) {
                desc = unescapeString(desc)
            }

            nv = {name: normalizedName, value: {type: 'href', url: href, desc: desc}}
            break
        case 'substitution':
            console.debug(`expression: value <-- ${JSON.stringify(expressions.value)}`)
            content = (expressions.value as Expression).evaluate(
                headMatch, tailMatch, bodyMatch, markerMatch, righterMatch) as ir.InlineSequence
            nv = {name: normalizedName, value: {type: 'substitution', content: content}}
            break
        case 'metadata':
        case 'counter':
            throw new TypeError(`${expressions.type} Named Value not yet supported`)
    }

    console.groupEnd()
    return nv
}
