import type * as ptss from '../ptss/index.js'
import {newCommonMarkSheet} from './commonmark.ptss.js'
import {newCommonMarkFWDSheet} from './commonmark-fwd.ptss.js'
import {newGFMStylesheet} from './gfm.ptss.js'
import {newPandocMarkdownStylesheet} from './pandoc-markdown.ptss.js'
import {newPandocMarkdownStrictStylesheet} from './pandoc-markdown-strict.ptss.js'
import {newMarkdownHSStylesheet} from './markdown_hs.ptss.js'
import {newPlainJaneStylesheet} from './plainjane.ptss.js'
import {newGracedownSheet} from './gracedown.ptss.js'
import {newRSTSheet} from './reStructuredText.ptss.js'
import {newKitchenSinkStylesheet} from './kitchen-sink.ptss.js'
import {newTestCompositionsSheet} from './test-composition.ptss.js'
import {newTestPtssOneSheet} from './test-ptss-one.js'
import {newTestReferencesSheet} from './test-references.ptss.js'
import {newTestWritersPrerogativeSheet} from './test-writers-prerogative.js'
import {newCaseFileStylesheet} from './case-file.ptss.js'
import {newCaseFileManipulationSheet} from './case-file-manipulation.ptss.js'
import {newRubyRhodStylesheet} from './rubyrhod.ptss.js'

//todo[arch] we can use https://www.npmjs.com/package/serialize-javascript as an interim solution
//   for runtime loading of arbitrary stylesheets.
export function loadSheet (name: string, options?: Record<string, unknown>): ptss.Stylesheet {
    let lower = name.toLowerCase()
    if (!lower.endsWith('.ptss')) {
        lower += '.ptss'
    }
    switch (lower) {
        case 'commonmark.ptss':
            return newCommonMarkSheet(options)
        case 'commonmark-fwd.ptss':
            return newCommonMarkFWDSheet(options)
        case 'gfm.ptss':
            return newGFMStylesheet(options)
        case 'pandoc-markdown.ptss':
            return newPandocMarkdownStylesheet(options)
        case 'pandoc-markdown-strict.ptss':
            return newPandocMarkdownStrictStylesheet(options)
        case 'markdown_hs.ptss':
            return newMarkdownHSStylesheet(options)
        case 'plainjane.ptss':
            return newPlainJaneStylesheet(options)
        case 'gracedown.ptss':
            return newGracedownSheet(options)
        case 'restructuredtext.ptss':
            return newRSTSheet(options)
        case 'kitchen-sink.ptss':
            return newKitchenSinkStylesheet(options)
        case 'rubyrhod.ptss':
            return newRubyRhodStylesheet(options)
        case 'test-composition.ptss':
            return newTestCompositionsSheet(options)
        case 'test-ptss-one.ptss':
            return newTestPtssOneSheet(options)
       case 'test-references.ptss':
            return newTestReferencesSheet(options)
       case 'test-writers-prerogative.ptss':
            return newTestWritersPrerogativeSheet(options)
        case 'case-file.ptss':
            return newCaseFileStylesheet(options)
        case 'case-file-manipulation.ptss':
            return newCaseFileManipulationSheet(options)
    }
    throw new Error(`no style sheet named "${name}"`)
}
