//  - See https://github.com/commonmark/commonmark.js/issues/168
//  - chose to end with lower case, because that's what we want names to look like in STIR and in HTML IDs
//  - for some reason, `.toUpperCase().toLowerCase()` doesn't work; at least one CommonMark test breaks.
//  - so perhaps this is the way to go, or is a temp hack, IDK.
export function normalizeName (s: string): string {
    return s.trim().replace(/[ \t\r\n]+/, ' ').toLowerCase().toUpperCase().toLowerCase()
}

export type ValueType = 'element' | 'href' | 'metadata' | 'counter' | 'substitution'

export type Value = ElementValue | HrefValue | SubstitutionValue

export type ElementValue = { type: 'element', element: ir.Block | ir.Inline }

/**
 *
 * href `desc`
 * -----------
 * HTML semantics of `title` attribute (to which this gets written:
 *  - whatwg.org (same as w3.org):
 *
 *    3.2.6.1 The title attribute
 *
 *    The title attribute represents advisory information for the element, such as would be appropriate for a tooltip. On a link, this could be the title or a description of the target resource; on an image, it could be the image credit or a description of the image; on a paragraph, it could be a footnote or commentary on the text; on a citation, it could be further information about the source; on interactive content, it could be a label for, or instructions for, use of the element; and so forth. The value is text.
 *
 *    Relying on the title attribute is currently discouraged as many user agents do not expose the attribute in an accessible manner as required by this specification (e.g., requiring a pointing device such as a mouse to cause a tooltip to appear, which excludes keyboard-only users and touch-only users, such as anyone with a modern phone or tablet).
 *
 *    If this attribute is omitted from an element, then it implies that the title attribute of the nearest ancestor HTML element with a title attribute set is also relevant to this element. Setting the attribute overrides this, explicitly stating that the advisory information of any ancestors is not relevant to this element. Setting the attribute to the empty string indicates that the element has no advisory information.
 *
 *    If the title attribute's value contains U+000A LINE FEED (LF) characters, the content is split into multiple lines. Each U+000A LINE FEED (LF) character represents a line break.
 *
 */
export type HrefValue = { type: 'href', url: string, desc: string | undefined }

export type SubstitutionValue = { type: 'substitution', content: ir.InlineSequence }

//! just ideas
export type MetadataValue = { type: 'metadata', value: string }
export type CounterValue = { type: 'counter', count: number }
// todo[spec] just an idea: support things like tabular data,
//  perhaps loaded from external files
export type DataSetValue = { type: 'data', data: unknown }


export type NamedValue = { name: string, value: Value }

export function stringifyNamedValue (nv: NamedValue): string {
    return stringifyNameAndValue(nv.name, nv.value)
}

export function stringifyNameAndValue (name: string, v: Value): string {
    return `${name} -> ${stringifyValue(v)}`
}

export function stringifyValue (v: Value): string {
    if (v.type === 'href') {
        let s = `href (url="${v.url}"`
        if (v.desc) s += `, desc="${v.desc}"`
        return s + ')'
    } else if (v.type === 'substitution') {
        return `substitution for "${JSON.stringify(v.content)}"`
    } else {
        const element = v.element
        if (element instanceof ir.Inline) {
            return `inline (${element.type} "${element.inlineBodyText()}")`
        } else {
            return `block (${element.toDebugString()})`
        }
    }
}
