// todo![fwd-parsing] look at back-burnered branch: `study/fwdchunk-optimize`

// todo[optimization] if memory allocation of individual Lines is expensive:
//   - we could allocate them in array-blocks, N Lines at a time, with next()
//     pointing to the next element in the array except for the last Line in
//     any block.
//   - we could use an object pool to re-use Line instances.

import {ParseContainer, parseContinuation} from './parseContainer.js'
import {isWhitespace} from '../orphans/parseUtil.js'
import {stringifyTruncate} from './debugUtil.js'
import type {ElementalBlockStyle} from './ptss/index.js'

/** Represents a plain text source line. */
export class Line {
    // line number within src, 1-based
    readonly num: number

    // start of line
    readonly SOL: number

    /**
     * End Of Content: points to the position immediately after the last
     * non-whitespace character on the line.
     * - If line is all whitespace, it will be the same value as `SOL`
     * - If line has no trailing whitespace it will be the same value as `EOL`.
     */
    readonly EOC: number

    /**
     * End Of Line: points to the newline character at the end of the line,
     * except for the last line if the file does not end with a newline it
     * points to the position after the last pos in the file.
     *
     * In either case,
     *  - the full content of the line is always `src.substring(SOL, EOL)`
     *  - the trailing whitespace is always `src.substring(EOC, EOL)`
     *  - the remaining part of the line is always `src.substring(ptr, EOL)`
     */
    readonly EOL: number

    //! The fields and properties prefixed with "ptr" are tightly
    //  coupled and represent the left-to-right progress of parsing
    //  this Line. `Line.ptr` is a straightforward pointer to a
    //  specific char position in the line, and is `Line.src`
    //  relative. But because of tab expansion, advancing one space
    //  is not always as simple as incrementing `ptr`. If there are
    //  unconsumed "virtual" spaces from an earlier tab char,
    //  advancing may simply decrement `ptrTabRemainder` and
    //  increment `ptrColumnAdj` instead. See the unit tests for a
    //  better understanding.

    // current parse position, relative to src
    ptr: number

    // add to `Line.ptr` to arrive at column (one-based)
    ptrColumnAdj = 0

    get ptrColumn (): number {
        return this.ptr - this.SOL + 1 + this.ptrColumnAdj
    }

    // number of spaces from last tab not yet consumed
    ptrTabRemainder = 0

    private _next: Line | null = null

    /**
     * Returns the next line.
     * If this is the last line in the src, returns null.
     *
     * Does no parsing or updating of Line state other than
     * to find the EOL if hasn't been determined yet. If this
     * is the first time the next line has been retrieved, it
     * will be initialized to starting state, unparsed, container
     * set to root and ptr set to SOL.
     */
    get next (): Line | null {
        if (this._next) {
            return this._next
        }

        const nextSOL = this.EOL + 1
        if (nextSOL < this.src.length) {
            return this._next = new Line(this.src, this.num + 1, nextSOL, this._pContainer.root())
        } else {
            return null
        }
    }

    //! The fields and properties prefixed with "pContainer" are tightly
    //  coupled.

    // the container context as of the current parse point, Line.ptr
    private _pContainer: ParseContainer

    get pContainer (): ParseContainer {
        return this._pContainer
    }

    set pContainer (value: ParseContainer) {
        this._pContainer = value
        this.pContainerSOL = this.ptr
        this.pContainerSlop = 0
    }

    // offset of the start of content for `Line.pContainer`
    pContainerSOL: number

    // number of slop spaces parsed within `Line.pContainer`
    pContainerSlop = 0

    // If not null, a bottom border for `Line.pContainer` was parsed on this line.
    // For BlockTreeParser control flow based on whether a bottom border was
    // encountered.
    //
    // todo[containers] per the comments on the parseContinuation function, this
    //   is supposed to contain content from the bottom border, supporting
    //   multi-line bottom borders, so styles that have content within
    //   the borders can extract and map the content via ptss.expressions.
    //   --
    //   THIS HAS YET TO BE IMPLEMENTED.
    //   - 🟧 If the bottom border spans multiple Lines, which one's
    //      pContainerBottomBorder is set? And that one contains the content
    //      from all the lines? May need to revisit this design idea.
    //   - 🟧 Line so far doesn't hold parsing matches or any other high level
    //      content parsing data, just low-level index pointers, etc. Probably
    //      any bottom border content match should be returned by the parse
    //      method to its caller via separate return value, as opposed to
    //      shoe-horning it into Line. ???
    pContainerBottomBorder: string[] | null

    readonly src: string

    constructor (src: string, num: number, offset: number, rootContainer: ParseContainer) {
        this.src = src
        this.num = num
        this._pContainer = rootContainer
        this.SOL = this.pContainerSOL = this.ptr = offset
        this.pContainerBottomBorder = null

        // We determine EOC and EOL up front because:
        //   1. We almost always check if the line is blank before anything else
        //      (in the BlockTreeParser iteration over lines), and to do this
        //      we essentially do the same thing to figure out EOC and EOL.
        //   2. WE frequently check if the remaining part of the line is blank
        //      outside of BlockTreeParser.
        //   3. We frequently check to see if we've reached EOL as we parse
        //      a line (`if line.ptr === line.EOL`).
        //   4. EOL is necessary for `Line.next` (`nextLine.SOL = curLine.EOL + 1`)
        //   5. We need EOL to extract the remaining part of the line
        //   6. Every time a fwdBlock is requested, even if some or all of the
        //      fwdBlock is rejected/not consumed.
        //   7. To mark the line as consumed (line.ptr = line.EOL)
        const EOF = src.length
        let i = src.indexOf('\n', offset)
        if (i === -1) {
            this.EOL = EOF
            i = EOF
        } else {
            this.EOL = i
        }
        //search for EOC in reverse, as trailing whitespace should be rare,
        //and a forward search will likely traverse many chars.
        do {
            i--
        } while (i >= offset && isWhitespace(src.charAt(i)))
        this.EOC = i + 1
    }

    toString (): string {
        return `[line ${this.num}] sol:${this.SOL} eoc:${this.EOC} eol:${this.EOL} len:${this.EOL - this.SOL + 1} ` +
               ` ptr:${this.ptr}(col:${this.ptrColumn} col+:${this.ptrColumnAdj} tab%:${this.ptrTabRemainder}) ` +
               ` pCon:[${this._pContainer.toString()}${(this.pContainerBottomBorder ? `🅱` : ``)}` +
               ` sol:${this.pContainerSOL} slop:${this.pContainerSlop}] ` +
               ` r:${stringifyTruncate(this.remainingSlice(), 15)}`
    }

    /**
     * Saves the current ptr state so that we can speculatively parse
     * ahead and backtrack on match fail. Use the restore() method on
     * the returned object to restore that state.
     *
     * A single checkpoint can be restored multiple times as
     * {@link LineCheckpoint.restore} is idempotent. For example, see its use
     * in {@link ./BlockTreeParser}'s "try each style" parse loop.
     *
     * Since checkpoints are separate objects from Line, multiple checkpoints
     * can be created for the same Line.
     *
     * todo![parsing] i'm pretty sure we don't want to checkpoint {@link Line._next}:
     *   - we'd be throwing out the EOC and EOL parsing.
     *   - the alternative would be for checkpoint to checkpoint the full _next
     *     chain of lines one-by-one, and likewise restore whole chains. This is
     *     wasteful as most of the time we don't speculatively parse past the
     *     cur line or 2 lines.
     *   - What we are already doing now is perfect: make local decisions on how
     *     to efficiently speculatively parse. For example see how
     *     {@link fwdChunkToTailOrBlank} checkpoints all the Lines it touches in
     *     the _next sequence.
     *   .
     *   IF this is right:
     *   - COMMIT TO THIS APPROACH AND DOCUMENT IT HERE OR SOMEWHERE.
     *   - delete the outdated TODOs such as the one at the end of this TSDoc
     *
     * todo![perf] is what we are doing in {@link fwdChunkToTailOrBlank}
     *   unnecessary? review. Remove and run tests to make sure.
     *
     * todo![arch] checkpoint does not save and restore Line.pContainer* state.
     *   Either:
     *     - change that fact (we may need it for speculative parsing, e.g. tables)
     *     - add a warning to this doc that checkpoint usage can't span any
     *       parsing of containers
     *       - rely on tests to catch violations of this.
     *       - put fail-fast logic that invalidates any checkpoints if pContainer
     *         is updated.
     *   GIVEN THE COMPLEXITY OF WHAT I JUST WROTE, just checkpoint the pContainer
     *   state already! Premature optimization!
     */
    checkpoint (): LineCheckpoint {
        return new Line.Checkpoint(this, this.ptr, this.ptrColumnAdj, this.ptrTabRemainder, this.pContainerSlop)
    }

    static Checkpoint = class implements LineCheckpoint {
        line: Line
        ptr: number
        ptrColumnAdj: number
        ptrTabRemainder: number
        pContainerSlop: number

        constructor (line: Line, ptr: number, ptrColumnAdj: number, ptrTabRemainder: number, pContainerSlop: number) {
            this.line = line
            this.ptr = ptr
            this.ptrColumnAdj = ptrColumnAdj
            this.ptrTabRemainder = ptrTabRemainder
            this.pContainerSlop = pContainerSlop
        }

        restore (): void {
            const line = this.line
            line.ptr = this.ptr
            line.ptrColumnAdj = this.ptrColumnAdj
            line.ptrTabRemainder = this.ptrTabRemainder
            line.pContainerSlop = this.pContainerSlop
        }
    }

    /**
     * Advances the ptr state max spaces and returns the amount. Because
     * of tab expansion, `Line.column` will advance the number of spaces
     * returned, but this does not always translate to `Line.ptr`
     * incrementing by the same amount or even at all.
     *
     * @param max
     * @param cancelIfMore if true and spaces exceed max, cancels the operation,
     *        leaving the line state unchanged.
     */
    parseSpaces (max: number = Number.POSITIVE_INFINITY, cancelIfMore = false): number {
        const src = this.src
        let spaces = 0
        let colAdj = 0

        //first consume any tab remainder
        if (this.ptrTabRemainder !== 0) {
            if (cancelIfMore && this.ptrTabRemainder > max) {
                return 0
            }
            spaces = colAdj = this.ptrTabRemainder
        }

        let ptr = this.ptr
        while (spaces < max) {
            if (src.charAt(ptr) === ' ') {
                spaces++
                ptr++
            } else if (src.charAt(ptr) === '\t') {
                const tabSpaces = 4 - ((ptr - this.SOL + this.ptrColumnAdj + colAdj) % 4)
                spaces += tabSpaces
                colAdj = tabSpaces - 1
                ptr++
            } else {
                break
            }
        }

        if (spaces > max) {
            if (cancelIfMore) {
                return 0
            }
            this.ptr = ptr
            const over = spaces - max
            this.ptrColumnAdj += colAdj - over
            this.ptrTabRemainder = over
            spaces = max
        } else {
            if (cancelIfMore && src.charAt(ptr) === ' ') {
                return 0
            }
            this.ptr = ptr
            this.ptrColumnAdj += colAdj
            this.ptrTabRemainder = 0 //consumed at the top of this method
        }

        return spaces
    }

    /**
     * Skip leading spaces up to max.
     */
    parseSlop (max: number, cancelIfMore = false) {
        this.pContainerSlop = this.parseSpaces(max, cancelIfMore)
    }

    /**
     * Parses `min` number of consecutive `ch`, returning the number parsed.
     * @param ch
     */
    parseChars (ch: string): number {
        //todo[perf] see if using /c{min,}/ regexp would be more efficient.
        const src = this.src
        let ptr = this.ptr
        let n = 0
        while (src.charAt(ptr) === ch) {
            n++
            ptr++
        }
        this.ptr = ptr
        return n
    }

    /**
     * todo[parsing] placeholder idea for factoring out oft repeated
     *  regex matching and parsing logic for these purposes:
     *  - DRY, simplify cod
     *  - encapsulation (of native Line-internals)
     *  - performance (doing line specific parsing inside Line
     *    may have benefits)
     *
     * Executes the regex at Line.ptr. If it matches:
     *  - updates Line.ptr
     *  - returns any match groups (perhaps {@link RegExpMatchArray} or
     *    {@link RegExpExecArray}
     * @param regex
     */
    parseMatch (regex: RegExp) {
        return
    }

    // todo![perf] evaluate new RegExp stuff:
    //   - `d` flag, just get start and end indices of match back:
    //     https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp/hasIndices
    //   - use `search`?
    //       "When you want to know whether a pattern is found in a string, use the
    //       test() or search() methods; for more information (but slower execution)
    //       use the exec() or match() methods. If you use exec() or match() and if
    //       the match succeeds, these methods return an array and update properties
    //       of the ...

    /**
     * todo![perf] see note on `d` flag, `test` and `search` above.
     * todo![fwd-parsing} useful?
     *
     * Executes the regex at Line.ptr, but with no side affects.
     * Instead, returns the indices of the match, so the caller
     * can efficiently decide whether and when it will update
     * Line state as a result.
     * @param regex
     */
    searchMatch (regex: RegExp): [start: number, end: number] {
        //todo Perhaps returns more indices, for any match groups.
        //  See the `d` regex flag.
        return [-1, -1]
    }

    /**
     * todo![perf] see note on `d` flag, `test` and `search` above.
     * todo![fwd-parsing} useful?
     *
     * Tests the regex at Line.ptr. No side affects
     * @param regex
     */
    testMatch (regex: RegExp): boolean {
        return false
    }

    /**
     * returns the entire line, inclusive of EOL char (newline) if there is
     * one (there may not be one at EOF).
     */
    srcLine (): string {
        return this.src.slice(this.SOL, this.EOL + 1)
    }

    // todo[perf] if this gets called a lot for the same line at the same ptr,
    //  (e.g. multiple styles trying to match), we could cache the value, and of
    //  course clear it if the the ptr changes (and thus invalidating it)
    //  - BUT ARGUABLY if this is getting called more than once per line, we
    //    are probably doing something wrong in the parsing logic.
    //  .
    //   🥃 easy test: count all calls, and after test suite, do a histogram of
    //     the number of calls per line.
    /**
     * returns the content from `Line.ptr` to EOL, inclusive of EOL char
     * (newline) if there is one (there may not be one at EOF)
     * and prepended by any unconsumed spaces from tab expansion.
     */
    remainingSlice (): string {
        if (this.ptrTabRemainder) {
            return ' '.repeat(this.ptrTabRemainder) + this.src.slice(this.ptr, this.EOL + 1)
        } else {
            return this.src.slice(this.ptr, this.EOL + 1)
        }
    }

    remainingIsBlank (): boolean {
        return this.ptr >= this.EOC
    }

    /**
     * If the next line hasn't already been determined to continue this line's
     * pContainer, then it will be parsed for that now.
     * todo![perf] if it has already previously been parsed to the same
     *   attempted container depth and failed, we'll end up redoing that
     *   work here. It may or may not be worth adding state and logic to
     *   avoid this.
     *
     * By default the next line is returned if and only if it does continue
     * this line's (deepest) pContainer *explicitly* (i.e. not lazily),
     * returning `null` if it does not.
     *
     * For bottom-bordered containers, the line(s) that are the bottom
     * border ar considered part of the container, and are returned as such.
     * They have no content besides the border. The caller can check to the
     * {@link pContainerBottomBorder} property.
     *
     * todo![test] need tests for this option
     * But if `allowSloppy` is `true`, then the next line will be returned if
     * it might lazily continue this line's pContainer. This will be true if
     * the remaining part of the line isn't empty and isn't a container bottom
     * border. Unlike {@link next} the returned line's parse state and
     * pContainer value will reflect the deepest continuing container even if
     * it doesn't continue all of them. This is needed for forward parsing
     * elements that can span lazily continued lines, e.g. CommonMark Setext
     * headings.
     */
    get nextInContainer (): Line | null {
        return this.getNextInContainer()
    }

    getNextInContainer (allowLazy = false): Line | null {
        if (this.pContainerBottomBorder !== null) {
            return null
        }
        const next = this.next
        if (next === null) {
            return null
        }
        // always true when we are in root container, so already optimized
        if (next._pContainer === this._pContainer) {
            return next
        }

        // todo[fwd-parsing] How do we know whether we've already made
        //   the `parseContinuation` call before? i.e. whenever we parse fwd via
        //   `nextInContainer`, we stop either on a blank line or a next line
        //   that is not in the cur Container. But if the next style parse does
        //   the same thing, we redundantly do the `parseContinuation` call below.
        //   OPTIONS:
        //     A. If the container continues on the next line:
        //        - we would not make it past the above checks AS LONG AS next._container
        //          is updated? If we DO NOT have a guarantee that the caller will do that,
        //          we should update Line._container in parseContinuation ALONG WITH the
        //          other Line state changes made. IT SHOULD BE AN ATOMIC change anyway.
        //     B. If the container terminates on this line:
        //        - 📥 we need to store that fact somehow.
        //     C. If the next line is a closing border:
        //        - 📥 we need to store that fact, both for the sake of
        //          this method, but also so that the bottom border doesn't need to
        //          be parsed twice.
        //   .
        //   For neither A, B or C do we need to reset anything the way we used to
        //   clear line._nextInContainer when the container changes. This is because
        //   for each of the paths A, B and C:
        //     A. If this line's container changes, we WILL now make it past the above
        //        checks and a new parseContinuation will be triggered.
        //     B. If we find a new nested container on this line, it won't change the
        //        fact that a parent container failed to continue on the next line.
        //     C. Same as for B: it won't change the fact.
        console.debug(`nextInContainer: parseContinuation: ${this._pContainer.toString()}  from line`, next.num, `${JSON.stringify(next.srcLine())}`)
        console.group()
        console.debug(next.toString())
        parseContinuation(this._pContainer, next)
        console.groupEnd()

        if (next._pContainer !== this._pContainer) {
            if (allowLazy && next.pContainerBottomBorder === null && !next.remainingIsBlank()) {
                return next
            }
            return null
        }

        return next
    }

    /**
     * Extract a block of content from current container context, starting from
     * this line and line.ptr, and ending on whichever of the following occurs
     * first:
     *  - a blank line (non inclusive), *if* `stopOnBlank` is true.
     *  - the line matching `stopPattern` (inclusive), if such pattern is given.
     *  - the last line of this line's container
     *  - EOF
     */
    fwdBlock (stopOnBlank: boolean, stopPattern?: RegExp): [text: string, endLine: Line] {
        return fwdBlock(this, stopOnBlank, stopPattern)
    }

    /**
     * Extracts a chunk (cannot span blank line) of content from current container
     * context, starting from this line and line.ptr.
     *
     * IMPORTANT: It is assumed that the line on which this is called belongs to
     * the current element, and therefor the following will ONLY be applied to
     * subsequent lines:
     * - parsing start of line indent or slop
     * - interruption checks
     *
     * The extent of the chunk is determined as follows:
     *  - If `tailPattern` given:
     *    - to the line ending in this pattern if such is found.
     *      - BUT if trailingBlankRequired is true, and the line following
     *        the matching line is not blank, returns null
     *    - if no such match is found before a blank line or end of container or file
     *      is encountered, it returns null
     *
     *  - If `tailPattern` is NOT given:
     *    - to a blank line, end of container or EOF, whichever comes first.
     *
     *  - In addition, indentation rules and interruption checks will be applied
     *    and can also terminate teh chunk.
     *
     * Because we don't want TextPlain to reject inputs or exit with errors,
     * we are going to silently ignore the case where the regex doesn't consume
     * to an EOL. Consuming even a single char of a line constitutes consuming it.
     * (todo[doc] Add this to PTSS spec)
     *
     * @returns [<the last line of the chunk>,
     *    <the body of the chunk up to but not including the tailPattern>,
     *    <the RegExpMatchArray resulting from the tailPattern match>]
     *
     *! ⚠️ DO NOT pass in allowedInterruptions if this call is itself part
     *     of an interruption check. All it will do is waste CPU cycles on a
     *     recursive check when a recursive check makes no sense!
     */
    fwdChunk (indent = 0,
              bodyPattern?: RegExp,
              tailPattern?: RegExp,
              trailingBlankRequired = true,
              //todo![test] ZERO test coverage for non-default values for the
              //  next four args
              maxLines = Number.MAX_VALUE,
              allowedInterruptions: ElementalBlockStyle[] | undefined = undefined,
              sloppyMargin = 0,
              allowLazy = false)
        // todo: string[] vs RegExpMatchArray inconsistency: unintentional.
        //   just got code to work. Cleanup later
        : [bodyMatch: string[], tailMatch: RegExpMatchArray | null, endLine: Line] | null {

        // todo![fwd-parsing] what about indent > 0 ???
        if (tailPattern !== undefined || trailingBlankRequired || bodyPattern === undefined) {
            return fwdChunkToTailOrBlank(this, indent, tailPattern,
                trailingBlankRequired, bodyPattern, maxLines, allowedInterruptions,
                sloppyMargin, allowLazy)
        }
        return fwdChunkToBodyMatch(this, bodyPattern, indent, maxLines, allowedInterruptions,
            sloppyMargin, allowLazy)
    }

    /**
     * Rules:
     *    - ✅ FIRST: current element always controls whether it can be interrupted. For
     *      example, a left border container with continuing left border doesn't
     *      check for interruption at all (the matching left border takes precedence)
     *      and a table that clearly sees table continuation because of column
     *      patterns doesn't check for interruption.
     *      - 📥 Make sure this is in the rules.
     *
     *    - ✅ SECOND, the set of style that *could* interrupt the current
     *      element is calculated, essentially applying adjacency rules.
     *
     *      Currently this is a static list calculated when the style is
     *      initialized, but if it needed to be more dynamic, it could do that).
     *
     *      We can evolve this into something like my idea for *adjacency
     *      control*, or *interruption control*. Adjacency control may
     *      result in violations of the "Always be recognizing rule", so
     *      would only be used for `strict syntax` styles sheets).
     *      Interruption control, I think, may be compatible with
     *      non-strict syntax style sheets.
     *
     *      Or we need some other generalized solution
     *      that can, for example:
     *      - in test-ptss-one, prevent 'Level 3/4 Setext Heading' form
     *        interrupting 'Level 1/2 Setext Heading', which would otherwise
     *        happen EVERY TIME!
     *
     *      I'm sure I'll have more examples as I implement Pandoc/GFM
     *
     *    - ✅ THIRD, each allowed interrupting style DECIDES whether it interrupts.
     *      1. Obviously it has to match to interrupt, something it decides.
     *      2. BUT EVEN ON A MATCH, it may decline, e.g.:
     *         - The style may require a preceding blank line.
     *         - A list item may decline if the start num is not 1 (CommonMark rule)
     *         - A list item may decide to interrupt ONLY if there are at least
     *           two items in the list (potential *Natural* rule)
     *      3. Precedence rules apply. The interrupted element doesn't get to choose.
     *      4. Likewise, the interrupted element doesn't get to choose a subset of
     *         allowed styles that can interrupt.
     *         - IF WE ALLOW THIS, it will only be a quirk mode for certain legacy styles.
     *      5. If we implement adjacency control, the rules for that may reduce the
     *         set of styles that can interrupt, but the interrupted element again
     *         doesn't get to control that.
     *
     * todo![docs, spec, interruption] move the above into ptss docs or test cases.
     *   dev strategy 🌶:
     *    - STOP TRYING TO DO ALL THIS IN YOUR HEAD, IMAGINING ALL THE PERMUTATIONS.
     *      Instead: create test cases that cover all the permutation, and let those
     *      drive the code, parsing principles and the PTSS spec.
     *      - Gracedown.ptss & RubyRhod.ptss
     *      - MarkdownHS.ptss
     *        -- This can do better than GFM/CommonMark, and *disregard their rules
     *           when they clearly violate what the human eye sees*.
     *           📥 Do I have this documented as MarkdownHS's purpose?
     *      - PlainJane.ptss
     */
    checkStyleMatch (styles: ElementalBlockStyle[]) {
        console.debug('checking for interruption')
        console.group()
        const checkpoint = this.checkpoint()
        for (const style of styles) {
            const r = style.parse(this, null, true)
            checkpoint.restore()
            if (r !== null) {
                console.debug(`Interruption by ${style}`)
                console.groupEnd()
                return true
            }
        }
        console.groupEnd()
        return false
    }

}


//todo[arch] either figure out how to make this inner to Line, or use namespaces ??
export type LineCheckpoint = {
    line: Line;
    ptr: number;
    ptrColumnAdj: number;
    ptrTabRemainder: number;
    /**
     * Restores the line's state back to its state when the checkpoint was
     * made.
     *
     * `restore` is idempotent and can be called repeatedly to restore a line
     * to the same point any number of times.
     */
    restore (): void
}

// todo![now] is there a reason the functions below aren't private class members?
//   can use # to enforce this.

function fwdBlock (startLine: Line,
                   stopOnBlank: boolean,
                   stopOnPattern?: RegExp): [text: string, endLine: Line] {
    console.debug(`fwdBlock: startLine:${startLine.num}` +
                  ` stopOnBlank:${stopOnBlank} stopPattern:${stopOnPattern}`)
    console.group()

    //todo![perf] why CAN'T WE DO ROOT CONTAINER OPTIMIZATION?
    // not just here, but in the other block extraction methods?
    // Can I resurrect the logic i nixed soon after HWM, which some
    // adjustments obviously (there was a reason i nixed it)
    //console.debug(`💥 ROOT OPTIMIZATION ROUTE`)

    let content = ''
    let line = startLine
    for (; ;) {
        const s = line.remainingSlice()
        content += s
        console.debug('+', line.num, JSON.stringify(s))
        if (stopOnPattern) {
            stopOnPattern.lastIndex = 0
            if (stopOnPattern.test(s)) {
                break
            }
        }
        const nextLine = line.nextInContainer
        if (nextLine === null
            || (stopOnBlank && nextLine.remainingIsBlank())
            || nextLine.pContainerBottomBorder !== null) {
            break
        }
        line = nextLine
    }
    console.debug(`endLine: ${line}`)
    console.groupEnd()
    return [content, line]
}

/**
 * fwd to tailPattern if given, else to blank line. If blankAfterTail is true, then
 * succeeds only on tail match AND blank line boundary.
 *
 * todo[perf] The root optimization discussed under fwdChunkToBodyMatch might
 *   make more sense for this method, as I think it will be used more, e.g.
 *   Setext Headings.
 *
 * todo[test] AllowLazy is NOT tested at all, no styles as of this writing
 *    use it for body match defined chunks
 */
function fwdChunkToTailOrBlank (startLine: Line,
                                indent: number,
                                tailPattern?: RegExp,
                                blankAfterTail = true,
                                bodyPattern?: RegExp,
                                maxLines = Number.MAX_VALUE,
                                allowedInterruptions: ElementalBlockStyle[] | undefined = undefined,
                                sloppyMargin = 0,
                                allowLazy = false)
// todo: string[] vs inconsistency: unintentional. just got code to work. Cleanup later
    : [bodyMatch: string[], tailMatch: RegExpMatchArray | null, endLine: Line] | null {

    console.debug(`fwdChunk: startLine:${startLine.num} indent:${indent} ` +
                  `tailPattern:${tailPattern} blankAfterTail:${blankAfterTail} ` +
                  `bodyPattern:${bodyPattern} maxLines:${maxLines} ` +
                  `allowedInterruptions:${allowedInterruptions !== undefined} ` +
                  `sloppyMargin:${sloppyMargin} allowLazy:${allowLazy} `)
    console.group()

    let body = ''
    let bodyMatch: string[] | null
    let tailMatch: RegExpMatchArray | null = null
    const checkpoints: LineCheckpoint[] = []

    // first line is special:
    //  - it's already had any slop or indent parsed out
    //  - a head pattern may have already consumed part of it
    //  - it cannot be interrupted, both because has already
    //    been partially consumed AND because precedence is how
    //    you beat a style on the first line.
    const firstRemaining = startLine.remainingSlice()
    let lastLine = startLine
    if (tailPattern) {
        tailPattern.lastIndex = 0
        tailMatch = tailPattern.exec(firstRemaining)
        if (tailMatch) {
            const b4tail = firstRemaining.slice(0, tailMatch.index)
            if (b4tail.length > 0) {
                body += b4tail
                console.debug('+', lastLine.num, JSON.stringify(b4tail))
            }
            console.debug('🆃', lastLine.num, JSON.stringify(tailMatch[0]))
        }
    }

    let i = 1
    if (!tailMatch) {
        body += firstRemaining
        console.debug('+', lastLine.num, JSON.stringify(firstRemaining))

        for (; i < maxLines; i++) {
            const line = lastLine.getNextInContainer(allowLazy)
            if (line === null
                || line.remainingIsBlank()
                || line.pContainerBottomBorder !== null) {
                break
            }
            //todo[sloppiness] per compare/sloppiness.md tests as well as all per
            //  all non-tail matching via fwd parsing styles, sloppiness ONLY
            //  applies to the anchor line, not every line of the chunk.
            //  -
            //  CHOOSE:
            //  - stick with this model, and fix this code (are any tests failing because of this?)
            //  - make it optional, e.g. let style sheet decide whether sloppiness applies to every line,
            //    though i'm not sure if that won't break everything!
            //  - decide that it ALWAYS applies to every line... but I think that DOES break everything.
            //  ALSO:
            //  - see notes in sloppiness.md. We may need a more fine grained model rather
            //    than just a style sheet-wide sloppiness setting.
            if (sloppyMargin > 0 || indent > 0) {
                const cp = line.checkpoint()
                line.parseSlop(sloppyMargin, true)
                if (line.parseSpaces(indent) < indent) {
                    cp.restore()
                    break
                }
                checkpoints.push(cp)
            }
            const s = line.remainingSlice()
            if (tailPattern) {
                tailPattern.lastIndex = 0
                tailMatch = tailPattern.exec(s)
                if (tailMatch) {
                    const b4tail = s.slice(0, tailMatch.index)
                    lastLine = line
                    if (b4tail.length > 0) {
                        body += b4tail
                        console.debug('+', lastLine.num, JSON.stringify(b4tail))
                    }
                    console.debug('🆃', lastLine.num, JSON.stringify(tailMatch[0]))
                    break
                }
            }
            if (allowedInterruptions && line.checkStyleMatch(allowedInterruptions)) {
                break
            }
            body += s
            lastLine = line
            console.debug('+', lastLine.num, JSON.stringify(s))
        }
    }

    // enforce tail rules
    if (i === maxLines) {
        restoreCheckpoints(checkpoints)
        console.debug('maxLines exceeded')
        console.groupEnd()
        return null
    }
    if (tailPattern) {
        if (tailMatch === null
            // or if the next line in the same container is not blank
            || (blankAfterTail
                //! i'm pretty sure we don't want a lazy non-blank line to fail the
                //   match, i.e. we don't want to call getNextInContainer(allowLazy)
                //   as we do above.
                && lastLine.nextInContainer
                && !lastLine.nextInContainer.remainingIsBlank())) {
            restoreCheckpoints(checkpoints)
            console.debug('failed tail match')
            console.groupEnd()
            return null
        }
    }

    console.debug(`body: ${JSON.stringify(body)}`)
    if (bodyPattern) {
        bodyPattern.lastIndex = 0
        bodyMatch = bodyPattern.exec(body)

        if (!bodyMatch) {
            restoreCheckpoints(checkpoints)
            console.debug('failed body match')
            console.groupEnd()
            return null
        }

        console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)} lastIndex: ${bodyPattern.lastIndex}`)

    } else {
        bodyMatch = [body]
    }

    console.debug(`tailMatch ${tailMatch}`)
    console.groupEnd()
    return [bodyMatch, tailMatch, lastLine]
}


/**
 *
 * todo[perf] we are reading lines until we reach a blank line or end of
 *   container, without checking for a body match after each successive time.
 *   I think this is smart: maybe 9 times out of 10 chunks that rely on this
 *   (e.g. link ref defs) will have a blank line after. Later, we can assess
 *   this empirically be scanning a large corpus of documents.
 *
 *
 * todo[perf] Optimize for root container, prob at least 90% of cases? 🏈
 * ------------------------------------------------------------------
 * The current implementation extracts out the "nested"
 * part of each line. This is unnecessary CPU and memory usage for
 * for far more common root container content.
 *
 * CONSIDERATIONS
 * --------------
 * ### Maybe not worth it, at least not for v0.1
 *  - For Link Ref Def parsing,
 *     - We were already at 80% of Commonmark.js's perf
 *     - 62% of markdown-it's.
 *    This is using the practical samples, not the patho
 *    cases in the samples from Commonmark.js's codebase.
 *  - 20% performance loss, or even the 40% compared to
 *    Markdown-it, for this particular parsing case will
 *    translate to a tiny even negligible fraction of the
 *    cost of parsing a typical document if few or rarely
 *    occurring styles use it. For CommonMark, it's just
 *    LinkRefDefs, which are relatively infrequent.
 *
 * ### Results of trial implementation Aug 2020
 * - Using practical link ref def bench samples, it yielded a
 *   10% gain for root link ref defs and, as expected,
 *   0% gain for those nested in block quotes.
 * - FAR MORE IMPORTANTLY, using a truly practical sample
 *   of README files showed 0% gain even though all its
 *   link ref defs were root level.
 *
 * ### The optimization itself had inefficiencies
 * - Javascript does not support end-bounded regex matching.
 *   One can specify the start index only. Thus the Regex
 *   may match many many lines past the first blank or even
 *   past many blank lines, or even past nested container
 *   starts, and the best we can do is *after the fact* see
 *   if the match crossed a blank line and reject the match.
 *   - One solution is to dynamically update the user-defined
 *     regex to incorporate a blank line check. We don't want
 *     to complicate things for the user and make them do it.
 *     TBD whether this is possible. Not all fwd chunks are
 *     required to end on a blank line, so we can't just append
 *     a blank line constraint at the end. We'd have to modify
 *     the pattern internally to make sure none of its parts
 *     can match `/\n\w+\n/`
 *
 * ### IF WE CAN INCORPORATE THE BLANK LINE CHECK INTO THE REGEX...
 * it might be a gain for cheap. Because we
 * - could ditch the line-by-line check for blank lines that
 *   we do now, though that should be cheap since Line.ts makes
 *   it nearly free on subsequence calls.
 * - we could run through the lines of the match after the fact
 *   to see if it was interrupted on any of the lines.
 *
 * ### More natural styles might not use fwdChunkToBodyMatch anyway.
 * They might be more likely to have tail or blank line delimiters,
 * not an arbitrary body match like Link Ref Def. In fact, just
 * looking at the complexity of its bodyPattern begs for something
 * more author friendly.
 *
 * 🌶 In fact, a hanging indent syntax consistent with footnotes
 *    makes far more sense.
 *
 * ### conclusion: PUNT
 * Unless we see evidence that fwdChunk to body match parsing
 * is causing a significant amount of impact for any reasonable
 * style sheet design against reasonable input samples, stop
 * worrying about this for now, if ever.
 *
 *
 * todo[test] allowLazy is NOT tested at all, no styles as of this writing
 *    use it for body match defined chunks.
 *
 */
function fwdChunkToBodyMatch (startLine: Line,
                              bodyPattern: RegExp,
                              indent: number,
                              maxLines = Number.MAX_VALUE,
                              allowedInterruptions: ElementalBlockStyle[] | undefined = undefined,
                              sloppyMargin = 0,
                              allowLazy = false)
    : [bodyMatch: string[], tailMatch: null, endLine: Line] | null {

    console.debug(`fwdChunkToBodyMatch: startLine:${startLine.num} ` +
                  `bodyPattern:${bodyPattern} indent:${indent} maxLines:${maxLines} ` +
                  `allowedInterruptions:${allowedInterruptions !== undefined} ` +
                  `sloppyMargin:${sloppyMargin} allowLazy:${allowLazy} `)
    console.group()

    // startLine has already had leading whitespace parsed and
    // is uninterruptible, so we deal with it first
    let content = startLine.remainingSlice()
    const lines: Line[] = [startLine]
    const offsets: number[] = [0]
    const checkpoints: Array<LineCheckpoint | null> = [null] // startLine no checkpoint

    let line: Line | null = startLine
    for (let i = 1; i < maxLines; i++) {
        line = line.getNextInContainer(allowLazy)
        if (line === null
            || line.remainingIsBlank()
            || line.pContainerBottomBorder !== null) {
            break
        }
        if (sloppyMargin > 0 || indent > 0) {
            // parsing spaces messes with a line's parse state
            const cp = line.checkpoint()
            line.parseSlop(sloppyMargin, true)
            if (line.parseSpaces(indent) < indent) {
                cp.restore()
                break
            }
            checkpoints.push(cp)
        } else if (allowedInterruptions && line.checkStyleMatch(allowedInterruptions)) {
            break
        } else {
            // we only need to checkpoint a line when we mess with its parse state
            checkpoints.push(null)
        }
        offsets.push(content.length)
        content += line.remainingSlice()
        lines.push(line)
    }
    console.debug(`fwd block: ${JSON.stringify(content)}`)

    bodyPattern.lastIndex = 0
    const bodyMatch = bodyPattern.exec(content)
    if (!bodyMatch) {
        restoreCheckpoints(checkpoints)
        console.debug('failed body match')
        console.groupEnd()
        return null
    }
    console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)} lastIndex: ${bodyPattern.lastIndex}`)

    // figure out lines consumed (see doc above), restoring checkpoint for those not
    const len = bodyMatch[0].length
    let i = offsets.length
    for (; ;) {
        if (i < 0) {
            console.debug('unexpected: body match, but zero lines')
            console.groupEnd()
            return null
        }
        i--
        line = lines[i]
        if (len > offsets[i]) {
            break
        }
        checkpoints[i]?.restore()
    }
    console.debug(`lines ${startLine.num}-${line.num}`)

    console.groupEnd()
    return [bodyMatch, null, line]
}

function restoreCheckpoints (checkpoints: (LineCheckpoint | null)[]): void {
    for (const cpt of checkpoints) {
        cpt?.restore()
    }
}
