type PipelineParts<OUTLET> = {
    inlet: Parse,
    pipes: Pipe[],
    outlet: OUTLET
}

// todo![pipeline api] the i/o signature needs to sync with update pipeline architecture
type Pipeline<IN, OUT, OUTLET> = PipelineParts<OUTLET> & {
    // the connected pipeline function
    pump: (input: IN) => OUT
}
export type SimplePipeline = Pipeline<string | STIR, string, RenderSTIR>
export type AssemblyPipeline =
    Pipeline<File, void, AssemblyRenderer> & {
    prime (): void,
    flush: () => void,
    stats: () => { [p: string]: RenderStats }
}


async function configPipes (configs?: Config[]): Promise<Pipe[]> {
    if (configs === undefined) {
        return []
    }
    const pipes: ((stir: STIR) => void)[] = []
    for (const cfg of configs) {
        const pipe = await configPipeProvider(cfg)
        if (pipe !== undefined) {
            pipes.push(pipe)
        }
    }
    return pipes
}

/**
 * Construct a pipeline programmatically, without a Config object or file.
 */
export function simplePipeline (inlet: Parse,
                                pipes: Pipe[],
                                outlet: RenderSTIR): SimplePipeline {
    const pump = (input: string | STIR) => {
        console.debug(`simple pipeline`)
        console.group()

        let stir: STIR
        if (typeof input === 'string') {
            console.debug(`INPUT: string: '${input.slice(0, 42)}...'`)
            stir = inlet(input)
        } else {
            if (ir.isBlock(input)) {console.debug(`INPUT stir: ${input.type}`)} else {console.debug(`INPUT stir: inline sequence`)}
            stir = input
        }

        for (let i = 0; i < pipes.length; i++) {
            pipes[i](stir)
        }

        const output = outlet(stir)
        console.groupEnd()
        return output
    }
    return {pump, inlet, pipes, outlet}
}

/**
 * For clients who already have a Config, or need to instantiate one themselves for whatever reason.
 */
export async function simplePipelineFromConfig (config: Config): Promise<SimplePipeline> {
    try {
        console.group(`configure simple pipeline segments per ${config.name()}`)
        const inlet = await configParseProvider(config.requiredSubConfig('inlet'))
        const pipes = await configPipes(config.subConfigs('pipes'))
        const outlet = await configRenderSTIRProvider(config.requiredSubConfig('outlet'))
        console.groupEnd()
        return simplePipeline(inlet, pipes, outlet)
    } catch (e) {
        console.error('--------------------- PIPELINE CONFIGURATION ERROR ---------------------')
        console.error(e)
        console.error()
        // will only print config for debug log level
        console.dir(config, {depth: null})
        console.error('-------------------------------------------------------------------------')
        throw Error('Textplain pipeline configuration error', {cause: e})
    }
}

export async function simplePipelineFromConfigFile (configPath: VfsPath): Promise<SimplePipeline> {
    const config = newConfigForVfsPath(configPath)
    return await simplePipelineFromConfig(config)
}


function assemblyPipeline (assembly: Assembly,
                           inlet: Parse,
                           pipes: Pipe[],
                           outlet: AssemblyRenderer): AssemblyPipeline {
    const pipeAndRenderSTIR = (stir: STIR, file: File) => {
        const tStir = stir as TunnelingSTIR
        tStir._tunnel = {
            srcFile: file,
            srcAssembly: assembly
        }
        for (let i = 0; i < pipes.length; i++) {
            pipes[i](stir)
        }
        outlet.render(stir)
    }


    const pump = (file: File) => {
        console.debug('assembly pipeline')
        console.group()

        //todo![renderAssembly, api] user control over which files are handled in which way?
        // Via pipeline config
        // Specifically:
        //   - how are files routed to parsers and particular stylesheets?
        //   - of the remaining files, which are resources to be copied?
        //     > SEE DOC NOTES under "resource copying", or search for "whitelist"
        //     > to find the notes.

        //todo[renderAssembly] TEMP HACK routing logic. TEMP location.
        // - we should have a registry that maps filetypes to actions?
        const type = fileTypeByFilename(file.name)
        if (type.name === 'MARKDOWN') {
            const input = file.readFileSync() //todo![renderAssembly] this should be async
            console.debug(`INPUT: structured text: '${input.slice(0, 42)}...'`)
            const stir = inlet(input)
            pipeAndRenderSTIR(stir, file)
        } else if (file.isGenerated()) {
            const stir = file.stir as STIR
            if (ir.isBlock(stir)) {console.debug(`INPUT: generated stir: ${stir.type}`)} else {console.debug(`INPUT stir: inline sequence`)}
            pipeAndRenderSTIR(stir, file)
        } else if (type.name === 'STIR (JSON)') {
            const stir = JSON.parse(file.readFileSync())
            if (ir.isBlock(stir)) {console.debug(`INPUT: stir: ${stir.type}`)} else {console.debug(`INPUT stir: inline sequence`)}
            pipeAndRenderSTIR(stir, file)
        } else if (type.name === 'TEXT' && file.parent.name === 'spec/') {
            //todo[hack] hack for commonmark case files having txt extension
            const input = file.readFileSync() //todo![renderAssembly] this should be async
            console.debug(`INPUT: commonmark spec: '${input.slice(0, 42)}...'`)
            const stir = inlet(input)
            pipeAndRenderSTIR(stir, file)
        } else if (type.name === 'HTML') {
            console.debug(`INPUT: html: ${file.extension()}`)
            // todo[renderAssembly] need to treat HTML as content to render, since we need
            //  to at least resolve cross references in links. It will of course
            //  render to the same type for Assembly2Website, but to some
            //  other form in other renderers, e.g. LaTeX or PDF.
            //  so need to "render" the src HTML to the dest HTML.
            // todo[hack] for now, treat it as a resource
            file.status = 'resource: in pipeline'
            outlet.copy(file)
        } else {
            if (type.type === 'media') {
                console.debug(`INPUT: resource: ${file.extension()}`)
                outlet.copy(file)
            } else {
                console.debug(`SKIPPING`)
            }
        }

        console.groupEnd()
    }

    // todo[pipeline api] 🏈 Question: should all pipeline components get a
    //  prepare step? Thoughts:
    //  - The distinction between component initialization (when they are
    //    instantiated and configured per the config) and this prime/prepare
    //    step is that the latter has access to the assembly data.
    //  - I don't think inlets and pipes need or should need any preparation
    //    that is dependent on the entire assembly, but maybe I haven't thought
    //    of something for which it makes sense.
    const prime = () => {
        outlet.prepare(assembly)
    }

    const flush = () => {
        outlet.finish()
    }

    const stats = () => {
        return outlet.stats()
    }

    return {prime, pump, flush, stats, inlet, pipes, outlet}
}


/**
 * Creates a pipeline to process the given TextAssembly.
 *
 * All items fed into it are assumed to belong to the same assembly,
 * and the backend of the pipeline will receive all of the individually
 * processed items and re-assemble them into an output, typically a
 * rendering of the text assembly into another form, e.g. a website.
 *
 * @param {Assembly} assembly
 * @param {SandboxFS} dest
 * @param {Config} config
 * @param {PipelineParts<AssemblyRenderer>} parentPipeline?
 * @return {Promise<AssemblyPipeline>}
 */
export async function assemblyPipelineFromConfig (assembly: Assembly,
                                                  dest: SandboxFS,
                                                  config: Config,
                                                  parentPipeline?: PipelineParts<AssemblyRenderer>)
    : Promise<AssemblyPipeline> {
    try {
        console.info(`configure assembly pipeline segments per ${config.name()}`)
        console.group()

        //todo![renderAssembly, test] pipeline inheritance from parent pipeline

        // downtree inlet overrides uptree
        let inlet
        if (!config.hasValue('inlet') && parentPipeline?.inlet !== undefined) {
            inlet = parentPipeline.inlet
        } else {
            inlet = await configParseProvider(config.requiredSubConfig('inlet'))
        }

        // downtree `pipes` overrides uptree `pipes` but if downtree
        // does not have `pipes`:
        //  = downtree `prepend_pipes` are prepended to uptree `pipes`
        //  - downtree `append_pipes` are appended to uptree `pipes`
        const pipes: ((stir: STIR) => void)[] = []
        if (config.hasValue('pipes')) {
            pipes.push(...await configPipes(config.requiredSubConfigs('pipes')))
        } else {
            if (config.hasValue('prepend_pipes')) {
                pipes.push(...await configPipes(config.requiredSubConfigs('prepend_pipes')))
            }
            if (parentPipeline?.pipes !== undefined) {
                pipes.push(...parentPipeline.pipes)
            }
            if (config.hasValue('append_pipes')) {
                pipes.push(...await configPipes(config.requiredSubConfigs('append_pipes')))
            }
        }

        // only one AssemblyRenderer, the one defined at the root, is allowed.
        // all downtree AssemblyRenderers are ignored.
        let outlet
        if (parentPipeline !== undefined) {
            outlet = parentPipeline.outlet
        } else { // this is the root pipeline
            const outletConfig = config.requiredSubConfig('outlet')
            outlet = await configAssemblyRendererProvider(dest, outletConfig)
        }

        console.groupEnd()
        return assemblyPipeline(assembly, inlet, pipes, outlet)
    } catch (e) {
        console.error('--------------------- PIPELINE CONFIGURATION ERROR ---------------------')
        console.error(e)
        console.error()
        // will only print config for debug log level
        console.dir(config, {depth: null})
        console.error('-------------------------------------------------------------------------')
        throw Error('Textplain pipeline configuration error', {cause: e})
    }
}
