import type {Block} from './block.js'
import {toJSON} from './serialization.js'

export type InlineSequence = string | (Inline | string)[]

export class Inline {

    /**
     * e.g. paragraph, heading, item, block_quote, strong, image...
     *
     * todo[api, arch] TEMP HACK: JSON deserialization code is currently using
     * the `_` prefix or lack thereof to distinguish between Block and Inline
     * elements in the JSON representation. See `fromJSON` and `fromObject`.
     */
    protected readonly _type: string
    public subtype: string | undefined

    public id: string | undefined

    /**
     * The main content.
     *
     *! `body` is special.
     *    - It is the main content tree
     *    - recurseInlineBodyTree walks the inline tree defined by body. It is used, for example, to:
     *      - recursively search for elements needing targeting IDs.
     *      No other part of a block's content model is recursively searched for these things.
     */
    public body?: InlineSequence

    // link, image
    public referent: Block | Inline | string | undefined
    public desc: string | undefined

    [key: string]: unknown

    constructor (type: string) {
        this._type = type
    }

    get type (): string {
        return this._type
    }

    bodyAppend (content: Inline | string | InlineSequence): void {
        if (this.body === undefined) {
            if (typeof content === 'string' || Array.isArray(content)) {
                this.body = content
            } else {
                this.body = [content]
            }
        } else if (typeof this.body === 'string') {
            if (Array.isArray(content)) {
                this.body = [this.body, ...content]
            } else {
                this.body = [this.body, content]
            }
        } else {
            if (Array.isArray(content)) {
                this.body.push(...content)
            } else {
                this.body.push(content)
            }
        }
    }

    toString () {
        return toJSON(this, 2, true)
    }

    inlineBodyText():string {
        return inlineBodyText([this])
    }

    traverseBodyTree(func: (inline: Inline | string) => void): void {
        traverseInlineBodyTree([this], func)
    }
}

//todo![now, test] make sure this doesn't produce run-on words.
export function inlineBodyText (seq: InlineSequence): string {
    if (!seq || seq === '') {
        return ''
    }
    let text = ''
    traverseInlineBodyTree(seq, function (inline): void {
        if (typeof inline === 'string') {
            text += inline
        }
    })
    return text
}

/**
 *
 * todo[perf, arch] would a this traverse benefit from a non-recursive
 *    implementation like `traverseBodyTree`? And with both pre- and
 *    post-order traversal callbacks like textAssembly/travers.ts?
 *
 * @param {InlineSequence} seq
 * @param {(inline: (Inline | string)) => void} func
 */
export function traverseInlineBodyTree (seq: InlineSequence, func: (inline: Inline | string) => void): void {
    if (typeof seq === 'string') {
        func(seq)
    } else {
        for (const item of seq) {
            func(item)

            // recurse body tree
            if (typeof item !== 'string' && item.body) {
                traverseInlineBodyTree(item.body, func)
            }
        }
    }
}
