import type StateInline from 'markdown-it/lib/rules_inline/state_inline.js'
import type Token from 'markdown-it/lib/token.js'
import markdownItUtils from 'markdown-it/lib/common/utils.js'
import type {RuleTokenizer} from './types.js'
import {stringifyValue} from '../ptss/index.js'


const isSpace = markdownItUtils.isSpace

export function createRule (): RuleTokenizer {

    /**
     * Modified version of markdown-it/lib/rules_inline/image.js
     * Mods annotated with "//!" comments.
     */
    function image (state: StateInline, silent: boolean) {
        let src
        let desc
        const max = state.posMax

        if (state.src.charCodeAt(state.pos) !== 0x21/* ! */) { return false }
        if (state.src.charCodeAt(state.pos + 1) !== 0x5B/* [ */) { return false }

        // ![alt](  <src>  "title"  )
        //  ^^^^^  parse alt text
        const altStart = state.pos + 2
        const altEnd = state.md.helpers.parseLinkLabel(state, state.pos + 1, false)

        // parser failed to find ']', so it's not a valid image
        if (altEnd < 0) { return false }

        console.debug(`parsing image`)
        console.group()

        let pos = altEnd + 1
        if (pos < max && state.src.charCodeAt(pos) === 0x28/* ( */) {
            pos++
            //
            // inline src
            //

            src = ''

            // ![alt](  <src>  "title"  )
            //        ^^ skipping these spaces
            for (; pos < max; pos++) {
                const code = state.src.charCodeAt(pos)
                if (!isSpace(code) && code !== 0x0A) { break }
            }

            // ![alt](  <src>  "title"  )
            //          ^^^^^ parsing image src
            let start = pos
            let res = state.md.helpers.parseLinkDestination(state.src, pos, state.posMax)
            if (res.ok) {
                src = state.md.normalizeLink(res.str)
                if (state.md.validateLink(src)) {
                    pos = res.pos
                } else {
                    src = ''
                }

                // ![alt](  <src>  "title"  )
                //               ^^ skipping these spaces
                start = pos
                for (; pos < max; pos++) {
                    const code = state.src.charCodeAt(pos)
                    if (!isSpace(code) && code !== 0x0A) { break }
                }

                // ![alt](  <src>  "title"  )
                //                 ^^^^^^^ parsing image title
                res = state.md.helpers.parseLinkTitle(state.src, pos, state.posMax)
                if (pos < max && start !== pos && res.ok) {
                    desc = res.str
                    pos = res.pos

                    // ![alt](  <src>  "title"  )
                    //                        ^^ skipping these spaces
                    for (; pos < max; pos++) {
                        const code = state.src.charCodeAt(pos)
                        if (!isSpace(code) && code !== 0x0A) { break }
                    }
                }
            }

            if (pos < max && state.src.charCodeAt(pos) === 0x29/* ) */) {
                pos++
                console.debug(`inline src: ${src}`)
            } else {
                pos = altEnd + 1
                // not inline src, try as named value
                src = undefined
            }
        }

        if (src === undefined) {
            //
            // reference src
            //

            let ref
            if (pos < max && state.src.charCodeAt(pos) === 0x5B/* [ */) {
                const start = pos + 1
                // ![alt][ref]
                //        ^^^ parsing ref
                pos = state.md.helpers.parseLinkLabel(state, pos)
                if (pos >= 0) {
                    ref = state.src.slice(start, pos++)
                } else {
                    pos = altEnd + 1
                }
            }
            // ![alt][ref]  ref is non empty string   CommonMark "full reference link"
            // ![alt][]     ref is ''                 CommonMark "collapsed reference link"
            // ![alt]       ref is undefined          CommonMark "shortcut reference link"

            // if collapsed or shortcut, use alt text as ref
            if (ref === undefined || ref === '') {
                ref = state.src.slice(altStart, altEnd)
            }

            console.debug(`reference name: ${ref}`)
            const value = state.env.namedValues.valueFor(ref)
            if (value === undefined) {
                console.debug(`no named value for ${ref}. aborting.`)
                console.groupEnd()
                return false
            }
            if (value.type !== 'href') {
                // todo![now, references, error] this should behave like any other
                //  reference error: ERROR or WARN per user option.
                console.warn(`image src references a non-href named value, "${ref}}"`)
                console.groupEnd()
                return false
            }
            console.debug(`named referent: ${stringifyValue(value)}`)
            src = value.url
            desc = value.desc
        }

        //
        // We found the end of the image, and know for a fact it's a valid image;
        // so all that's left to do is to call tokenizer.
        //
        if (!silent) {
            const content = state.src.slice(altStart, altEnd)

            const tokens: Token[] = []
            state.md.inline.parse(
                content,
                state.md,
                state.env,
                tokens
            )

            const token = state.push('image', 'img', 0)
            token.attrs = [['src', src], ['alt', '']]
            token.children = tokens
            token.content = content

            if (desc) {
                token.attrs.push(['desc', desc])
            }
        }

        state.pos = pos
        console.groupEnd()
        return true
    }

    return image
}
