import type {Stylesheet} from '../ptss/index.js'
import {createUnescaper} from './unescaper_parameterized.js'


export function createLinkDestParser (stylesheet: Stylesheet) {
    const allowSpaces = stylesheet.linkDestAllowSpaces

    if (stylesheet.escapeSyntax.escapeChar === undefined) {
        throw Error(`Escape inline style enabled, but no escape char defined for ${stylesheet.name}}`)
    }
    const escapeCode = stylesheet.escapeSyntax.escapeChar[0].charCodeAt(0)
    const ESCAPE_LOOKUP = stylesheet.escapeSyntax.ESCAPE_LINK_DEST_LOOKUP
    const unescaper = createUnescaper(true, stylesheet.escapeSyntax.escapeChar, ESCAPE_LOOKUP, true)

    /**
     * Modified version of markdown-it/lib/helpers/parse_link_destination.js
     *
     *! Modified with parameterized behavior
     */
    function parseLinkDestination (str: string, start: number, max: number) {
        let code, level,
            pos = start
        const result = {
            ok: false,
            pos: 0,
            lines: 0,
            str: ''
        }

        if (str.charCodeAt(pos) === 0x3C /* < */) {
            pos++
            while (pos < max) {
                code = str.charCodeAt(pos)
                if (code === 0x0A /* \n */) { return result }
                if (code === 0x3C /* < */) { return result }
                if (code === 0x3E /* > */) {
                    result.pos = pos + 1
                    result.str = unescaper(str.slice(start + 1, pos))
                    result.ok = true
                    return result
                }
                if (code === escapeCode && pos + 1 < max) {
                    const ch1 = str.charCodeAt(pos + 1)
                    if (ch1 < 256 && ESCAPE_LOOKUP[ch1] !== undefined) {
                        pos += 2
                        continue
                    }
                }

                pos++
            }

            // no closing '>'
            return result
        }

        // this should be ... } else { ... branch

        level = 0
        while (pos < max) {
            code = str.charCodeAt(pos)

            if (code === 0x20/* space */) {
                if (allowSpaces) {
                    if (pos + 1 < max) {
                        if (str.charCodeAt(pos + 1) === 0x22) { break }
                    }
                } else {
                    break
                }
            }

            // ascii control characters
            if (code < 0x20 || code === 0x7F) { break }

            if (code === escapeCode && pos + 1 < max) {
                const ch1 = str.charCodeAt(pos + 1)
                if (ch1 < 256 && ESCAPE_LOOKUP[ch1] !== undefined) {
                    pos += 2
                    continue
                }
            }

            if (code === 0x28 /* ( */) {
                level++
                if (level > 32) { return result }
            }

            if (code === 0x29 /* ) */) {
                if (level === 0) { break }
                level--
            }

            pos++
        }

        if (start === pos) { return result }
        if (level !== 0) { return result }

        result.str = unescaper(str.slice(start, pos))
        result.pos = pos
        result.ok = true
        return result
    }

    return parseLinkDestination
}
