import type StateInline from 'markdown-it/lib/rules_inline/state_inline.js'
import markdownItUtils from 'markdown-it/lib/common/utils.js'
import type {RuleTokenizer} from './types.js'
import type * as ir from '../../stir/index.js'
import {stringifyValue} from '../ptss/index.js'

// todo![now, references] because Inline elements can be referents too, and because
//  a reference to an inline can occur *before* the latter occurs, we can't
//  resolve all references immediately as we are doing below. We should
//  instead just collect a list of references and resolve them *after*
//  all the parsing is done.

type StateWithLinkLevel = StateInline & { linkLevel: number }

const isSpace = markdownItUtils.isSpace

export function createRule (): RuleTokenizer {

    /**
     * Modified version of markdown-it/lib/rules_inline/link.js
     * Mods annotated with "//!" comments.
     */
    function reference (state: StateWithLinkLevel, silent: boolean) {
        let referent: string | ir.Block | ir.Inline | undefined
        let desc
        const max = state.posMax

        if (state.src.charCodeAt(state.pos) !== 0x5B/* [ */) { return false }

        //  [anchor text](  <href>  "description"  )
        //  ^^^^^^^^^^^^^  parse anchor text
        const anchorStart = state.pos + 1
        const anchorEnd = state.md.helpers.parseLinkLabel(state, state.pos, true)

        // parser failed to find ']', so it's not a valid link
        if (anchorEnd < 0) { return false }

        console.debug(`parsing reference`)
        console.group()

        let pos = anchorEnd + 1
        if (pos < max && state.src.charCodeAt(pos) === 0x28/* ( */) {
            pos++
            //
            // Inline link
            //

            referent = ''

            //  [anchor text](  <href>  "description"  )
            //                ^^ skipping these spaces
            for (; pos < max; pos++) {
                const code = state.src.charCodeAt(pos)
                if (!isSpace(code) && code !== 0x0A) { break }
            }

            //  [anchor text](  <href>  "description"  )
            //                  ^^^^^^ parsing href
            let start = pos
            let res = state.md.helpers.parseLinkDestination(state.src, pos, state.posMax)
            if (res.ok) {
                referent = state.md.normalizeLink(res.str)
                if (state.md.validateLink(referent)) {
                    pos = res.pos
                } else {
                    referent = ''
                }

                //  [anchor text](  <href>  "description"  )
                //                        ^^ skipping these spaces
                start = pos
                for (; pos < max; pos++) {
                    const code = state.src.charCodeAt(pos)
                    if (!isSpace(code) && code !== 0x0A) { break }
                }

                //  [anchor text](  <href>  "description"  )
                //                          ^^^^^^^^^^^^^ parsing optional description
                res = state.md.helpers.parseLinkTitle(state.src, pos, state.posMax)
                if (pos < max && start !== pos && res.ok) {
                    desc = res.str
                    pos = res.pos

                    //  [anchor text](  <href>  "description"  )
                    //                                       ^^ skipping these spaces
                    for (; pos < max; pos++) {
                        const code = state.src.charCodeAt(pos)
                        if (!isSpace(code) && code !== 0x0A) { break }
                    }
                }
            }

            if (pos < max && state.src.charCodeAt(pos) === 0x29/* ) */) {
                pos++
                console.debug(`inline referent: ${referent}`)
            } else {
                pos = anchorEnd + 1
                // not inline referent, try as named value
                referent = undefined
            }
        }

        if (referent === undefined) {
            //
            // named value
            //

            let ref
            if (pos < max && state.src.charCodeAt(pos) === 0x5B/* [ */
                // hard coding for now that note references can
                //  participate in this syntax
                && state.src.charCodeAt(pos+1) !== 0x5E/* ^ */) {
                const start = pos + 1
                //  [anchor text][ref]
                //                ^^^ parsing ref
                pos = state.md.helpers.parseLinkLabel(state, pos)
                if (pos >= 0) {
                    ref = state.src.slice(start, pos++)
                } else {
                    pos = anchorEnd + 1
                }
            }
            // [anchor text][ref]  ref is non empty string   CommonMark "full reference link"
            // [anchor text][]     ref is ''                 CommonMark "collapsed reference link"
            // [anchor text]       ref is undefined          CommonMark "shortcut reference link"

            // if collapsed or shortcut, use anchor text as ref
            if (ref === undefined || ref === '') {
                ref = state.src.slice(anchorStart, anchorEnd)
            }

            console.debug(`reference name: ${ref}`)
            const value = state.env.namedValues.valueFor(ref)
            if (value === undefined) {
                console.debug(`no named value for ${ref}. aborting.`)
                console.groupEnd()
                return false
            }
            if (value.type === 'href') {
                referent = value.url
                desc = value.desc
            } else if (value.type === 'element') {
                referent = value.element
            } else {
                // todo![now, references, error] this should behave like any other
                //  reference error: ERROR or WARN per user option.
                console.warn(`link references a non-linkable named value, "${ref}}"`)
                console.groupEnd()
                return false
            }
            console.debug(`named referent: ${stringifyValue(value)}`)
        }

        if (desc !== undefined) {
            console.debug(`desc: ${desc}`)
        }

        //
        // We found the end of the link, and know for a fact it's a valid link;
        // so all that's left to do is to call tokenizer.
        //
        if (!silent) {
            state.pos = anchorStart
            state.posMax = anchorEnd

            let token = state.push('link_open', 'a', 1)
            //! 'as string` is a hack/cheat. It might be a BLock or Inline, but
            //   again, this is all a temp hack until we have our own inline parser.
            token.attrs = [['referent', referent as string]]
            if (desc) {
                token.attrs.push(['desc', desc])
            }

            //  [anchor text](  <href>  "description"  )
            //   ^^^^^^^^^^^   tokenize anchor text
            state.linkLevel++
            state.md.inline.tokenize(state)
            state.linkLevel--

            token = state.push('link_close', 'a', -1)

            state.posMax = max
        }

        state.pos = pos
        console.groupEnd()
        return true
    }

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return reference
}
