import {Node, Folder, FolderInterface, resolvePath} from './node.js'
import type {Assembly} from './assembly.js'
import type {File} from './file.js'
import type {Config} from '../config.js'
import type {AssemblyPipeline} from '../pipeline.js'


export class Subfolder implements FolderInterface {
    // Must have trailing slash. See https://www.w3.org/TR/ldp-bp/#include-a-trailing-slash-in-container-uris
    readonly name: `${string}/`

    /**
     * subfolder *always* has parent.
     * Root folders are type {@link Assembly}
     */
    readonly parent: Folder

    get title (): string | undefined {
        return this.openingContent?.title
    }

    get isIndexable (): boolean {
        return this.openingContent !== undefined
    }

    openingContent: File | undefined
    readonly content: Node[] = []
    hasIncludedContent = false

    sortSpec: string[] | undefined
    textplainConfig: Config | undefined
    pipeline: AssemblyPipeline | undefined

    outNode: string | undefined

    constructor (name: `${string}/`, parent: Folder) {
        this.name = name
        this.parent = parent
    }

    isFile (): this is File {
        return false
    }

    isFolder (): this is Folder {
        return true
    }

    isAssemblyRoot (): this is Assembly {
        return false
    }

    assemblyRoot (crossAssemblyBoundary = false): Assembly {
        return this.parent.assemblyRoot(crossAssemblyBoundary)
    }

    innerAssemblyPath (): string {
        return this.parent.innerAssemblyPath() + this.name
    }

    outerAssemblyPath (): string {
        return this.parent.outerAssemblyPath() + this.name
    }

    crossAssemblyPath (): string {
        return this.parent.crossAssemblyPath() + this.name
    }

    resolvePath (path: string): Node | undefined {
        return resolvePath(path, this)
    }

    searchUpForFile (name: string, crossAssemblyBoundary = false): File | undefined {
        for (const c of this.content) {
            if (c.isFile() && c.name === name) {
                return c
            }
        }
        if (this.parent === undefined) {
            return undefined
        }
        return this.parent.searchUpForFile(name, crossAssemblyBoundary)
    }

    searchUpForProperty (property: string, crossAssemblyBoundary = false): { node: Node, value: unknown } | undefined {
        if (property in this) {
            const value = (this as unknown as Record<string, unknown>)[property]
            if (value !== undefined) {
                return {node: this, value}
            }
        }
        return this.parent.searchUpForProperty(property, crossAssemblyBoundary)
    }
}
