import * as ptss from '../ptss/index.js'

export function newTestReferencesSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('PTSS Test TWO Stylesheet')

    //todo[spec] just copied CommonMark's. Haven't even thought about natural escapes.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    const atx = new ptss.ChunkStyle('ATX Heading', stylesheet)
    atx.irNode.type = new ptss.Expression('heading')
    atx.canInterruptParagraph = true
    atx.canInterruptLazy = true
    atx.headPattern = /(#{1,6})(?=[ \t]|$)/ym
    atx.bodyPattern = /(?:[ \t]+(.*?))??(?:[ \t]+#+)?[ \t]*$/ym
    atx.maxLines = 1
    atx.indent = 'optional'
    atx.irNode.level = new ptss.Expression(['length', ['head', 1]])
    atx.irNode.body = new ptss.Expression(['parseInlines', ['first', ['body', 1], '']])
    atx.ptNamedValue = {
        type: 'element',
        name: new ptss.Expression(['replace', ['trim', ['first', ['body', 1], '']], /[ \t\r\n]+/, ' ']),
        value: 'self'
    }

    const footnote = new ptss.ContainerStyle('Footnote', stylesheet)
    footnote.hangingIndent = 'auto'
    // todo![footnotes, gracedown] test-references allows spaces in names.
    //   - do we break GFM compatibility if we add them to GFM or RubyRhod?
    //! we don't include the `^` in the name, because for test-references.ptss, we want a single
    //  namespace, where the `^` is only used to determine the type of element, and in the
    //  reference, the type of reference (regular link vs note-style link in superscript)
    footnote.marker = /\[(\^([^\]]+))]:/y
    footnote.body = 'BLOCKS'
    footnote.irNode.type = new ptss.Expression('note')
    footnote.irNode.subType = new ptss.Expression('footnote')
    footnote.ptNamedValue = {
        type: 'element',
        name: new ptss.Expression(['marker', 2]),  //! test-references non-segregated namespace, so `^` is NOT included
        value: 'self'
    }

    const linkDef = new ptss.ChunkStyle('Link Ref Definition', stylesheet)
    linkDef.headPattern = /\[\s*([[\w ]+)\s*]:/y
    linkDef.bodyPattern = /\s*(?:<((?:[^<>\n\\]|\\.)*)>|([^ <\n]\S*))\s*(\s(?:"((?:\\\S|[^"])*)"|'((?:\\\S|[^'])*)'|\(((?:\\\S|[^()])*)\))\s*?)?$/ym
    linkDef.ptNamedValue = {
        type: 'href',
        name: new ptss.Expression(['head', 1]),
        value: new ptss.Expression(['first', ['body', 1], ['body', 2], '']),
        description: new ptss.Expression(['first', ['body', 4], ['body', 5], ['body', 6], ''])
    }

    const substitutionDef = new ptss.ChunkStyle('Substitution Ref Definition', stylesheet)
    substitutionDef.headPattern = /\[\s*([[\w ]+)\s*]:/y
    substitutionDef.bodyPattern = /\s*(.+)?\s*$/ym
    substitutionDef.ptNamedValue = {
        type: 'substitution',
        name: new ptss.Expression(['head', 1]),
        value: new ptss.Expression(['parseInlines', ['body', 1]])
    }

    const blockQuote = new ptss.ContainerStyle('Block Quote', stylesheet)
    blockQuote.irNode.type = new ptss.Expression('block_quote')
    blockQuote.leftBorder = '>'
    blockQuote.leftPadding = 'per first line'
    blockQuote.canInterruptParagraph = true
    blockQuote.canInterruptLazy = true
    blockQuote.indent = 'optional'
    blockQuote.body = 'BLOCKS'

    const list = new ptss.ListStyle('List', stylesheet)
    list.unorderedMarkers = '-*+'
    list.orderedMarkers = '.)'
    list.indent = 'optional'

    const indentedCode = new ptss.ContainerStyle('Indented Code Block', stylesheet)
    indentedCode.irNode.type = new ptss.Expression('code_block')
    indentedCode.indent = 'markdown(4,4)'
    indentedCode.body = 'RAW_NL'
    indentedCode.canInterruptParagraph = false
    indentedCode.canInterruptLazy = false

    stylesheet.blockStyles.push(
        // has to come first styles with 'optional'
        // indentation for it to every be recognized.
        indentedCode,
        atx,
        footnote,
        // linkDef must come before synonymDef as the latter's RHS is a superset of the former's
        linkDef,
        substitutionDef,
        blockQuote,
        list
    )

    stylesheet.inlineStyles = [
        'autolink',
        'backticks',
        'emphasis',
        'entity',
        'escape',
        'image',
        'noteRef(spaces)',
        'reference',
        'newline'
    ]

    return stylesheet
}
