import * as ptss from '../ptss/index.js'

//todo[spec, regex] There are some rules for the style regular expressions, that
//  probably should be enforced by PTSS code, not the style author.
//     - y flag -- should be added by the code automatically. The fact that the implementation depends on
//         stickiness should be hidden inside the black-box.
//     - ^ and $ have ALREADY been removed from all patterns. But add tests to verify the code is properly enforcing the rule.
//     - \n -- some patterns have this at the end. it would be better if the style designer didn't have to do that.

//todo[perf] regex optimization
//    https://www.regular-expressions.info/catastrophic.html
//    https://mariusschulz.com/blog/why-using-the-greedy-in-regular-expressions-is-almost-never-what-you-actually-want
//    https://www.loggly.com/blog/five-invaluable-techniques-to-improve-regex-performance/
//    https://www.loggly.com/blog/regexes-the-bad-better-best/
//    .
//    Should i convert many of the parenthetical expressions to non-capturing? e.g. (?: )

// todo[api] should options be Record<string, string>? i guess the config file could support object values or arrays.

export function newCommonMarkSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('CommonMark')

    const strict = options?.strict === true
    let defaultIndentOption: 'no' | 'optional'
    let leftPadding: 'per first line' | 'sloppy'
    let indentedCodeCanInterruptPara: boolean
    if (strict) {
        // in strict mode, we replace sloppiness with optional indentation
        stylesheet.sloppyMargin = 0
        defaultIndentOption = 'optional'
        leftPadding = 'per first line'
        // in strict mode, we must allow indented code to interrupt a
        // paragraph, otherwise the following results in a list:
        // ```
        // paragraph text
        //         1. this should not be a list item
        // ```
        // This is the right choice anyway, as it yields more consistency,
        // both for lists and the meaning of four-space indents. Current
        // Markdown behavior is bad, as shown in the docs.
        indentedCodeCanInterruptPara = true
    } else {
        stylesheet.sloppyMargin = 3
        defaultIndentOption = 'no'
        leftPadding = 'sloppy'
        indentedCodeCanInterruptPara = false
    }

    stylesheet.escapeSyntax.escapeChar = '\\'
    /**
     * https://spec.commonmark.org/0.29/#backslash-escapes
     *
     * ```
     * Any ASCII punctuation character may be backslash-escaped:
     *
     * !"#$%&'()*+,-./:;<=>?@[\]^_`{|}~
     * ```
     *
     * [JGM's original reasoning](https://archive.is/zDyK2#selection-247.0-259.135):
     * > There are only a few places where this spec says things that contradict
     * > the canonical [Gruber's] syntax description:
     * > - It allows all punctuation symbols to be backslash-escaped, not just
     * >   the symbols with special meanings in markdown. I found that it was
     * >   just too hard to remember which symbols could be escaped.
     */
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'
    stylesheet.linkDestAllowSpaces = false

    const para = stylesheet.get('Paragraph') as ptss.ChunkStyle
    para.lazyContinuation = !strict
    para.irNode.body = new ptss.Expression(
        [
            'parseInlines',
            ['trim', ['body']],
            'backslashNewlineIsHardBreak', 'twoSpaceNewlineIsHardBreak'
        ]
    )

    const atx = new ptss.ChunkStyle('ATX Heading', stylesheet)
    atx.irNode.type = new ptss.Expression('heading')
    atx.canInterruptParagraph = true
    atx.canInterruptLazy = true
    //todo[stylesheets, spec] Should we go all unicode and recognize other whitespace chars?
    //  if not for CommonMark.ptss, then maybe for:
    //  - CommonMarkUnicode.ptss
    //  - Gracedown.ptss
    //  - (some CJK style).ptss
    //  - PlainJane.ptss
    // See https://stackoverflow.com/a/3469155/8910547 for how to do this.
    // Likewise, we may want to examine all our regexes for other Unicode
    // support, not just whitespace.
    atx.headPattern = /(#{1,6})(?=[ \t]|$)/ym
    atx.bodyPattern = /(?:[ \t]+(.*?))??(?:[ \t]+#+)?[ \t]*$/ym
    atx.maxLines = 1
    atx.indent = defaultIndentOption
    atx.irNode.level = new ptss.Expression(['length', ['head', 1]])
    atx.irNode.body = new ptss.Expression(['parseInlines', ['first', ['body', 1], '']])
    //todo[perf] is using a tailPattern to first parse out the tail more efficient?
    //  This is the previous tailPattern version that passed all tests:
    //  atxStyle.tailPattern = /[ \t]+#+[ \t]*$/
    //  atxStyle.ire.body = new ptss.Expression(['trim', ['body']]) //todo[perf] trim may be redundant with latter handling. clean up.

    const thematicBreak = new ptss.ChunkStyle('Thematic Break', stylesheet)
    thematicBreak.irNode.type = new ptss.Expression('thematic_break')
    thematicBreak.headPattern = /(?:(?:\*[ \t]*){3,}|(?:_[ \t]*){3,}|(?:-[ \t]*){3,})[ \t]*$/ym
    thematicBreak.maxLines = 1
    thematicBreak.canInterruptParagraph = true
    thematicBreak.canInterruptLazy = true
    thematicBreak.indent = defaultIndentOption

    const setext = new ptss.ChunkStyle('Setext Heading', stylesheet)
    setext.irNode.type = new ptss.Expression('heading')
    setext.tailPattern = /((=+|-+)[ \t]*$)/ym
    setext.consumesBackChunk = true
    setext.maxLines = 5
    setext.indent = defaultIndentOption
    setext.canInterruptParagraph = false
    setext.canInterruptLazy = false
    // this setting is per spec. to match ref implementation behavior, set true
    // todo[rollout] use this as an example
    setext.lazyContinuation = false  // = !strict
    setext.irNode.level = new ptss.Expression(['map', ['at', ['tail', 2], 0], {'=': 1, '-': 2}])
    setext.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body']]]) //todo[perf] trim may be redundant with latter handling. clean up.

    // todo![now, references, perf] wouldn't the way i do this in test-references.ptss be more efficient?
    //   - if headPattern includes the full `[name]:` part of the style, we could fail the style
    //     match without the fwdChunk style
    //   - wrap the capture group with `\S*` to effectively trim the name within the regex
    const linkRefDef = new ptss.ChunkStyle('Link Ref Definition', stylesheet)
    linkRefDef.canInterruptParagraph = false
    linkRefDef.canInterruptLazy = false
    // this setting is per spec. to match ref implementation behavior, set true
    // todo[rollout] use this as an example
    linkRefDef.lazyContinuation = false  // = !strict
    //todo[perf] regex for a literal pattern expensive, esp a single char? maybe support literal (string) headPattern?
    linkRefDef.headPattern = /\[/y
    // eslint-disable-next-line no-control-regex
    linkRefDef.bodyPattern = /((?:[^\\[\]]|\\.){0,1000})]:\s*(?:<((?:[^<>\n\\\x00]|\\.)*)>|([^ <\n]\S*))\s*(\s(?:"((?:\\\S|[^"\x00])*)"|'((?:\\\S|[^'\x00])*)'|\(((?:\\\S|[^()\x00])*)\))\s*?)?$/ym
    linkRefDef.indent = defaultIndentOption
    //todo[spec, references, internal links] Current RefDef.ts code just returns null when passed an empty name. But should
    //   we us an "Assert not empty, != ''" expression that fails it at style parse time?
    linkRefDef.ptNamedValue = {
        type: 'href',
        name: new ptss.Expression(['replace', ['trim', ['body', 1]], /[ \t\r\n]+/, ' ']),
        value: new ptss.Expression(['first', ['body', 2], ['body', 3], '']),
        description: new ptss.Expression(['first', ['body', 5], ['body', 6], ['body', 7], ''])
    }

    const blockQuote = new ptss.ContainerStyle('Block Quote', stylesheet)
    blockQuote.irNode.type = new ptss.Expression('block_quote')
    blockQuote.leftBorder = '>'
    blockQuote.leftPadding = leftPadding
    blockQuote.canInterruptParagraph = true
    blockQuote.canInterruptLazy = true
    blockQuote.indent = defaultIndentOption
    blockQuote.body = 'BLOCKS'

    //todo[arch] we have to do two separate fenced code styles because the
    //  backtick version has a different info string requirement (no backticks)
    //  - IS THERE A BETTER WAY?
    //  - perhaps not. IT IS NOT WORTH adding any complexity JUST to avoid this rather
    //    minor trifle of having to do to styles. The rule about backticks is very
    //    specific to the inline code span logic which might only ever apply to Markdown Mode.
    const fencedCodeTick = new ptss.ContainerStyle('Backtick Fenced Code Block', stylesheet)
    fencedCodeTick.irNode.type = new ptss.Expression('code_block')
    fencedCodeTick.topBorder = '`'
    fencedCodeTick.topBorderMinLength = 3
    // todo[spec] Is this really a `righter`? Is a righter simply content at the right end of
    // the top border. or is it content to the right and outside of the *rectangular frame*
    // of the bordered container? This is akin to the difference between a CommonMark ordered
    // list numeric marker, which forces an indent of the container *past* the marker thereby
    // keeping the number outside the frame, and that of djot, which only requires only an
    // indent of one space or more, making the number simple prepended to the first line.
    fencedCodeTick.righter = /([^`]*?)$/ym
    fencedCodeTick.body = 'RAW_NL'
    fencedCodeTick.canInterruptParagraph = true
    fencedCodeTick.canInterruptLazy = true
    fencedCodeTick.indent = defaultIndentOption
    fencedCodeTick.irNode.info = new ptss.Expression(['unescape', ['trim', ['righter', 0]]])

    //todo[spec] this is a good case for style inheritance, even within a stylesheet:
    //
    //     fencedCodeTilde inherit fencedCodeTick {
    //        <modifications go here>
    //     }
    //
    // It should work for inheriting and customizing styles from other stylesheets, or
    // overriding a style when customizing a stylesheet.
    const fencedCodeTilde = new ptss.ContainerStyle('Tilde Fenced Code Block', stylesheet)
    fencedCodeTilde.irNode.type = new ptss.Expression('code_block')
    fencedCodeTilde.topBorder = '~'
    fencedCodeTilde.topBorderMinLength = 3
    fencedCodeTilde.righter = /(.*?)$/ym
    fencedCodeTilde.body = 'RAW_NL'
    fencedCodeTilde.canInterruptParagraph = true
    fencedCodeTilde.canInterruptLazy = true
    fencedCodeTilde.indent = defaultIndentOption
    fencedCodeTilde.irNode.info = new ptss.Expression(['unescape', ['trim', ['righter', 0]]])

    const indentedCode = new ptss.ContainerStyle('Indented Code Block', stylesheet)
    indentedCode.irNode.type = new ptss.Expression('code_block')
    indentedCode.indent = 'markdown(4,4)'
    indentedCode.body = 'RAW_NL'
    // spec: "An indented code block cannot interrupt a paragraph.
    //        (This allows hanging indents and the like.)"
    indentedCode.canInterruptParagraph = indentedCodeCanInterruptPara
    indentedCode.canInterruptLazy = indentedCodeCanInterruptPara
    // todo[spec] how come we don't have to do
    //       indentedCode.irNode.body =
    //   for some styles and not for others? This is confusing.
    //   Not having a simple mental model will result in people defining styles
    //   using trial and error, or referring to the code or other existing style defs.
    //   NOT GOOD.

    const list = new ptss.ListStyle('List', stylesheet)
    list.unorderedMarkers = '-*+'
    list.orderedMarkers = '.)'
    list.indent = defaultIndentOption

    // todo[spec] should this be generalized?
    //    NO: this is purely a Markdown Mode affordance. So that is all we will support.
    //    MAYBE: but we'll wait for a solid argument that it should be.
    //    YES: no justification comes to mind yet.
    const htmlCodeBlock = new ptss.HTMLBlockStyle('HTML Block', stylesheet)
    htmlCodeBlock.indent = defaultIndentOption


    // todo[perf] Order based on performance impact:
    //   - more commonly used styles earlier
    //   - more expensive to match later
    //   - CAN easily measure the match fail cost of a style disabling all other styles
    //     and benchmarking pure paragraph input (either make sure there are blanks between
    //     each line so the cantInterruptPara shortcut doesn't skew the numbers, OR use
    //     a good average number of lines per para if the cantInterruptPara shortcut should
    //     be part of the measurement).
    //
    // todo[spec] IDEA: instead of requiring styles be ordered in a list,
    //   each style simply declares that it must have higher|lower
    //   precedence than some other specific style, and it doesn't care
    //   about the rest. Likewise this is also how mods to existing stylesheets
    //   are supported.
    //   .
    //   So at stylesheet initialization: the code dynamically figures out a
    //   linear ordering that satisfies all the requirements.
    //
    // todo[arch]:
    //   Here we are adding each style to the stylesheet. But above we've already
    //   associated the stylesheet to each style (it's passed into the style
    //   constructor. Shouldn't this be an atomic operation? OTOH:
    //   - Stylesheet.initAndValidate makes sure the link is bidirectional
    //   - When we switch to the DSL it'll all be different.
    stylesheet.blockStyles.push(
        atx,
        setext, //! above Thematic Break in back matching mode two own '------'
        blockQuote,
        thematicBreak, //! above List so can match '- - -'
        list,
        linkRefDef,
        fencedCodeTick,
        fencedCodeTilde,
        indentedCode,
        htmlCodeBlock
    )

    if (strict) {
        // for strict mode we aren't using sloppiness but instead
        // setting`indent='optional'` for all style elements other than
        // Indented Code Block. So for the latter to "own" any four-space
        // indent, we put give it highest precedence.
        //
        // todo[strict] we can use another approach, e.g.:
        //     indent = 'markdown,0,3'
        // Not sure if it has any material difference other than allowing
        // us to re-order precedence to optimize performance.
        stylesheet.remove('Indented Code Block')
        stylesheet.blockStyles.unshift(indentedCode)
    }

    stylesheet.inlineStyles = [
        'autolink',
        'backticks(commonmark)',
        'emphasis',
        'entity',
        'escape',
        'html_inline',
        'image',
        'reference',
        'newline',
        'html'
    ]

    return stylesheet
}
