import type {Provider} from './provider.js'
import type {STIR} from './stir.js'
import type {Config} from './config.js'
import {lookupPipe} from './registry.js'

// todo[arch] note how unlike render, we don't use classes, but a closure.
//    This functional approach definitely is well suited for transforms, but
//    perhaps we should do the same for render, either because it is better,
//    or for consistency (less confusing for users), or both.

/**
 * Interface for a Pipe operator in a STIR processing pipeline.
 *
 * IMPORTANT
 * =========
 * Modifies the input for efficiency reasons; rarely is the original
 * input needed once a transform is applied.
 *
 * If the original input is needed unmodified, it is the responsibility of the
 * caller to first make a *deep* copy.
 */
export type Pipe = (stir: STIR) => void

export type PipeProvider = Provider<Pipe>

export async function configPipeProvider (config: Config): Promise<Pipe | undefined> {
    const providerName = config.requiredValue('provider')

    // command-line on/off switches
    const onSwitch = config.value('onSwitch')
    if (onSwitch !== undefined) {
        if (process.argv.includes(onSwitch)) {
            console.info(`enabled pipe: ${providerName}`)
        } else {
            return undefined
        }
    }
    const offSwitch = config.value('offSwitch')
    if (offSwitch !== undefined && process.argv.includes(offSwitch)) {
        console.info(`disabled pipe: ${providerName}`)
        return undefined
    }

    console.group(`configure pipe: ${providerName}`)
    const provider = lookupPipe(providerName)
    if (provider === undefined || provider.configure === undefined) {
        throw new Error(`invalid pipe: ${providerName}`)
    }
    const providerConfig = config.subConfig('config')
    const pipe = await provider.configure(providerConfig)
    console.groupEnd()
    //todo[perf] does this indirection being added to this provider as well as
    // all the others, *just* so that we can wrap the calls to each with console
    // logging get stripped out in the dist build? If not what is the perf
    // impact? If significant, how do we remove it for dist builds?
    return (stir: STIR) => {
        console.debug(`♒️ PIPE ${providerName} ${providerConfig ? JSON.stringify(providerConfig.data()) : ''}`)
        console.group()
        pipe(stir)
        console.groupEnd()
    }
}
