import * as ptss from '../ptss/index.js'
import {newCommonMarkSheet} from './commonmark.ptss.js'

// todo[spec] Configurability (like Pandoc, disable any extension)
//   - This can be implemented most simply by accepting a config object. But however it
//     is done, it maybe should be done in a standard way for all plain text stylesheets.


export function newPandocMarkdownStrictStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = newCommonMarkSheet(options)
    stylesheet.name = 'Pandoc Markdown (Strict)'

    /**
     * Per:
     * https://github.com/jgm/pandoc/blob/9bcf1ff8d6823c9980ff1b9959e7a48968de0275/MANUAL.txt#L4833-L4839
     *
     * and:
     * https://daringfireball.net/projects/markdown/syntax#backslash
     *
     * ```
     * Markdown provides backslash escapes for the following characters:
     *
     * \   backslash
     * `   backtick
     * *   asterisk
     * _   underscore
     * {}  curly braces
     * []  square brackets
     * ()  parentheses
     * #   hash mark
     * +   plus sign
     * -   minus sign (hyphen)
     * .   dot
     * !   exclamation mark
     * ```
     *
     * But Pandoc's own strict parser also allows `"` and `~` to be  escapable.
     * Issue files: https://github.com/jgm/pandoc/issues/8777
     * PTSS implementation will reflect Pandoc implementation
     * 
     * todo![pandoc, spec] Submitted issue resolved, and two extra chars no 
     *   longer escapable. Verify this with new Pandoc release when available
     *   by updating to it and using it to update all the test case files.
     *   https://github.com/jgm/pandoc/commit/f9bbbf6f402edfac34db5482c9c5d9c4f230b875
     */
    stylesheet.escapeSyntax.escapeLiterals = '\\`*_{}[]()>#+-.!' // pre Pandoc Manual, orig Markdown
                                + '"~' // https://github.com/jgm/pandoc/issues/8777

    // override inherited CommonMark, restore PTSS default
    stylesheet.linkDestAllowSpaces = true
    //todo![ptss, spec, inline] TEMP HACK: the above and below are interdependent/redundant (if the above is true,
    //  below must match spaces in the link dest, if false, then not. This should be fixed when we replace
    //  the regex with our own inline parser for link dests, which will be used both for links and link ref defs.
    const linkRefDef = stylesheet.get('Link Ref Definition') as ptss.ChunkStyle
    // eslint-disable-next-line no-control-regex
    linkRefDef.bodyPattern = /((?:[^\\[\]]|\\.){0,1000})]:\s*(?:<((?:[^<>\n\\\x00]|\\.)*)>|([^ <\n][^\n]*))\s*(\s(?:"((?:\\\S|[^"\x00])*)"|'((?:\\\S|[^'\x00])*)'|\(((?:\\\S|[^()\x00])*)\))\s*?)?$/ym

    const para = stylesheet.get('Paragraph') as ptss.ChunkStyle
    para.irNode.body = new ptss.Expression(
        [
            'parseInlines',
            ['trim', ['body']],
            'twoSpaceNewlineIsHardBreak'
        ]
    )

    const setext = stylesheet.remove('Setext Heading') as ptss.ChunkStyle
    setext.consumesBackChunk = false
    setext.maxLines = 2
    setext.canInterruptParagraph = false  //pandoc ext -blank_before_header only applies to ATX
    setext.canInterruptLazy = false
    stylesheet.insertAbove('ATX Heading', setext)

    // no space required after #
    const atx = stylesheet.get('ATX Heading') as ptss.ChunkStyle
    atx.headPattern = /(#{1,6})(?!#)/ym
    atx.bodyPattern = /(?:[ \t]*(.*?))??(?:[ \t]+#+)?[ \t]*$/ym
    atx.canInterruptParagraph = true  //pandoc ext: -blank_before_header
    atx.canInterruptLazy = true

    // todo[api] the DSL for stylesheets should make modifying inherited
    //   styles easy, e.g. something like:
    //   ```
    //   Pandoc extends CommonMark {
    //       override 'List' {
    //           orderedMarkers = '.'
    //           honorStartNum = false
    //           canInterruptParagraph = true
    //           canInterruptParaWithinListOnly = true
    //           canInterruptLazy = true
    //       }
    //   }
    //   ```
    const list = stylesheet.get('List') as ptss.ListStyle
    list.orderedMarkers = '.' //apparently ')' not supported for pandoc ext: -fancy_lists
    list.honorStartNum = false
    list.canInterruptParagraph = false //pandoc ext: -lists_without_preceding_blankline
    list.canInterruptParaWithinList = true
    list.canInterruptLazy = true

    const th = stylesheet.get('Thematic Break')
    th.canInterruptParagraph = false
    th.canInterruptLazy = false

    stylesheet.remove('Backtick Fenced Code Block')
    stylesheet.remove('Tilde Fenced Code Block')

    const indentedCodeBlock = stylesheet.get('Indented Code Block') as ptss.ContainerStyle
    // todo[spec] Pandoc-Markdown's different behavior (does not conform to
    //  "literal-blocks_trailing-newline.md"). To observe the difference,
    //  replace the expression below which strips the body's trailing newline
    //  with the following pass-through expression to see the resulting
    //  Pandoc spec test failures:
    //  ```
    //  s.irNode.body = new ptss.Expression(['body'])
    indentedCodeBlock.irNode.body = new ptss.Expression(['replace', ['body'], /\n$/, ''])

    stylesheet.inlineStyles[stylesheet.inlineStyles.indexOf('backticks(commonmark)')] = 'backticks(trim)'

    return stylesheet
}
