import type {RenderSTIR, RenderSTIRProvider} from '../../../renderSTIR.js'
import type {Config} from '../../../config.js'
import {Stir2Html, HTMLOut} from './stir2html.js'
import type * as ir from '../../../../stir/index.js'
import type {STIR} from '../../../stir.js'
import {registerSTIRRenderer} from '../../../registry.js'

import Handlebars from 'handlebars'
import './handlebars.js'

//  todo[arch, textplain] Should this be recast as a plugin to Stir2Html rather
//   than as subclass?
//   - It's not even a very generic solution for interjections, as it relies on
//     Bootstrap.

// See also https://gohugo.io/getting-started/configuration-markup/#markdown-render-hooks

// Bootstrap 3.4
//   <div class="alert alert-success" role="alert">...</div>
//   <div class="alert alert-info" role="alert">...</div>
//   <div class="alert alert-warning" role="alert">...</div>
//   <div class="alert alert-danger" role="alert">...</div>
const intOpenTmpl = Handlebars.compile('<div class="alert alert-{{subType}}" role="alert">')
const intCloseTmpl = Handlebars.compile('</div>')


export class Stir2BootstrapHtml extends Stir2Html {

    constructor (options?: Config) {
        super(options)

        this.registerBlockWriter('interjection', (b: ir.Block, out: HTMLOut) => {
            out.buf += intOpenTmpl(b)
            // todo[template api] this is a total hack. we aren't letting the template handle this.
            //    See my notes on "Per my notes on "open/close templates vs partials" under the topic
            //    *STIR Element-level Templates*.
            if (b.title) {
                this.tag('h3', [], false, out)
                this.writeInlineSequence(b.title as ir.InlineSequence, out)
                this.tag('/h3', [], false, out)
                this.tag('hr/', [], false, out)
                out.buf += '\n'
            }
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.buf += intCloseTmpl(b)
            out.buf += '\n'
        })
    }
}


