export const provider: RenderSTIRProvider = {
    async configure (config?: Config): Promise<RenderSTIR> {
        const stir2bootstrapHtml = new Stir2BootstrapHtml(config)
        return function (stir: STIR) {
            return stir2bootstrapHtml.render(stir)
        }
    }
}

registerSTIRRenderer('Stir2BootstrapHtml', provider)

export default provider


