/*  TWO PROBLEMS

1. We use ParseContainer to for the section context/path for `define`
    BUT WE CANNOT do that for `valueFor` (which should be renamed to `resolve`?)

    ❓‼️ I think we need Block.parent

2. NOT ALL REFERENTS should be pathed:
   - section titles can be non-unique: SHOULD BE PATHED
   - inline text that is named SHOULD NOT (HAVE TO) BE REFERENCED BY PATH

   ON THE OTHER HAND: if the name is unique, it can be
   resolved without a path regardless!
   - REMEMBER: `./name` resolves to `name` in the current section,
     but the simple pathless `name` resolves `name` if it is unique
     regardless of path.
   - REVIEW the resolution table in [NamedValues.md] in light of the above.

   OPTIONS
   -------
   A) rely on the semantics for pathless references per [NamedValues.md]
      - this may overlap with Option C
   B) add a `unique name` attribute to Named ReferentValues, which, when set,
      causes named referents to behave just like named substitutions/hrefs: only one per name
   C) IT IS THE PLAIN TEXT AUTHOR'S RESPONSIBILITY ❓‼️
      - Don't try to solve it with PTSS syntax/declaration distinctions/rules.
        - The author knows what to do and should be expected to do the logical thing.
        - If there is an ambiguity, it is the author's fault. Issue a warning/error.
      - if a named value is expected to be unique, the author should naturally
        make the reference without a path (i.e. `name` instead of `./name`),
        - a reference without a path ONLY resolves to unique names.
        - Option C1) If non-unique, we ERROR. We DITCH THE SEARCH described [NamedValues.md].
        - Option C2) If non-unique, we WARN. We use the search described [NamedValues.md] as a fallback.
      - if a named value is expected to be non-unique, the author should know
        that and make the appropriate pathed reference
      - this option is more consistent with the notion that it is not only NOT WRONG
        but sometimes even NATURAL that multiple things have the same name and are
        references are contextually resolved.
        - not just section names. Maybe a table named `problems` exists in every
          subsection. Maybe multiple recipes are in the file, one in each
          subsection, and within each there is a list titled `ingredients`.
      - We can always add Option B ON TOP OF Option C. Do 🏈 B until something
        shows us we *need* it.

   ✅🤔 Option C seems like the best option, as even if we do B, we would do it
        on top of C:
        - If the `unique name` attribute is set, we ERROR. i.e. Option B results in C1
        - If the `unique name` attribute is not set, we WARN, i.e. C2

 */


//todo![now, spec, named values, references] NAMED VALUES ARE A PURELY PTSS
//  ABSTRACTION representing referencable named values defined implicitly
//  or explicitly through the plain text style's syntax and referencable
//  through other syntax syntax of the style.
//  * * *
//  AS SUCH, Named Values only exist abstractly/logically in the source plain
//  text and concretely in the constructs in this file DURING PARSING as a means
//  of implementing PTSS named value definitions. They do not have a life
//  beyond the plain text EXCEPT for those that are exported, in which case
//  they are converted to STIR's representation with STIR's semantics.


//todo![now, spec, named values, references] use the example of a metadata variable named `title` in a doc that
//   happens to have a section who's title is `title`. You can argue that PTSS
//   should simply have separate namespaces for cross-referencable element
//   naming and metadata keys. But PTSS would rather force the stylesheet to
//   make such namespace segregation explicit in the plain text, not implicit.
//   e.g. the stylesheet can solve it with a syntax where all metadata names
//   are prefixed with `$` just as all footnote names are prefixed with `^`.

// todo![now, references] path-relative name resolution
//
// If we want path-relative name resolution, either for visibility scopes or
// disambiguation, we need to know the path at which a NamedValue is
// ***defined***.
//
// Ways we might do this:
// - add the list of stir.Blocks that make up the path, as below
// - just store the path string
// - introduce `Block.parent` to the STIR model. But then we'd still have
//   to associate the NamedValue definition with a stir.Block in the tree
//   ***within which it was defined***, which is NOT the same as a Block
//   that might be its *value*. (or will that never be a distinction that
//   means anything?)
//
//path: stir.Block[]

// todo![now, references] how do we handle hierarchical scope?    🏈
//
//  - FIRST: what does that mean?
//    - as a visibility mechanism (e.g. name hiding)
//    - as a disambiguation mechanism (pathed names, absolute and relative path semantics)
//
//  - SECOND: how does that play with exported names?
//    - Does it make sense for a Named Value to be inaccessible from
//      from some STIR internal scope because of name hiding, but it is
//      accessible from outside the STIR?
//      - arguably, yes: one can consider an assembly of STIRs itself as
//        a tree, with the content of each STIR being lower level branches
//        in the tree. Thus access from outside a STIR is analogous to a
//        parent section having access to all the names in its subsection,
//        provided paths are provided for disambiguation.
//        - OTOH, what about cross-assembly references from different branches
//          of the assembly? Isn't that analogous to references within a STIR
//          across branches of the section hierarchy?
//
//          ✅ logically the behavior should be the same for all cross-branch
//             references, be they assembly branches or internal STIR section
//             branches.

type PathedElementsValue = { type: 'pathed elements', map: { [pathedID: string]: ElementValue }}

/**
 * NamedValues tracks the reference names assigned to plain text content
 * elements. These names are used to establish cross-reference links within that
 * content per its PTSS defined syntax and semantics.
 *
 * The names are mapped to the STIR nodes corresponding to the named elements,
 * to facilitate ptss-parser's translating the source cross-references into
 * STIR cross-references.
 *
 * An index of names and what they refer to. Used to resolve references in
 * cross-reference links, etc. They do not necessarily result in hyperlinks.
 * In printed form, a heading reference might resolve to the title of the
 * heading it refers to, rendered in italics to signify that it is a title,
 * perhaps with a page number in parenthesis.
 *
 * Ambiguity
 * ----------
 * They are "names" not "IDs" because they cannot be guaranteed to be unique.
 * The stylesheet chooses how ambiguity is handled:
 *  - strict mode: ambiguity is reported as an error and processing stops.
 *  - warn mode: ambiguity is reported as a warning and processing continues,
 *    per one of the options below.
 *  - Chosen rule: ambiguity is resolver per style sheet selected rule. PTSS
 *    spec will define one ore more rules to choose from, TBD. The spec MAY
 *    end up only having one available rule, in which case this list gets
 *    shorter/simpler.
 *  - Default resolution: ambiguity is resolved via default rule.
 *
 * todo![spec ptss TextAssembly references test] add test case where same
 *  ref name is defined for two or more elements.
 */
export class NamedValues {
    /**
     * The structure of the index enforces these namespace rules:
     * - each unique name (sans any path prefix) can only be assigned
     *   to exactly one of the following:
     *   - an HREF value
     *   - a synonym
     *   - one or more content elements, where each content element with
     *     the same name is the only one with that name in its immediate
     *     containing section. In other words, a name can be associated
     *     with multiple content elements as long as each has a unique
     *     *section path*.
     *
     *     todo![now, references] multiple sections sharing the same name
     *      is legit, but does it make sense for other elements? Can
     *      two named inlines (e.f. key words), have the same name?
     *      OPTIONS:
     *      - leave it to the content author. If it doesn't make sense to
     *        have multiple non-sections share a name, even if in separate
     *        parent sections, they can see that. PTSS will just warn/error/skip
     *        as it does for duplicate section names.
     *      - have distinct rules for sections vs other elements, and change
     *        the structure below.
     *      - let the style sheet define the rules.
     */
    public index: {
        [name: string]: HrefValue | SubstitutionValue | PathedElementsValue
    } = {}

    /**
     *
     * @return whether or not the definition is recognized.
     *
     * todo![now, references] this value currently being passed in for
     *   section is the immediate parent, whether or not it is a section.
     *   Need to decide what is actually correct, and either pass in a
     *   section or change the name of this param.
     */
    public define (nv: NamedValue, section: ir.Block | undefined): boolean {
        let existing = this.index[nv.name]
        if (nv.value.type === 'element') {
            const {path, sectioningRoot} = ir.idPath(section)
            if (sectioningRoot !== undefined) {
                // todo[arch, hack] not sure this is the right place to enforce
                //   this. This might become a PTSS style sheet selectable rule,
                //   not something inherent to named values, so belongs in the
                //   parser proper.
                //   OTOH, if we allow names within section roots, we will have
                //   to decide if the name is scoped by the root when referenced,
                //   in which case it will go here, UNLESS we create separate
                //   NamedValue indexes as every sectioning root node.
                return false
            }

            // todo![now, references] should this be named 'path' or 'pathedName'
            //  rather than ID because this is about PTSS named values, not STIR
            //  IDs, even though we end up assigning the stir node this named path
            //  as an id later in the method.
            const id = path ? path + '/' + nv.name : nv.name

            if (existing === undefined) {
                existing = {type: 'pathed elements', map: {}}
                existing.map[id] = nv.value
                this.index[nv.name] = existing
            } else if (existing.type !== 'pathed elements') {
                throw new Error(`PTSS Named Value TYPE collision on ${nv.name}: ${existing.type} and ${nv.value.type}`)
            } else {
                if (id in existing.map) {
                    // todo![now, references, error] 🏈 user can select first wins + warn or ERROR.
                    // todo![now, references] that ir.Block.toDEBUGString() using the pContainer-like path
                    //   would be really useful now!!!!
                    console.warn(`PTSS Named Value ID collision for "${id}".`)
                    return false
                }
                existing.map[id] = nv.value
            }

            nv.value.element.id = id
        } else {
            if (this.index[nv.name]) {
                // CommonMark rules give precedence to first registration; others are ignored
                // todo![now, references, spec] Should duplicate NamedValue be silently ignored, even if
                //  they have conflicting referents/desc?
                // todo![now, references, spec] PTSS offers stylesheets a choice of ambiguity resolution rules, e.g.
                //  - first def wins (like CommonMark)
                //  - scope-based (defined by section and page hierarchies)
                //  - strict/fail-fast on any name collision.
                return false
            }
            this.index[nv.name] = nv.value
        }
        return true
    }

    public valueFor (name: string): Value | undefined {
        // todo[spec] this normalization is CommonMark-specific
        // todo[arch] this normalization  call is made in many places; poor encapsulation and non-DRY
        const v = this.index[normalizeName(name)]
        if (v === undefined) {
            return undefined
        }
        if (v.type === 'pathed elements') {
            //! this implements the spec's unambiguous pathless reference
            //  resolution semantics:
            //  - if a name is uniquely associated with a single element,
            //    a pathless reference will resolve to it
            //  - if not unique, a path must be given.
            // todo![now, references] What about relative path references?
            if (name in v.map) {
                return v.map[name]
            }
            if (Object.keys(v.map).length === 1) {
                return v.map[Object.keys(v.map)[0]]
            }
            console.warn(`ambiguous "${name}}" reference: name refers to multiple entries: ${Object.keys(v.map)}`)
            return undefined
        }
        return v
    }

    public toString () {
        let s = ''
        const sorted = Object.entries(this.index)
            .sort((a, b) => a[0].localeCompare(b[0]))
        for (const [name, val] of sorted) {
            if (val.type === 'pathed elements') {
                s += `${name}: `
                s += '\n'
                for (const path in val.map) {
                    s += `  ${path} ➞ `
                    s += stringifyValue(val.map[path])
                    s += '\n'
                }
            } else  {
                s += stringifyNameAndValue(name, val)
                s += '\n'
            }
        }
        return s
    }
}
