import type {Stylesheet} from './stylesheet.js'
import {BaseStyleType, ElementalBlockStyle, evalPTNamedValueExpr, evalIRBlockPropertyExpr, RE_MARKDOWN_INDENT, BlockStyleType, ParseEffects, NamedValueExpressions} from './elementalBlockStyle.js'
import {Block} from './block.js'
import type {NamedValue} from './namedValue.js'
import {Expression} from './expression.js'
import * as ir from '../../stir/index.js'
import type {Line} from '../line.js'
import {ParseContainer} from '../parseContainer.js'

const reIndentContainerTrailHack = /(\n *)*$/

export class ContainerStyle implements ElementalBlockStyle {
    public baseType: BlockStyleType = BaseStyleType.Container

    public name: string

    public indent: 'yes' | 'no' | 'optional' | `markdown(${number},${number})` = 'no'
    private mmIndentMin = -1
    private mmIndentMax = -1

    public hangingIndent: 'no' | 'auto' | 'marker' | number = 'no'
    public hangingIndentRequired = false
    public marker: RegExp | undefined

    //todo![now] i don't like this inconsistency where here we are using `null`
    //  go back to `undefined` , and for bottomBorder have 'match top' mean that.
    public topBorder: string | null = null
    // bottomBorder:
    // - null means: *there is no bottom border*.
    //   - explicitly disable bottom border by setting it to '' (null for now).
    //     If disabled, at least left border or indent must be specified, otherwise
    //     there would be no terminal condition for the block.
    // - undefined means: must match top border
    // - otherwise an string representing the set of characters allowed for the border.
    public bottomBorder: string | null | undefined = undefined
    //todo[spec, docs, markdown flaws] CommonMark's allowing a Setext underline
    // of a single character is rather dumb. Even two character borders or underlines
    // is making a lot of people pay for the laziness of a few, and oh, look at
    // this level of laziness! PTSS should be designed somehow to encourage
    // good design and discourage bad, starting with defaults. And maybe a
    // a stylesheet setting such as an explicit `natural` declaration that
    // imposes stricter rules?
    public topBorderMinLength = 8 //more dynamic rules (e.g. min length relative to content) via property expressions?
    public topBorderMaxLength = 120

    public leftBorder: string | null = null
    // Padding is ALWAYS at most one space, otherwise there's too much
    // ambiguity between padding and indent.
    // - 'per first line' means the first line determines padding. This means that
    //    if the first line has padding, and a subsequent line does not, the
    //   left border container will terminate, and potentially a new, zero
    //   padding container will start.
    // - 'sloppy' is Markdown's way, which means padding is optional on a line-by-line
    // basis, resulting in odd (bad) behavior (described in the docs)
    public leftPadding: 'yes' | 'no' | 'per first line' | `sloppy` | undefined

    public righter: RegExp | null = null

    /**
     *
     * Source Container Content Models
     * ===============================
     * (distinct from and independent of target STIR element content model)
     *
     * INLINE_OR_BLOCKS
     * ------
     * InlineSequence | BlockSequence
     *
     * If the content of the container is a single chunk, it will be treated
     * as an InlineSequence.
     *
     * BLOCKS
     * ------
     * BlockSequence
     *
     * RAW
     * ---
     * The term `RAW` is better than `LITERAL` because the raw source
     * might be taken literally, or it might not (e.g. it might be
     * inline parsed, or otherwise manipulated). This way we avoid confusion.
     * One describes how to parse the source, and the other describes the
     * semantics of the source.
     *
     * We may update the Content Model types to include a `LITERAL`, which would
     * be a shortcut way to say `RAW` and then copy it directly to the STIR
     * body with no manipulation. We'd only do that if it were a common enough
     * thing to give it special terse expression.
     *
     * The extracted raw text will NOT include a trailing newline, as depicted
     * below. The trailing newline is considered an artifact of the form (the
     * bottom border cannot exist on the same line as the last line of content.
     * Another reason, is for consistency guarantees for semantically equivalent
     * content: a container terminated by a bottom border, a parent container
     * EOC, or EOF should be identical. Downstream processing shouldn't have to
     * deal with the uncertainly, have if-else logic, etc.
     *
     * IF the style needs the trailing newline, it can use `RAW_NL` instead.
     * See below.
     *
     * This:
     *   ~~~~~~~~~~~~~~~~~~ markdown
     *   ``````````````
     *   <-- from here \n
     *      to here -->\n
     *   ``````````````
     *   ~~~~~~~~~~~~~~~~~~
     * yields: '<-- from here \n   to here -->'
     *
     * and this:
     *   ~~~~~~~~~~~~~~~~~~ markdown
     *   ``````````````
     *        \n
     *   ``````````````
     *   ~~~~~~~~~~~~~~~~~~
     * yields: '     '
     *
     * and this:
     *   ~~~~~~~~~~~~~~~~~~ markdown
     *   ``````````````
     *   \n
     *   ``````````````
     *   ~~~~~~~~~~~~~~~~~~
     * yields: ''
     *
     * RAW_NL
     * ------
     * Same as RAW but includes trailing newline (guaranteed, even at EOF).
     * We may nix this and just have style that need it append a newline to
     * the body via PTSS Expression.
     *
     * Regex
     * -----
     * entire match available as expression: ['body']
     * match groups available by index, e.g.: ['body' 2]
     *
     *
     * @type {'INLINE_OR_BLOCKS' | 'BLOCKS' | 'RAW' | 'RAW_NL' | RegExp}
     *
     * todo![spec, ptss, dsl, composition] Would it be more elegant and consistent
     *  if we nixed 'BLOCKS' and replace it with the following?
     *  ```
     *  body = 'RAW' | 'RAW_NL' | RegExp = 'RAW'
     *  irNode.body = ['parseBlocks' ['body'] ...]
     *  ```
     *  and also nixed 'RAW_NL' as suggested above.
     *  - - -
     *  PERHAPS THE ANSWER IS "NO".
     *  ==KEY PRINCIPLE==:
     *     - this `body` field makes a declaration ABOUT THE PLAIN TEXT SOURCE ELEMENT'S BODY
     *     - irNode.body makes a declaration ABOUT THE OUTPUT STIR ELEMENT'S BODY
     *  Thoughts, given this principle:
     *  - keep 'BLOCKS'. It means "the source body is composed of block
     *    elements as defined by this style sheet. In other words, this is a
     *    point of style sheet recursion."
     *  - - -
     *  HOW DOES COMPOSITION FIT IN?
     *  ```
     *  body = [
     *    $block(heading),
     *    'BLOCKS',
     *    $block('Thematic Break'),
     *    $block('paragraph')
     *  ]
     *  irNode.title = ['body', 1]
     *  irNode.body = ['body', 2]
     *  irNode.footer = ['body', 4]
     *  ```
     *  or we could use names (and we'd do the same with Regex groups):
     *  ```
     *  body = {
     *    heading: $block(heading),
     *    body: 'BLOCKS',
     *    divider: $block('Thematic Break'),
     *    footer: $block('paragraph')
     *  }
     *  irNode.title = ['body', 'heading']  // STIR title <= this PTSS element's "heading" <= PTSS container body element 1.
     *  irNode.body = ['body', 'body']      // STIR body <= this PTSS element's "body" <= PTSS container body elements 2 thru n-2.
     *  irNode.footer = ['body', 'footer']  // STIR footer <= this PTSS element's "footer" <= PTSS container body elements n.
     *  ```
     */
    public body: 'INLINE_OR_BLOCKS' | 'BLOCKS' | 'RAW' | 'RAW_NL' | RegExp = 'INLINE_OR_BLOCKS' //todo[spec] better to default RAW?.

    //todo[spec] support minLines OR allowZeroLineContainers
    //   See case files for examples/visual analysis.
    //   - if we have the minLine rule, we'd have to parse ahead to
    //     make sure we have min lines. BUT CAN WE DO THIS without
    //     having to parse the contents normally, (which we can't do
    //     within `parse`, only BlockTreeParser can do that) and
    //     then we'd have to back out all the parsing state changes
    //     if min lines fails. LIKELY CRAZY COMPLEX.
    //   - fuck `minLines`. There is no need to support any minLines
    //     value greater than one, so we should just do
    //     `allowZeroLineContainers`, in which case we only have to
    //     look one line ahead, and we can do that immediately within
    //     parse open, and existing Line checkpointing is more than
    //     sufficient. In fact, all we have to do is make sure the
    //     next line isn't a bottom border or end of parent container!

    /** One instance of continues func to be shared by all occurrences of this style. */
        // initialize with non-null default to avoid null check
    public continues: (line: Line) => boolean = () => false

    public canInterruptParagraph = true
    public canInterruptLazy = true

    readonly consumesBackChunk = false

    public styleSheet: Stylesheet

    // property expressions
    private _irNode?: { [key: string]: Expression }
    private _ptNamedValue?: NamedValueExpressions

    constructor (name: string, styleSheet: Stylesheet) {
        this.name = name
        this.styleSheet = styleSheet
    }

    get irNode (): { [p: string]: Expression } {
        if (this._irNode === undefined) {
            this._irNode = {}
        }
        return this._irNode
    }

    set ptNamedValue (value: NamedValueExpressions) {
        this._ptNamedValue = value
    }

    initAndValidate (): void {
        if (this._ptNamedValue?.value === 'self' && this._irNode === undefined) {
            throw new Error(`ptNamedValue.value is set to ${this._ptNamedValue.value} but the style is not configured to generate one`)
        }

        const markdownIndentMatch = RE_MARKDOWN_INDENT.exec(this.indent)

        if (!markdownIndentMatch
            && this.topBorder === null
            && this.leftBorder === null
            && this.indent !== 'yes'
            && this.hangingIndent === 'no') {
            throw new Error('invalid container style: no definitive container delimiter(s)')
        }

        if (markdownIndentMatch) {
            this.mmIndentMin = Number.parseInt(markdownIndentMatch[1])
            if (this.mmIndentMin < 0) {
                throw new Error('invalid container style: indent min cannot be negative')
            }
            if (this.mmIndentMin <= this.styleSheet.sloppyMargin) {
                throw new Error('invalid container style: indent min must be greater than allowed sloppy margin')
            }
            this.mmIndentMax = Number.parseInt(markdownIndentMatch[2])
            if (this.mmIndentMax < this.mmIndentMin) {
                throw new Error('invalid container style: indent max must be at least indent min')
            }
        }

        if (this.topBorder) {
            if (this.bottomBorder === null && this.leftBorder === null
                && this.indent !== 'yes' && this.mmIndentMin < 1) {
                throw new Error('invalid container style: disabling bottom border without specifying'
                                + ' a left border or indent results in no terminal condition')
            }
            // todo[spec] TEMP HACK: the min length was reduced to 1 to support the
            //  interjection container hack. When the hack is no longer needed,
            //  what should the min be? Why not allow 1?
            /*
            if (this.topBorderMinLength < 1) {
                throw new Error('topBorderMinLength too low')
            }
            */
        }

        if (this.leftBorder) {
            if (!this.leftPadding) {
                throw new Error('invalid container style: leftPadding must be specified for left border')
            }
        } else if (this.leftPadding) {
            throw new Error('invalid container style: leftPadding only makes sense if there is a left border')
        }

        if (this.righter && !this.topBorder) {
            throw new Error('invalid container style: righter only support with top border')
        }

        if (this.hangingIndent !== 'no') {
            if (this.topBorder !== null || this.leftBorder !== null || this.bottomBorder) {
                throw new Error('invalid container style: hanging indent containers cannot have any borders')
            }

            const noMaterialMarker =
                this.marker === undefined ||
                this.marker.test('') ||
                this.marker.test(' ') ||
                this.marker.test('\t')

            if (this.hangingIndent === 'marker') {
                if (noMaterialMarker) {
                    throw new Error('invalid container style: hangingIndent is \'marker\' but no valid marker is defined')
                }
            } else if (this.hangingIndent === 'auto') {
                //
            } else if (!(this.hangingIndent > 1)) {
                throw new Error('invalid container style: invalid hangingIndent declaration')
            }

            if (this.canInterruptParagraph && noMaterialMarker) {
                throw new Error('invalid container style: unmarked hanging indent containers cannot interrupt')
            }
        }

        if (this._irNode === undefined) {
            throw new Error('invalid container style: target irNode node not defined')
        }

        if (this._irNode.type === undefined) {
            throw new Error('invalid container style: target irNode node type not specified')
        }

        if (Object.values(this._irNode)
                .filter(s => JSON.stringify(s).indexOf('body') > -1)
                .length === 0) {
            // the default rule for `body` if one is not made explicitly
            this._irNode.body = new Expression(['body', 0])
            console.debug(`${this.styleSheet.name} container style "${this.name}" does use the source 'body' value. It will be automatically written to the STIR body.`)
        }

        this.continues = sourceContinuesFunc()
    }

    /**
     * RETURN CASES:
     *  - NO TOP BORDER, NO HEADER
     *    - "open" IR Node with continues method
     *    - endLine === curLine
     *    - endLine PARTIALLY consumed
     *  - NO TOP BORDER, WITH HEADER
     *    - "open" IR Node with continues method
     *    - endLine >= curLine (depends on how many lines header consumed)
     *    - endLine FULLY consumed (unless we decide to support partial width header)
     *  - TOP BORDER, NO HEADER
     *    - "open" IR Node with continues method
     *    - endLine === curLine
     *    - endLine FULLY consumed
     *  - TOP BORDER, WITH HEADER
     *    - "open" IR Node with continues method
     *    - endLine > curLine
     *    - endLine FULLY consumed (unless we decide to support partial width header)
     *  - LITERAL CONTENT MODEL
     *    - "closed" IR Node. NO continues method
     *    - endLine > curLine
     *
     *  BOTTOM BORDER is handled by the continues method except in the case of
     *  LITERAL CONTENT MODEL.
     */
    parse (firstLine: Line, fromBackLine: Line | null, lookahead = false): ParseEffects | null {
        //todo[test] add tests to verify the rules defined in the docs, including:
        //  - sloppiness and block indentation are mutually exclusive
        //    (the code subtly enforces this below, but for efficiency there aren't
        //    any explicit assertions. This applies to chunk styles as well.
        //  - (see the indentation vs sloppiness doc as well as others regarding
        //    whitespace and container structure)

        const src = firstLine.src
        let endLine: Line = firstLine

        // uncomment and use this line to trigger the debugger at a certain line
        //if (src.startsWith('   ```\n', pf.curOffset)) debugger

        // indent will be zero if there is slop (which was already parsed by
        // BlockTreeParser) because they are mutually exclusive
        let indent
        if (this.mmIndentMin !== -1) {
            indent = firstLine.parseSpaces(this.mmIndentMax)
            if (indent < this.mmIndentMin) {
                return null
            }
        } else {
            indent = firstLine.parseSpaces()
            if (indent === 0 && this.indent === 'yes') {
                return null
            }
            if (indent > 0 && this.indent === 'no') {
                return null
            }
        }

        // sloppiness of successive container lines is governed by
        // rules, implemented here:
        let successiveSloppiness
        if (this.leftBorder) {
            // for a left border, the stylesheet's sloppiness
            // applies to each line, e.g.:
            //   > this is
            // > a sloppy
            //  > blockquote
            successiveSloppiness = this.styleSheet.sloppyMargin
        } else if (indent > 0 || this.hangingIndent !== 'no') {
            // indent and sloppiness are mutually exclusive
            successiveSloppiness = 0
        } else {
            // sloppiness is limited to the sloppiness of the opening line
            successiveSloppiness = firstLine.pContainerSlop
        }

        let topBorderChar: string | undefined
        let topLength = 0
        let leftBorderChar: string | undefined
        let bottomBorderChar: string | undefined
        let marker: string | undefined
        let hangingIndent = 0
        let padding: boolean | undefined

        let markerMatch: RegExpMatchArray | null = null
        let righterMatch: RegExpMatchArray | null = null

        // TOP BORDER / LEFT BORDER
        // if opening line has top border, left border starts on
        // next line, so only one or the other can occur here.
        if (this.topBorder) {
            // todo[spec] header inset within topBorder:
            //   The rules:
            //    - inset must be separated by a space on either side
            //    - if the inset text contains the topBorderChar, it
            //      must either NOT exceed two in a row OR not follow
            //      a space. This avoids ambiguity.
            for (const c of this.topBorder) {
                topLength = firstLine.parseChars(c)
                if (topLength > 0) {
                    if (topLength < this.topBorderMinLength) {
                        return null
                    }
                    if (topLength > this.topBorderMaxLength) {
                        return null
                    }
                    topBorderChar = c
                    break
                }
            }
            if (topBorderChar === undefined) {
                return null
            }

            if (this.righter) {
                this.righter.lastIndex = firstLine.ptr
                righterMatch = this.righter.exec(src)
                if (!righterMatch) {
                    console.debug('ABORT: righterMatch failed')
                    return null
                }

                // todo[spec] shouldn't this either be ignored (see notes/docs for ChunkStyles)
                //    OR just fail the top border match as opposed to hard error like this?
                if (righterMatch.index !== firstLine.ptr) {
                    // Ensures content between top border and righter is not unintentionally dropped. Styles
                    //  that want to ignore non-matching content should write their regexp accordingly.
                    throw new Error('righter pattern failed to match match all content after top border.')
                }
                firstLine.ptr += righterMatch[0].length
            }

            // ==== MATCH CERTAINTY =====
            if (lookahead) {
                return {}
            }
            console.debug(`parse Container: ${this.name}  topBorder match: ${JSON.stringify(topBorderChar.repeat(topLength))}` +
                          `  righterMatch: ${JSON.stringify(righterMatch)}`)
            console.group()

            if (this.leftBorder) {
                leftBorderChar = this.leftBorder
                switch (this.leftPadding) {
                    case 'yes':
                        padding = true
                        break
                    case 'no':
                        padding = false
                        break
                    case 'per first line':
                        //todo[padding, fwd-parsing] TEMP HACK: cur line is top
                        //  border, we need to fwd-parse at least one body line
                        padding = true
                        break
                    case 'sloppy':
                        firstLine.parseSpaces(1)
                        break
                    default:
                        throw new Error('invalid style declaration')
                }
            }

            switch (this.bottomBorder) {
                case undefined:
                    bottomBorderChar = topBorderChar
                    break
                case null:
                    bottomBorderChar = undefined
                    break
                default:
                    bottomBorderChar = this.bottomBorder
            }

            if (!firstLine.remainingIsBlank()) {
                // Ensures content is not unintentionally dropped. Styles
                //  that want to ignore non-matching content should write their regexp accordingly.
                throw new Error('top border / right pattern failed to match match all content after top border.')
                // todo[errors] Perhaps we should do what DocUtils does?
                //    see: http://rst.ninjs.org/#CmxpbmUgMQogIGxpbmUgMgogbGluZSAzCgoKaGVhZGluZyAxCj09PT09CgpoZWFkaW5nIDIKPT09PT09PT09PQoKPT09PT09PT09CmhlYWRpbmcgMwo9PT09PQoKPT09PT09PT09CmhlYWRpbmcgNAptdWx0aWxpbmUKPT09PT0KCnRoZSBlbmQuCgoKCg==
                // todo[errors] we could have a "strict mode" like Handlebars: https://handlebarsjs.com/api-reference/compilation.html#handlebars-compile-template-options
            }

        } else if (this.leftBorder) {
            leftBorderChar = src.charAt(firstLine.ptr++)
            //todo[spec] change this to support any of a set of chars defined in the style
            //  as we already do for top and bottom border.
            if (leftBorderChar !== this.leftBorder) return null

            switch (this.leftPadding) {
                case 'yes':
                    if (firstLine.parseSpaces(1) === 0) {
                        return null
                    }
                    padding = true
                    break
                case 'no':
                    padding = false
                    break
                case 'per first line':
                    padding = firstLine.parseSpaces(1) === 1
                    break
                case 'sloppy':
                    firstLine.parseSpaces(1)
                    // padding remains undefined, meaning it can be sloppy (optional on a per-line basis)
                    break
                default:
                    throw new Error('invalid style declaration')
            }

            // ==== MATCH CERTAINTY =====
            if (lookahead) {
                return {}
            }
            console.debug(`parse Container: ${this.name}  leftBorder match: ${JSON.stringify(this.leftBorder)}}`)
            console.group()
        } else {
            // must be some kind of indent container
            if (this.hangingIndent === 'no') {
                // simple indent demarcated container

                // ==== MATCH CERTAINTY =====
                if (lookahead) {
                    return {}
                }
                console.debug(`parse Container: ${this.name}  indent match: ${indent}`)
                console.group()
            } else {
                // hanging indent container

                let markerLen = 0
                if (this.marker !== undefined) {
                    this.marker.lastIndex = firstLine.ptr
                    markerMatch = this.marker.exec(src)
                    if (markerMatch === null) {
                        return null
                    }
                    markerLen = markerMatch![0].length
                    // marker must be followed by a space or newline
                    if (src[firstLine.ptr + markerLen] !== ' '
                        && src[firstLine.ptr + markerLen] !== '\t'
                        && src[firstLine.ptr + markerLen] !== '\n') {
                        return null
                    }
                    if (!lookahead) {
                        firstLine.ptr += markerLen
                        if (this.hangingIndent === 'marker') {
                            //! same as ListStyle (with the exception of Markdown quirks which we'll need to
                            //   make non-default before implementing ListStyle as hanging-marker
                            hangingIndent = firstLine.pContainerSlop + indent + markerLen + firstLine.parseSpaces()
                            console.debug(`parse Container: ${this.name}  hanging marker match: ${markerMatch}  indent: ${hangingIndent}`)
                            console.group()
                        } else if (this.hangingIndent !== 'auto') {
                            firstLine.parseSpaces()
                            hangingIndent = firstLine.pContainerSlop + indent + this.hangingIndent
                            console.debug(`parse Container: ${this.name}  hanging marker match: ${markerMatch}  hanging indent match: ${hangingIndent}`)
                            console.group()
                        }
                    }
                }

                // look at next non-empty line to figure out hanging indent
                // if it can't be determined from the first line OR if
                // there must be at least one hanging indent line for a match.
                if (hangingIndent === 0 || this.hangingIndentRequired) {
                    let nextLine = firstLine.nextInContainer
                    // skip blank lines
                    while (nextLine !== null && nextLine.remainingIsBlank()) {
                        nextLine = nextLine.nextInContainer
                    }
                    if (nextLine === null) {
                        //! Unmarked hanging indent requires at least one following,
                        //  indented line. Otherwise it's just a regular paragraph/chunk.
                        if (markerLen === 0 || this.hangingIndentRequired) {
                            return null
                        }
                        // we have a single line marked hanging indent
                        if (lookahead) {
                            return {}
                        }
                        // This value doesn't matter as we've hit end of
                        // container. Set high to make any bug obvious.
                        hangingIndent = 999
                    } else {
                        // line lookahead to determine next line indent
                        const cp = nextLine.checkpoint()
                        let nextIndent = nextLine.parseSpaces()
                        cp.restore()
                        if (this.hangingIndent === 'auto') {
                            if (nextIndent < firstLine.pContainerSlop + indent + 1) {
                                if (markerLen === 0 || this.hangingIndentRequired) {
                                    return null
                                }
                                // We definitely have a single line marked
                                // hanging indent. Set this value to >1 so that
                                // it doesn't consume the non indented lines
                                // that immediately follow. We'll set it high
                                // to make any bug obvious.
                                nextIndent = 999
                            }
                            if (lookahead) {
                                return {}
                            }
                            hangingIndent = nextIndent
                        } else {
                            hangingIndent = firstLine.pContainerSlop + indent + (this.hangingIndent as number)
                            if (nextIndent < hangingIndent || this.hangingIndentRequired) {
                                return null
                            }
                            if (lookahead) {
                                return {}
                            }
                        }
                    }
                    firstLine.parseSpaces()
                    console.debug(`parse Container: ${this.name}  hanging indent match: ${hangingIndent}${this.hangingIndent === 'auto' ? '(auto)' : ''}`)
                    console.group()
                }
            }
        }

        console.debug(`max sloppiness:${successiveSloppiness}  indent:${indent}  padding:${padding}`)

        //todo[spec] if there is a headerPattern, match against it.
        //  apply header property expressions

        const ptBlock = new Block(this)
        ptBlock.style = this
        ptBlock.sloppiness = successiveSloppiness
        ptBlock.indent = hangingIndent || indent
        ptBlock.padding = padding
        if (topBorderChar) {
            ptBlock.topBorderChar = topBorderChar
            ptBlock.bottomBorderChar = bottomBorderChar
            ptBlock.topBorderLength = topLength
        }
        ptBlock.leftBorderChar = leftBorderChar
        ptBlock.continues = this.continues
        ptBlock.lineStart = firstLine.num

        //the type should be overwritten by expression.
        // todo[spec] what should the default be? it should be the base type IR for containers, else just 'block'
        //    whatever it is, it must be in the STIR dictionary, with default rendering semantics spelled out.
        const irBlock = new ir.Block('container')
        irBlock.source = ptBlock

        let bodyMatch: string[] | null = null
        // if a block container's body is the provenance of BlockTreeParser...
        if (this.body === 'BLOCKS' || this.body === 'INLINE_OR_BLOCKS') {
            // BlockTreeParser will handle parsing nested block content
            // as well as recognizing block termination including
            // any bottom border
            console.debug(`body: (will be added to STIR container body as we go)`)

            // todo[spec] given the way irNode/ptNamedValue expressions are evaluated (keyed by
            //    irNode/ptNamedValue field, NOT by input) it makes sense to defer all expression
            //    evaluation to when the container is closed and when we have
            //    ALL the inputs, from top, left, righter, bottom border etc...
            //    - We can implement this by moving some of the logic below to
            //      a separate method that only is called on container close,
            //      which happens immediately for non block containers, and later
            //      when BlockTreeParser detects the container close for block containers.
            //    - Or we could move that logic into a "close" closure for block
            //      containers, which would nicely hold references to the inputs
            //      (e.g. headMatch), attach that closure to the irNode.source (just
            //      as we do with the continues method), so that BlockTreeParser can call it.
            // todo![composition] we MUST do as described above for two reasons:
            //    - bottom border can contain info
            //    - irNode/ptNamedValue expressions can refer to Block elements
        } else { //else body is RawText | RegExp
            let body: string | undefined
            [body, endLine] = this.extractRawBody(firstLine, irBlock,
                leftBorderChar === undefined && bottomBorderChar === undefined)
            console.debug(`body: ${JSON.stringify(body)}`)
            if (body) {
                if (this.body === 'RAW' || this.body === 'RAW_NL') {
                    bodyMatch = [body]
                } else { // RegExp
                    this.body.lastIndex = 0
                    bodyMatch = this.body.exec(body)
                }
            }
            console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)}`)
        }
        ptBlock.lineEnd = endLine.num

        if (this._irNode !== undefined) {
            evalIRBlockPropertyExpr(this._irNode, irBlock, null, null, bodyMatch, markerMatch, righterMatch)
        }

        let ptNamedValue: NamedValue | undefined = undefined
        if (this._ptNamedValue !== undefined) {
            ptNamedValue = evalPTNamedValueExpr(this._ptNamedValue, irBlock,
                null, null, bodyMatch, markerMatch, righterMatch)
        }

        console.groupEnd()
        return {irBlock: irBlock, ptNamedValue: ptNamedValue, endLine: endLine}
    }

    // faster and simpler to extract raw container content here and now than leaving it to BlockTreeParser
    extractRawBody (line: Line, irBlock: ir.Block, isIndentContainer: boolean): [text: string, endLine: Line] {
        line.pContainer = new ParseContainer(irBlock, line.pContainer)
        console.debug(`extracting raw content for ${line.pContainer}`)
        console.group()

        let body: string
        let endLine: Line

        //todo[perf] interestingly fenced code block in the root container
        //  DOESN'T get to take advantage of the optimized version of line.fwdBlock()
        //  because even though the content has no left margin and thus could be
        //  extracted in one go rather than line-by-line, it is technically not at
        //  root level (WE JUST ADDED IT ABOVE IN THE FIRST LINE OF THIS METHOD)
        //  We could change the logic to recognize any content extraction
        //  situation where there is no left margin (i.e. either root container, or
        //  a container with ZERO left margin).

        if (line.ptr === line.EOL) {
            // path taken when first line is a top border

            const nextLine = line.nextInContainer
            if (nextLine !== null && nextLine.pContainerBottomBorder === null) {
                [body, endLine] = nextLine.fwdBlock(false)
            } else {
                // when we have a top border that opens a container
                // but it ends without a single line of content. See
                // *Source Container Content Model* spec.
                //
                // todo[errors, strict] We could decide that a strict style would
                //  not allow this and return an error if this occurs for a strict
                //  style.
                console.groupEnd()
                return ['', line]
                //todo[spec, rst] how does this jive with RST's retroactive nesting?
                //  - the way to do RST's logic is to parse whitespace up front,
                //    way ahead until it ends, and then backtrack on the interpretation.
                //  .
                //                BUT FIRST
                //    really look at RST's behavior. Is it really Human?
            }
        } else {
            [body, endLine] = line.fwdBlock(false)
        }

        if (isIndentContainer) {
            //! If the raw content is not bound by borders, it is thus only delineated by
            //  indentation. As such the content continues up to but not including the first
            //  blank line. Remember, for an indent-only container, there is no way to
            //  *visually* distinguish between trailing blank lines belonging to the container
            //  vs belonging to the parent container. PTSS always assume the later, here
            //  in this case as well as in others such as list items. If raw content must
            //  include trailing blanks lines, then use a style with a left or bottom border.
            //
            // todo[fwd-parsing] TEMP HACK: if we properly fwd-parsed the
            //  extent of the indent container, we wouldn't do the hack below.
            //  But since extra blank lines are skipped anyway, this hack HAS
            //  ONLY ONE CONSEQUENCE: the endLine will be wrong, but it will
            //  only result in the source-pos meta data being wrong, and not in
            //  a horrible way (it will just include the blank lines). Should
            //  fix this, but low priority.
            if (this.body === 'RAW') {
                body = body.replace(reIndentContainerTrailHack, '')
            } else {
                body = body.replace(reIndentContainerTrailHack, '\n')
            }
        } else {
            // Border containers support trailing blank lines. So we only
            // strip the final newline. We must use this conditional to cover
            // the rare case there is no trailing newline because the last line
            // terminated with an EOF rather than EOL.

            if (this.body === 'RAW') {
                if (body.at(-1) === '\n') {
                    body = body.slice(0, -1)
                }
            } else {
                if (body.at(-1) !== '\n') {
                    body += '\n'
                }
            }
        }

        console.groupEnd()
        return [body, endLine]
    }

    // todo[spec] there maybe should be an explicit document style, which in most cases will
    //   always match (as the hardcoded one below does), but imagine a document style that
    //   supports optional or required front matter, or BOM chars, etc.
    static newRootContainer (rootIRElemType: string, ptss: Stylesheet): ir.Block {
        const ptRoot = new Block(ptss.rootContainerStyle)
        ptRoot.continues = () => true

        const irRoot = new ir.Block(rootIRElemType) //todo[spec] should be getting type from the style def?
        irRoot.source = ptRoot
        return irRoot
    }

}

function sourceContinuesFunc (): (line: Line) => boolean {
    return function (this: Block, line: Line): boolean {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const ptBlock = this
        const style = ptBlock.style as ContainerStyle
        const indent = ptBlock.indent

        // line.remainingIsBlank() is super efficient, as is the
        // logic within this case, so we do this case first.
        if (line.remainingIsBlank()) {
            if (style.leftBorder !== null) {
                console.debug(`container closed on previous non-blank line ⬅️ `)
                return false
            }
            // we only need to bother parsing out the
            // indent for RAW content
            if (style.body === 'RAW' || style.body === 'RAW_NL') {
                line.parseSpaces(indent)
            }
            return true
        }

        const src = line.src
        const cp = line.checkpoint()

        // slop and indentation are mutually exclusive
        if (indent > 0) {
            // an outdent terminates the container
            const spaces = line.parseSpaces(indent)
            if (spaces < indent) {
                console.debug(`container closed on previous line: reached end of indent ⬅️ `)
                cp.restore()
                return false
            }
        } else if (ptBlock.sloppiness !== undefined && ptBlock.sloppiness > 0) {
            // Content between fences is stripped of slop up to the slop of the
            // top fence, but not more, even if it is less than the amount of
            // slop allowed by the stylesheet.
            line.parseSlop(ptBlock.sloppiness, style.body !== 'RAW_NL')
        }

        const bottomBorderCharSet = ptBlock.bottomBorderChar as string
        if (bottomBorderCharSet) {
            const bbcp = line.checkpoint()

            // BEGIN Markdown sloppy fenced container logic hack
            //   Unlike the normal slop logic for the content between the top
            //   and bottom fences (see above), the bottom fence can have more
            //   slop than the top fence, up to the max for the stylesheet.
            //   todo[spec]: I think this should only be MARKDOWN-MODE-ONLY
            //     behavior. OTOH, I don't think non-Markdown stylesheets will
            //     allow slop at all. TBD.
            if (indent === 0) {
                // because we already parsed slop up to the top fence's slop,
                // we only need to parse any additional spaces up to the max
                if (line.pContainerSlop < style.styleSheet.sloppyMargin) {
                    line.parseSpaces(style.styleSheet.sloppyMargin - line.pContainerSlop, true)
                }
            }
            // END Markdown logic hack

            for (const c of bottomBorderCharSet) {
                const len = line.parseChars(c)
                if (len > 0) {
                    if (len >= (ptBlock.topBorderLength as number) && line.remainingIsBlank()) {
                        console.debug(`container closes on this line: bottom border encountered 🅱 `)
                        // todo[arch] line.pContainerBottomBorder is supposed to
                        //   contain content from the bottom border, supporting
                        //   multi-line bottom borders, so styles that have
                        //   content within the borders can extract and map the
                        //   content via ptss.expressions.
                        line.pContainerBottomBorder = []
                        return true
                    }
                    break
                }
            }
            bbcp.restore()
        }

        if (style.leftBorder) {
            if (src.charAt(line.ptr) !== ptBlock.leftBorderChar) {
                cp.restore()
                console.debug(`container closed on previous line: reached end of left border ⏹ `)
                return false
            }
            line.ptr++

            // empty content ALWAYS matches.
            if (line.remainingIsBlank()) {
                return true
            }

            if (ptBlock.padding === undefined) {
                line.parseSpaces(1)
            } else if (ptBlock.padding) {
                if (line.parseSpaces(1) === 0) {
                    console.debug(`container closed on previous line: MISSING CONTAINER LEFT PADDING ⏹ `)
                    cp.restore()
                    return false
                }
            }
        }

        return true
    }
}

