import type {Pipe, PipeProvider} from '../pipe.js'
import {registerPipe} from '../registry.js'
import type {TOCEmptyHeading, TOCHeading, TOC} from '../toc.js'
import * as ir from '../../stir/index.js'
import type {STIR} from '../stir.js'
import type {Config} from '../config.js'

// todo![now, references] delete all the other TOC generation code once this has taken over


/**
 *
 *
 *
 * fillLevelGapsWithEmptyTOCHeadings
 * =================================
 * This option lets the user choose how to handle gaps in heading levels.
 *
 * background
 * ----------
 * Problem: what to do when there are gaps in heading levels?
 * In the following hierarchy, the level jumps from 2 to 4
 * between headings "Markdown" and "Option X ". This example
 * is based on the Pandoc documentation, where it is clear that
 * the gap was intentional; The author wanted all Options to
 * appear as level 4 and SubOptions at level 5, even if there
 * was an Option belonging to no specific Feature:
 *    # Pandoc
 *    ##
 *    ## Markdown
 *    #### Option X
 *    ##### Option Xa
 *    ### Feature A
 *    #### Option A1
 *    ##### SubOption A1a
 *    #### Option A2
 *    ### Feature B
 *    #### Option B1
 *
 * What should the TOC for the above look like. If it was not
 * intentional, or if gaps in the rendered TOC are not allowed
 * for any reason, then Option X would have to appear in the
 * TOC as a direct child of Markdown. But if it were intentional
 * as above, then the TOC, which is a hierarchical data structure,
 * not a flat list as above, would need to fill the gap with a
 * nameless subheading level.
 *
 *
 * What the HTML Living Standard has to say
 * ----------------------------------------
 * https://html.spec.whatwg.org/#outline
 *
 * > The following example is conforming, but not encouraged as it has no [heading](https://html.spec.whatwg.org/#concept-heading) whose [heading level](https://html.spec.whatwg.org/#heading-level) is 1:
 * >
 * > ```
 * > <!DOCTYPE HTML>
 * > <html lang=en>
 * > <title>Alphabetic Fruit</title>
 * > <section>
 * > <h2>Apples</h2>
 * > <p>Pomaceous.</p>
 * > </section>
 * > <section>
 * > <h2>Bananas</h2>
 * > <p>Edible.</p>
 * > </section>
 * > <section>
 * > <h2>Carambola</h2>
 * > <p>Star.</p>
 * > </section>
 * > ```
 * >
 * > The document's [outline](https://html.spec.whatwg.org/#outline) could be presented as follows:
 * >
 * > 1. 1. Apples
 * >    2. Bananas
 * >    3. Carambola
 *
 * and
 *
 * > The following example is conforming, but not encouraged as the first [heading](https://html.spec.whatwg.org/#concept-heading)'s [heading level](https://html.spec.whatwg.org/#heading-level) is not 1:
 * >
 * > ```
 * > <!DOCTYPE HTML>
 * > <html lang=en>
 * > <title>Feathers on The Site of Encyclopedic Knowledge</title>
 * > <h2>A plea from our caretakers</h2>
 * > <p>Please, we beg of you, send help! We're stuck in the server room!</p>
 * > <h1>Feathers</h1>
 * > <p>Epidermal growths.</p>
 * > ```
 * >
 * > The document's [outline](https://html.spec.whatwg.org/#outline) could be presented as follows:
 * >
 * > 1. 1. A plea from our caretakers
 * > 2. Feathers
 *
 *
 * @param root
 * @param fillLevelGapsWithEmptyTOCHeadings - See above
 */
export function extractTOC (root: ir.Block,
                            fillLevelGapsWithEmptyTOCHeadings = false): TOC {
    const sections = root.hasSections()
    const targetType = sections ? 'section' : 'heading'
    const field = sections ? 'title' : 'body'

    const tocRoot: TOCEmptyHeading = {heading: undefined, subheadings: []}
    let curTOCHeading: TOCHeading | TOCEmptyHeading = tocRoot
    let curTOCHeadingLevel = 0
    const stack: [(TOCHeading | TOCEmptyHeading), number][] = []

    const seenIDs: Record<string, boolean> = {}

    root.traverseBodyTree(function (node) {
        // todo![pipeline, spec] since we are not traversing into
        //  sectioning roots, nor into inlines, we are not truly
        //  guaranteeing uniqueness. An alternative to having to
        //  reconstruct the seenID index *every* time we need to
        //  do something like this, or being forced to traverse
        //  all the way into inlines, is to maintain a seenID
        //  list that is passed down the pipeline and everyone
        //  keeps up-to-date. That combined with Block.assignUniqueID
        //  (we'd generalize it for Inlines too) will provide for
        //  consistent, DRY, efficient ID management.
        if (node.id !== undefined) {
            seenIDs[node.id] = true
        }

        // stop current branch at new sectionRoot boundary
        if (node.isSectioningRoot()) {
            return 1
        }

        if (node.type === targetType) {
            const nodeLevel = node.level as number
            if (fillLevelGapsWithEmptyTOCHeadings) {
                while (nodeLevel > curTOCHeadingLevel + 1) {
                    const newTOCHeading: TOCEmptyHeading = {
                        heading: undefined,
                        subheadings: []
                    }
                    if (curTOCHeading.subheadings === undefined) {
                        curTOCHeading.subheadings = []
                    }
                    curTOCHeading.subheadings.push(newTOCHeading)
                    stack.push([curTOCHeading, curTOCHeadingLevel])
                    curTOCHeading = newTOCHeading
                    curTOCHeadingLevel++
                }
            }
            while (nodeLevel <= curTOCHeadingLevel) {
                [curTOCHeading, curTOCHeadingLevel] = stack.pop() as [TOCHeading, number]
            }
            if (curTOCHeading.subheadings === undefined) {
                curTOCHeading.subheadings = []
            }

            if (node.id === undefined) {
                ir.assignUniqueID(node, seenIDs)
            }

            const newTOCHeading: TOCHeading = {
                heading: node[field] as ir.InlineSequence,
                referentID: node.id as string,
                referent: node
            }
            // todo[spec] should we be copying *all* the properties of
            // the STIR section element to the toc entry?
            // I think the following would make more sense:
            //  - copy ONLY those STIR spec-defined properties that make
            //    sense. It may just be `title` which we've already done
            //    above. Maybe not. But we can hard-code this since we know
            //    all the STIR props defined by the spec in advance.
            //  - copy ALL use-define properties, which we should identify
            //    with a common prefix/namespace, e.g. `$` or something.
            //
            // Though I think we need this for the error count attribs.
            // Unless we find another solution for that. See [Dashboard.md].
            // Maybe the TOC data structure can link to the STIR heading
            // nodes so no copying is necessary.
            //
            // Look at other STIR design notes. For example, I think we're
            // going to prefix
            //  - maybe we should only copy over user-defined metadata,
            //    i.e. not the STIR defined stuff?
            //  - maybe if the STIR spec segregates STIR
            for (const k in node) {
                if (k === field || k === 'level' || k === 'type' || k === 'id') {
                    continue
                }
                const v = node[k]
                if (typeof v === 'string' || typeof v === 'number' || typeof v === 'boolean') {
                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                    // @ts-ignore
                    newTOCHeading[k] = v
                }
            }

            curTOCHeading.subheadings.push(newTOCHeading)
            stack.push([curTOCHeading, curTOCHeadingLevel])
            curTOCHeading = newTOCHeading
            curTOCHeadingLevel = nodeLevel
        }
        return 0
    })

    return tocRoot.subheadings as TOC
}


const provider: PipeProvider = {
    async configure (config?: Config): Promise<Pipe> {
        const fillLevelGapsWithEmptyTOCHeadings = config?.value('fillLevelGapsWithEmptyTOCHeadings') as boolean
        return (root: STIR) => {
            if (ir.isBlock(root)) {
                if (root._tunnel === undefined) {
                    root._tunnel = {}
                }
                (root._tunnel as {toc: TOC}).toc
                    = extractTOC(root, fillLevelGapsWithEmptyTOCHeadings)
            }
        }
    }
}

registerPipe('AddTOC', provider)

export default provider

