import type {Pipe, PipeProvider} from '../pipe.js'
import {registerPipe} from '../registry.js'
import type {STIR, TunnelingSTIR} from '../stir.js'
import * as ir from '../../stir/index.js'

/**
 * Calls the validate function in the STIR module on each stir
 * that passes through the pipeline.
 *
 * validates that:
 *  - all IDs are unique
 *  - all references are valid
 *  - content model defined for each type is fulfilled
 *  - ...
 *
 * See also [STIR validator.md]
 */
const validate: Pipe = (root: STIR) => {
    if (ir.isBlock(root)) {
        // todo![pipeline rethink] would be good to have src metadata even if
        //  not text assembly context, even if stdin.
        const srcInfo = (root as TunnelingSTIR)._tunnel?.srcFile?.crossAssemblyPath() ?
            `\n  src: ${(root as TunnelingSTIR)._tunnel.srcFile.crossAssemblyPath()}` : ''

        const seenIDs: Record<string, unknown> = {}
        root.traverseBodyTree(function(b: ir.Block){
            if (b.id !== undefined) {
                if (b.id in seenIDs) {
                    throw new TypeError(`stir node has duplicate id: ${b.toDebugString()}${srcInfo}`)
                }
                seenIDs[b.id] = true
            }

            if (b !== root) {
                if (!ir.isBlock(b.parent)) {
                    throw new TypeError(`stir node missing parent: ${b.toDebugString()}${srcInfo}`)
                }
            }
            return 0
        })
    }
    /*
    !todo[test] add lineEnd validation

    - walk block tree, for each node
      - assert lineStart >= parent.lineStart
      - assert lineEND <= parent.lineEND

      in addition, if maintain a stack we can keep track of last sibling on each level:
      - assert lineStart > prior sibling lineEND

    THE ABOVE WILL NOT VALIDATE accurate spec conformance, e.g. whether endLine
    includes a blank line when it shouldn't, because we really need unit tests
    for that). BUT IT WILL validate CONSISTENCY.
    */
    return
}

async function configure (): Promise<Pipe> {
    return validate
}

const provider: PipeProvider = {
    async configure (/*config?: Config*/): Promise<Pipe> {
        return configure()
    }
}

registerPipe('ValidateSTIR', provider)

export default provider

