import {Block} from './block.js'
import type {NamedValue} from './namedValue.js'
import {BaseStyleType, ElementalBlockStyle, evalPTNamedValueExpr, evalIRBlockPropertyExpr, BlockStyleType, ParseEffects, RE_MARKDOWN_INDENT, NamedValueExpressions} from './elementalBlockStyle.js'
import type {Expression} from './expression.js'
import type {Stylesheet} from './stylesheet.js'
import * as ir from '../../stir/index.js'
import type {Line} from '../line.js'

export class ChunkStyle implements ElementalBlockStyle {
    name: string
    baseType: BlockStyleType = BaseStyleType.Chunk

    public indent: 'yes' | 'no' | 'optional' = 'no'

    //structure
    headPattern?: RegExp
    bodyPattern?: RegExp
    tailPattern?: RegExp

    // todo[spec] Ditch maxLines? Replace with 'single line' | 'multiline'
    //  Reasoning:
    //    - *Let Humans Decide* principle. Let the author of the content
    //      decide that a tail anchored chunk is too long to be seen as
    //      such by the human eye. Max lines should be a guideline, not
    //      a rule enforced by machines on humans.
    //  🥃 OTOH, what about a Setext heading that is 50 lines long?
    //    that's just crazy, and forward parsing that far is really
    //    expensive, and guess what? it's really expensive for the human
    //    eye too! So we have a maxLines too!!!  So I think we leave it
    //    in and strongly recommend that for any "natural style", it should
    //    be generous (for Always Be Recognizing principle), but that being
    //    too generous doesn't actually help that principle and it hurts
    //    alignment with the human eye AND it costs in performance (e.g.
    //    a document with long paragraphs with many soft breaks to stay
    //    within 60 cals is going to fwd parse EVERY LINE for many lines!
    maxLines?: number //todo[test] missing test cases covering styles with maxLines > 1

    canInterruptParagraph = false
    canInterruptLazy = false

    lazyContinuation = false

    interruptibleBy: ElementalBlockStyle[] = []

    consumesBackChunk = false

    styleSheet: Stylesheet

    // property expressions
    private _irNode?: { [key: string]: Expression }
    private _ptNamedValue?: NamedValueExpressions

    constructor (name: string, styleSheet: Stylesheet) {
        this.name = name
        this.styleSheet = styleSheet
    }

    get irNode (): { [p: string]: Expression } {
        if (this._irNode === undefined) {
            this._irNode = {}
        }
        return this._irNode
    }

    set ptNamedValue (value: NamedValueExpressions) {
        this._ptNamedValue = value
    }

    //todo![validation] missing checks in some styles that i do in others. Need
    //  to implement the common ones in a shared function declared in Block.ts
    initAndValidate (): void {
        if (this._ptNamedValue?.value === 'self' && this._irNode === undefined) {
            throw new Error(`ptNamedValue.value is set to ${this._ptNamedValue.value} but the style is not configured to generate one`)
        }

        // todo![adjacency] construction of this filter would be moved to
        //   adjacency control logic?
        this.interruptibleBy =
            this.styleSheet.blockStyles
                .filter(s => s.canInterruptParagraph && s !== this)

        const match = RE_MARKDOWN_INDENT.exec(this.indent)
        if (match) {
            throw new Error(`markdown mode indent is not supported for chunks`)
        }
        if (this.maxLines === 1) {
            if (!this.headPattern) {
                throw new Error('single-line chunk styles require a head pattern')
            }
            if (this.tailPattern) {
                //todo[spec] Currently:
                //  1. we don't allow this combo
                //  2. but we DO allow a head+tail pattern to end up
                //     being one-line. See code below.
                //  Thus we are saying:
                //  - You CAN have a head+tail.
                //  - It MAY end up being one line or multiple lines.
                //  - But you CANNOT require it to be one line.
                //  Does this make sense?
                //  .
                //  ======== IMPLEMENTATION ============
                //  We could update the `if maxLines === 1` clause of open(),
                //  OR we can implement it in a latter clause since Line.fwdChunk
                //  supports the single line case (i think), and let the more
                //  generic maxLines check there handle it.
                //  .
                //  Look at what's most efficient. Could even nix the entire
                //  `if maxLines === 1` clause if the Line.fwdChunk is as efficient.
                throw new Error('single-line chunk styles CANNOT have tail-pattern')
            }
        }

        if (!this.headPattern && !this.tailPattern) {
            // the only exception to this is the default: paragraph
            throw new Error('chunk styles must have a head or tail pattern')
        }

        // todo[spec] what about indented with tail?
        if (this.consumesBackChunk) {
            if (this.headPattern) {
                throw new Error('chunk styles with head pattern cannot match backwards')
            }
            if (!this.tailPattern) {
                throw new Error('chunk styles without tail pattern cannot match backwards')
            }
            if (this.maxLines === 1) {
                throw new Error('single line chunk styles cannot match backwards')
            }
        }
    }

    /**
     * Returns null if doesn't match
     *
     * todo[test] all variations of structure in Chunk docs
     *
     *  todo[perf] HOW TO AVOID REDUNDANT "FAIL" PARSES?
     *     - WE ALREADY avoid redundant success parses by opportunistically
     *       saving container continuation done for any fwd parse requests
     *       in the state of each Line.
     *     - what if we did that for FAILED parsing? e.g. that we already
     *       checked a line for a bottom border before?
     *       - we could attach the info to the Line
     *       - we could also save the info in the ContainerStyle instance,
     *         just a single number representing "we've already checked
     *         every line up to this line N for this particular feature,
     *         e.g. this instances bottom border.
     *  todo[fwd-parsing] IDEA: We save various fwd parse discoveries
     *   in Line or other:
     *     - We ALREADY save parsed container continuation
     *     - We CAN save new element start detection
     *       - this may have to be tentative if whether an element starts
     *         depends on adjacency rules with the previous element which we
     *         haven't yet determined
     *     - We can save indentation/whitespace parsing
     *       e.g. we have EOC (end of content), so we could add:
     *       - SOC (start of content), or perhaps pContainerSOC.
     *         if pContainerSOC:
     *             pContainerSOL < pContainerSOC < EOC
     *         > 📥❓ we have pContainerSOL, and it looks like we are setting
     *         > it correctly, but are we actually using it?
     *       - see similar note in tableStyle.
     */
    parse (firstLine: Line, fromBackLine: Line | null, lookahead = false): ParseEffects | null {
        const src = firstLine.src
        let lastLine: Line

        // uncomment and use this line to trigger the debugger at a certain line
        //if (firstLine.srcLine().startsWith('   ```\n') debugger

        // indent will be zero if there is slop (which would be already parsed out)
        const indent = firstLine.parseSpaces()
        if (indent === 0 && this.indent === 'yes') {
            return null
        }
        if (indent > 0 && this.indent === 'no') {
            return null
        }

        const startOffset = firstLine.ptr

        // undefined: style does not have head/tail pattern
        // null: pattern did not match
        let headMatch: RegExpMatchArray | null = null
        let tailMatch: RegExpMatchArray | null = null
        let bodyMatch: string[] | null = null

        //! initAndValidate() guarantees valid combinations if isSingleLine, headPattern and tailPattern
        if (this.maxLines === 1) {

            //! single-line, head anchored

            const headPattern = this.headPattern as RegExp
            headPattern.lastIndex = startOffset
            headMatch = headPattern.exec(src)
            if (!headMatch) return null

            console.debug(`parse Chunk: ${this.name} (single-line, head anchored)`)
            console.group()
            console.debug(`indent: ${indent}`)
            console.debug(`headMatch: ${JSON.stringify(headMatch)}  index: ${headMatch?.index}  lastIndex: ${headPattern.lastIndex}`)

            // ==== MATCH CERTAINTY =====
            if (lookahead) {
                console.groupEnd()
                return {}
            }
            firstLine.ptr = headPattern.lastIndex
            if (this.bodyPattern) {
                this.bodyPattern.lastIndex = firstLine.ptr
                bodyMatch = this.bodyPattern.exec(src)
                if (bodyMatch) {
                    console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)} lastIndex: ${this.bodyPattern.lastIndex}`)
                } else {
                    bodyMatch = [firstLine.remainingSlice()]
                }
            } else {
                bodyMatch = [firstLine.remainingSlice()]
            }
            console.debug(`body: ${JSON.stringify(bodyMatch[0])}`)
            lastLine = firstLine

        } else if (this.headPattern) {

            //! multiline, head anchored
            //   - anchor head on first line
            //   - if tail, search ahead for tail match line
            //     - body = everything between head and tail OR bodyPattern match
            //   - else
            //     - body = everything between head and next blank line OR bodyPattern match

            this.headPattern.lastIndex = startOffset
            headMatch = this.headPattern.exec(src)
            if (!headMatch) return null
            firstLine.ptr = this.headPattern.lastIndex
            console.debug(`parse Chunk: ${this.name} (multiline, head anchored)`)
            console.group()
            console.debug(`indent: ${indent}`)
            console.debug(`headMatch: ${JSON.stringify(headMatch)}  index: ${headMatch?.index}  lastIndex: ${this.headPattern.lastIndex}`)

            const r = firstLine.fwdChunk(indent,
                this.bodyPattern, this.tailPattern,
                false, this.maxLines,
                //! interruption checks SHOULD NOT be recursive!
                lookahead ? undefined : this.interruptibleBy,
                this.styleSheet.sloppyMargin, this.lazyContinuation)
            if (r === null) {
                console.debug('ABORT: match failed')
                console.groupEnd()
                return null
            }
            [bodyMatch, tailMatch, lastLine] = r

            if (this.tailPattern) {
                console.debug(`tailMatch: ${JSON.stringify(tailMatch)}  index: ${tailMatch?.index}  lastIndex: ${this.tailPattern.lastIndex}`)
            }
            console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)}`)
            if (lookahead) {
                console.groupEnd()
                return {}
            }

        } else if (!this.consumesBackChunk) {

            //! multiline, tail anchored, forward matching

            console.debug(`parse Chunk: ${this.name} (multiline, tail anchored, forward matching)`)
            console.group()
            console.debug(`indent: ${indent}`)
            const tailPattern = this.tailPattern as RegExp

            //todo[test] add tests for maxLines
            const r = firstLine.fwdChunk(indent,
                this.bodyPattern, this.tailPattern,
                false, this.maxLines,
                //! interruption checks SHOULD NOT be recursive!
                lookahead ? undefined : this.interruptibleBy,
                this.styleSheet.sloppyMargin, this.lazyContinuation)
            if (r === null) {
                //todo[spec] tail pattern in this case is optional according to spec,
                //  this should be handled by style property assertion instead
                console.debug('ABORT: match failed')
                console.groupEnd()
                return null
            }
            [bodyMatch, tailMatch, lastLine] = r

            if (bodyMatch[0].length === 0) {
                // Chunk spec says a body is required for a tail-anchored chunk.
                // Update spec first if someone comes up with a use case against this rule.
                console.debug('ABORT: empty body')
                console.groupEnd()
                return null
            }

            console.debug(`tailMatch: ${JSON.stringify(tailMatch)}  index: ${tailMatch?.index}  lastIndex: ${tailPattern.lastIndex}`)
            console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)}`)
            if (lookahead) {
                console.groupEnd()
                return {}
            }

        } else {

            //! multiline, tail anchored, backward matching
            //   - body = backLines + curLine residual

            if (fromBackLine === null) {
                return null
            }

            lastLine = firstLine
            firstLine = fromBackLine
            if (this.maxLines) {
                if (lastLine.num - firstLine.num + 1 > this.maxLines) return null
            }
            const tailPattern = this.tailPattern as RegExp
            tailPattern.lastIndex = startOffset
            tailMatch = tailPattern.exec(src)
            if (!tailMatch) return null
            console.debug(`parse Chunk: ${this.name} (multiline, tail anchored, backward matching)`)
            console.group()
            console.debug(`tailMatch: ${JSON.stringify(tailMatch)}  index: ${tailMatch?.index}  lastIndex: ${tailPattern.lastIndex}`)
            bodyMatch = [src.slice(startOffset, tailMatch.index)]

            let l = fromBackLine
            let u = ''
            while (l.num < lastLine.num) {
                u += l.remainingSlice()
                l = l.next as Line
            }

            console.debug(`using back unresolved: ${JSON.stringify(u)}`)
            if (u !== '') {
                bodyMatch[0] = u + bodyMatch[0]
            } else {
                // Chunk spec says a body is required for a tail-anchored chunk.
                // Update spec first if someone comes up with a use case against this rule.
                console.debug('ABORT: empty body')
                console.groupEnd()
                return null
            }
            console.debug(`body: ${JSON.stringify(bodyMatch[0])}`)
            if (lookahead) {
                console.groupEnd()
                return {}
            }
            if (this.bodyPattern) {
                this.bodyPattern.lastIndex = 0
                bodyMatch = this.bodyPattern.exec(bodyMatch[0])
                console.debug(`bodyMatch: ${JSON.stringify(bodyMatch)} lastIndex: ${this.bodyPattern.lastIndex}`)
            }
        }

        let irBlock: ir.Block | undefined = undefined
        if (this._irNode !== undefined) {
            const ptBlock = new Block(this)
            ptBlock.lineStart = firstLine.num
            ptBlock.lineEnd = lastLine.num

            //the type should get overwritten by expression.
            // todo[spec] what should the default be? it should be the base type for blocks.
            //    whatever it is, it must be in the STIR dictionary, with default rendering semantics spelled out.
            irBlock = new ir.Block('block')
            evalIRBlockPropertyExpr(this._irNode, irBlock, headMatch, tailMatch, bodyMatch, null, null)
            irBlock.source = ptBlock
        }

        let ptNamedValue: NamedValue | undefined = undefined
        if (this._ptNamedValue !== undefined) {
            ptNamedValue = evalPTNamedValueExpr(this._ptNamedValue, irBlock,
                headMatch, tailMatch, bodyMatch, null, null)
        }

        if (irBlock === undefined && ptNamedValue === undefined) {
            console.debug('ABORT: no output artifacts')
            console.groupEnd()
            return null
        }

        console.groupEnd()
        //todo[arch] do we really need to return lastLine from all these style parse open methods, since
        //  internally they are all setting ptBlock.lineEnd? Cant BlockTreeParser pull the value
        //  from there if it needs it? Or does keeping it here somehow makes things clearer?
        return {irBlock: irBlock, ptNamedValue: ptNamedValue, startLine: firstLine, endLine: lastLine}
    }

}
