// the reason we use `mdurl/encode` rather than the built-in encodeURI is because the former
// has logic to avoid double-encoding.
import {decodeHTML} from 'entities/lib/decode.js'

const C_BACKSLASH = 92
export const ENTITY = '&(?:#x[a-f0-9]{1,6}|#[0-9]{1,7}|[a-z][a-z0-9]{1,31});'

const reBackslashOrAmp = /[\\&]/

export const ESCAPABLE = '[!"#$%&\'()*+,./:;<=>?@[\\\\\\]^_`{|}~-]'

const reEntityOrEscapedChar = new RegExp('\\\\' + ESCAPABLE + '|' + ENTITY, 'gi')

const reAttribSpecial = new RegExp('[&<>"]', 'g')

const reTextSpecial = new RegExp('[&<>]', 'g')

const reWhitespace = /\s/

export function isWhitespace(ch: string) {
    return reWhitespace.test(ch)
}

const unescapeChar = function (s: string): string {
    if (s.charCodeAt(0) === C_BACKSLASH) {
        return s.charAt(1)
    } else {
        return decodeHTML(s)
    }
}

// Replace entities and backslash escapes with literal characters.
export const unescapeString = function (s: string): string {
    if (reBackslashOrAmp.test(s)) {
        return s.replace(reEntityOrEscapedChar, unescapeChar)
    } else {
        return s
    }
}

const replaceUnsafeAttribChar = function (s: string): string {
    switch (s) {
        case '&':
            return '&amp;'
        case '<':
            return '&lt;'
        case '>':
            return '&gt;'
        case '"':
            return '&quot;'
        default:
            return s
    }
}

export const escapeAttrib = function (s: string): string {
    if (reAttribSpecial.test(s)) {
        return s.replace(reAttribSpecial, replaceUnsafeAttribChar)
    } else {
        return s
    }
}

const escapeTextChar = function (s: string): string {
    switch (s) {
        case '&':
            return '&amp;'
        case '<':
            return '&lt;'
        case '>':
            return '&gt;'
        default:
            return s
    }
}

export const escapeText = function (s: string): string {
    if (reTextSpecial.test(s)) {
        return s.replace(reTextSpecial, escapeTextChar)
    } else {
        return s
    }
}
