import type {PipeProvider} from './pipe.js'
import type {RenderSTIRProvider} from './renderSTIR.js'
import type {AssemblyRendererProvider} from './render/assembly/AssemblyRenderer.js'
import type {PluginProvider} from './render/stir/html/pluginProvider.js'

const pipeRegistry: Record<string, PipeProvider> = {}
const renderSTIRRegistry: Record<string, RenderSTIRProvider> = {}
const assemblyRendererRegistry: Record<string, AssemblyRendererProvider> = {}
const pluginRegistry: Record<string, PluginProvider> = {}

// todo[api] How do we support non-built-in, custom parse/pipe/render providers
//  and plugins? Maybe:
//  - Add a way to declare custom Providers (name and impl path) so that they
//    can be registered. This could happen in the config of the user installation
//    of Textplain (e.g. in ~/.textplain), in a special section of the pipeline
//    config, or support both. In either case we'd use JS dynamic imports.
//  - Support URLs as well as paths, so that user can use community developed
//    Providers. like Deno does for Typescript modules, or node does for GitHub
//    hosted modules.

console.debug('initializing pipeline component registry...')

//todo[api] we should support Stylesheets registration too?
//  or is the domain of ptss-parser? I have plans somewhere
//  about how a ptss-parser install has its own config,
//  stylesheets referenced by URL (like how Deno does it for
//  packages), etc.

export function registerPipe (name: string, provider: PipeProvider) {
    if (pipeRegistry[name]) {
        throw Error(`duplicate pipe registration for "${name}"`)
    }
    pipeRegistry[name] = provider
    //todo[api] the name tells us the registered name, but no indication
    //  of the actual source implementation (e.g. path to the source file,
    //  it's name in package.json, etc. Things we might do:
    //  - if we use dynamic imports, as we must for user plugins,
    //    we can log both name and source file. OTOH that won't work if
    //    the user needs to pass in an implementation, not point at a file
    //    to import. We can either not allow that, or not worry about that
    //    case as it's the user's own code, not them loading a third-party
    //    plugin, for which we can require a filename or a plugin folder
    //    with a package.json or something.
    //  - add a description() and/or vendor() methods/props to the API
    console.debug(`registered pipe: ${name}`)
}

export function lookupPipe (name: string): PipeProvider {
    return pipeRegistry[name]
}


export function registerSTIRRenderer (name: string, provider: RenderSTIRProvider) {
    if (renderSTIRRegistry[name]) {
        throw Error(`duplicate STIR renderer registration for "${name}"`)
    }
    renderSTIRRegistry[name] = provider
    console.debug(`registered STIR renderer: ${name}`)
}

export function lookupSTIRRenderer (name: string): RenderSTIRProvider {
    return renderSTIRRegistry[name]
}

export function registerAssemblyRenderer (name: string, provider: AssemblyRendererProvider) {
    if (renderSTIRRegistry[name]) {
        throw Error(`duplicate STIR renderer registration for "${name}"`)
    }
    assemblyRendererRegistry[name] = provider
    console.debug(`registered STIR renderer: ${name}`)
}

export function lookupAssemblyRenderer (name: string): AssemblyRendererProvider {
    return assemblyRendererRegistry[name]
}


export function registerPlugin (name: string, plugin: PluginProvider) {
    if (pluginRegistry[name]) {
        throw Error(`duplicate plugin registration for "${name}"`)
    }
    pluginRegistry[name] = plugin
    console.debug(`registered plugin: ${name}`)
}

export function lookupPlugin (name: string): PluginProvider {
    return pluginRegistry[name]
}
