import * as ptss from '../ptss/index.js'

export function newRSTSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('reStructuredText')

    //todo[spec] just copied CommonMark's. Haven't even thought about natural escapes.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    const thematicBreak = new ptss.ChunkStyle('Thematic Break', stylesheet)
    thematicBreak.irNode.type = new ptss.Expression('thematic_break')
    thematicBreak.headPattern = /(?:(?:\*[ \t]*){3,}|(?:_[ \t]*){3,}|(?:-[ \t]*){3,})[ \t]*$/ym
    thematicBreak.maxLines = 1
    thematicBreak.canInterruptParagraph = true

    const list = new ptss.ListStyle('List', stylesheet)
    list.unorderedMarkers = '-*+'
    list.orderedMarkers = '.)'

    // todo[spec]  should we have something fall through to this in tests to test the concept?
    //   OR should a test for that go in another simpler text ptss?
    // this represents the built-in default indentation container.
    const defaultIndentContainer = new ptss.ContainerStyle('Default Indent Block', stylesheet)
    defaultIndentContainer.irNode.type = new ptss.Expression('block_quote')
    defaultIndentContainer.indent = 'yes'
    defaultIndentContainer.body = 'BLOCKS'

    stylesheet.blockStyles.push(
        thematicBreak,
        list,
        defaultIndentContainer
    )

    stylesheet.inlineStyles = [
        // 'autolink',
        'backticks',
        'emphasis',
        // 'entity',
        'escape',
        // 'html_inline',
        // 'image',
        // 'reference',
        // 'newline',
        // 'html'
    ]

    return stylesheet
}
