import type StateInline from 'markdown-it/lib/rules_inline/state_inline.js'
import type {RuleTokenizer} from './types.js'
import {stringifyValue} from '../ptss/index.js'


export function createRule (arg = ''): RuleTokenizer {
    const flags = arg.split(',')
    // 'segregated' means *segregated note namespace*.
    //  The `^` prefix is included in the ref name ensuring a note_ref
    //  can only reference a note (this assumes that the note style also
    //  includes the `^` prefix in note names.
    const refOffset = flags.includes('segregated') ? 1 : 2
    const noSpace = !flags.includes('spaces')

    function notRef (state: StateInline, silent: boolean) {
        if (state.src.charCodeAt(state.pos) !== 0x5B/* [ */) { return false }
        if (state.src.charCodeAt(state.pos + 1) !== 0x5E/* ^ */) { return false }

        let pos = state.pos + 2
        const max = state.posMax
        let code
        while ((code = state.src.charCodeAt(pos)) !== 0x5D /* ] */) {
            if (pos >= max) return false
            if (noSpace && code === 0x20/* space */) return false
            pos++
        }

        const ref = state.src.slice(state.pos + refOffset, pos)
        console.debug(`parsed note ref "${ref}"`)
        console.group()

        const value = state.env.namedValues.valueFor(ref)
        if (value === undefined) {
            console.debug(`no named value for ${ref}. aborting.`)
            console.groupEnd()
            return false
        }
        if (value.type !== 'element') {
            // todo![now, references, error] this should behave like any other
            //  reference error: ERROR or WARN per user option.
            console.warn(`note referent is not an element`)
            console.groupEnd()
            return false
        }
        console.debug(`named referent: ${stringifyValue(value)}`)

        if (!silent) {
            const token = state.push('note_ref', '', 0)
            token.attrs = [['referent', value.element]]
        }

        state.pos = pos + 1
        console.groupEnd()
        return true
    }

    return notRef
}
