// the case/example block format.
// exported as it is used by the conversion and gen scripts
import * as ptss from '../ptss/index.js'

export function caseBlockStyle (stylesheet: ptss.Stylesheet): ptss.ContainerStyle {
    const style = new ptss.ContainerStyle('Case Block', stylesheet)
    style.irNode.type = new ptss.Expression('case_block')
    //todo![fallback inheritance] TEMP HACK: this type extension info should be
    // written ONCE in the stir type metadata section, not in each instance of
    // the type. See [Fallback Inheritance.md].
    style.irNode.typeExtends = new ptss.Expression('code_block')
    style.topBorder = '`'
    style.topBorderMinLength = 25
    style.righter = / (case|example)(.*)/ym
    style.body = /^([\s\S]*\n)\.\n([\s\S]*)/ym
    style.canInterruptParagraph = true
    style.canInterruptLazy = true
    style.irNode.class = new ptss.Expression(['righter', 1])
    style.irNode.parameters = new ptss.Expression(['righter', 2])
    style.irNode.input = new ptss.Expression(['first', ['body', 1], ''])
    style.irNode.expected = new ptss.Expression(['first', ['body', 2], ''])
    return style
}

export function catchAllAsLiteral (stylesheet: ptss.Stylesheet): ptss.ChunkStyle {
    const style = new ptss.ChunkStyle('Catch All As Literal', stylesheet)
    style.irNode.type = new ptss.Expression('block')
    style.irNode.class = new ptss.Expression(['pass-thru'])
    style.irNode.body = new ptss.Expression(['body'])

    return style
}
