import type {Vfs, EntryInfo, DirEntry} from './vfs.js'
import type {VfsPath} from './vfsPath.js'
import posix from 'path-browserify'

interface File {
    name: string
    parent: Dir
    content: string
    modTime: Date
}

interface Dir {
    name: `${string}/`
    parent: Dir | undefined
    entries: Record<string, File | Dir>
    modTime: Date
}

function isDir (n: File | Dir): n is Dir {
    return 'entries' in n
}

function pathFor (node: File | Dir): string {
    if (node.parent === undefined) {
        return node.name
    }
    return pathFor(node.parent) + node.name
}

export class MemFS implements Vfs {
    private readonly _root: Dir = {
        name: '/',
        parent: undefined,
        entries: {},
        modTime: new Date()
    }
    private _readOnly = false

    constructor () {
        //
    }

    makeReadOnly () {
        this._readOnly = true
    }

    isReadOnly (): boolean {
        return this._readOnly
    }

    private _resolvePath (path: string, throwOnInvalidPath = true): { node: File | Dir, remainder: string[] } {
        path = path.trim()
        if (!path.startsWith('/')) throw new Error(`path must be absolute: "${path}"`)
        if (path === '/') return {node: this._root, remainder: []}

        let node: File | Dir = this._root
        const isExplicitlyDirPath = path.endsWith('/')
        const segments = isExplicitlyDirPath ?
            path.slice(1, -1).split('/') :
            path.slice(1).split('/')

        // todo![links] this does not implement `.` and `..`. See ta.Node.resolve...
        let i = 0
        for (i = 0; i < segments.length; i++) {
            const seg = segments[i]
            if (seg === '') {
                if (throwOnInvalidPath) {
                    throw new Error(`invalid path: "${path}"`)
                }
            }
            if (seg === '.') {
                continue
            }
            if (seg === '..') {
                if (node.parent !== undefined) {
                    node = node.parent
                    continue
                }
                if (throwOnInvalidPath) {
                    throw new Error(`invalid path: "${path}", "${pathFor(node)}" has no parent directory`)
                }
            }
            // todo! add test case for this  (also check coverage when done)
            if (!isDir(node)) {
                if (throwOnInvalidPath) {
                    throw new Error(`invalid path: "${path}", "${pathFor(node)}" is not a directory`)
                }
                break
            }
            const next: File | Dir | undefined = node.entries[seg]
            if (next === undefined) break
            node = next
        }
        if (isExplicitlyDirPath && i === segments.length && !isDir(node)) {
            if (throwOnInvalidPath) {
                throw new Error(`invalid path: "${path}", "${pathFor(node)}" is not a directory`)
            }
            return {node: node.parent, remainder: segments.slice(i - 1)}
        }

        return {node: node, remainder: segments.slice(i)}
    }

    readFileSync (path: string): string {
        const {node, remainder} = this._resolvePath(path)
        if (node === undefined || remainder.length > 0) throw new Error(`invalid path: "${path}"`)
        if (isDir(node)) throw new Error(`${path} is not a file`)
        return node.content
    }

    readInfoSync (path: string): EntryInfo {
        throw new Error(`not implemented`)
    }

    readDirSync (path: string, withInfo: false): DirEntry[]
    readDirSync (path: string, withInfo: true): (DirEntry & EntryInfo)[]
    readDirSync (path: string, withInfo: false | true): DirEntry[] {
        const {node, remainder} = this._resolvePath(path)
        if (node === undefined || remainder.length > 0) throw new Error(`invalid path: "${path}"`)
        if (!isDir(node)) throw new Error(`${path} is not a directory`)

        const entries = Object.entries(node.entries)
        return entries.map(e => {
            const eNode = e[1]
            if (isDir(eNode)) {
                if (withInfo) {
                    return {
                        name: e[0], isFile: false, isDir: true,
                        size: -1,
                        modTime: eNode.modTime
                    }
                } else {
                    return {name: e[0], isFile: false, isDir: true}
                }
            } else {
                if (withInfo) {
                    return {
                        name: e[0], isFile: true, isDir: false,
                        size: eNode.content.length,
                        modTime: eNode.modTime
                    }
                } else {
                    return {name: e[0], isFile: true, isDir: false}
                }
            }
        })
    }

    //todo![renderAssembly]  add overwrite:boolean arg
    //  or do we want: mode: 'create'|'update'|'create or update' ?
    writeFileSync (path: string, data: string): void {
        const overwrite = false
        const {node, remainder} = this._resolvePath(path)
        if (node === undefined) throw new Error(`invalid path: "${path}"`)
        switch (remainder.length) {
            case 0:
                if (isDir(node)) throw new Error(`"${path}" is a directory`)
                if (!overwrite) throw new Error(`"${path}" already exists`)
                node.content = data
                break
            case 1:
                if (!isDir(node)) throw new Error(`Cannot create file in "${pathFor(node)}". It is not a directory.`)
                if (remainder[0] === '') throw new Error(`invalid filename: "${remainder[0]}"`)
                node.entries[remainder[0]] = {name: remainder[0], parent: node, content: data, modTime: new Date()}
                break
            default:
                throw new Error(`Cannot create file in "${posix.join(pathFor(node), ...remainder.slice(0, -1))}". It does not exist.`)
        }
    }

    copySync (fromPath: string | VfsPath, toPath: string, overwrite?: boolean): void {
        throw new Error(`not implemented`)
    }

    mkdirSync (path: string, recursive = false): void {
        const {node, remainder} = this._resolvePath(path)
        if (node === undefined) throw new Error(`invalid path: "${path}"`)
        switch (remainder.length) {
            case 0:
                throw new Error(`"${path}" already exists`)
            case 1:
                if (!isDir(node)) throw new Error(`Cannot create subdirectory in "${pathFor(node)}". It is not a directory.`)
                if (remainder[0] === '') throw new Error(`invalid file name: "${remainder[0]}"`)
                node.entries[remainder[0]] = {name: remainder[0] + '/', parent: node, entries: {}} as Dir
                break
            default:
                if (recursive) {
                    if (!isDir(node)) throw new Error(`Cannot create subdirectory in "${pathFor(node)}". It is not a directory.`)
                    let dir = node
                    for (const name of remainder) {
                        if (name === '') throw new Error(`invalid path: "${path}"`)
                        const newDir = {name: name + '/', parent: dir, entries: {}} as Dir
                        console.log(`adding ${name} tp ${pathFor(dir)}`)
                        dir.entries[name] = newDir
                        dir = newDir
                    }
                } else {
                    throw new Error(`Cannot create ${remainder.at(-1)} in "${posix.join(pathFor(node), ...remainder.slice(0, -1))}". It does not exist.`)
                }
        }
    }

    existsSync (path: string): boolean {
        const {node, remainder} = this._resolvePath(path, false)
        return node !== undefined && remainder.length === 0
    }

    toString () {
        return `MemFS[${Object.keys(this._root.entries).length} root entries]`
    }
}
