type Loc = { 'start': { 'line': number, 'column': number }, 'end': { 'line': number, 'column': number } }

function reportTemplateError (error: string, options: Handlebars.HelperOptions): void {
    //todo![errors]

    // user configurable, e.g.
    // 1. console.warn
    // 2. fail-fast
    // 3. Author Mode / Debug Mode behavior
    // 4. ?

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const loc: Loc = options.loc as Loc
    console.warn(`template error "${error}" at ${loc.start.line}, ${loc.start.column}`)
}

/**
 * Template API {{ href }} returns an href link to the current page, or to the
 * TextAssembly node specified as the first arg. The href returned reflects the
 * href_style setting.
 *
 * See Template API documentation.
 *
 * TODO![renderAssembly] needs to reflect `href_style` setting and all call-specific
 *  overrides. Currently 'local' style and 'abs' override are supported.
 *  - add tests to cover all legit combos.
 */
Handlebars.registerHelper('href', function (...args) {
    const options: Handlebars.HelperOptions = args.pop()
    const from = options.data['root'] as ta.Node
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore  todo![template api] specifying the API will take care of this hack
    // todo![test] should we add test coverage for this? Reasons:
    //  1. confirm that this info is being set on the context
    //  2. we can use this to confirm that the correct template is being selected. The alternative
    //     is to make each template have some unique content so that we can identify the template
    //     used directly from the rendering.
    // Question: Do we have to test by examining stderr? Or can we test the context object somehow?
    console.debug(`{{ href }} called from ${from.debug.templatePath} for ${from.outerAssemblyPath()}`)

    // console.debug(`  options: ${Object.keys(options)}`)
    // console.debug(`  data:    ${Object.keys(options.data)}`)
    // console.debug(`  hash:    ${JSON.stringify(options.hash)}`)
    // console.debug(`  abs:     ${options.hash['style'] === 'abs'}`)

    if (args.length > 1) {
        reportTemplateError('too many args', options)
    }
    if (options.hash['style'] !== undefined && options.hash['style'] !== 'abs') {
        throw new Error(`"${options.hash['style']}" href style not implemented`)
    }

    const to: ta.Node | string = args.length === 0 ? from : args[0]
    const abs = options.hash['style'] === 'abs'

    if (typeof to === 'string') {
        return 'NOT IMPLEMENTED'
    } else {
        return convertFilePathToUriPath(abs ? outPathForNode(to) : relativeOutPath(from, to))
    }
})

//todo[template api] maybe shouldn't return HTML but instead just the list
//  of links and the template can use CSS to style? e.g. like
//  https://krasimirtsonev.com/blog/article/CSS-before-and-after-pseudo-elements-in-practice#breadcrumbs-navigation
Handlebars.registerHelper('breadcrumbs', function (f: ta.File) {
    let cur: ta.Node
    if (f.isOpeningContent()) {
        cur = f.parent
    } else {
        cur = f
    }
    let breadcrumbs = cur.title as string
    while (cur.parent !== undefined) {
        cur = cur.parent
        breadcrumbs = `<a href="${relativeOutPath(f, cur)}">${cur.title}</a> / ` + breadcrumbs
    }
    return breadcrumbs
})

// todo![test] add tests for posix
type HREF_STYLE = 'rel' | 'abs' | 'url' | 'local'

export class Assembly2Website implements AssemblyRenderer {
    private readonly renderSTIR: RenderSTIR
    private readonly dest: SandboxFS

    // resources that aren't part of the TextAssembly, but needed for the rendered website
    private readonly webResourceSrcRoot: SandboxFS | undefined

    private renderCnt = 0
    private copyCnt = 0

    readonly searchIndex: MiniSearch

    /**
     * Determines the href style used to point to a node in the rendered website.
     *
     * todo[api] the option design below is a first idea. Maybe replace with
     *    low-level switches. See *API Design Resources* below.
     *
     * The current design of this setting offers high level, semantic choices
     * such as `'local'`, rather than low-level switches such as `'include
     * index.html' on directories .
     *
     * `posix`: like the posix standard: files don't have trailing slashes,
     *          slashes denote directories.
     * `local`: use this if you want the links to work if the rendered files
     *          are opened on the local locally rather than served by a
     *          webserver.
     *
     * API Design Resources
     * --------------------
     * See:
     *  - https://www.zachleat.com/web/trailing-slash/ (written by the creator of Eleventy and employee of Netlify)
     *  - https://github.com/slorber/trailing-slash-guide
     *  - https://stackoverflow.com/questions/5948659/when-should-i-use-a-trailing-slash-in-my-url
     *  - https://searchfacts.com/url-trailing-slash/
     *  -
     */
    private readonly hrefStyle: HREF_STYLE = 'rel'

    constructor (dest: SandboxFS,
                 renderSTIR: RenderSTIR,
                 webResourceSrcRoot?: SandboxFS,
                 hrefStyle?: HREF_STYLE) {
        this.renderSTIR = renderSTIR
        this.dest = dest
        this.webResourceSrcRoot = webResourceSrcRoot
        this.hrefStyle = hrefStyle || 'rel'
        this.searchIndex = new MiniSearch(
            {
                fields: ['path', 'title', 'body'],
                idField: 'path',
                storeFields: ['path', 'title']
            })
        console.log(`instantiated new Assembly2Website (dest=${dest})`)
    }

    prepare (assembly: ta.Assembly): void {
        this.renderCnt = 0
        this.copyCnt = 0
        this.searchIndex.removeAll()
        this.prepareFolder(assembly)

        //todo![renderAssembly] why is this in prepare and not in render?
        //- presumably because we need insert these properties on every node
        //  in the tree in advance, because templates need them as properties,
        //  not prototype methods, which are a problem. See Template API docs.
        //  🏈 until we tackle the Template API as a whole.
        console.info('hacking Handlebars...')
        ta.traverse(assembly, (node: ta.Node): ta.PreorderVisitReturn => {
            /* eslint-disable */
            const page = node as ta.Node & Record<string, unknown>
            if (node.isFile()) {
                page.isGeneratedHACK = node.isGenerated()
                page.isOpeningContentHACK = node.isOpeningContent()
            } else {
                //
            }
            page.isFileHACK = node.isFile()
            page.isFolderHACK = node.isFolder()
            page.innerAssemblyPathHACK = node.innerAssemblyPath()
            page.outerAssemblyPathHACK = node.outerAssemblyPath()
            /* eslint-enable */
            return 'continue'
        }, undefined)

        if (this.webResourceSrcRoot !== undefined) {
            console.info('copying web assembly resources...')
            this.dest.copySync({vfs: this.webResourceSrcRoot, path: '/'}, '/', true)
        }
    }

    private prepareFolder (folder: ta.Folder) {
        // todo[correctness] should use convertNameToWebName for folder names too?
        folder.outNode = folder.name
        if (folder.parent !== undefined) {
            console.debug(`mkdir ${outPathForNode(folder)}`)
            this.dest.mkdirSync(outPathForNode(folder), false)
        }

        for (const n of folder.content) {
            if (n.isFolder()) {
                if (n.hasIncludedContent) {
                    this.prepareFolder(n)
                }
            } else {
                if (n.status === 'sText: waiting' || n.status === 'resource: waiting') {
                    n.outNode = destForFile(n)
                    if (n.outNode === undefined) {
                        throw new Error(`unexpected state for ${n.outerAssemblyPath()}`)
                    }
                }
            }
        }

        if (folder.openingContent !== undefined) {
            folder.openingContent.outNode = destForFile(folder.openingContent)
        } else {
            throw new Error(`unexpected indexable folder w/o opening content`)
        }
    }

    render (stir: STIR): void {
        //todo![renderAssembly] the pipeline logging has the inner renderer showing up as '♒️ OUTLET', not the assembly renderer.

        const tStir = stir as TunnelingSTIR
        const file = tStir._tunnel.srcFile
        const text = resolveCrossReferencesAndExtractText(stir, file, this.hrefStyle)
        this.searchIndex.add({id: this.renderCnt, path: outPathForNode(file), title: file.title, body: text})
        const html = this.renderSTIR(stir)
        this.dest.writeFileSync(outPathForNode(file), html)
        console.debug(`rendered to: ${outPathForNode(file)}`)
        file.status = 'sText: rendered'
        this.renderCnt++
    }

    copy (file: ta.File): void {
        if (file.status !== 'resource: in pipeline') {
            throw new Error(`unexpected status for ${file.outerAssemblyPath()}: ${file.status}`)
        }

        console.debug(`copying resource: ${file.crossAssemblyPath()}`)
        this.dest.copySync({vfs: file.assemblyRoot().srcFS(), path: file.innerAssemblyPath()}, outPathForNode(file))
        file.status = 'resource: copied'
        this.copyCnt++
    }

    finish () {
        console.info(`finishing website render...`)
        console.group()

        // test search //todo![now, test] replace this with a cli.test.js query
        const results = this.searchIndex.search('asynchronous')
        console.info(`search "asynchronous":`)
        results.forEach(result => console.info(`  ${result.title}`))

        this.publishSearchIndex()

        console.groupEnd()
    }

    private publishSearchIndex (): void {
        const jsonIndex = this.searchIndex.toJSON()
        const serializedIndex = JSON.stringify(jsonIndex)
        const indexFilePath = '/_resources/MiniSearch_Index.json'
        if (!this.dest.existsSync('/_resources/')) {
            this.dest.mkdirSync('/_resources/', false)
        }
        this.dest.writeFileSync(indexFilePath, serializedIndex)
        console.info(`static index: ${(serializedIndex.length / (1024 * 1024)).toFixed(2)} Mb written to ${indexFilePath} (serializationVersion: ${jsonIndex.serializationVersion})`)
    }

    stats (): { [p: string]: RenderStats } {
        return {
            'pages rendered': this.renderCnt,
            'files copied': this.copyCnt,
            'index generated': {
                'pages': this.searchIndex.documentCount,
                'terms': this.searchIndex.termCount
            }
        }
    }
}

function resolveCrossReferencesAndExtractText (stir: STIR, file: ta.File, hrefStyle: HREF_STYLE): string {
    let text = ''

    if (hrefStyle !== 'rel' && hrefStyle !== 'local') {
        throw new Error(`"${hrefStyle}" href style not implemented`)
    }
    console.debug('resolving cross references')
    console.group()
    // 1. need to traverse the STIR for links
    // 2. resolve the link (i.e. using a resolveCrossReferences function)
    //
    // Option A: use Block.traverseBodyTree and Inline.traverseInlineBodyTree.
    // But won't find links outside the body tree without knowing the structure
    // of each node type encountered, e.g. the way parser.ts#parseBlockInlines
    // is hardcoded to look in block.title as well as block.body.
    //
    // Option B: do link resolution at final render time, in htmlOut, since that
    // code will by definition know all of the structures, even for custom
    // types. We could pass htmlOut a resolveCrossReferences function.
    //
    // Option C: Factor a structure-aware traversal out of htmlOut, making it
    // useful for purposes other than the final rendering. Custom block defs
    // are required to tell htmlOut (somehow) about their structure.
    //
    // Option D: Require custom types to register their structure in some
    // generic way (but hey, we own the PTSS spec! Of course we have access!)
    //
    // OBVIOUSLY the solution involves whatever mechanism we settle on for
    // custom block types to register themselves and their structure.

    // BUT FOR NOW:
    const inlineBodyVisitor = function (inline: ir.Inline | string): void {
        // console.debug(`inline: ${inline}`)
        if (typeof inline === 'string') {
            text += ' ' + inline
        }
        if (typeof inline === 'object' && (inline.type === 'xref' || inline.type === 'image')) {
            const referent = inline.referent
            // todo[hack] implement proper detection of URL
            if (typeof referent === 'string' && !referent.startsWith('http:')) {
                console.debug(`resolving link from ${file.outerAssemblyPath()} to ${referent}`)
                const targetNode = file.resolvePath(referent)
                console.debug(`  resolved target: ${targetNode?.outerAssemblyPath()}`)
                if (targetNode !== undefined) {
                    inline.referent = relativeOutPath(file, targetNode)
                    //todo[WIP, hack]
                    if (targetNode.isFolder() && hrefStyle === 'local') {
                        inline.referent += '/index.html'
                    }
                    console.debug(`  rendered path:   ${inline.referent}`)
                } else {
                    console.debug(`  ❌ BROKEN LINK`)
                }
            }
        }
    }
    if (ir.isBlock(stir)) {
        // todo![now] we have a common need to traverse the ENTIRE STIR:
        //   - both Block and Inline
        //   - all paths, not just 'body' and 'title', including custom
        //     content models.
        //   <br>
        //   ADD a `traverseSTIR` or `traversTree` or just `traverse`
        //   function to the STIR package that knows how to do this.
        //   When doing so, decide whether it should implement both
        //   pre- and post-order callbacks as I do for `textAssembly/traverse`.
        //   <br>
        //   MERGE this note with the note that includes
        //    "I've had an epiphany that flies two birds on one wing:",
        //    and possible move to the Whiteboard.
        stir.traverseBodyTree(function (b: ir.Block) {
            // console.debug(`block: ${b.type}`)
            if (b.title) {
                ir.traverseInlineBodyTree(b.title, inlineBodyVisitor)
            }
            if (ir.isInlineSequence(b.body)) {
                ir.traverseInlineBodyTree(b.body, inlineBodyVisitor)
            }
            return 0
        })
    } else {
        ir.traverseInlineBodyTree(stir, inlineBodyVisitor)
    }

    console.groupEnd()
    return text
}

function outPathForNode (n: ta.Node): string {
    let op = n.outNode
    if (op === undefined) {
        throw new Error(`outPath not available for ${n.outerAssemblyPath()}`)
    }
    let p: ta.Folder | undefined = n.parent
    while (p !== undefined) {
        op = p.outNode + op
        p = p.parent
    }
    return op
}

function relativeOutPath (from: ta.Node, to: ta.Node): string {
    if (from.isFile()) {
        from = from.parent
    }
    const rel = posix.relative(outPathForNode(from), outPathForNode(to))
    return rel.length === 0 ? '.' : rel
}

// todo[api] support aggregation (folding of branch into single node)
//  and multiplexing (e.g. html and pdf)
function destForFile (file: ta.File): string | undefined {
    if (file.isOpeningContent()) {
        return 'index.html'
    }

    const type = fileTypeByFilename(file.name)
    //todo[hack] hack for commonmark case files having txt extension
    if (type.name === 'TEXT' && file.parent.name === 'spec/') {
        return convertNameToWebName(file.nameSansExt()) + '.html'
    }
    if (type.name === 'MARKDOWN') {
        return convertNameToWebName(file.nameSansExt()) + '.html'
    } else if (type.name === 'HTML') {
        return file.name
    } else if (type.type === 'media' || type.type === 'resource') {
        return file.name
    }
    return undefined
}


