//   SHOULD this be made the template's responsibility?
//   - or should Textplain do that to ensure consistency and make templates easier to write?
function convertNameToWebName (n: string) {
    return n.replace(/\s/g, '-')
}

//todo[web] not sure this is correct, but it works for most links now
const convertFilePathToUriPath = function (path: string): string {
    try {
        return encode(path, ';/:@&=+$,-_.!~*\'()')
    } catch (err) {
        return path
    }
}


export const provider: AssemblyRendererProvider = {
    async configure (dest: SandboxFS, config?: Config): Promise<AssemblyRenderer> {
        if (config === undefined) {
            // todo![renderAssembly, zero config] should we support zero config by implementing a
            // default config here? Or should that happen upstream?
            throw new Error(`missing AssemblyRendererProvider config`)
        }
        const outletConfig = config.requiredSubConfig('formats.html')
        const renderSTIR = await configRenderSTIRProvider(outletConfig)
        const webResourcesPath = config.resolvedPathValue('webResourcesPath')
        const webResourcesFS = webResourcesPath ? new SandboxFS(webResourcesPath, 'readonly') : undefined
        const hrefStyle = config.value('href_style')
        return new Assembly2Website(dest, renderSTIR, webResourcesFS, hrefStyle)
    }
}

registerAssemblyRenderer('Assembly2Website', provider)

export default provider
