import Handlebars from 'handlebars'
import {stirType, toJSON} from '../../../../stir/index.js'

Handlebars.registerHelper('concat', function (a, b) {
    return a + b
})

Handlebars.registerHelper('json', function (context, indent) {
    const st = stirType(context)
    if (st === 'unknown' || st === undefined ) {
        return JSON.stringify(context, undefined, indent)
    }
    return toJSON(context, indent, true)
})

//todo[api] consider including some library of handlebar functions
// e.g.:
//  - https://www.npmjs.com/package/handlebars-helpers
Handlebars.registerHelper({
    eq: (a, b) => a === b,
    ne: (a, b) => a !== b,
    lt: (a, b) => a < b,
    gt: (a, b) => a > b,
    lte: (a, b) => a <= b,
    gte: (a, b) => a >= b,
    and: (a, b) => a && b,
    or: (a, b) => a || b
})

Handlebars.registerHelper('match', function (text, pattern) {
    return new RegExp(pattern).test(text)
})

Handlebars.registerHelper('match_i', function (text, pattern) {
    return new RegExp(pattern, 'i').test(text)
})
