import * as ptss from '../ptss/index.js'
import {newGFMStylesheet} from './gfm.ptss.js'

export function newMarkdownHSStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    //todo[markdown_hs]: 🏈 so we are building off of commonmark-strict:
    //   why have both?
    //   - i guess cm-strict is simply to study removing sloppiness and laziness,
    //   - while markdown HS goes further?
    //   Can punt as I have a todo to de-prioritize if not totally ditch this style anyway.
    const stylesheet = newGFMStylesheet({strict: true})
    stylesheet.name = 'MarkdownHS'

    const setext = stylesheet.remove('Setext Heading') as ptss.ChunkStyle
    setext.name = 'Setext Section Heading'
    setext.irNode.type = new ptss.Expression('section')
    setext.consumesBackChunk = false
    setext.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['body']]])
    delete setext.irNode.body

    const atx = stylesheet.remove('ATX Heading') as ptss.ChunkStyle
    atx.name = 'ATX Section Heading'
    atx.irNode.type = new ptss.Expression('section')
    atx.canInterruptParagraph = false
    atx.irNode.title = new ptss.Expression(['parseInlines', ['first', ['body', 1], '']])
    delete atx.irNode.body

    const thematicBreak = stylesheet.remove('Thematic Break')

    const linkRefDef = stylesheet.remove('Link Ref Definition')

    const pipeTable = stylesheet.remove('GFM Table') as ptss.TableStyle
    pipeTable.name = 'Pipe Table SV'
    pipeTable.indent = 'optional'
    pipeTable.heading = 'delimited, optional'
    //todo![spec, tables] to support `-` at intersections, the delim row will
    // now just be an line of `------------`. without column info.
    // We'd have to use the heading row instead to determine columns. Ff the
    // table style supported both heading and delim determined columns, we'd
    // have to dynamically see what was up at parse time, e.g. check the delim
    // row for any intersection chars that were not also headingDelim chars,
    // and if none, use the heading row.
    pipeTable.delimIntersection = '|+'
    pipeTable.sideBorderLeftRule = 'optional'
    pipeTable.sideBorderRightRule = 'match'
    // todo[spec, tables] does MarkdownHS require aligned table with inferred content
    //   alignment? Or does it support both (in which case we need to specify
    //   that the indicator overrides inferred?)
    //pipeTable.columnsAlignIndicator = ':'
    pipeTable.gfmQuirks = false

    const list = stylesheet.remove('List')

    const fencedCodeTick = stylesheet.remove('Backtick Fenced Code Block')
    const fencedCodeTilde = stylesheet.remove('Tilde Fenced Code Block')

    const emailBlockQuote = stylesheet.remove('Block Quote') as ptss.ContainerStyle
    emailBlockQuote.name = 'Email-Style Block Quote'

    // Markdown HS reserves indents for block quotes
    stylesheet.remove('Indented Code Block')

    // We are using "Markdown Mode" indentation logic to implement this in
    // MarkdownHS, simply so we can demonstrate its use and the nature of
    // *magical indentation thresholds*. In this case, indents of 2-3 map
    // to verse, while those above 4 become block quotes.

    const indentedBlockquote = new ptss.ContainerStyle('Indented Block Quotation', stylesheet)
    indentedBlockquote.irNode.type = new ptss.Expression('block_quote')
    indentedBlockquote.indent = 'markdown(4,999)'

    // this style has to come LAST because we are using Markdown Mode indentation
    // (to implement a *magical indentation thresholds*, see above) at 4 spaces,
    // and even though this has an indent setting of `markdown,2,3`, the `3`
    // only limits the indentation assigned to the container. It will still
    // match and consume lines with more than `3` leading spaces, assigning
    // all the excess to the content.
    const verse = new ptss.ContainerStyle('Verse', stylesheet)
    verse.irNode.type = new ptss.Expression('verse')
    verse.canInterruptParagraph = false
    verse.canInterruptLazy = false
    verse.indent = 'markdown(2,3)'
    verse.body = 'RAW_NL'

    const htmlCodeBlock = stylesheet.remove('HTML Block')

    const footnote = stylesheet.remove('Footnote')

    if (stylesheet.blockStyles.length > 0) {
        throw Error(`all inherited styles should have been temporarily removed at this point. but ${stylesheet.blockStyles[0].name} was not.`)
    }
    stylesheet.blockStyles.push(
        setext,
        atx,
        pipeTable,
        list,
        linkRefDef,
        fencedCodeTick,
        fencedCodeTilde,
        emailBlockQuote,
        thematicBreak,
        indentedBlockquote,
        verse,
        footnote,
        htmlCodeBlock
    )

    return stylesheet
}
