import {AbstractOut, AbstractWriter} from './abstractWriter.js'
import * as ir from '../../../stir/index.js'
import type {STIR} from '../../stir.js'

type StringOut = AbstractOut & { depth: number }

type BlockWriter = (b: ir.Block, out: StringOut, flags?: Record<string, unknown>) => void
type InlineWriter = (i: ir.Inline, out: StringOut) => void

/**
 * A {Renderer} that outputs the *Structured Text Intermediate Representation*
 * as-is in a simple, hierarchically indented format.
 */
export class Stir2String extends AbstractWriter<StringOut> {

    constructor () {
        super()
        this.initWriters(this.initBlockWriters(), this.initInlineWriters())
    }

    render (stir: STIR): string {
        const out: StringOut = {buf: '', depth: 0}
        if (ir.isBlock(stir)) {
            this.writeBlock(stir, out)
        } else {
            this.writeInlineSequence(stir, out)
        }
        return out.buf
    }

    writeIndent (out: StringOut) {
        out.buf += '    '.repeat(out.depth)
    }

    writeText (t: string, out: StringOut): void {
        this.writeIndent(out)
        out.buf += '"'
        out.buf += t
        out.buf += '"\n'
    }

    writeIndentedLine (line: string | null, out: StringOut) { //todo[stir] must accept null?
        this.writeIndent(out)
        out.buf += line
        out.buf += '\n'
    }

    writeIndentedTextBlock (textBlock: string, out: StringOut) {
        if (!textBlock) return

        for (const t of textBlock.split('\n')) {
            this.writeIndentedLine('|' + t, out)
        }
    }


    initBlockWriters (): { [index: string]: BlockWriter } {
        const writers: { [index: string]: BlockWriter } = {}

        writers['structured_text_intermediate_representation'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('STIR:', out)
            out.depth++
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        writers['paragraph'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('PARA:', out)
            out.depth++
            this.writeInlineSequence(b.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['section'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`SECTION "${ir.inlineBodyText(b.title as ir.InlineSequence)}" (level ${b.level})${b.class ? ' (class=' + b.class + ')' : ''}:`, out)
            out.depth++
            this.writeBlockSequence(b.body as ir.BlockSequence, out)
            out.depth--
        }

        writers['heading'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`HEADING ${b.level}:`, out)
            out.depth++
            this.writeInlineSequence(b.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['block'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`BLOCK (class=${b.class}):`, out)
            out.depth++
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        writers['pre'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`PRE:`, out)
            out.depth++
            this.writeSequence(b.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['code_block'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`CODE_BLOCK (info=${b.info}):`, out)
            out.depth++
            this.writeIndentedTextBlock(b.body as string, out)
            out.depth--
        }

        writers['verse'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`VERSE:`, out)
            out.depth++
            this.writeIndentedTextBlock(b.body as string, out)
            out.depth--
        }

        writers['thematic_break'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('THEMATIC_BREAK', out)
        }

        writers['block_quote'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('BLOCK_QUOTE:', out)
            out.depth++
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        //! not in CommonMark, introduced by PTSS IR
        writers['interjection'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`INTERJECTION (subtype=${b.subType}):`, out)
            out.depth++
            this.writeInlineSequence(b.title as ir.InlineSequence, out)
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        writers['list'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine(`LIST (${b.listOrdered ? 'ordered' : 'unordered'}` +
                                   (b.listOrdered ? ` start=${b.listStartNum}, buf, indent):` : '):'), out)
            out.depth++
            const itemWriter = this.bWriters.list_item
            const flags = {compact: b.hints['commonmark.list.tight'] === true}
            for (const item of (b.body as ir.BlockSequence)) {
                itemWriter(item, out, flags)
            }
            out.depth--
        }

        writers['list_item'] = (b: ir.Block, out: StringOut, flags?) => {
            this.writeIndentedLine('LIST_ITEM:', out)
            if (b.body === undefined) {
                // empty item
                return
            } else if (ir.isBlockSequence(b.body)) {
                out.depth++
                const bodySequence = b.body as ir.BlockSequence
                if (flags?.compact) {
                    for (let i = 0; i < bodySequence.length; i++) {
                        const bodyItem = bodySequence[i]
                        if (bodyItem.type === 'paragraph') {
                            this.writeInlineSequence(bodyItem.body as ir.InlineSequence, out)
                        } else {
                            this.writeBlock(bodyItem, out)
                        }
                    }
                } else {
                    this.writeBlockSequence(bodySequence, out)
                }
                out.depth--
            } else {
                throw new Error(`invalid list item body: ${b.body}`)
            }
        }

        writers['table'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('TABLE:', out)
            out.depth++
            const headRows = b.head as ir.BlockTable
            if (headRows) {
                for (const headRow of headRows) {
                    this.writeIndentedLine('HEADER:', out)
                    out.depth++
                    for (let i = 0; i < headRow.length; i++) {
                        this.writeBlock(headRow[i], out)
                    }
                    out.depth--
                }
            }
            const bodyRows = b.body as ir.BlockTable
            if (bodyRows !== undefined) {
                for (const headRow of bodyRows) {
                    this.writeIndentedLine('ROW:', out)
                    out.depth++
                    for (let i = 0; i < headRow.length; i++) {
                        const cell = headRow[i]
                        this.writeBlock(cell, out)
                    }
                    out.depth--
                }
            }
            out.depth--
        }

        writers['cell'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('CELL:', out)
            out.depth++
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        writers['note'] = (b: ir.Block, out: StringOut) => {
            //todo![now referents] since we aren't attaching the plain text referent name
            //  to the node, we can't print it here. If we change our mind as per a
            //  design note, then update below so it writes it out.
            this.writeIndentedLine(`NOTE (subtype=${b.subType}, orig ref name=${b.ptRefNameDELETE_ME}):`, out)
            out.depth++
            this.writeSequence(b.body as ir.BlockSequence | ir.InlineSequence, out)
            out.depth--
        }

        writers['html_block'] = (b: ir.Block, out: StringOut) => {
            this.writeIndentedLine('HTML_BLOCK:', out)
            out.depth++
            this.writeIndentedTextBlock(b.body as string, out)
            out.depth--
        }

        return writers
    }

    initInlineWriters (): { [index: string]: InlineWriter } {
        const writers: { [index: string]: InlineWriter } = {}

        writers['linebreak'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += '<LINEBREAK>'
            out.buf += '\n'
        }

        writers['xref'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += `XREF ${i.referent} "${i.desc}":`
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['note_ref'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += `NOTE_REF ${i.referent} "${i.desc}":`
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['image'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += `IMAGE ${i.referent} "${i.desc}":`
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['emph'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'EM:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['strong'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'STRONG:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['strike'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'STRIKEOUT:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['subscript'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'SUBSCRIPT:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['superscript'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'SUPERSCRIPT:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['highlight'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'HIGHLIGHT:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['code'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'CODE:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        writers['checkbox'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            if (i.checked === true) {
                out.buf += '<CHECKBOX (checked)>'
            } else {
                out.buf += '<CHECKBOX>'
            }
            out.buf += '\n'
        }

        writers['html_inline'] = (i: ir.Inline, out: StringOut) => {
            this.writeIndent(out)
            out.buf += 'HTML_INLINE:'
            out.buf += '\n'
            out.depth++
            this.writeInlineSequence(i.body as ir.InlineSequence, out)
            out.depth--
        }

        return writers
    }

}
