import json5 from 'json5'
import {Block, BlockSequence} from './block.js'
import {Inline, InlineSequence} from './inline.js'

// todo[arch, serialization] if this gets too complex, consider using
//  - https://www.npmjs.com/package/class-transformer

// For eventual serialized JSON pipelines, see if this makes a performance
// difference:
// https://www.npmjs.com/package/fast-json-stringify

// todo[spec, robustness, gracefulness] should we rely on duck typing rather than `instanceof` ?

/**
 * This will be fully or partially constructed from the head of the STIR-JSON
 * file (the spec will state that the very first key(s) of the JSON object MUST BE
 * X as we need read the type info from it before we can correctly interpret the
 * rest of the JSON.
 *
 * todo[spec, stir, serialization] we could serialize either (TBD):
 *   - all types, both fixed spec-defined types and custom types defined by style sheet
 *   - OR, just the custom types, as others are defined by the spec.
 *   - OR, we support both (compact vs full forms)
 */
const typeMap: { [key: string]: 'block' | 'inline' | undefined } = {
    structured_text_intermediate_representation: 'block',
    paragraph: 'block',
    section: 'block',
    heading: 'block',
    list: 'block',
    list_item: 'block',
    table: 'block',
    block_quote: 'block',
    interjection: 'block',
    code_block: 'block',
    emph: 'inline',
    strong: 'inline',
    xref: 'inline',
    code: 'inline'
}

export const blockTypes = Object.keys(typeMap).filter(k => typeMap[k] === 'block')
export const inlineTypes = Object.keys(typeMap).filter(k => typeMap[k] === 'inline')

/**
 * Serialize a complete STIR tree or fragment into its standard JSON form.
 */
export function toJSON (stir: Block | BlockSequence | Inline | InlineSequence,
                        space?: string | number | undefined,
                        isFragment = false): string {
    return JSON.stringify(stir,
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        function (key: string, value: any) {
            // keys starting with `~` are temp processing data
            if (key[0] === '~') {
                return undefined
            }
            if (!isFragment && key === '' && value === stir) {
                if (!(value instanceof Block)) {
                    throw new TypeError(`argument to stir.toJSON not a STIR. Expecting Block, encountered ${
                        typeof value === 'object' ?
                            value.constructor ?
                                value.constructor.name
                                :
                                typeof value
                            :
                            typeof value
                    }`)
                }
                const r: Record<string, unknown> = {
                    // todo[spec, stir, serialization] 'STIR' or 'structured_text_intermediate_representation' ?
                    //   - adding 30-40 bytes isn't an issue.
                    //   - we can be like most tech and use code like 'STIR'
                    //   - or we can be more explicit (and also never have to worry about ambiguity/name collision)
                    // todo[spec, stir, versioning]
                    //   - using type and subtype as below ONLY makes sense if it is consistent with the semantics
                    //     of those properties as used on non-root nodes.
                    //   - consider these options:
                    //     - version is part of type:
                    //       ```
                    //       type: 'structured_text_intermediate_representation,v1'
                    //       subtype: 'basic_profile'
                    //       ```
                    //     - traditional separate version field:
                    //       ```
                    //       type: 'structured_text_intermediate_representation'
                    //       subtype: 'technical'
                    //       version: '1'
                    //       ```
                    type: 'structured_text_intermediate_representation',
                    subtype: 'basic V1',  // this is my idea of different STIR profiles
                    types: {
                        block: blockTypes,
                        inline: inlineTypes
                    }
                }
                for (const k in value) {
                    if (k !== 'source' && k !== 'type') {
                        r[k] = value[k]
                    }
                }
                return r
            }
            if (!value || typeof value !== 'object' || Array.isArray(value)) {
                return value
            }
            if (value instanceof Block || value.type === 'cell') {
                //! parent is NOT part of the STIR spec, though obviously
                // the parent is implied by the structure, and can be
                // reconstituted as a Block.ts instance with a parent pointer.

                // eslint-disable-next-line @typescript-eslint/no-unused-vars
                const {parent, source, ...rest} = value
                return rest
            }
            if (value instanceof Inline) {
                const r: Record<string, unknown> = {}
                for (const k in value) {
                    if (k === '_type') {
                        r['type'] = value[k]
                    } else if (k === 'referent') {
                        const referent = value[k]
                        if (typeof referent === 'object') {
                            //todo![serialization] fromJSON needs to do the
                            //  reverse: reconstitute this reference as a
                            //  direct pointer to the element.
                            r['referent'] = `@id:${referent.id}`
                        } else {
                            r['referent'] = referent
                        }
                    } else {
                        r[k] = value[k]
                    }
                }
                return r
            }
            return value
        }, space)
}

/**
 * Deserializes a STIR tree from JSON into TextPlain's
 * classes rather than plain JS objects.
 *
 * Possible alternative ways to unmarshal STIR data:
 *   https://github.com/typestack/class-transformer
 *   https://stackoverflow.com/questions/5873624/parse-json-string-into-a-particular-object-prototype-in-javascript
 *   https://www.npmjs.com/package/typescript-json-serializer
 *   https://serialazy.teqlab.net/#getting-started
 */
export function fromJSON (stirJSON: string): Block {
    const o = json5.parse(stirJSON, function (key, value) {
        if (!value || typeof value !== 'object' || Array.isArray(value)) {
            return value
        }
        const isRoot = key === ''
        if (value.type) {
            switch (typeMap[value.type]) {
                case 'block': {
                    const block = new Block(value.type)
                    for (const k in value) {
                        if (k === 'body') {
                            const body = value.body
                            if (Array.isArray(body) && body[0] instanceof Block) {
                                body.forEach(b => {
                                    block.appendToBody(b)
                                })
                            } else {
                                block.body = body
                            }
                        } else if (!isRoot || (k !== 'subtype' && k !== 'types')) {
                            block[k] = value[k]
                        }
                    }
                    return block
                }
                case 'inline': {
                    const inline = new Inline(value.type)
                    for (const k in value) {
                        if (k !== 'type') {
                            inline[k] = value[k]
                        }
                    }
                    return inline
                }
                default:
                    throw new TypeError(`unknown type "${value.type}"`)
            }
        }
        return value
    })
    return o as Block
}


/**
 * See https://github.com/microsoft/TypeScript/issues/1897
 * I saw it again by chance in https://github.com/iammerrick/json-lisp/blob/master/index.ts
 */
export type JSONValue =
    | string
    | number
    | boolean
    | null
    | JSONValue[]
    | { [key: string]: JSONValue }


export interface JSONObject {
    [k: string]: JSONValue
}
