import type {Node} from './node.js'

export type PreorderVisitReturn = 'continue' | 'abort branch' | 'abort traversal'
export type PostorderVisitReturn = 'continue' | 'abort traversal'

/**
 * traverse assembly efficiently (non recursive)
 * @param fromNode
 * @param preorderVisit
 * @param postorderVisit
 *
 * todo[arch] do we really need a non-recursive traverse?
 *   - assemblies, unlike STIRs, are unlikely to be deep. ✅
 *   - a non-recursive solution may make is much easier to implement the
 *     async version below, and possibly make it easy to merge the two
 *     versions, perhaps by the type of the preorderVisit/postorderVisit
 *     args (e/g. whether or not they return a Promise) determining whether
 *     internal calls are sync or async
 */
export function traverse (fromNode: Node,
                          preorderVisit: ((n: Node) => PreorderVisitReturn) | undefined,
                          postorderVisit: ((n: Node) => PostorderVisitReturn) | undefined)
    : void {
    // stack to schedule preorderVisit or postorderVisit calls on nodes,
    // false for preorder, true for postorder
    const stack: [Node, boolean][] = []

    if (preorderVisit) {
        // if we have both pre- and postorder visits, we only need to
        // schedule the preorder, as any postorder will be scheduled
        // at the time of the preorder visit.
        stack.push([fromNode, false])
    } else if (postorderVisit) {
        stack.push([fromNode, true])
    } else {
        return
    }

    let n: Node
    let isPost: boolean

    while (stack.length > 0) {
        [n, isPost] = stack.pop() as [Node, boolean]

        if (isPost) {
            if (postorderVisit?.(n) === 'abort traversal') {
                return
            }
        } else {
            if (n.isFile()) {
                switch (preorderVisit?.(n)) {
                    case 'continue':
                        if (postorderVisit?.(n) === 'abort traversal') {
                            return
                        }
                        break
                    case 'abort branch':
                        //todo[api] should abort branch on a File abort the parent Folder, i.e. all siblings?
                        break
                    case 'abort traversal':
                        return
                }

            } else {
                switch (preorderVisit?.(n)) {
                    case 'continue':
                        if (postorderVisit) {
                            stack.push([n, true])
                        }
                        // push children on stack in reverse order so they are popped in correct order
                        for (let i = n.content.length - 1; i >= 0; i--) {
                            stack.push([n.content[i], false])
                        }
                        // push opening content last so it's traversed first
                        // (if the tree is traversed before the opening content
                        //  has been chosen from content, it will have been
                        //  pushed above)
                        if (n.openingContent) {
                            stack.push([n.openingContent, false])
                        }
                        break
                    case 'abort branch':
                        break
                    case 'abort traversal':
                        return
                }
            }
        }
    }
}

/**
 * traverse assembly efficiently (non recursion) with asynchronous visitors
 * @param fromNode
 * @param preorderVisit
 * @param postorderVisit
 *
 * todo[arch] Is this the right way to do an async tree walk?
 * - if we do, shouldn't we do it properly, where the nodes are are truly
 *   visited asynchronously? Below we are not; we force synchronicity on each
 *   node visit with `await`.
 *   - The main reason we have to do this is to support the
 *     'abort branch' | 'abort traversal' return values, which by definition
 *     must be returned synchronously to make any sense.
 *   - The more logical alternative (I'm pretty sure 🤔) is that the async
 *     version of tree traversal NOT support aborting the traversal. That seems
 *     to follow from the nature of "async".
 *   - UNLESS what we really mean by "async" is NOT that the tree is traversed
 *     asynchronously, but that we accept async visitors, which the traversal
 *     code will execute synchronously to be able to support aborting traversals.
 *   - Thinking about this, both are legit options, and just need to be named
 *     correctly, or defined with the proper signature (assuming we actually NEED
 *     both for TextPlain:
 *
 *     - SYNCHRONIZED tree traversal (that supports asynchronous visitors)
 *       ```
 *       traverseAssembly(node,
 *                        (node) => PreorderVisitReturn | (node) => Promise<PreorderVisitReturn>
 *                        )
 *       ```
 *       Will visit each node synchronously, using `wait` if given a async
 *       visitor as in the impl below, resulting in synchronized pre/postorder
 *       visits that can abort the branch or the entire traversal synchronously.
 *
 *     - ASYNCHRONOUS tree traversal (does NOT support branch or traversal
 *       abortions).
 *       ```
 *       traverseAsync(node,
 *                             (node) => Promise<void>
 *                             returnAllCompletePromise: boolean)
 *       ```
 *       Will fire off visits to each node asynchronously and independently,
 *       i.e. no visit is predicated on the result of any other visit. They all
 *       happen concurrently. Thus no option to abort a branch ot the tree.
 *       If returnAllCompletePromise is true, will return a Promise that will
 *       resolve only after all node visits resolve (internally using
 *       `Promise.all`) and resolving immediately if false (leaving it for the
 *       callers' visitor implementation to do whatever needs to be done when
 *       done).
 *
 * todo[api] 🏈 this runs counter to the JS norm: the synchronous version should
 *  be named `traverseSync` and this async version should just be `traverse`.
 * Punt this change until this module is fully formed, as i don't even know if
 * if we'll need both of these methods down the road.
 */
export async function traverseAsync (fromNode: Node,
                                     preorderVisit: ((n: Node) => Promise<PreorderVisitReturn>) | undefined,
                                     postorderVisit: ((n: Node) => Promise<PostorderVisitReturn>) | undefined) {
    // stack to schedule preorderVisit or postorderVisit calls on nodes,
    // false for preorder, true for postorder
    const stack: [Node, boolean][] = []

    if (preorderVisit) {
        // if we have both pre- and postorder visits, we only need to
        // schedule the preorder, as any postorder will be scheduled
        // at the time of the preorder visit.
        stack.push([fromNode, false])
    } else if (postorderVisit) {
        stack.push([fromNode, true])
    } else {
        return
    }

    let n: Node
    let isPost: boolean

    while (stack.length > 0) {
        [n, isPost] = stack.pop() as [Node, boolean]

        if (isPost) {
            if (await postorderVisit?.(n) === 'abort traversal') {
                return
            }
        } else {
            if (n.isFile()) {
                switch (await preorderVisit?.(n)) {
                    case 'continue':
                        if (await postorderVisit?.(n) === 'abort traversal') {
                            return
                        }
                        break
                    case 'abort branch':
                        //todo[api] should abort branch on a File abort the parent Folder, i.e. all siblings?
                        break
                    case 'abort traversal':
                        return
                }

            } else {
                switch (await preorderVisit?.(n)) {
                    case 'continue':
                        if (postorderVisit) {
                            stack.push([n, true])
                        }
                        // push children on stack in reverse order so they are popped in correct order
                        for (let i = n.content.length - 1; i >= 0; i--) {
                            stack.push([n.content[i], false])
                        }
                        // push opening content last so it's traversed first
                        // (if the tree is traversed before the opening content
                        //  has been chosen from content, it will have been
                        //  pushed above)
                        if (n.openingContent) {
                            stack.push([n.openingContent, false])
                        }
                        break
                    case 'abort branch':
                        break
                    case 'abort traversal':
                        return
                }
            }
        }
    }
}
