import type * as absTOC from '../../../toc.js'
import type * as htmlTOC from '../../../renderSTIR.js'
import type {RenderSTIR} from '../../../renderSTIR.js'

// todo![template API] currently just a set of functions, but maybe this
//  should be set up differently, e.g. pass in a page context and all the
//  template API methods are added to it?


export function toc (aTOC: absTOC.TOC, render: RenderSTIR): htmlTOC.TOC {
    const hTOC: htmlTOC.TOC = []
    for (const aEntry of aTOC) {
        if (aEntry.heading === undefined) {
            hTOC.push({
                heading: undefined,
                subheadings: toc(aEntry.subheadings, render)
            })
        } else {
            const items: htmlTOC.TOCHeading = {
                heading: render(aEntry.heading),
                // todo![spec, html, rollout] figure out the right rules for this.
                //   I think the replacement below is too strict. From what I've
                //   read, IDs can have anything except whitespace, and the real
                //   reason you want to exclude certain chars has to do with not
                //   forcing the need for escaping when using the ID in a CSS
                //   selector. See https://www.w3.org/TR/CSS21/syndata.html#value-def-identifier.
                //   But since these IDs will also be used as fragment identifiers,
                //   we want to play nice with URL syntax and URL parsers as well.
                //   https://stackoverflow.com/questions/26088849/url-fragment-allowed-characters
                id: aEntry.referentID
                    .toLowerCase()
                    // Unlike CommonMark, i'm allowing `/`, which is
                    // the STIR ID path delim. If that turns out to be a bad idea,
                    // should replace with some other character, not strip it as
                    // the following line does with every other special char.
                    .replace(/[^\p{L}\p{N}\s_./-]+/gu, '')
                    .trim()
                    .replace(/\s+/g, '-')
                    .replace(/^$/, 'section')
            }
            for (const k in aEntry) {
                if (k !== 'heading' && k !== 'referentID' && k !== 'referent') {
                    items[k] = aEntry[k]
                }
            }
            if (aEntry.subheadings !== undefined) {
                items.subheadings = toc(aEntry.subheadings, render)
            }
            hTOC.push(items)
        }
    }
    return hTOC
}
