import * as ir from '../stir/index.js'
import type {Stylesheet} from './ptss/index.js'
import type {NamedValues} from './namedValues.js'

import * as linkify from 'linkifyjs'

// Markdown-it deep imports
// todo[arch] remove Markdown-it from package.json non-dev dependencies when we ditch Markdown-it
import MarkdownIt from 'markdown-it/lib/index.js'
import type markdownItToken from 'markdown-it/lib/token.js'
//import markdownItUtils from 'markdown-it/lib/common/utils.js'
import subscript from 'markdown-it-sub'
import superscript from 'markdown-it-sup'
import highlight from 'markdown-it-mark'

import * as newline_parameterized from './mdit_rules/newline_parameterized.js'
import * as escape_parameterized from './mdit_rules/escape_parameterized.js'
import * as backticks_parameterized from './mdit_rules/backticks_parameterized.js'
import * as gfm_strikethrough from './mdit_rules/gfm_strikethrough.js'
import {createLinkDestParser} from './mdit_rules/parseLinkDest_parameterized.js'
import * as reference from './mdit_rules/reference.js'
import * as image from './mdit_rules/image.js'
import * as autolink from './mdit_rules/autolink.js'
import * as noteRef from './mdit_rules/note_ref.js'


const RE_OPEN_ANGLE = /<\s*$/
const RE_TRAILING_SPACE_OR_OPEN_PARENS = /[ \t(]$/


/*
 The InlineParser object.

 An InlineParser keeps track of a subject (a string to be
 parsed) and a position in that subject.
*/
export class InlineParser {
    private readonly _stylesheet: Stylesheet

    private readonly _markdownIt

    constructor (stylesheet: Stylesheet) {
        this._stylesheet = stylesheet
        this._markdownIt = this.configureMarkdownIt()

        //todo[smartquotes] Haven't decided where "smart quotes" belongs:
        // - as a ptss-parser option
        // - as a style sheet option
        // - as a pipeline transform
        // - as a renderer option
        // - two or more of the above
        // - all of the above
    }

    private configureMarkdownIt (): MarkdownIt {
        // todo![inline] TEMP HACK OF A TEMP HACK. My hack is supposed
        //   to take a list of names of Markdown-it core/inline elements
        //   to enable.
        //   When ready to implement, see:
        //   - https://markdown-it.github.io/markdown-it/#MarkdownIt
        //   - MarkdownIt.prototype.configure implementation
        //   - markdown-it/lib/presets

        const markdownIt = new MarkdownIt('zero')

        const rules = []
        for (let styleName of this._stylesheet.inlineStyles) {
            const match = styleName.match(/^(.+?)\((.*)\)$/)
            let styleArg = undefined
            if (match) {
                styleName = match[1]
                styleArg = match[2]
            }

            if (styleName === 'html') {
                markdownIt.set({'html': true})
            } else if (styleName === 'newline') {
                //HACK: first enabling the orig and then replacing so that it goes in the right order.
                markdownIt.enable('newline')
                markdownIt.inline.ruler.at('newline', newline_parameterized.tokenize)
            } else if (styleName === 'noteRef') {
                markdownIt.inline.ruler.before('link', 'noteRef', noteRef.createRule(styleArg))
            } else if (styleName === 'reference') {
                markdownIt.inline.ruler.before('link', 'reference', reference.createRule())
            } else if (styleName === 'image') {
                //HACK: first enabling the orig and then replacing so that it goes in the right order.
                markdownIt.enable('image')
                markdownIt.inline.ruler.at('image', image.createRule())
            } else if (styleName === 'autolink') {
                //HACK: first enabling the orig and then replacing so that it goes in the right order.
                markdownIt.enable('autolink')
                markdownIt.inline.ruler.at('autolink', autolink.tokenize)
            } else if (styleName === 'gfm_strikethrough') {
                markdownIt.inline.ruler.before('emphasis', 'gfm_strikethrough', gfm_strikethrough.tokenize)
                // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                // @ts-ignore @types/markdown-it is wrong. Don't have the time for their PR process ATM.
                markdownIt.inline.ruler2.before('emphasis', 'gfm_strikethrough', gfm_strikethrough.postProcess)
            } else if (styleName === 'escape') {
                //HACK: first enabling the orig and then replacing so that it goes in the right order.
                markdownIt.enable('escape')
                markdownIt.inline.ruler.at('escape', escape_parameterized.createRule(this._stylesheet))
            } else if (styleName === 'backticks') {
                //HACK: first enabling the orig and then replacing so that it goes in the right order.
                markdownIt.enable('backticks')
                markdownIt.inline.ruler.at('backticks', backticks_parameterized.createRule(styleArg))
            } else if (styleName === 'bare_links') {
                // markdown-it's 'linkify' doesn't work for
                // it inline parser, so we will do later when
                // we process the tokens
            } else if (styleName === 'checkbox_text') {
                // handled by `convertTokens` below
            } else if (styleName === 'subscript') {
                markdownIt.use(subscript)
            } else if (styleName === 'superscript') {
                markdownIt.use(superscript)
            } else if (styleName === 'text_highlight') {
                markdownIt.use(highlight)
            } else {
                // just enable the corresponding built-in Markdown-it rule
                rules.push(styleName)
            }
        }
        markdownIt.enable(rules)

        // Disabling Markdown-it's urlEncoding of link targets. This is a
        // render-side and rendering format-specific concern.
        //
        // Markdown-it assumes all link destinations are URLs and so
        // URL encodes them at parse time rather than at render time.
        //
        // But PTSS ***cannot*** make this assumption:
        // - the dest may be internal text assembly cross-reference paths.
        //   Encoding them would ruin them. They cannot be messed with until
        //   AFTER they are resolved.
        // - cannot assume the rendering is HTML. Links in different renderings
        //   will take different forms.
        markdownIt.normalizeLink = (s) => s

        markdownIt.helpers.parseLinkDestination = createLinkDestParser(this._stylesheet)

        return markdownIt
    }

    /**
     * Implements the PTSS Expressions `parse` function.
     *
     *
     * Whitespace Handling
     * -------------------
     * todo![stir, spec] ☸️ Unlike HTML and Markdown, STIR is not designed for
     *  editor convenience. It is designed for abstractness and zero-ambiguity.
     *  All whitespace is considered deliberate.
     *
     *  i.e. this except from MDN on HTML whitespace DOES NOT APPLY to STIR:
     *
     *  > These characters allow you to format your code in a way that will
     *  > make it easily readable by yourself and other people. In fact, much
     *  > of our source code is full of these whitespace characters, and we
     *  > only tend to get rid of it in a production build step to reduce
     *  > code download sizes.
     *
     *  - from https://developer.mozilla.org/en-US/docs/Web/API/Document_Object_Model/Whitespace
     *
     *  CONSIDER all of the CSS whitespace options and consider supporting
     *  some or all of them.
     *
     * @param {string} inlineText
     * @param {string[]} parseArgs - ptss `parse` expression args
     * @param {NamedValues} namedValues
     * @return {InlineSequence}
     */
    parse (inlineText: string,
           parseArgs: string[] = [],
           namedValues: NamedValues)
        : ir.InlineSequence {
        // console.debug('inlineText:', JSON.stringify(inlineText))
        // console.debug('refs:', JSON.stringify(synDictionary.asMarkdownItRefMap()))

        const env = {
            namedValues: namedValues,
            preserveWhitespace: false,
            preserveNewlines: false,
            newlineIsHardBreak: false,
            backslashNewlineIsHardBreak: false,
            backslashEOCIsHardBreak: false,
            twoSpaceNewlineIsHardBreak: false
        }

        for (const arg of parseArgs) {
            if (arg === 'preserveWhitespace') env.preserveWhitespace = true
            else if (arg === 'preserveNewlines') env.preserveNewlines = true
            else if (arg === 'newlineIsHardBreak') env.newlineIsHardBreak = true
            else if (arg === 'backslashNewlineIsHardBreak') env.backslashNewlineIsHardBreak = true
            else if (arg === 'backslashEOCIsHardBreak') env.backslashEOCIsHardBreak = true
            else if (arg === 'twoSpaceNewlineIsHardBreak') env.twoSpaceNewlineIsHardBreak = true
            else throw Error(`unrecognized parse option: '${arg}'`)
        }

        // markdown-it doesn't consistently do this internally,
        // so let's ensure this here.
        if (!env.preserveWhitespace) {
            inlineText = inlineText.trim()
        }

        const tokens = new Array<markdownItToken>()
        this._markdownIt.inline.parse(inlineText, this._markdownIt, env, tokens)
        // console.debug('output:', JSON.stringify(tokens))

        if (tokens.length === 0) {
            return ''
        }

        const tmp = new ir.Inline('TMP') // a bit of a hack, but not a big deal
        this.convertTokens(tmp, tokens, namedValues)
        return tmp.body as ir.InlineSequence
    }

    convertTokens (root: ir.Inline, tokens: markdownItToken[],
                   namedValues: NamedValues): void {
        const bare_links = this._stylesheet.inlineStyles.includes('bare_links')
        const checkbox_text = this._stylesheet.inlineStyles.includes('checkbox_text')

        let curInline = root
        let newInline: ir.Inline
        const stack = [] as ir.Inline[]

        for (let i = 0; i < tokens.length; i++) {
            const token = tokens[i]
            switch (token.type) {
                case 'text':
                    if (i === 0 && checkbox_text) {
                        if (token.content.startsWith('[ ] ')) {
                            newInline = new ir.Inline('checkbox')
                            curInline.bodyAppend(newInline)
                            // we want to leave the space in between
                            token.content = token.content.slice(3)
                        } else if (token.content.startsWith('[x] ')) {
                            newInline = new ir.Inline('checkbox')
                            newInline.checked = true
                            curInline.bodyAppend(newInline)
                            // we want to leave the space in between
                            token.content = token.content.slice(3)
                        }
                    }
                    if (bare_links && curInline.type !== 'xref') {
                        curInline.bodyAppend(this.parseBareLinks(token.content))
                    } else {
                        curInline.bodyAppend(token.content)
                    }
                    break
                case 'text_special':
                    curInline.bodyAppend(token.content)
                    break
                case 'softbreak':
                    // todo![stir, spec] what is LaTeX and PDF's take on the
                    //   following design fork?

                    // todo![stir, spec] DESIGN FORK
                    //   Regardless of fork path chosen: CommonMark's position
                    //   that it is a rendering decision whether to treat
                    //   newlines as soft or hard breaks goes against STIR
                    //   principles: An author intends one thing or the other,
                    //   that the line must break here or must flow normally.
                    //   It makes no sense to offer any "rendering choice".
                    //   .
                    //   Below when I say "semantic softbreak" or "semantic
                    //   hardbreak", I am referring, as usual, to the author's
                    //   *intended* semantics.
                    //   .
                    //   option a: HTML/LaTeX SEMANTICS (current implementation)
                    //   ------------------------------
                    //   plain text, BY DEFAULT, in STIR has same semantics as
                    //   it does in HTML, which is that newlines are rendered as
                    //   plain whitespace and will be collapsed along with other
                    //   adjacent whitespace to a single space, unless it is
                    //   contained in a container that specifies otherwise,
                    //   e.g. <pre>. Thus:
                    //   - it shouldn't matter whether a semantic soft break is
                    //     represented in STIR text as a space or `\n`.
                    //   - a semantic hard break is represented by a STIR
                    //     `linebreak` inline element, analogous to HTML <br>.
                    //   .
                    //   This default is can be overridden in the style
                    //   definition.
                    //   .
                    //   option b: LITERAL WHITESPACE/NEWLINES
                    //   -------------------------------------
                    //   Text whitespace is normalized. This means that if, for
                    //   example, the input has HTML/LaTeX semantics, those
                    //   semantics should be applied (newlines converted to
                    //   spaces, then consecutive spaces collapsed) BY THE
                    //   PARSING, BEFORE IT IS WRITTEN TO THE STIR.
                    //   .
                    //   The result is that STIR text can be taken literally:
                    //   a space is a space, two spaces are two spaces, and a
                    //   newline is a new line.
                    //   .
                    //   analysis
                    //   --------
                    //   - The fact that option B can be stated in two sentences
                    //     pretty much sells itself.
                    //   - But that both LaTeX and HTML go the other way can't
                    //     be dismissed easily.
                    //   - For option B to work, there can be no ambiguity about
                    //     newlines in STIR text. All "soft break" newlines need
                    //     to be replaced with a space (and then collapsed along
                    //     with other spaces.
                    //   - Normalizing whitespace would have a performance
                    //     penalty, at least for plain text styles that have
                    //     sloppy content (e.g. Markdown, as well as HTML).
                    //     Ultimately we break even, as we've shifted the work
                    //     from the final renderer (e.g. the browser doesn't
                    //     have to do it) to the parser, but it will make
                    //     Textplain seem less efficient in comparison. THOUGH
                    //     this my turn out to be negligible --- should at least
                    //     run a benchmark comparison before ditching B.
                    //   .
                    //   what about PRE blocks?
                    //   ----------------------
                    //   how do each of the two options support STIR PRE blocks?
                    //   - if we go with option B, it seems like there is NO NEED
                    //     for PRE blocks. 🤔
                    //   - with option A, i think STIR would be exactly like
                    //     HTML: different semantics inside a PRE block.
                    curInline.bodyAppend('\n')
                    break
                case 'hardbreak':
                    curInline.bodyAppend(new ir.Inline('linebreak'))
                    break
                case 'em_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('emph')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'em_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 'strong_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('strong')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'strong_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 's_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('strike')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 's_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 'sub_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('subscript')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'sub_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 'sup_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('superscript')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'sup_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 'mark_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('highlight')
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'mark_close':
                    // trusting Markdown-it for well-formed token stream
                    curInline = stack.pop() as ir.Inline
                    break
                case 'link_open':
                    stack.push(curInline)
                    newInline = new ir.Inline('xref')
                    //todo![spec, doc] For the serialized form of STIR,
                    //  the referent will always be a string, with the ID of
                    //  the internal element in the case of an internal reference.
                    //  But the in-memory form will have direct in-memory pointers
                    //  to the referent.
                    newInline.referent = token.attrGet('referent') || ''  //! would be `token.attrGet('referent')` but it's not worth changing markdown-it
                    newInline.desc = token.attrGet('desc') || undefined
                    curInline.bodyAppend(newInline)
                    curInline = newInline
                    break
                case 'link_close':
                    // trusting Markdown-it for well-formed token stream
                    if (curInline.body === undefined) {
                        // special case empty link text
                        curInline.body = ''
                    }
                    curInline = stack.pop() as ir.Inline
                    break
                case 'image':
                    //! 'image' is handled differently from links because
                    //   the image inline parser creates a token with the
                    //   children already added on.
                    //   - this MAY BE due to the the alt text not being stylized
                    //     like link text can be.
                    //   - or it MAY BE just an impl difference, and we could
                    //     sync them to the same impl.  NOT SURE.
                    newInline = new ir.Inline('image')
                    newInline.referent = token.attrGet('src') || ''
                    newInline.desc = token.attrGet('desc') || undefined
                    if (token.children && token.children.length > 0) {
                        this.convertTokens(newInline, token.children, namedValues)
                    } else {
                        newInline.body = ''
                    }
                    curInline.bodyAppend(newInline)
                    break
                case 'note_ref':
                    newInline = new ir.Inline('note_ref')
                    newInline.referent = token.attrGet('referent') || ''
                    curInline.bodyAppend(newInline)
                    break
                case 'code_inline':
                    newInline = new ir.Inline('code')
                    newInline.body = token.content
                    curInline.bodyAppend(newInline)
                    break
                case 'html_inline':
                    newInline = new ir.Inline('html_inline')
                    newInline.body = token.content
                    curInline.bodyAppend(newInline)
                    break
                default:
                    throw Error(`unknown token type: "${token.type}"`)
            }
        }
    }

    // https://linkify.js.org/docs/options.html
    // https://github.com/Hypercontext/linkifyjs
    private parseBareLinks (text: string): ir.InlineSequence {
        console.group(`parseBareLinks "${text}"`)
        const tokens = linkify.tokenize(text)
        if (tokens.length === 1 && !tokens[0].isLink) {
            console.groupEnd()
            return text
        }
        const seq: ir.InlineSequence = []
        let canLink = true
        for (const tok of tokens) {
            console.log(tok.toString())
            if (canLink && tok.isLink) {
                const irRef = new ir.Inline('xref')
                irRef.referent = tok.toHref()
                //irRef.description = token.attrGet('desc') || undefined
                irRef.bodyAppend(tok.v)
                seq.push(irRef)
                canLink = false
            } else {
                seq.push(tok.v)
                canLink = RE_TRAILING_SPACE_OR_OPEN_PARENS.test(tok.v) && !RE_OPEN_ANGLE.test(tok.v)
            }
        }
        console.groupEnd()
        return seq
    }
}
