import {Node, Folder, resolvePath} from './node.js'
import {Assembly} from './assembly.js'
import {Subfolder} from './subfolder.js'
import {File} from './file.js'
import {traverse} from './traverse.js'
import {SandboxFS, VfsPath, Vpath} from '../../vfs/index.js'
import {Config} from '../config.js'

import JSON5 from 'json5'

// todo[api] need a name that won't potentially clash with an actual
//   user file or folder name. e.g. `.sort` (e.g. like `.gitignore`)
//   Maybe offer a variant that isn't hidden by default, e.g.
//   `__sort` or something.
const TEXTASSEMBLY_SORT_SPEC_FILENAME = 'sort'

// todo[api] at present, these files aren't "Textplain Configs" because they don't
//  configure the Textplain instance, but instead a rendering pipeline. A single
//  Textplain session may load multiple differently configured pipelines, for
//  example as required for custom processing unique to a specific branch in a
//  text assembly tree.
//  ---
//  See notes/docs on the naming of the various config files, or the roadmap for it.
const TEXTPLAIN_PIPELINE_CONFIG_FILENAME = 'pipeline.json5'


// todo[arch] could move the read code inside Assembly/Subfolder such that
//   they are loaded with data when they are constructed at which point become
//   truly readonly.

/**
 * Recursively populates a {@link Assembly} from the content of sourcePath.
 *
 * All assembly content traversal is through its sandbox fs, with
 * the exception of grafted subassemblies, which by definition exist outside
 * that sandbox but usually on the same source FS, and often referenced with a
 * path relative to the graft point. Thus sourcePath must be of the local FS,
 * otherwise subassembly graft reference resolution will fail.
 *
 * Principles
 * ----------
 * 1. known files that are "meta", e.g. config files embedded in the
 *    assembly tree, are read and used for what they are, but are left
 *    out of the resulting assembly tree, which is intended to represent
 *    assembly content.
 *
 * @param sourcePath the path of the assembly root in the source FS
 */
export function readAssembly (sourcePath: VfsPath): Assembly {
    // since an assembly source MUST be a directory, we can simplify
    // internal logic by normalizing it with trailing slash.
    //  - consistency between root and non-root path
    //  - can use + instead of join internally
    if (!sourcePath.path.endsWith('/')) {
        sourcePath = {vfs: sourcePath.vfs, path: sourcePath.path + '/'}
    }

    console.info(`📦 readAssembly ${sourcePath.path}`)
    console.group()
    const sandboxFs = new SandboxFS(sourcePath, 'readonly')
    const assembly = new Assembly(sandboxFs)

    //todo[arch] this could be async, nixing the sync calls to fs.
    //  in fact, i think hugo split off async threads for each subdir.
    console.group('reading assembly tree...')
    readFolderContent(assembly, sourcePath)
    console.groupEnd()

    console.group('determining opening content...')
    determineOpeningContent(assembly)
    console.groupEnd()

    console.group('sorting content...')
    sortContent(assembly)
    console.groupEnd()

    console.debug('\nsource assembly as read:')
    console.group()
    console.debug(assembly.toString())
    console.groupEnd()

    console.groupEnd()
    return assembly
}

function readSubAssembly (sourcePath: VfsPath, parent: Folder, virtualFolderName: `${string}/`, assemblyID: string): Assembly {
    console.info(`📦 readSubAssembly ${sourcePath.path}`)
    console.group()
    const volume = new SandboxFS(sourcePath, 'readonly')
    const assembly = new Assembly(volume, parent, virtualFolderName, assemblyID)

    console.group('reading subassembly tree...')
    readFolderContent(assembly, sourcePath)
    console.groupEnd()

    console.groupEnd()
    return assembly
}

function readFolderContent (f: Folder, folderSrc: VfsPath) {
    const sandboxFs = f.assemblyRoot().srcFS()
    const sandboxPath = f.innerAssemblyPath()

    const dirents = sandboxFs.readDirSync(sandboxPath, true)
        .filter(n => !n.name.startsWith('.'))

    dirents.forEach(dirent => {
        console.debug(`dirent: ${dirent.name}`)
        //todo[api] we should support a way to specify exclusions.
        //Thoughts:
        // - if it doesn't make sense to specify exclusions for a
        //   TextAssembly (because why would you? It would be like specifying
        //   exclusions for particular headings in a Markdown file), then
        //   it would be a Textplain power user setting, with no mention
        //   in the TextAssembly spec.
        //! If you want to see how well Textplain renders many third-party
        //   README's, disable this exclusion and browse the many README's
        //   that get rendered for any node_modules directories included
        if (dirent.name === 'node_modules') {
            return
        }

        if (dirent.isDir) {
            // todo[perf] do this non-recursively, e.g. like traverseAssembly ?
            //   arg against: assemblies are unlikely to be deep, unlike a stir
            const subdir = new Subfolder(dirent.name + '/' as `${string}/`, f)
            console.log(`subfolder ${subdir.innerAssemblyPath()}`)
            console.group()
            readFolderContent(subdir, {vfs: folderSrc.vfs, path: folderSrc.path + dirent.name + '/'})
            console.groupEnd()
            f.content.push(subdir)
        } else {
            //todo![renderAssembly] shouldn't we use filetype.ts#fileTypeByFilename here?
            // - it can identify sort spec (and we can ditch the constant above,
            //   as well as allow users to define a different filename for it).
            // - we could make it identify config files ?
            // - we could filter by filetype at the beginning (i.e. here) rather
            //   than all the downstream, avoiding adding skipped files to the
            //   tree (or do we want to add everything for debug purposes?)
            //   - we could set File.status to 'sText: waiting' |
            //     'resource: waiting' | skipped right here.

            const filePath = sandboxPath + dirent.name

            if (dirent.name === TEXTPLAIN_PIPELINE_CONFIG_FILENAME) {
                const file = new File(dirent.name, f)
                const data = JSON5.parse(file.readFileSync())
                // todo[arch] should this resolve impl be put in some shared place?
                //
                // NOTE: we have two sorts of resolve:
                //   a) ass.Node.resolvePath => ass.Node
                //   b) path (+folder context) => path
                //
                //   (a) the ass.Node tree acts as its own Vfs (e.g. its a
                //       virtual tree with branches potentially grafted from
                //       multiple sources), and resolves paths by actual tree
                //       traversal, including subassemblies, and also supporting
                //       cross-assembly paths. It will throw an error if the
                //       path leads to a non-existent node.
                //   (b) operates on underlying FS paths, even if virtual.
                //
                //  We can't merge those because they serve different purposes,
                //  e.g. the code below uses (b) and using (a) does not make sense.
                const configPathResolver = (path: string): VfsPath => {
                    // if path is a cross-assembly reference
                    const match = path.match(/^{(\w*)}\/(.*)/)
                    if (match !== null) {
                        const assembly = f.assemblyRoot(true).assemblyForID(match[1])
                        if (assembly === undefined) {
                            // todo[errors] review all the error handling around path lookups and readFiles.
                            throw new Error(`no such subassembly: {${match[1]}}`)
                        } else {
                            const node = resolvePath(match[2], assembly)
                            if (node === undefined) throw new Error(`no file at path: ${path}`)
                            return {vfs: node.assemblyRoot().srcFS(), path: node.innerAssemblyPath()}
                        }
                    }
                    // if path is absolute
                    if (path.startsWith('/')) {
                        return {vfs: sandboxFs, path: path}
                    }
                    // else relative
                    path = Vpath.join(f.innerAssemblyPath(), path)
                    return {vfs: sandboxFs, path: path}
                }
                f.textplainConfig = new Config(dirent.name, data, configPathResolver, false, f.assemblyRoot())
                return
            }

            // Check for subassembly graftpoint.
            // todo[api] is this the rule we want for subassembly graftpoint links?
            //   - any file of less then 256B?
            //   - i don't like looking for a file extension, because i want
            //     the graftpoint filename to occupy the *exact* same space as
            //     the virtual folder that results from the graft for many reasons:
            //     - safety: prevents collisions between graftpoint and a folder with the same name
            //     - simplicity: name of the graftpoint file is the virtual folder name, rather than another property in the file
            //     - elegance: a directory listing shows the names of the child nodes exactly as they will be
            //   - whether or not we gate by file.size, we certainly can read a file's first N bytes rather than
            //     its entirety.
            //     - that said, that's got to be slower than filtering by file size first!
            //   Note the code below assumes this.
            if (dirent.size < 256) { //probably too short. max path len + max id len ?
                const content = sandboxFs.readFileSync(filePath)
                if (content.startsWith('graftpoint:')) {
                    const lines = content.split('\n')
                    if (lines.length > 1 && lines[1].startsWith('subassembly:')) {
                        const id = lines[0].slice('graftpoint:'.length).trim()
                        const path = lines[1].slice('subassembly:'.length).trim()
                        let fullPath = path.startsWith('/') ? path : folderSrc.path + path
                        if (!fullPath.endsWith('/')) fullPath += '/'
                        if (!folderSrc.vfs.existsSync(fullPath)) {
                            throw new Error(`invalid subassembly path "${path}" specified in graftpoint ${folderSrc.path + dirent.name}`)
                        }
                        const vfsPath = {vfs: folderSrc.vfs, path: fullPath}
                        if (id.length > 0 && path.length > 0) {
                            console.log(`subassembly graftpoint: "${f.innerAssemblyPath()}${dirent.name}/"  id="${id}"  source path="${vfsPath}"`)
                            const sass = readSubAssembly(vfsPath, f, `${dirent.name}/`, id)
                            f.content.push(sass)
                            return
                        }
                    }
                }
            }

            // We do this *after* testing it as a subassembly graftpoints to allow
            // for graftpoints to sort just like a regular subfolder.
            // todo[api] Maybe we need a more rigorous approach to meta files within
            //  a TextAssembly. Meta files should co-exist with content files WITHOUT
            //  taking up the namespace (i.e. why can't I have a folder or content file
            //  named 'sort'? Ideas:
            //  - meta files should have something akin to a MAGIC BYTE. Without it, it
            //    is considered a content file / non-meta
            //  - All TextAssembly config files have a specific file extension, regardless
            //    of which type of config (pipeline, sort, etc). Parsing the file will
            //    determine the role.
            //  - The same as the preceding idea, except that the config format is the
            //    same, but options are placed in the file under a heading or a JSON key,
            //    etc. In other words, sort spec is in a config file under `sort: {}`, and
            //    pipeline is in config under `pipeline: {}`. The user can put each of
            //    those in separate files (`sort.tac` and `pipeline.tac`) or in a single
            //    file (`all.tac`).
            if (dirent.name === TEXTASSEMBLY_SORT_SPEC_FILENAME) {
                f.sortSpec = sandboxFs.readFileSync(filePath).trim().split(/\s*\n\s*/)
                return
            }

            //console.log(`TextAssembly file: ${dirent.name}`)
            f.content.push(new File(dirent.name, f))
        }
    })
}

function determineOpeningContent (assembly: Assembly) {
    traverse(assembly, (n: Node) => {
        if (n.isFolder()) {
            for (let i = 0; i < n.content.length; i++) {
                const c = n.content[i]
                //todo[textassembly API] need to support (sync with spec):
                //  - other names (e.g. README, HOME, index)
                //  - other extensions (e.g. .md, .pj, .html)
                //  - case insensitive
                //  - respect precedence
                if (c.name === 'README.md' && c.isFile()) {
                    //console.log(`setting homepage of ${n.assemblyPath()} to ${c.name}`)
                    n.openingContent = c
                    n.content.splice(i, 1)
                    break
                }
            }
        }
        return 'continue'
    }, undefined)
}

function sortContent (assembly: Assembly) {
    // todo[perf] This is slightly inefficient:
    //   - we are traversing down and then, at each Folder node, traversing back
    //     up to find the sort spec.
    //   - Worst case is the only sort spec is at the root (or there is none
    //     at all, and we do this inane up search to find the same item every
    //     time.
    // This won't matter for small assemblies, but might make a difference for
    // assemblies containing thousand of deep folders? Probably not?
    // Note that readAssembly blocks all assembly processing until it is done,
    // so perhaps the inefficiency is significant.
    // Note we are only parsing the sort spec once, so no redundancy there.
    //
    // The fix is to have a stack, and keep track of sort specs as we traverse
    // down, obviating the need to search back up. The following code is just simpler.
    traverse(assembly, (n: Node) => {
        if (n.isFolder()) {
            const sorter = getNodeSorter(n.searchUpForProperty('sortSpec', true)?.value as string[])
            n.content.sort(sorter)
            console.groupEnd()
        }
        return 'continue'
    }, undefined)
}

function relativeNodeSortOrder (sortSpec: string[], n: Node): number {
    return sortSpec.findIndex(
        function (r) {
            const regExp = new RegExp('^' + r + '$', 'i')
            if (n.isFolder()) {
                return regExp.test(n.name) || regExp.test(n.name.slice(0, -1))
            } else {
                return regExp.test(n.name)
            }
        }
    )
}

export function getNodeSorter (sortSpec: string[] | undefined): (a: Node, b: Node) => number {
    if (sortSpec === undefined) {
        return placeholderDefaultTextAssemblySort
    }
    return (a: Node, b: Node) => {
        const len = sortSpec.length

        const iDefault = (() => {
            const i = sortSpec.indexOf('...')
            return i === -1 ? len : i
        })()

        const iFolderDefault = (() => {
            const i = sortSpec.indexOf('.../')
            return i === -1 ? iDefault : i
        })()

        let iA = relativeNodeSortOrder(sortSpec, a)
        if (iA === -1) iA = a.isFolder() ? iFolderDefault : iDefault

        let iB = relativeNodeSortOrder(sortSpec, b)
        if (iB === -1) iB = b.isFolder() ? iFolderDefault : iDefault

        // console.log(`${a.name}:${iA}  ${b.name}:${iB}`)
        if (iA === iB) {
            return placeholderDefaultTextAssemblySort(a, b)
        }

        return (-1 * len + iA) - (-1 * len + iB)
    }
}

// todo[spec] pending Text Assembly spec. See notes in `textassembly-sort.test.js`
function placeholderDefaultTextAssemblySort (a: Node, b: Node): number {
    const aName = a.name.toLocaleLowerCase()
    const bName = b.name.toLocaleLowerCase()
    const aSansExt = a.isFolder() ? aName : a.nameSansExt().toLocaleLowerCase()
    const bSansExt = b.isFolder() ? bName : b.nameSansExt().toLocaleLowerCase()
    return aSansExt === bSansExt
        ? (aName < bName ? -1 : 1)
        : (aSansExt < bSansExt ? -1 : 1)
}
