import type StateInline from 'markdown-it/lib/rules_inline/state_inline.js'
import markdownItUtils from 'markdown-it/lib/common/utils.js'
import type {RuleTokenizer} from './types.js'
import type {Stylesheet} from '../ptss/index.js'


export function createRule (stylesheet: Stylesheet): RuleTokenizer {
    if (stylesheet.escapeSyntax.escapeChar === undefined) {
        throw Error(`Escape inline style enabled, but no escape char defined for ${stylesheet.name}}`)
    }
    const escapeCode = stylesheet.escapeSyntax.escapeChar[0].charCodeAt(0)
    const ESCAPE_LOOKUP = stylesheet.escapeSyntax.ESCAPE_INLINE_LOOKUP

    /**
     * Modified version of markdown-it/lib/rules_inline/escape.js
     * Mods annotated with "//!" comments.
     *
     *! Modified with parameterized behavior
     */
    function tokenize (state: StateInline, silent: boolean) {
        let pos = state.pos

        if (state.src.charCodeAt(pos) !== escapeCode) return false
        pos++

        if (pos >= state.posMax) {
            //! inline parse function 'backslashEOCIsHardBreak' option
            if (state.env.backslashEOCIsHardBreak) {
                if (!silent) {
                    state.push('hardbreak', 'br', 0)
                }
                state.pos = pos
                return true
            }
            return false
        }

        let ch1 = state.src.charCodeAt(pos)

        //! inline parse function 'backslashNewlineIsHardBreak' option
        if (ch1 === 0x0A/* \n */ && state.env.backslashNewlineIsHardBreak) {
            if (!silent) {
                state.push('hardbreak', 'br', 0)
            }

            pos++

            //! inline parse function 'preserveWhitespace' option
            if (!state.env.preserveWhitespace) {
                // skip spaces after newline (i.e. skip leading spaces on next line)
                while (pos < state.posMax) {
                    ch1 = state.src.charCodeAt(pos)
                    if (!markdownItUtils.isSpace(ch1)) break
                    pos++
                }
            }

            state.pos = pos
            return true
        }

        if (ch1 < 256 && ESCAPE_LOOKUP[ch1] !== undefined) {
            if (!silent) {
                state.pending += ESCAPE_LOOKUP[ch1]
            }
            pos++
            state.pos = pos
            return true
        }

        return false
    }

    return tokenize as RuleTokenizer
}
