import type {RenderSTIR, RenderSTIRProvider} from '../../../renderSTIR.js'
import * as templateAPI from './templateAPI.js'
import type {Config} from '../../../config.js'
import {registerSTIRRenderer, lookupSTIRRenderer} from '../../../registry.js'
import {isBlock} from '../../../../stir/index.js'
import type {STIR, TunnelingSTIR} from '../../../stir.js'
import type {TOC} from '../../../toc.js'
import {extractTOC} from '../../../pipe/addTOC.js'


import Handlebars from 'handlebars'

// todo[arch] We have handlebars helpers definer all over the codebase.
//  Make sure they are located with the Textplain components that they
//  exist for.
//  - For example, all the Handlebars helpers registered in assembly2website.ts
//    belong here as built-in helpers

//todo[html] Does this renderer work for HTML only?
// - if so, renamed to Stir2HtmlTemplate ?

type MatchRule = {
    path?: string
    type?: string
    template: string
}

const stirRenderer = function (config: Config, renderStir: (stir: STIR) => string): RenderSTIR {
    const defaultHBS = config.requiredReadFileSyncAt('templates.default')
    const defaultTemplate = Handlebars.compile(defaultHBS)

    const render: RenderSTIR = (stir: STIR) => {
        const page: Record<string, unknown> = {}
        page.body = renderStir(stir)
        if (isBlock(stir)) {
            page.toc = () => {
                return templateAPI.toc(extractTOC(stir), renderStir)
            }
        }
        page.stir = stir
        page.title = 'not implemented'
        return defaultTemplate(page, {
            allowProtoMethodsByDefault: true,
            allowProtoPropertiesByDefault: true
        })
    }

    console.groupEnd()
    return render
}

const assemblyRenderer = function (config: Config, renderStir: (stir: STIR) => string): RenderSTIR {
    const indexHBS = config.requiredReadFileSyncAt('templates.section')
    const standardHBS = config.requiredReadFileSyncAt('templates.page')
    // todo[perf] See docs on Handlebars.compile for perf options.
    //   https://handlebarsjs.com/api-reference/compilation.html#handlebars-compile-template-options
    const indexTemplate = Handlebars.compile(indexHBS)
    const casePageTemplate = Handlebars.compile(standardHBS)

    const render: RenderSTIR = (stir: STIR) => {
        const tunnel = (stir as TunnelingSTIR)._tunnel
        const file = tunnel.srcFile

        // todo![template api, security] See Template API docs.
        // todo![template api]: fully define this type (no "unknown"). if must
        //   support custom data, it should go in a sub-property anyway to prevent clashes
        //   with API additions (i.e. the entire namespace directly under `page` is reserved,
        //   and custom data must be added under `page.custom`, or require that custom
        //   fields be prefixed with `_` or something (we can make this typesafe e.g.
        //   `[object type with all API specified] & Record<_${string}`, unknown>`))
        const page = file as unknown as Record<string, unknown> & {debug: Record<string, unknown>}
        page.body = renderStir(stir)
        if (isBlock(stir) && tunnel.toc !== undefined) {
            // a function for lazy conversion
            // todo![template API] lazy is nice, but it impacts the template API;
            //    user has to remember to use parens (see template). Is there a
            //    better way?
            page.toc = () => templateAPI.toc(tunnel.toc as TOC, renderStir)
        }
        page.site = tunnel.srcAssembly
        page.stir = stir

        //todo![template api] if we redo the API where the context is more "global" and
        //  `page` is a property (e.g. `{{ page.title }}` instead of `{{ title }}`), then
        //  `debug` should be top level, along with `site` and various other things that
        //  should not be a property of the page.
        page.debug = {}

        //todo![template api] we have a lot of control over template execution on a per
        //  call basis: helpers, partials and arbitrary data/objects specific to a call.
        //  See https://handlebarsjs.com/api-reference/runtime-options.html
        //
        //todo![template api] if we move to helper functions, maybe we can disable the security
        // issues below? Or are these not a security concern for Textplain?
        if (file.isOpeningContent()) {
            //todo![template api] maybe this should instead be one of:
            // - debug.template.path  (allows having other template props than path)
            // - template.path   (should template be a debug property?)
            page.debug.templatePath = config.requiredValue('templates.section')
            return indexTemplate(page, {
                //todo![template api] unsafe to allow proto methods.
                // See Template API docs.
                allowProtoMethodsByDefault: true,
                allowProtoPropertiesByDefault: true
            })
        } else {
            page.debug = {templatePath: config.requiredValue('templates.page')}
            return casePageTemplate(page, {
                allowProtoMethodsByDefault: true,
                allowProtoPropertiesByDefault: true
            })
        }
    }

    console.groupEnd()
    return render
}

/**
 * used not just for rendering the full stir, but any portions
 * extracted, e.g. page title or TOC entries.
 *
 * @type {{configure(config?: Config): Promise<RenderSTIR>}}
 *
 * Expected config structure:
 * ``` JSON
 * {
 *     renderer: {
 *         provider: string,
 *         config: {
 *             plugins: [
 *                 {
 *                     provider: string
 *                     config?: Record<string, unknown>
 *                 }
 *             ]
 *         }
 *     },
 *     helpers: {
 *          * path to file(s) defining custom Handlebars helpers
 *     },
 *     templates: {
 *         opening: '/index-template.hbs',
 *         standard: '/case-doc-template.hbs',
 *     },
 *     matchRules: MatchRule[]
 * }
 * ```
 */
const provider: RenderSTIRProvider = {
    async configure (config?: Config): Promise<RenderSTIR> {
        if (config === undefined) {
            throw new Error('missing RenderSTIRProvider configuration')
        }
        const internalRendererConfig = config.requiredSubConfig('renderer')
        const providerName = internalRendererConfig.requiredValue('provider')
        console.group(`configure internal renderer: ${providerName}`)
        const provider = lookupSTIRRenderer(providerName)
        if (provider === undefined || provider.configure === undefined) {
            throw new Error(`invalid renderer: ${providerName}`)
        }
        const providerConfig = internalRendererConfig.subConfig('config')
        const renderStir = await provider.configure(providerConfig)

        if (config.textAssembly() === undefined) {
            return stirRenderer(config, renderStir)
        } else {
            return assemblyRenderer(config, renderStir)
        }
    }
}

registerSTIRRenderer('Stir2Template', provider)

export default provider

