import type {Provider} from './provider.js'
import type {Inlet} from './inlet.js'
import type {Config} from './config.js'
import * as ptssParser from '../ptss-parser/index.js'
import {Stir2String} from './render/stir/stir2string.js'

// todo[arch] note how unlike render, we don't use classes, but a closure.
//    This functional approach definitely is well suited for transforms, but
//    perhaps we should do the same for render, either because it is better,
//    or for consistency (less confusing for users), or both.

/**
 * Interface for a Parse operator used for inputs to a STIR processing pipeline.
 */
export type Parse = Inlet<string>

export type ParseProvider = Provider<Parse>

export async function configParseProvider (config: Config): Promise<Parse> {
    const providerName = config.requiredValue('provider')
    console.group(`configure inlet: ${providerName}`)
    if (providerName !== 'ptss-parser') {
        throw new Error(`HACK: hardcoded support for ptss-parser only`)
    }
    //! HACK: instead of looking up a registered provider, we are hardcoding
    //  an instantiation of ptss-parser. When we implement true inlet provider
    //  lookup, and implement a ptss-parser provider interface, look at
    //  configOutletSegment for the proper logic; this should look almost
    //  identical. Some of the lines below would be moved into the ptss-parser
    //  provider init code.
    const providerConfig = config.requiredSubConfig('config')
    const stylesheet = providerConfig.requiredSubConfig('stylesheet')
    const stylesheetName = stylesheet.requiredValue('name')
    const stylesheetOptions = stylesheet.subConfig('options')?.data()
    const parserOptions = config.subConfig('options')?.data()
    const inlet =
        new ptssParser.Parser(
            ptssParser.loadSheet(stylesheetName, stylesheetOptions),
            parserOptions)

    console.groupEnd()
    return (input: string) => {
        console.debug(`♒️ INLET ptss-parser ${stylesheetName}`)
        console.group()
        const stir = inlet.parse(input)
        console.groupEnd()

        console.debug('Stir2String:')
        console.group()
        console.debug(new Stir2String().render(stir))
        console.groupEnd()
        return stir
    }
}
