import type {PipeProvider, Pipe} from '../pipe.js'
import type {Config} from '../config.js'
import {registerPipe} from '../registry.js'
import type {STIR} from '../stir.js'
import * as ir from '../../stir/index.js'
import {normalizeName} from '../../ptss-parser/ptss/index.js'


// todo![now, references] test this by verifying that:
//   - for an input with only H1 headings, the exports for
//     a CommonMark.ptss + ExportAllSectionsOrHeadings === RubyRhod.ptss
//   - for one with subheadings:
//     CommonMark.ptss + ConvertHeadingsToSections + ExportAllSectionsOrHeadings === RubyRhod

async function configure (config?: Config): Promise<Pipe> {
    const transform: Pipe = (stir: STIR) => {
        if (!ir.isBlock(stir)) {
            return
        }
        const sections = stir.hasSections()
        const targetType = sections ? 'section' : 'heading'
        const field = sections ? 'title' : 'body'

        console.debug(`exporting all ${targetType}s...`)
        console.group()

        const exports = stir.exported as ir.Exports || {}
        if (stir.exported === undefined) {
            stir.exports = exports
        }

        stir.traverseBodyTree(function (node) {
            if (node.isSectioningRoot()) {
                return 1
            }
            if (node.type === targetType) {
                const title = node[field] as ir.InlineSequence
                // todo![now, references, spec] had to remember to call
                //   `normalizeName`. Not DRY.
                const name = normalizeName(ir.inlineBodyText(title))

                const {path, sectioningRoot} = ir.idPath(node.parent)
                if (sectioningRoot !== undefined) {
                    // we don't export things inside a sectioning root.
                    // though this condition should never happen since we don't
                    // walk into sectioning roots in the first place (first line
                    // of this visitor callback above).
                    return 1
                }
                const id = path ? path + '/' + name : name
                // todo: what about collision with unexported iD?
                if (id in exports) {
                    // todo![now, references, error] 🏈 user can select first wins + warn or ERROR.
                    console.warn(`STIR id path collision for "${id}".`)
                    return 1
                }

                node.id = id
                exports[id] = {title: title}
                console.debug(`${field} exported as "${node.id}"`)
            }
            return 0
        })
        console.groupEnd()
    }

    return transform
}

const provider: PipeProvider = {
    async configure (config?: Config): Promise<Pipe> {
        return configure(config)
    }
}

registerPipe('ExportAllSectionsOrHeadings', provider)

export default provider
