import entities from 'markdown-it/lib/common/entities.js'
import {isValidEntityCode, fromCodePoint} from 'markdown-it/lib/common/utils.js'

const DIGITAL_ENTITY_TEST_RE = /^#(x[a-f0-9]{1,8}|[0-9]{1,8})$/i

function replaceEntityPattern (match: string, name: string) {
    let code

    if (Object.hasOwn(entities, name)) {
        return entities[name]
    }

    if (name.charCodeAt(0) === 0x23/* # */ && DIGITAL_ENTITY_TEST_RE.test(name)) {
        code = name[1].toLowerCase() === 'x' ?
            parseInt(name.slice(2), 16) : parseInt(name.slice(1), 10)

        if (isValidEntityCode(code)) {
            return fromCodePoint(code)
        }
    }

    return match
}

export function createUnescaper (decodeHtmlEntities: boolean, escapeChar: string, ESCAPE_LOOKUP: string[] = [], syntaxEscapeOnly = false) {
    // escapes the escapeChar if is a regex special character
    const ESCAPE_CHAR_RE = escapeChar.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')
    // eslint-disable-next-line no-control-regex
    const ESCAPE_RE = new RegExp(ESCAPE_CHAR_RE + `([\x00-\xFF])`, 'g')
    const ENTITY_RE = /&([a-z#][a-z0-9]{1,31});/gi
    const UNESCAPE_ALL_RE = new RegExp(ESCAPE_RE.source + '|' + ENTITY_RE.source, 'gi')

    if (decodeHtmlEntities) {
        return function (str: string) {
            if (str.indexOf(escapeChar) < 0 && str.indexOf('&') < 0) { return str }

            return str.replace(UNESCAPE_ALL_RE, function (match, escaped: string, entity: string) {
                if (escaped) {
                    const val = ESCAPE_LOOKUP[escaped[0].charCodeAt(0)]
                    if (val !== undefined) {
                        return syntaxEscapeOnly ? escaped : val
                    }
                    return match
                }
                return replaceEntityPattern(match, entity)
            })
        }
    } else {
        return function (str: string) {
            if (str.indexOf(escapeChar) < 0) { return str }

            return str.replace(ESCAPE_RE, function (match, escaped: string) {
                const val = ESCAPE_LOOKUP[escaped[0].charCodeAt(0)]
                if (val !== undefined) {
                    return syntaxEscapeOnly ? escaped : val
                }
                return match
            })
        }
    }
}
