import {registerPlugin} from '../../../registry.js'
import type {Stir2Html} from './stir2html.js'
import type {PluginProvider} from './pluginProvider.js'

import Handlebars from 'handlebars'

// todo![doc, site, perf, rollout] Fixing performance and maybe sizing issues
//  of case block rendering. Possibilities:
//  - ditch iframes, which have high overhead:
//
//    > Warning: Because each browsing context is a complete document environment,
//    > every `<iframe>` in a page requires increased memory and other computing
//    > resources. While theoretically you can use as many `<iframe>`s as you like,
//    > check for performance problems.
//    > ~ https://developer.mozilla.org/en-US/docs/Web/HTML/Element/iframe
//
//    - https://html.duckduckgo.com/html?q=alternative%20to%20iframe
//
//    maybe use <embed> or <object> instead:
//    - https://developer.mozilla.org/en-US/docs/Learn/HTML/Multimedia_and_embedding/Other_embedding_technologies
//    - https://stackoverflow.com/questions/16660559/difference-between-iframe-embed-and-object-elements
//    - https://stackoverflow.com/a/64135042/8910547
//    - but it looks like <embed> is deprecated, or discouraged, or not supported by
//      all browsers, and designed for plugins and things like video not embedded HTML.
//      Recommended by at least one page to use <object> instead.
//      - https://stackoverflow.com/a/1244854/8910547
//
//  - resizing behavior may be better without iframes.
//  - not sure if using shadow DOM does anything for this use case because
//    we don't update the page, just show and hide parts.

const CASE_TEMPLATE = `<div class="case">
  <div class="input">
<pre><code class="language-markdown">{{input}}</code></pre>
  </div>
  <div class="output">
    <div class="expected rendered">
      <iframe height="100%" srcdoc="<head><link href='/_resources/body.css' rel='stylesheet' type='text/css'></head><body>{{realTabs expected}}</body>"></iframe>
    </div>
    <div class="expected html hidden">
<pre><code class="language-html">{{expected}}</code></pre>
    </div>
  </div>
</div>
`

const ERROR_CASE_TEMPLATE = `<div class="case">
  <div class="input">
<pre><code class="language-markdown">{{input}}</code></pre>
  </div>
  <div class="output error">
    <div class="expected rendered">
      <iframe height="100%" srcdoc="<head><link href='/_resources/body.css' rel='stylesheet' type='text/css'></head><body>{{realTabs expected}}</body>"></iframe>
    </div>
    <div class="expected html hidden">
<pre><code class="language-html">{{expected}}</code></pre>
    </div>
    <div class="actual rendered hidden">
      <iframe height="100%" srcdoc="<head><link href='/_resources/body.css' rel='stylesheet' type='text/css'></head><body>{{realTabs actual}}</body>"></iframe>
    </div>
    <div class="actual html hidden">
<pre><code class="language-html">{{actual}}</code></pre>
    </div>
  </div>
</div>
`

const caseTemplate = Handlebars.compile(CASE_TEMPLATE)
const caseErrorTemplate = Handlebars.compile(ERROR_CASE_TEMPLATE)

Handlebars.registerHelper('realTabs', function (text) {
    return text.replace(/→/g, '\t')
})

// todo[api] THIS PLUGIN DOESN'T BELONG AS A BUILT-IN
// - it is specific to case-file.ptss, tightly coupled to it
//
// IDEALLY:
// - it should be colocated with case-file.ptss

// todo[api] consistent file naming for plugins and transforms
//  (registered name is independent)

/**
 * todo[api] this method or whatever it evolves to becomes
 *  part of the renderer's plugin API
 */
function install (renderer: Stir2Html, options: Record<string, unknown>) {
    // todo![fallback inheritance] We have both situations being tested now:
    //    1. a renderer that knows about the new block type
    //    2. a renderer that does not (stir2string, which is called during by
    //       debug logging for every file when web rendering.
    //  If we implement fallback inheritance, then
    //   a renderer without this plugin should still be able to render a
    //   `case_block` like a `code_block`, based on its `typeExtends` property
    //   and info in the `type metadata` section of the stir.
    renderer.registerBlockWriter('case_block', function (b, out) {
        if (b.failed) {
            out.buf += caseErrorTemplate(b)
        } else {
            out.buf += caseTemplate(b)
        }
    })
}


const provider: PluginProvider = {
    install: (renderer: Stir2Html, options: Record<string, unknown> = {}) => {
        install(renderer, options)
    }
}

registerPlugin('CaseBlock2HtmlPlugin', provider)

export default provider

