import {Node, Folder, FolderInterface, resolvePath} from './node.js'
import type {File} from './file.js'
import {traverse} from './traverse.js'
import type {SandboxFS} from '../../vfs/index.js'
import type {Config} from '../config.js'
import type {AssemblyPipeline} from '../pipeline.js'

/**
 * This will represent the abstracted text assembly:
 *  - each source file will be represented as a node
 *    in the source tree.
 *  - the source tree itself is abstracted:
 *    - in normal cases it will be identical to the
 *      concrete source tree in structure
 *    - but it can be transformed arbitrarily (see
 *      [Text Assembly Pipeline](...). For example,
 *      virtual nodes or branch splices declared in
 *      the source TextAssembly will ultimately be
 *      fully resolved in this representation.
 */
export class Assembly implements FolderInterface {
    /** text assembly source folder via SandboxFS */
    private readonly _src: SandboxFS

    /**
     * will only have a value for subassemblies. must be unique across all
     * assemblies participating in the composite assembly.
     */
    readonly assemblyID: string
    // only defined on the root (outer) assembly
    readonly assemblyIndex: Record<string, Assembly> | undefined

    // Must have trailing slash. See https://www.w3.org/TR/ldp-bp/#include-a-trailing-slash-in-container-uris
    readonly name: `${string}/`

    /**
     * Undefined if standalone assembly,
     * {@link Folder} if subassembly
     */
    readonly parent: Folder | undefined

    get title (): string | undefined {
        return this.openingContent?.title
    }

    get isIndexable (): boolean {
        return true
    }

    openingContent: File | undefined
    readonly content: Node[] = []
    hasIncludedContent = false

    sortSpec: string[] | undefined
    textplainConfig: Config | undefined
    pipeline: AssemblyPipeline | undefined

    outNode: string | undefined

    /**
     * Regular (standalone) Assembly constructor.
     * @param {SandboxFS} src assembly folder as SandboxFS volume.
     */
    constructor (src: SandboxFS)

    /**
     * Subassembly constructor.
     * @param {SandboxFS} src assembly folder as SandboxFS volume.
     * @param parent the parent folder within the parent assembly that this
     *               subassembly will be grafted as a virtual subfolder.
     * @param {`${string}/`} virtualFolderName the name that this subassembly
     *                               will have as a virtual folder within the
     *                               parent Folder. It will be used for path
     *                               resolution for links coming from outside
     *                               pointing in.
     * @param {string} assemblyID the ID used for direct cross-assembly links
     *                            within the composite assembly. It must be
     *                            unique among all assembly names within the
     *                            composite assembly.
     */
    constructor (src: SandboxFS, parent: Folder, virtualFolderName: `${string}/`, assemblyID: string)

    /** @internal */
    constructor (src: SandboxFS, parent?: Folder, virtualFolderName: `${string}/` = '/', assemblyID = '') {
        this.name = virtualFolderName
        this.parent = parent
        this._src = src
        this.assemblyID = assemblyID

        if (!src.isReadOnly()) {
            throw new Error(`assembly volume must be initialized as readonly`)
        }

        if (parent === undefined) {
            this.assemblyIndex = {
                [assemblyID]: this
            }
        } else {
            if (assemblyID.length < 1) {
                throw new Error('missing subassembly ID')
            }
            const idx = this.assemblyRoot(true).assemblyIndex
            if (idx === undefined) {
                throw new Error('invalid assembly state: root assembly missing subassembly index')
            }
            if (idx[assemblyID] !== undefined) {
                throw new Error(`duplicate assembly ID: ${assemblyID}`)
            }
            idx[assemblyID] = this
        }

        console.debug('new Assembly')
        console.group()
        console.debug(`assemblyID: ${this.assemblyID}`)
        console.debug(`srcFS:      ${this._src}`)
        console.debug(`innerAssemblyPath: ${this.innerAssemblyPath()}`)
        console.debug(`outerAssemblyPath: ${this.outerAssemblyPath()}`)
        console.debug(`crossAssemblyPath: ${this.crossAssemblyPath()}`)
        console.groupEnd()
    }

    isFile (): this is File {
        return false
    }

    isFolder (): this is Folder {
        return true
    }

    isAssemblyRoot (): this is Assembly {
        return true
    }

    assemblyRoot (crossAssemblyBoundary = false): Assembly {
        if (crossAssemblyBoundary && this.parent !== undefined) {
            return this.parent.assemblyRoot(true)
        }
        return this
    }

    assemblyForID (id: string): Assembly | undefined {
        return this.assemblyIndex === undefined
            ? this.assemblyRoot(true).assemblyForID(id)
            : this.assemblyIndex[id]
    }

    srcFS (): SandboxFS {
        return this._src
    }

    innerAssemblyPath (): string {
        return '/'
    }

    outerAssemblyPath (): string {
        return this.parent === undefined ?
            '/' : this.parent.outerAssemblyPath() + this.name
    }

    crossAssemblyPath (): string {
        return this.assemblyID === undefined ? '{}/' : `{${this.assemblyID}}/`
    }

    resolvePath (path: string): Node | undefined {
        return resolvePath(path, this)
    }

    searchUpForFile (name: string, crossAssemblyBoundary = false): File | undefined {
        for (const c of this.content) {
            if (c.isFile() && c.name === name) {
                return c
            }
        }
        if (this.parent === undefined || !crossAssemblyBoundary) {
            return undefined
        }
        return this.parent.searchUpForFile(name, crossAssemblyBoundary)
    }

    searchUpForProperty (property: string, crossAssemblyBoundary = false): { node: Node, value: unknown } | undefined {
        if (property in this) {
            const value = (this as unknown as Record<string, unknown>)[property]
            if (value !== undefined) {
                return {node: this, value}
            }
        }
        if (this.parent === undefined || !crossAssemblyBoundary) {
            return undefined
        }
        return this.parent.searchUpForProperty(property, crossAssemblyBoundary)
    }

    toString (): string {
        let s = ''
        let level = 0
        traverse(this,
            (n: Node) => {
                s += '    '.repeat(level)
                s += n.name
                if (n.isFolder()) {
                    if (n.openingContent) s += ` (${n.openingContent.name})`
                    if (n.isAssemblyRoot()) s += ` 📦{${n.assemblyID}} ${n._src}`
                    if (n.textplainConfig) s += ` ⚙️ ${n.textplainConfig.name()}`
                    level++
                }
                s += '\n'
                return 'continue'
            },
            (n: Node) => {
                if (n.isFolder()) {
                    level--
                }
                return 'continue'
            })
        return s
    }
}
