import * as ptss from './ptss/index.js'
import * as ir from '../stir/index.js'
import {Line} from './line.js'
import {parseContinuation, ParseContainer} from './parseContainer.js'
import type {NamedValues} from './namedValues.js'
import {stringifyTruncate} from './debugUtil.js'

enum TryResult {
    NO_MATCH,
    MATCHED_PARTIAL_LINE, //todo[arch] or MATCHED_CONTAINER_MARGIN?
    MATCHED_TO_EOL
}


//todo[doc] merge this with the class doc below. MAYBE move most of it to Parsing Design doc.
//
//  State:
//    - **BlockTreeParser**
//       - **The STIR tree**, incrementally built up as we parse.
//       - where we are vertically (curLine)
//         (where we are horizontally within each line is
//         maintained by Line)
//       - current container nesting (curContainer Stack)
//       - any lazily continuing container
//       - pending back chunk
//       - access to fwd lines
//       - **Line** maintains the "horizontal parsing state", for each line:
//          - responsible for parsing spaces, tabs, columns, EOL.
//          - the last parsed position for the line
//          - the corresponding ContainerStack entry for that parsed position
//
//   Parsing:
//    - **BlockTreeParser** parses sequences and nestings of blocks.
//      - Delegates to ptss.ElementalBlockStyle parsers the parsing of
//        each specific block style.
//      - Constructs the block-level IR tree.
//    - **ptss.ElementalBlockStyle** parsers specific block styles,
//      delegating to Line for whitespace parsing.
//      - Constructs block-level IR nodes, populating them
//        with content or values derived from the parsed
//        text.
//    - **Line** parses the whitespace parts of the line.
//    - **InlineParser** parses all marked up (non-literal)
//      text content of the block-level IR nodes, attaching
//      text-level IR nodes.
/**
 * BlockTreeParser parses sequences and nestings of blocks. It is analogous
 * to InlineParser, which parses sequences and nestings of inline structures.
 *
 * The recognition and parsing of each type and style of block is
 * handed off to the {@link ptss.ElementalBlockStyle} implementations: {@link ptss.ChunkStyle},
 * {@link ptss.ContainerStyle}, {@link ptss.ListStyle} and {@link TableStyle}.
 * BlockTreeParser enforces the precedence of BlockStyles as declared by the Stylesheet.
 *
 * While the ptss.ElementalBlockStyle parsers will produce individual block-level IR elements and
 * block-level PTSS NamedValues, BlockTreeParser is responsible for assembling them into the
 * IR tree and resolving all the links.
 *
 *  todo[doc] REWRITE OR DISCARD THE BELOW. Otherwise we move it to a
 *    README.md or other file supported by JDoc.
 *
 *  - it starts with the root container
 *  - when it encounters a nested container, it uses a stack to
 *    handle nesting rather than using recursion
 *  - In effective it is Recursive Descent block parsing, even if
 *    not in literal implementation. This is not just for efficiency.
 *    Given the complexities of lazy continuation and sloppiness,
 *    stack-based recursive parsing won't be as simple as recursion
 *    usually is.
 *    We use:
 *    - a stack only to track the current nesting of containers.
 *    - it only parses out nested text blocks on a need-to basis.
 *    - it parses a container's contents, i.e. sequence of block elements
 *    - following the stylesheet's declared precedence order, it
 *      gives each style a shot, handing off element parsing to that
 *      style's parser.
 *    - it enforces container-level rules, e.g. element adjacency
 *      (e.g. canInterruptParagraph).
 *  - it maintains the container-level parse state.
 *  - it constructs the IR tree from nodes created by the style
 *    specific parsers.
 *
 */
export class BlockTreeParser {
    /* the inputs */
    private readonly src: string
    private readonly styleSheet: ptss.Stylesheet

    /* the output */
    private readonly irRoot: ir.Block

    /**
     * The current line at which the next parse decision is rooted.
     *
     * It *does not necessarily* demarcate the boundary between what we've finished
     * parsing and what we have yet to parse, as there may be
     * unresolved prior lines if {@link unresolvedFrom} is set.
     */
    private curLine: Line

    // the current non-lazy container being parsed
    private curPContainer: ParseContainer
    private readonly rootPContainer: ParseContainer

    /**
     * @link{curLazyPContainer} and @link{curPContainerHasLazyLines} track
     * lazy continuation for style sheets that require it.
     *
     * todo[perf] what's the overhead for styles that don't use it?
     *
     *  @link{curLazyPContainer} represents the container that owns the
     *  unresolved back chunk because it began in this container.
     *  So it will only be non-null when:
     *   - there is an unresolved back chunk
     *   - they are NOT owned by the current non-lazy container (curContainer).
     *  @link{curLazyPContainer} may become the curContainer again if a
     *  subsequent line matching parses for the container *non-lazily*.
     *  In this case, @link{curPContainerHasLazyLines} keeps track of the fact
     *  that the container includes one or more lazy lines. This is needed as
     *  some styles my not support laziness and therefore cannot consume those
     *  lines.
     *
     * ILLUSTRATED EXAMPLE
     * -------------------
     * Given:
     *
     *    > this container
     *    has lazy
     *    > lines
     *
     * When on line 1, after parsing the `>`:
     *    curPContainer = <block quote>
     *    curLazyPContainer = null
     *    curPContainerHasLazyLines = false
     * When on line 2, after parseContinuation(curPContainer, line)
     *    curPContainer = <root>
     *    curLazyPContainer = <block quote>
     *    curPContainerHasLazyLines = false
     * When on line 3, after parseContinuation(curPContainer, line)
     *    curPContainer = <block quote>
     *    curLazyPContainer = null
     *    curPContainerHasLazyLines = true
     */
    private curLazyPContainer: ParseContainer | null
    /** See explanation under @link{curLazyPContainer} */
    private curPContainerHasLazyLines = false

    //todo[parsing] redo this doc
    /**
     * backward view, unresolved lines.
     * {@link curLazyPContainer}, if it is not null, owns these lines,
     * else {@link curPContainer} owns them.
     *
     * null if there no unresolved lines.
     */
    private unresolvedFrom: Line | null

    private readonly namedValues: NamedValues

    public constructor (src: string,
                        stylesheet: ptss.Stylesheet,
                        rootIRElemType: string,
                        namedValues: NamedValues) {
        this.src = src
        this.styleSheet = stylesheet

        this.irRoot = ptss.ContainerStyle.newRootContainer(rootIRElemType, stylesheet)

        this.rootPContainer = this.curPContainer = new ParseContainer(this.irRoot)
        this.curLazyPContainer = null

        this.namedValues = namedValues

        // initialize with line 1
        this.curLine = new Line(this.src, 1, 0, this.rootPContainer)
        this.unresolvedFrom = null
        this.irRoot.source!.lineStart = 1
    }

    public parse (): ir.Block {
        do {
            const line = this.curLine
            console.debug()
            console.debug(line.num, stringifyTruncate(line.srcLine()))
            console.group()
            console.debug(line.toString())

            // uncomment and use this line to trigger the debugger at a certain line
            //if (this.lineRemainingContent() === '- * * *\n') debugger

            // reference for efficiency  todo[perf] test whether this makes any difference, else inline it.
            const curPContainer = this.curPContainer

            // PARSE CONTAINER CONTINUATION
            // ============================
            // Parses line for continuation of the all the containers in the
            // stack of nested containers still open from the previous line.
            //
            // optimization: no need to do this for root container
            if (curPContainer.parent !== null) {
                console.debug(`parsing continuation of ${curPContainer.tip().toDebugString()} in ${JSON.stringify(line.srcLine())}`)
                console.group()
                parseContinuation(curPContainer, line)
                console.groupEnd()
            }

            if (line.pContainerBottomBorder !== null) {
                // BOTTOM BORDER CONTAINER TERMINATION
                // ===================================
                // if the cur container or one of its parents terminated with a bottom
                // border on this line:
                //   - Call resolveUnresolved(), which will resolved pending back.
                //     content and add it to the appropriate container.
                //   - The line is resolved. DO NOT continue parsing the line as it
                //     was occupied/owned by the bottom border. Do NOT treat the
                //     lack of content on the line as a blank line.
                //   - Switch to the new current container, proceed to the next
                //     line.

                console.debug(`🅱 bottom border for ${line.pContainer} encountered`)
                this.defaultResolveBackChunk(false, true)
                this.curPContainer = this.curLine.pContainer.parent as ParseContainer
                console.debug('......<︎', curPContainer.toString())

                // end the group for this Line before continuing to the next
                console.groupEnd()
                continue
            }

            if (line.pContainer != curPContainer) {
                // OTHER CONTAINER TERMINATION
                // ===========================
                // Current container and one or more of its parents did not continue
                // on current line.
                //
                // Handle closed container vs lazy continuation possibility.
                //
                // CASES:

                if (!this.hasUnresolvedBackChunk()) {
                    // NO UNRESOLVED CONTENT
                    // =====================
                    // Any lazy continuation container is irrelevant.

                    // If there is no left border, reassign any blank previously
                    // assigned to the just closed container to the deepest
                    // continuing parent. See doc on Structural White Space.
                    const source = curPContainer.irContainer.source as ptss.Block
                    if (source.lastLineBlank) {
                        //  Blanks can't fall through a hard boundary, i.e a left
                        //  border, in which case trailing blank line stays with
                        //  a closing left border container.
                        if (source.style?.baseType !== ptss.BaseStyleType.Container ||
                            !(source.style as ptss.ContainerStyle).leftBorder) {
                            source.lastLineBlank = false;
                            (line.pContainer.irContainer.source as ptss.Block).lastLineBlank = true
                            console.debug(`     reassigning LAST BLANK to ${line.pContainer.toString()}`)
                        }
                    }
                } else {
                    // THERE IS UNRESOLVED CONTENT
                    // ===========================

                    if (this.styleSheet.lazyContinuation) {
                        // LAZINESS ENABLED
                        // ================
                        //  - If there IS an open lazy container, it stays open.
                        //    The containers that parseContinuation found did
                        //    not continue on the current line are terminated.
                        //  - If there IS NOT an open lazy container, the entire
                        //    container stack from the previous line stays open
                        //    as a lazy container.
                        //  - The unresolved content belongs to the lazy
                        //    container. Continuity of unresolved content will
                        //    keep the lazy container alive.

                        if (!this.curLazyPContainer) {
                            this.curLazyPContainer = curPContainer
                        }
                        console.debug('potential lazy container continuation onto',
                            this.curLazyPContainer.toString())
                    } else {
                        // LAZINESS NOT ENABLED
                        // ====================
                        //  - The containers that parseContinuation found did
                        //    not continue on the current line are terminated.
                        //  - `defaultResolveBackChunk()` will resolved the
                        //    back chunk and add it to the appropriate
                        //    container.

                        this.defaultResolveBackChunk(false, true)
                    }
                }

                // Update the current container to the deepest continuing
                // container, which was set on the current line
                // by parseContinuation.
                this.curPContainer = this.curLine.pContainer
            }

            if (this.curLine.remainingIsBlank()) {
                // BLANK LINE
                // ==========

                console.debug('parse Blank Line')
                console.group()
                this.defaultResolveBackChunk();
                (this.curPContainer.irContainer.source as ptss.Block).lastLineBlank = true
                console.debug(`SET LAST BLANK for ${this.curPContainer.toString()}`)
                console.groupEnd()

                // end the group for this Line before continuing to the next
                console.groupEnd()
                continue
            }

            allStylesFromTop:
                // todo![perf] AN AWESOME IDEA: ALMOST EVERY STYLE HAS A KEY CHARACTER
                //  that must occur somewhere on the first line. For most styles,
                //  that key character must be the first non-whitespace character.
                //  We should be able to take advantage of this to narrow the set
                //  of styles that could possibly match at the current parse
                //  position. (Perhaps in the same step, we could further narrow
                //  the set based on other factors, e.g. whether a paragraph
                //  interruption is involved, etc.
                //  For example:
                //   - we may or may not support this optimization for special
                //     chars that occur elsewhere than the first non-whitespace
                //     position. This example assumes we don't.
                //   - at parser initialization time, identify the lead special
                //     character is for each style. If a style doesn't have one,
                //     it's lead char is effectively "any".
                //     - if we can't programmatically figure it out when we init
                //       each style, we can have the style def supply it.
                //   - during the style matching loop (i.e. this point in the
                //     code where this comment is), parse the first non-whitespace
                //     character and efficiently get the set of styles that could
                //     match. Perhaps we have an array that maps char code (index
                //     into array) to a list of styles (the value at that index).
                //   - likewise the interrupt check loop (right now we have two,
                //     one in TableStyle and one in Line that maybe should be
                //     consolidated) calls this method too for smarter interrupt
                //     checks, or maybe it has its own version which includes
                //     narrowing logic specific to interrupts.
                //   - This method could even incorporate the perf ideas below,
                //     e.g. one-time indent parsing, and the filtering logic
                //     further below (all the calls to 'continue nextStyle' that
                //     happen without trying the current style.)
                for (; ;) {
                    // skip leading spaces if <= this.styleSheet.sloppyMargin AS IF
                    // they never existed in the first place, otherwise leave the ptr as-is.
                    line.parseSlop(this.styleSheet.sloppyMargin, true)

                    // todo[perf] what if we parsed the indent right here and:
                    //   - skipped styles with indent = NO
                    //   - for other styles, passed the indent info down so it
                    //     is only ever parsed once! Literal containers like
                    //     Indented Code Blocks can even add the indent back to
                    //     the line's content.
                    //   DRAWBACKS:
                    //   - element parsers would not independent of calling
                    //     parser. An alternative is that Line saves the indent
                    //     parsing done on a line even if reverted.
                    //   OPPORTUNITY:
                    //   - We have other cases where a style parser
                    //     will throw out work (e.g. forward parse up to interrupting
                    //     new elem start) that a subsequent style parsing redoes
                    //     redundantly. Maybe we can have a generalized solution
                    //     within Line for common speculative parsing that gets saved.

                    //we will restore to this same checkpoint after each
                    //   tryBlockStyle call that fails in the loop
                    const checkpoint = this.curLine.checkpoint()
                    nextStyle:
                        for (const style of this.styleSheet.blockStyles) {
                            // todo[perf] For CommonMark,
                            //   `!style.consumesBackChunk && !style.canInterruptParagraph`
                            //   is true for 2 of 10 styles (indentedCode, linkRefDef).
                            //   This may be more useful for stylesheets that require
                            //   blank lines.
                            // todo[perf] we could also pre-process all styles and make a
                            //   list of 'quick checks' for each of them, either by analysis
                            //   or supporting an explicit 'perf hint' in the style declaration.
                            //   e.g. without making function calls, we can filter candidate styles
                            //   based on the next char in the stream (e.g. ATX would get filtered
                            //   out immediately if no `#`). Possibly could parse the Regex's and
                            //   determine first character invariants or similar.
                            //   i.e. we'd be mapping trigger characters to styles.
                            //   <br>
                            //   Inlines
                            //   -------
                            //   For Inlines we could do the same, but since we have to
                            //   scan every character of content, it would probably be more
                            //   efficient to compile a Regex that includes all of those
                            //   characters, and we'd execute `RegExp.exec()` with a sticky
                            //   flag, so we can find the next match continuing either from
                            //   where we left off or from wherever we want setting regex.index.
                            //   <br><br>
                            //   If we ever decide to not splice out body content but just keep
                            //   pointers to the backing text, this can still work:
                            //   - If the index returned goes beyond the end of inline content
                            //     for the current element, it would mean there are no special
                            //     inlines in that element's content.
                            //   - If it is beyond, we can save the index for when we process the
                            //     next element's content (e.g. is possibleInlineIndex < curPos,
                            //     we have to do a new search, if possibleInlineIndex > curPos,
                            //     etc
                            // todo[perf] we are doing the same lookups (e.g. calls to
                            //   curPContainerHasUnresolvedBackChunk()) for each style we check,
                            //   even though the value doesn't change as we loop thru styles.
                            //   Since we do this loop a lot should do this better?
                            //   e.g. we can call all three of these just ahead of this
                            //   for-loop:
                            //     - curPContainerHasUnresolvedBackChunk
                            //     - hasUnresolvedBackChunk
                            //     - this.styleSheet.lazyContinuation
                            //   Also, we can combine the latter two into a single
                            //   bool, e.g. 'hasLazyBack'.
                            //   That said, is it possible to have unresolved back
                            //   that isn't included in curPContainerHasUnresolvedBackChunk
                            //   when styleSheet.lazyContinuation === false? If not.
                            //   we can skip the styleSheet.lazyContinuation check.

                            // todo![interrupt] make sure we are apply the following
                            //  constraints in `Line.checkStyleMatch` which is used
                            //  for fwd matching interrupt checks, which I'm pretty
                            //  sure must be consistent with any rules below that
                            //  prevent a style from matching because we have
                            //  unresolved back. At least `canInterruptParagraph` is
                            //  enforced when we precalculate `ChunkStyle.interruptibleBy`

                            // special list logic
                            if (this.curPContainerHasUnresolvedBackChunk()) {
                                if (!style.consumesBackChunk) {
                                    if (!style.canInterruptParagraph) {
                                        if (!(style instanceof ptss.ListStyle)
                                            || !style.canInterruptParaWithinList
                                            || this.rootPContainer.openList === null) {
                                            continue nextStyle
                                        }
                                    }
                                }
                            }

                            // if back consuming chunk isn't lazy, it should
                            //  not be able to consume a back chunk which has
                            //  one or more lazy lines
                            if ((this.curLazyPContainer !== null || this.curPContainerHasLazyLines)
                                && style instanceof ptss.ChunkStyle
                                && style.consumesBackChunk
                                && !style.lazyContinuation) {
                                continue nextStyle
                            }

                            // if fwd consuming chunk can't interrupt lazy
                            if (this.hasUnresolvedBackChunk()
                                && !style.consumesBackChunk
                                && this.styleSheet.lazyContinuation
                                && !style.canInterruptLazy) {
                                continue nextStyle
                            }

                            switch (this.tryBlockStyle(style)) {
                                case TryResult.NO_MATCH:
                                    // continue to next style in precedence list
                                    checkpoint.restore()
                                    continue nextStyle
                                case TryResult.MATCHED_PARTIAL_LINE:
                                    // console.debug(`line:${this._curLineNum} offset:${line.ptr}` +
                                    //               ` tab%:${this.curTabRemainder} eol:${(this.lineEnd(line.ptr))}`)
                                    if (line.remainingIsBlank()) {
                                        break allStylesFromTop
                                    }
                                    continue allStylesFromTop
                                case TryResult.MATCHED_TO_EOL:
                                    break allStylesFromTop
                            }
                        }
                    //no styles matched

                    this.leaveCurLineUnresolved()
                    break
                }

            console.groupEnd()
        } while (this.advance())

        console.debug()
        console.debug('[EOF]')
        console.group()
        this.defaultResolveBackChunk(true, true)
        console.groupEnd()
        console.debug()

        return this.irRoot
    }

    /**
     * Advance to next line.
     *
     * @returns false if no more lines
     */
    private advance (): boolean {
        if (this.curLine.next === null) {
            return false
        }

        this.curLine = this.curLine.next
        return true
    }

    private tryBlockStyle (blockStyle: ptss.ElementalBlockStyle): TryResult {
        switch (blockStyle.baseType) {
            case ptss.BaseStyleType.Chunk:
                return this.tryChunkStyle(blockStyle as ptss.ChunkStyle)
            case ptss.BaseStyleType.Container:
                return this.tryContainerStyle(blockStyle as ptss.ContainerStyle)
            case ptss.BaseStyleType.List:
                return this.tryListStyle(blockStyle as ptss.ListStyle)
            case ptss.BaseStyleType.Table:
                return this.tryTableStyle(blockStyle as ptss.TableStyle)
            case ptss.BaseStyleType.TEMP_HTML_HACK:
                return this.tryHTMLBlockStyle(blockStyle as ptss.HTMLBlockStyle)
        }
    }

    private tryChunkStyle (chunkStyle: ptss.ChunkStyle): TryResult {
        //! we only support back matching from the current non-lazy container.
        const result = chunkStyle.parse(this.curLine,
            this.curPContainerHasUnresolvedBackChunk() ? this.unresolvedFrom : null)
        if (!result) {
            return TryResult.NO_MATCH
        }
        console.group() //untitled so that actions below appear part of chunkStyle.parse()'s group

        if (result.startLine && result.endLine) {
            if (result.startLine.num < this.curLine.num) {
                // we back matched
                this.clearUnresolvedBackChunk()

            } else if (result.startLine.num === this.curLine.num) {
                this.defaultResolveBackChunk()
            }
        } else {
            throw new Error('start and end line expected from chunkStyle.parse')
        }
        this.curLine = result.endLine as Line

        // handle result artifacts
        const irBlock = result.irBlock
        if (irBlock) {
            console.debug(`✨ new chunk: ptss=${chunkStyle.name}  stir=${irBlock.type}${irBlock.subType ? '/' + irBlock.subType: ''}`)
            // todo![spec] can only chunks introduce new sections? For example,
            //    - why can't a container? Imagine a section hierarchy defined
            //      by an explicit container hierarchy rather than the implicit
            //      hierarchy of heading levels
            //    - what about numbered lists being used to represent a numbered
            //      hierarchy, e.g. like legal documents?
            //   See [[Hierarchy Syntax]] for analysis.
            //   <br>
            //   One way to support this:
            //   - `ParseContainer.appendImplicitSection(irBlock)` - the current `appendSection` that works for
            //      an implicit section hierarchy from heading levels.
            //   - `ParseContainer.appendSection(irBlock)` - works for explicit plain-text container-based sections.
            //
            // todo![RepoDoc] the above `[[Hierarchy Syntax]]` wikilink should resolve!
            if (irBlock.type === 'section') {
                this.curPContainer.appendSection(irBlock)
            } else {
                this.curPContainer.append(irBlock)
            }
        }
        if (result.ptNamedValue) {
            if (this.namedValues.define(result.ptNamedValue, this.curPContainer.tip())) {
                console.debug(`✨ new NamedValue: ${ptss.stringifyNamedValue(result.ptNamedValue)}`)
                console.debug('updated NamedValues:')
                console.group()
                console.debug(this.namedValues.toString())
                console.groupEnd()
            } else {
                console.debug(`❌ skipped NamedValue: ${ptss.stringifyNamedValue(result.ptNamedValue)}`)
            }
        }

        console.groupEnd()
        return TryResult.MATCHED_TO_EOL
    }

    private tryContainerStyle (containerStyle: ptss.ContainerStyle): TryResult {
        const result = containerStyle.parse(this.curLine,
            this.curPContainerHasUnresolvedBackChunk() ? this.unresolvedFrom : null)
        if (!result) {
            return TryResult.NO_MATCH
        }
        console.group() //untitled so that actions below appear part of containerStyle.parse()'s group

        this.defaultResolveBackChunk()
        this.curLine = result.endLine as Line

        // handle result artifacts
        const irBlock = result.irBlock as ir.Block
        console.debug(`✨ new container: ptss=${containerStyle.name}  stir=${irBlock.type}${irBlock.subType ? '/' + irBlock.subType: ''}`)
        this.curPContainer.append(irBlock)

        if (result.ptNamedValue) {
            if (this.namedValues.define(result.ptNamedValue, this.curPContainer.tip())) {
                console.debug(`✨ new NamedValue: ${ptss.stringifyNamedValue(result.ptNamedValue)}`)
                console.debug('updated NamedValues:')
                console.group()
                console.debug(this.namedValues.toString())
                console.groupEnd()
            } else {
                console.debug(`❌ skipped NamedValue: ${ptss.stringifyNamedValue(result.ptNamedValue)}`)
            }
        }

        let retVal: TryResult
        if (containerStyle.body === 'BLOCKS' || containerStyle.body === 'INLINE_OR_BLOCKS') {
            this.addNewSubPContainerToLineAndTree(irBlock)
            if (this.curLine.ptr === this.curLine.EOL) {
                retVal = TryResult.MATCHED_TO_EOL
            } else {
                retVal = TryResult.MATCHED_PARTIAL_LINE
            }
        } else {
            // for simpler, non recursive content models, the container is parsed
            // to the end, and closed (much like parsing a chunk), so we
            // don't update the container stack with it.
            retVal = TryResult.MATCHED_TO_EOL

            // also, if it had a bottom border, it was not included in the
            // the `result.endLine` above, so we advance past that here:
            if (this.curLine.next && this.curLine.next.pContainerBottomBorder !== null) {
                this.curLine = this.curLine.next
            }
        }
        console.groupEnd()
        return retVal
    }

    private tryListStyle (listStyle: ptss.ListStyle): TryResult {
        const firstLine = this.curLine

        // todo![spec] TWO HUGE PROBLEMS
        //  1. The CM spec says "list item other than `1.` can't interrupt a
        //     paragraph. It can interrupt anything else!!!!  But a back chunk
        //     isn't necessarily a paragraph!!! The only reason we are passing
        //     tests is because Setext (which is based on a back chunk) has
        //     higher precedence and will have consumed any back chunk by now.
        //     I don't like why this works, that there now is a convoluted
        //     and unrelated reason why one has to have higher precedence than
        //     the other.
        //  2. we are passing curPContainerHasUnresolvedBackChunk, which is not
        //     the same as hasUnresolvedBackChunk(), which is what we pass for
        //     HTMLBlockStyle.
        //     - but i think this is a flaw in the CM spec.
        const result
            = listStyle.parse(firstLine,
            this.curPContainerHasUnresolvedBackChunk() ? this.unresolvedFrom : null)
        if (!result) {
            return TryResult.NO_MATCH
        }
        console.group() //untitled so that actions below appear part of chunkStyle.parse()'s group

        this.defaultResolveBackChunk()

        const irListItem = result.irBlock as ir.Block
        // todo[debug] in this (and container and others?) we are only reporting the name of the style.
        //    but should we also not report the *instance* of the style, e.g. the bullet marker?
        //    isn't an element a combination of the style def AND specific options of that def?
        //    e.g. Interjection{warning, indent(5)}  List{unordered, '*'}
        //    we want to capture all the elements that determine its continuation from line to line, no?
        console.debug(`✨ new list item: ptss=${listStyle.name}  stir=${irListItem.type}${irListItem.subType ? '/' + irListItem.subType: ''}`)
        this.curPContainer.appendListItem(irListItem, result.irNewList)

        this.addNewSubPContainerToLineAndTree(irListItem)

        console.groupEnd()
        return TryResult.MATCHED_PARTIAL_LINE
    }

    private tryTableStyle (tableStyle: ptss.TableStyle): TryResult {
        const result = tableStyle.parse(this.curLine,
            this.curPContainerHasUnresolvedBackChunk() ? this.unresolvedFrom : null)
        if (!result) {
            return TryResult.NO_MATCH
        }
        console.group() //untitled so that actions below appear part of chunkStyle.parse()'s group

        this.defaultResolveBackChunk()
        this.curLine = result.endLine as Line

        // handle result artifacts
        const irBlock = result.irBlock as ir.Block
        console.debug(`✨ new table: style=${tableStyle.name} endLine=${this.curLine.num} ` +
                      ` cols=${irBlock?.tableMeta?.head?.length} ` +
                      ` rows=${irBlock?.body?.length}`)
        this.curPContainer.append(irBlock)

        console.groupEnd()
        return TryResult.MATCHED_TO_EOL
    }

    private tryHTMLBlockStyle (htmlBlockStyle: ptss.HTMLBlockStyle): TryResult {
        const result = htmlBlockStyle.parse(this.curLine, this.unresolvedFrom)
        if (!result) {
            return TryResult.NO_MATCH
        }
        console.group() //untitled so that actions below appear part of chunkStyle.parse()'s group

        this.defaultResolveBackChunk()
        this.curLine = result.endLine as Line

        // handle result artifacts
        const irBlock = result.irBlock as ir.Block
        console.debug(`✨ new html block: ptss=${htmlBlockStyle.name}  stir=${irBlock.type}${irBlock.subType ? '/' + irBlock.subType: ''}`)
        this.curPContainer.append(irBlock)

        console.groupEnd()
        return TryResult.MATCHED_TO_EOL
    }

    addNewSubPContainerToLineAndTree (containerElem: ir.Block): void {
        this.curPContainer =
            new ParseContainer(containerElem, this.curPContainer)
        this.curLine.pContainer = this.curPContainer
        console.debug(this.curLine.toString())
    }

    /**
     * to be called when we give up on the curLine, and before calling
     * advance() to proceed to the next line.
     */
    leaveCurLineUnresolved (): void {
        console.debug(`unresolved: ${stringifyTruncate(this.curLine.remainingSlice())}`)
        console.group()

        if (this.unresolvedFrom === null) {
            this.unresolvedFrom = this.curLine
        } else {
            console.debug(`since line: ${this.unresolvedFrom.num}` +
                          `  ${stringifyTruncate(this.getUnresolvedBackChunkAndContainer(true)[0])}`)
        }

        // because lazy containers can resume, e.g. the following
        // Markdown blockquote:
        //     > In a block quote.
        //     Still in the same block quote
        //     > This does not start a new block quote; it
        //     > continues the above block quote.
        if (this.curLazyPContainer) {
            this.curPContainer = this.curLazyPContainer
            this.curPContainerHasLazyLines = true
            this.curLazyPContainer = null
        }

        // backChunk can only resolve to something other than a list item,
        // when it does resolve it will trigger the closure of any open list.
        // pre-emptively closing it now simplifies the logic later.
        if (this.curPContainer.openList !== null) {
            console.debug(`❌ preemptively closing list ${this.curPContainer.toString()}`)
        }
        this.curPContainer.openList = null

        console.groupEnd()
    }

    hasUnresolvedBackChunk (): boolean {
        return this.unresolvedFrom !== null
    }

    clearUnresolvedBackChunk (): void {
        this.unresolvedFrom = null
        this.curLazyPContainer = null
        this.curPContainerHasLazyLines = false
    }


    //todo[perf] as paragraphs are the most common, optimizing them may be
    //  worthwhile. Possibilities:
    //    - Have a *virtual* built-in default style, that if not overridden, is
    //      applied. Constructing the IR node for this style is fast, not
    //      using ptss.Expressions.
    //    - since most paragraphs are at the root, there is no need to extract
    //      lines from src as we do, only to concatenate them here.
    defaultResolveBackChunk (includeCurLine = false, containerIsClosing = false): void {
        if (this.hasUnresolvedBackChunk()) {
            const [content, lineStart, lineEnd, pContainer]
                = this.getUnresolvedBackChunkAndContainer(includeCurLine)

            console.debug('resolving unresolved onto' +
                          (this.curPContainer === pContainer ? '' : ' (lazy)') +
                          ` ${pContainer.toString()}\n` +
                          `    ${stringifyTruncate(content)}`)
            console.group()

            if (pContainer !== this.curPContainer) {
                containerIsClosing = true
            }

            if (containerIsClosing
                && ((pContainer.irContainer.source as ptss.Block)?.style as ptss.ContainerStyle).body === 'INLINE_OR_BLOCKS'
                && pContainer.irContainer.body === undefined
            ) {
                ptss.evalIRBlockPropertyExpr(
                    {
                        body: new ptss.Expression(['parseInlines', ['body']])
                    },
                    pContainer.irContainer, null, null, [content], null, null)
                console.debug(`appending inline content to ${pContainer.tip().toDebugString()}`)
            } else {
                const defaultBlockStyle = this.styleSheet.defaultBlockStyle
                const ptBlock = new ptss.Block(defaultBlockStyle)
                ptBlock.lineStart = lineStart
                ptBlock.lineEnd = lineEnd
                //the type should get overwritten by expression.
                const irNode = new ir.Block('default')
                ptss.evalIRBlockPropertyExpr(defaultBlockStyle.irNode, irNode, null, null, [content], null, null)
                irNode.source = ptBlock
                console.debug(`✨ new default elem: ptss=${defaultBlockStyle.name}  stir=${irNode.type}${irNode.subType ? '/' + irNode.subType: ''}`)
                pContainer.append(irNode)
            }
            this.clearUnresolvedBackChunk()
            console.groupEnd()
        }
    }


    curPContainerHasUnresolvedBackChunk (): boolean {
        return this.curLazyPContainer === null && this.unresolvedFrom !== null
    }

    /**
     * Returns unresolved lines ONLY if they belong to *the
     * current container*.
     *
     * If the lines are consumed, caller is responsible for setting:
     *     this._unresolvedBackChunk = null
     *     this._currentLineUnresolved = false
     *
     * (I can't think of a case where a partial resolve
     * makes sense, forcing the default resolution on
     * earlier lines. We can change this later.)
     */
    curPContainerUnresolvedFrom (): Line | null {
        if (this.curLazyPContainer) {
            return null
        }
        return this.unresolvedFrom
    }

    /**
     * Returns the unresolved back chunk and the container to which it would belong if it
     * were to be resolved now. It doesn't necessarily belong to the current container;
     * if the stylesheet includes lazy continuation, it might belong to a lazily continuing
     * container.
     */
    getUnresolvedBackChunkAndContainer (includeCurLine = false)
        : [content: string, lineStart: number, lineEnd: number, pContainer: ParseContainer] {
        if (this.unresolvedFrom === null) {
            return ['', -1, -1, this.rootPContainer]
        }

        const c = this.curLazyPContainer ?
            this.curLazyPContainer : this.curPContainer

        // root container optimization
        if (c === this.rootPContainer && this.curLazyPContainer === null) {
            console.debug(`💥 ROOT OPTIMIZATION ROUTE`)
            if (includeCurLine) {
                return [this.src.slice(this.unresolvedFrom.SOL, this.curLine.EOL),
                    this.unresolvedFrom.num, this.curLine.num, c]
            } else {
                return [this.src.slice(this.unresolvedFrom.SOL, this.curLine.SOL),
                    this.unresolvedFrom.num, this.curLine.num - 1, c]
            }
        }

        let l = this.unresolvedFrom
        let s = ''
        while (l.num < this.curLine.num) {
            s += l.remainingSlice()
            l = l.next as Line
        }
        if (includeCurLine) {
            s += this.curLine.remainingSlice()
        }

        // todo![parsing] assertion legit? or add null check?
        const lineStart = (this.unresolvedFrom as Line).num
        const lineEnd = includeCurLine ? this.curLine.num : this.curLine.num - 1

        return [s, lineStart, lineEnd, c]
    }

}
