import * as ptss from '../ptss/index.js'

export function newKitchenSinkStylesheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('PTSS Kitchen Sink Demonstration')

    //todo[spec] just copied CommonMark's.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    const setext = new ptss.ChunkStyle('Setext Section Heading', stylesheet)
    setext.irNode.type = new ptss.Expression('section')
    setext.tailPattern = /((=+|-+)[ \t]*$)/ym
    setext.maxLines = 5
    setext.indent = 'optional'
    setext.canInterruptParagraph = false
    setext.irNode.level = new ptss.Expression(['map', ['at', ['tail', 2], 0], {'=': 1, '-': 2}])
    setext.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['body']]])

    const pipeTable = new ptss.TableStyle('Pipe Table SV', stylesheet)
    pipeTable.tableType = 'sv'
    pipeTable.indent = 'optional'
    pipeTable.heading = 'delimited, optional'
    pipeTable.headingDelim = '-'
    pipeTable.delimIntersection = '|+'
    pipeTable.sideBorder = '|'
    pipeTable.sideBorderLeftRule = 'optional'
    pipeTable.sideBorderRightRule = 'match'
    pipeTable.columnsDelim = '|'
    pipeTable.columnsNumber = 'per head delim'
    pipeTable.columnsAlignIndicator = ':'

    const list = new ptss.ListStyle('List', stylesheet)
    list.unorderedMarkers = '-*+'
    list.orderedMarkers = '.)'
    list.indent = 'optional'

    const fencedCode = new ptss.ContainerStyle('Backtick Fenced Code Block', stylesheet)
    fencedCode.irNode.type = new ptss.Expression('code_block')
    fencedCode.indent = 'optional'
    fencedCode.topBorder = '`'
    fencedCode.topBorderMinLength = 3
    fencedCode.righter = /([^`]*?)$/ym
    fencedCode.body = 'RAW_NL'
    fencedCode.canInterruptParagraph = true
    fencedCode.irNode.info = new ptss.Expression(['unescape', ['trim', ['righter', 0]]])

    const emailBlockQuote = new ptss.ContainerStyle('Email-Style Block Quote', stylesheet)
    emailBlockQuote.irNode.type = new ptss.Expression('block_quote')
    emailBlockQuote.leftBorder = '>'
    emailBlockQuote.leftPadding = 'per first line'
    emailBlockQuote.indent = 'optional'
    emailBlockQuote.canInterruptParagraph = true

    // We are using "Markdown Mode" indentation logic to implement this in
    // Kitchen-Sink, simply so we can demonstrate its use and the nature of
    // *magical indentation thresholds*. In this case, indents of 2-3 map
    // to verse, while those above 4 become block quotes.

    const indentedBlockquote = new ptss.ContainerStyle('Indented Block Quotation', stylesheet)
    indentedBlockquote.irNode.type = new ptss.Expression('block_quote')
    indentedBlockquote.indent = 'markdown(4,999)'

    // this style has to come LAST because we are using Markdown Mode indentation
    // (to implement a *magical indentation thresholds*, see above) at 4 spaces,
    // and even though this has an indent setting of `markdown,2,3`, the `3`
    // only limits the indentation assigned to the container. It will still
    // match and consume lines with more than `3` leading spaces, assigning
    // all the excess to the content.
    const verse = new ptss.ContainerStyle('Verse', stylesheet)
    verse.irNode.type = new ptss.Expression('verse')
    verse.canInterruptParagraph = false
    verse.indent = 'markdown(2,3)'
    verse.body = 'RAW_NL'

    stylesheet.blockStyles.push(
        setext,
        pipeTable,
        list,
        fencedCode,
        emailBlockQuote,
        indentedBlockquote,
        verse
    )

    stylesheet.inlineStyles = [
        'autolink',
        'backticks',
        'emphasis',
        'entity',
        'escape',
        'html_inline',
        'image',
        'reference',
        'newline',
        'html'
    ]

    return stylesheet
}
