import {BaseStyleType, ElementalBlockStyle, BlockStyleType, ParseEffects, RE_MARKDOWN_INDENT} from './elementalBlockStyle.js'
import {Block} from './block.js'
import {Expression, INLINE_PARSE_FLAG} from './expression.js'
import type {Stylesheet} from './stylesheet.js'
import * as ir from '../../stir/index.js'
import type {Line} from '../line.js'
import {stringifyTruncate, stringifyReplacerForDebugLogging} from '../debugUtil.js'


export class TableStyle implements ElementalBlockStyle {
    name: string
    baseType: BlockStyleType = BaseStyleType.Table

    public indent: 'yes' | 'no' | 'optional' = 'no'

    public tableType: 'sv' | 'ag' = 'ag'

    ///// SV table structure (TBD some fields may be shared with AG table) /////
    //todo![spec] consider making this SVTableStyle.ts, sep from AGTableStyle.ts,
    //    both a subclass of TableStyle.
    //    This is an internal breakdown, doesn't impact PTSS style settings,
    //    as the tableType value can decide which class to instantiate.

    //todo![spec, arch] possible AGTablesStyle.ts strategy:
    //  - See notes under PTSS-REF/tables_AG.html#cell-content-model
    //  - If the AG Table cell spans multiple lines, collect it into
    //    a single `\n` delimited string.
    //  - Similarly to how we tag the SV table cell content with `INLINE_PARSE_FLAG`,
    //    tag the AG table cell with `NESTED_BLOCK_SEQUENCE_PARSE_FLAG`. Later
    //    we will walk the tree and for each cell encountered with this flag we
    //    will call textplain.Parser.parse with the cell as the ir.block serving
    //    as the root container for that call.
    //  - We *could* do away with the deferred parsing for both inline and nexted
    //    block parsing simply by every place where we set one of the above flags
    //    just make the inline or block parse calls right then and there.

    public heading: 'none' | 'first row' | 'delimited, optional' | 'delimited, required' = 'none'
    public headingDelim?: string //e.g. '-'
    public delimIntersection?: string // e.g. '|-+', defaults to columnsDelim

    // todo![spec]: consistent border prop semantics with containerStyle?

    // if non-null, the top border will be required
    public topBorder?: string

    // bottomBorder:
    // - null means: *there is no bottom border*.
    // - undefined means: must match top border
    // - otherwise an string representing the set of characters allowed for the border.
    public bottomBorder: string | null | undefined = undefined

    public sideBorder?: string //e.g. '|'
    public sideBorderLeftRule: 'always' | 'never' | 'optional' | 'lazy' = 'optional'
    public sideBorderRightRule: 'match' | 'never' | 'lazy' = 'match'

    public columnsDelim?: string //e.g. '|'
    public columnsNumber: 'per head delim' | 'per first row' | 'per widest row' = 'per head delim'
    public columnsAlignIndicator?: string //e.g. ':'

    public bodyRowAtLeastOneMarker: 'yes' | 'no' = 'yes'
    public bodyRowTooShort: 'as-is' | 'add-empty' /* | 'span last cell' */ = 'as-is'
    public bodyRowTooLong: 'as-is' | 'truncate' /* | 'merge last cell' */ = 'as-is'

    public cell: Expression | undefined

    // if given, allows field values to be wrapped by the given pair of chars.
    // When so wrapped, any special chars need not be escaped. If more than one
    // quote char pair is given, use of one can allow the unescaped use of the
    // other(s). For example, if:
    //      columnsDelim  ,
    //      escapeChar    /
    //      quoteChars    ""''⎡⎦
    // all of the following are equivalent:
    //      'She said, "Hi!"'
    //      ⎡She said, "Hi!"⎦
    //      She said\, \"Hi!\"
    // todo[spec] review"
    //   https://en.wikipedia.org/wiki/Comma-separated_values
    //   https://en.wikipedia.org/wiki/Delimiter-separated_values
    //   https://en.wikipedia.org/wiki/Tab-separated_values
    public quoteCharPairs?: string

    // todo![spec] to allow a stylesheet to achieve
    //  backward GFM compatibility.
    public gfmQuirks = false

    canInterruptParagraph = true
    canInterruptLazy = true

    interruptibleBy: ElementalBlockStyle[] = []

    readonly consumesBackChunk = false

    styleSheet: Stylesheet

    // property expressions
    private _irNode?: { [key: string]: Expression }
    private _ptNode?: { [key: string]: Expression }

    //todo![spec] start with ^ ???

    // instance copies so parse methods are reentrant
    //   for parallel executing parser instances.
    private RE_H_DELIM = new RegExp('')
    private RE_INTERSECTION = new RegExp('')
    //todo![spec] use style defined chars
    //todo![spec] support different sep and right border
    private RE_DATA = /(?:(?:(?:\\\|)|[^|\n])*)/y


    constructor (name: string, styleSheet: Stylesheet) {
        this.name = name
        this.styleSheet = styleSheet
    }

    get irNode (): { [p: string]: Expression } {
        if (this._irNode === undefined) {
            this._irNode = {}
        }
        return this._irNode
    }

    get ptNode (): { [p: string]: Expression } {
        if (this._ptNode === undefined) {
            this._ptNode = {}
        }
        return this._ptNode
    }

    initAndValidate (): void {
        // todo![adjacency] construction of this filter would be moved to
        //   adjacency control logic?
        this.interruptibleBy =
            this.styleSheet.blockStyles
                .filter(s => s.baseType !== BaseStyleType.Table)

        const match = RE_MARKDOWN_INDENT.exec(this.indent)
        if (match) {
            throw new Error(`markdown mode indent is not supported for tables`)
        }
        if (this.tableType === 'sv') {
            if (this.heading.startsWith('delimited')) {
                if (this.headingDelim === undefined) {
                    throw new Error(`headingDelim must be specified for heading type ${this.headingDelim}`)
                }
                if (this.columnsDelim === undefined) {
                    throw new Error(`columnsDelim must be specified`)
                }

                let delimPattern = '[ \\t]*'
                if (this.columnsAlignIndicator) {
                    delimPattern += `(${this.columnsAlignIndicator})?`
                }
                // todo[test] need to escape some headingDelim choices, and add test cases accordingly
                delimPattern += this.headingDelim + '+'
                if (this.columnsAlignIndicator) {
                    delimPattern += `(${this.columnsAlignIndicator})?`
                }
                delimPattern += '[ \\t]*'
                this.RE_H_DELIM = RegExp(delimPattern, 'y')

                let intersectionPattern = ''
                if (this.delimIntersection === undefined) {
                    intersectionPattern = this.columnsDelim
                } else if (this.delimIntersection.length === 1) {
                    intersectionPattern = this.delimIntersection
                } else {
                    intersectionPattern = '[' + this.delimIntersection + ']'
                }
                this.RE_INTERSECTION = new RegExp(intersectionPattern, 'y')
            } else {
                throw new Error(`table heading type "${this.heading}" not yet implemented`)
            }
            if (this.sideBorderLeftRule !== 'never' && !this.sideBorder) {
                throw new Error(`side border is"${this.sideBorderLeftRule}" but sideBorder char(s) is not specified`)
            }
        } else {
            throw new Error('AG tables not yet implemented')
        }

        if (this.topBorder || this.bottomBorder) {
            throw new Error('table top/bottom border not yet implemented')
        }
    }

    parse (firstLine: Line, fromBackLine: Line | null, lookahead = false): ParseEffects | null {
        const src = firstLine.src

        // uncomment and use this line to trigger the debugger at a certain line
        //if (firstLine.srcLine().startsWith('   ```\n') debugger

        const delimLine = firstLine.nextInContainer
        if (delimLine === null || delimLine.remainingIsBlank()) {
            return null
        }

        // indent will be zero if there is slop (which would be already parsed out)
        const tableIndent = firstLine.parseSpaces()
        if (tableIndent === 0 && this.indent === 'yes') {
            return null
        }
        if (tableIndent > 0 && this.indent === 'no') {
            return null
        }

        const startOffset = firstLine.ptr
        let lineIndent: number
        let lineHasMarker = false

        //! //////////    delimLine    //////////////
        //  first fully parse delimLine (quicker fail-fast)
        // todo![spec] can we replace most of this with a style-custom Regex?
        //   finish this, then try on another branch to compare speed.

        // todo![spec] ???? remove all the pre-recognition debug logging ????
        console.debug(`parse SV Table: ${this.name}`)
        console.group()
        console.debug('====== head delim')
        console.debug(delimLine.num, stringifyTruncate(delimLine.srcLine()))
        console.debug(delimLine.toString())
        // todo![perf] we've optimized parsing of slop by making the call for
        //  the cur line once in BlockTreeParser. But now we are undermining
        //  that by parsing slop in this forward parse, and then throwing that
        //  away if it turns out not to be a table (which it will MOST OF THE
        //  TIME.
        //  .
        //  It's even worse for parsing Indentation, as we don't have that
        //  optimized at all, and it's done once per line per indent-supporting
        //  style, + one more time per line when we forward parse the delim
        //  row here.
        // todo![fwd-parsing] to-do in ChunkStyle.ts discusses this.
        const delimCheckpoint = delimLine.checkpoint()
        delimLine.parseSlop(this.styleSheet.sloppyMargin, true)
        lineIndent = delimLine.parseSpaces(tableIndent)
        if (lineIndent != tableIndent) {
            delimCheckpoint.restore()
            console.debug('ABORT: delim line mismatch')
            console.groupEnd()
            return null
        }
        let leftBorder: 'yes' | 'no' | 'lazy' = 'no'
        let rightBorder: 'yes' | 'no' | 'lazy' = 'no'
        if (this.sideBorderLeftRule !== 'never') {
            if (src.charAt(delimLine.ptr) === this.sideBorder) {
                delimLine.ptr += 1
                leftBorder = this.sideBorderLeftRule !== 'lazy' ? 'yes' : 'lazy'
                lineHasMarker = true
            } else {
                switch (this.sideBorderLeftRule) {
                    case 'always':
                        delimCheckpoint.restore()
                        console.debug('ABORT: delim line mismatch')
                        console.groupEnd()
                        return null
                    case 'optional':
                        leftBorder = 'no'
                        break
                    case 'lazy':
                        leftBorder = 'lazy'
                }
            }
        }
        if (this.interruption(delimLine, lineHasMarker)) {
            delimCheckpoint.restore()
            console.debug('ABORT: delim line interruption')
            console.groupEnd()
            return null
        }
        if (this.sideBorderRightRule !== 'never') {
            if (this.sideBorderRightRule === 'match') {
                rightBorder = leftBorder
            } else {
                rightBorder = 'lazy'
            }
        }
        console.debug(`side border: ${this.sideBorder}, left=${leftBorder}, right=${rightBorder}`)
        this.RE_H_DELIM.lastIndex = delimLine.ptr
        let match = this.RE_H_DELIM.exec(src)
        console.debug('col 1 match:', JSON.stringify(match), this.RE_H_DELIM.lastIndex)
        if (match === null) {
            delimCheckpoint.restore()
            console.debug('ABORT: delim line mismatch')
            console.groupEnd()
            return null
        }
        const cols: ir.ColumnMeta[] = [match[1] === undefined ?
            (match[2] === undefined ? {align: ''} : {align: 'R'}) :
            (match[2] === undefined ? {align: 'L'} : {align: 'C'})]
        delimLine.ptr = this.RE_H_DELIM.lastIndex
        if (!lineHasMarker && delimLine.ptr === delimLine.EOL) {
            delimCheckpoint.restore()
            console.debug('ABORT: delim line mismatch')
            console.groupEnd()
            return null
        }
        // parse rest of line, build cols data
        while (delimLine.ptr !== delimLine.EOL) {
            if (src.charAt(delimLine.ptr) === this.sideBorder
                && rightBorder !== 'no'
                && delimLine.ptr + 1 === delimLine.EOC) {
                //todo![spec] we have ditched needing to move pointer
                // to EOL artificially to mark a line as resolved.
                // delimLine.ptr++ should be enough as it consumes
                // the just detected closing side border, yet replacing
                // this with delimLine.ptr++ fails some tests. Why?
                delimLine.ptr = delimLine.EOL
                break
            }
            this.RE_INTERSECTION.lastIndex = delimLine.ptr
            if (this.RE_INTERSECTION.exec(src)) {
                this.RE_H_DELIM.lastIndex = delimLine.ptr + 1
                match = this.RE_H_DELIM.exec(src)
                console.debug(`col ${cols.length + 1} match:`, JSON.stringify(match), this.RE_H_DELIM.lastIndex)
                if (match === null) {
                    // missing delim segment
                    delimCheckpoint.restore()
                    console.debug('ABORT: delim line mismatch')
                    console.groupEnd()
                    return null
                }
                cols.push(match[1] === undefined ?
                    (match[2] === undefined ? {align: ''} : {align: 'R'}) :
                    (match[2] === undefined ? {align: 'L'} : {align: 'C'}))
                delimLine.ptr = this.RE_H_DELIM.lastIndex
            } else {
                // unexpected char
                console.debug(`unexpected char ${delimLine.ptr}: ${(src.charAt(delimLine.ptr))}`)
                delimCheckpoint.restore()
                console.debug('ABORT: delim line mismatch')
                console.groupEnd()
                return null
            }
        }
        console.debug(`cols: ${JSON.stringify(cols)}`)
        // didn't consume line because the remainder of the line didn't match
        if (delimLine.ptr !== delimLine.EOL) {
            delimCheckpoint.restore()
            console.debug('ABORT: delim line mismatch')
            console.groupEnd()
            return null
        }
        //! /////// end parse delimiter line ////////

        const irBlock = new ir.Block('table')

        //////// parse header line
        console.debug('====== headings')
        const header = this.parseData(src, firstLine, leftBorder, rightBorder, irBlock)
        if (header === null) {
            delimCheckpoint.restore()
            console.debug('ABORT: header mismatch')
            console.groupEnd()
            return null
        }
        // per GFM rules
        if (header.length !== cols.length) {
            delimCheckpoint.restore()
            console.debug('ABORT: header mismatch')
            console.groupEnd()
            return null
        }

        // ==== MATCH CERTAINTY =====
        if (lookahead) {
            delimCheckpoint.restore()
            console.groupEnd()
            return {}
        }

        //////// parse body rows
        console.debug('====== body')
        let endLine = delimLine
        const rows: ir.Row[] = []
        for (let line = delimLine.nextInContainer;
             line !== null && !line.remainingIsBlank();
             line = line.nextInContainer) {
            const checkpoint = line.checkpoint()
            // todo![perf] see comments in the delimLine parsing section
            //   about how we're undermining our optimization of slop
            //   parsing and that we lack indent parsing optimization at all
            line.parseSlop(this.styleSheet.sloppyMargin, true)
            lineIndent = line.parseSpaces(tableIndent)
            if (lineIndent != tableIndent) {
                checkpoint.restore()
                break
            }
            let row = this.parseData(src, line, leftBorder, rightBorder, irBlock)
            if (row === null) {
                checkpoint.restore()
                break
            }
            if (row.length < cols.length) {
                if (this.bodyRowTooShort === 'add-empty') {
                    while (row.length < cols.length) {
                        // todo![spec] remove this hack
                        //   eslint-disable-next-line @typescript-eslint/ban-ts-comment
                        //   @ts-ignore
                        row.push({type: 'cell', body: '', colSpan: 1, rowSpan: 1, parent: irBlock})
                    }
                }
            } else if (row.length > cols.length) {
                if (this.bodyRowTooLong === 'truncate') {
                    row = row.slice(0, cols.length)
                }
            }
            rows.push(row)
            endLine = line
        }


        const ptTable = new Block(this)
        ptTable.style = this
        ptTable.lineStart = firstLine.num
        ptTable.lineEnd = endLine.num
        ptTable.indent = tableIndent

        irBlock.head = [header]
        irBlock.body = rows
        irBlock.tableMeta = { //todo![spec] if there is no meta, don't set these fields
            head: cols,
            body: cols
        }
        irBlock.source = ptTable

        console.groupEnd()
        return {irBlock: irBlock, ptNamedValue: undefined, endLine: endLine}
    }

    parseData (src: string,
               line: Line,
               leftBorder: 'yes' | 'no' | 'lazy',
               rightBorder: 'yes' | 'no' | 'lazy',
               irBlock: ir.Block): ir.Row | null {
        console.debug(line.num, stringifyTruncate(line.srcLine()))
        console.group()
        console.debug(line.toString())

        let lineHasMarker = false

        if (leftBorder !== 'no') {
            if (src.charAt(line.ptr) === this.sideBorder) {
                line.ptr += 1
                lineHasMarker = true
            } else if (leftBorder === 'yes') {
                console.groupEnd()
                return null
            }
        }

        if (this.interruption(line, lineHasMarker)) {
            console.groupEnd()
            return null
        }

        const row: ir.Row = []
        let match: RegExpExecArray
        while (!line.remainingIsBlank()) {
            this.RE_DATA.lastIndex = line.ptr
            //! type assertion valid given the regex pattern can match empty string
            match = this.RE_DATA.exec(src) as RegExpExecArray
            // todo![spec] use the stylesheet defined escape char, tableStyle defined quote chars, etc.
            // todo![test] add tests for the various ways to escape/quote values
            const fieldVal = match[0].replace(/\\\|/g, '|')

            // This is an attempt to generalize Gracedown's needed special handling
            // of table cells. `cell: Expression` was added to TableStyle and
            // new functions were added to Expressions *just* to support this.
            //
            // It works like this:
            //  1. The default is that the source cell content will be treated
            //     as inline content and parsed accordingly. The table style
            //     definition doesn't need to specify anything. The default
            //     table declaration is simple.
            //  2. The style declaration can OPTIONALLY define an expression
            //     that will be applied to the content of each source table cell
            //     (just as one can define an arbitrary expression that is
            //     applied to the body or title of a source element).
            //
            // todo![content model, spec] Is this good for the spec, or is it a
            //   hack that needs a different solution? Is it a generalization
            //   that will have uses for others, or will it only ever get used
            //   for Gracedown, and thus putting the lie to "generalization"?
            //   Even so, if it is elegant and cost-free, keep it anyway?
            if (this.cell === undefined) {
                //! for SV table, cell bodies are always an InlineSequence
                row.push({
                        type: 'cell',
                        body: [INLINE_PARSE_FLAG, fieldVal],
                        colSpan: 1,
                        rowSpan: 1,
                        parent: irBlock
                    } as ir.Cell
                )
            } else {
                console.debug(`EVALUATING EXPRESSION: cell <-- ${this.cell.toString()}`)
                console.group()
                const cellBody = this.cell.evaluate(null, null, [fieldVal], null, null)
                console.groupEnd()
                if (cellBody instanceof ir.Block) {
                    if (cellBody.type === 'cell') {
                        if (cellBody.body?.length === 1
                            && cellBody.body[0] instanceof ir.Block
                            && cellBody.body[0].type === 'paragraph') {
                            row.push({
                                    type: 'cell',
                                    body: cellBody.body[0].body as ir.InlineSequence,
                                    colSpan: 1,
                                    rowSpan: 1,
                                    parent: irBlock
                                } as ir.Cell
                            )
                        } else {
                            row.push({
                                    ...(cellBody as ir.Block),
                                    colSpan: 1,
                                    rowSpan: 1,
                                    parent: irBlock
                                } as ir.Cell
                            )
                        }
                    } else {
                        // todo[spec] punt: we probably want to be able to handle
                        //  anything reasonable returned by `this.cell.evaluate`
                        throw Error('not yet thought through')
                    }
                } else { //it's a BlockSequence or InlineSequence
                    row.push({
                            type: 'cell',
                            body: cellBody,
                            colSpan: 1,
                            rowSpan: 1,
                            parent: irBlock
                        } as ir.Cell
                    )
                }
            }

            line.ptr = this.RE_DATA.lastIndex

            if (line.ptr === line.EOL) {
                if (rightBorder === 'yes') {
                    console.groupEnd()
                    return null
                }
                break
            }
            lineHasMarker = true
            if (src.charAt(line.ptr) !== this.columnsDelim) {
                //! given the match pattern, the ch MUST be a right border
                if (rightBorder === 'no') {
                    console.groupEnd()
                    return null
                }
                line.ptr += 1
                break
            }
            line.ptr += 1

            //! handle columnDelim/sideBorder ambiguity
            if (this.columnsDelim === this.sideBorder
                && rightBorder !== 'no'
                && line.remainingIsBlank()) {
                // what we assumed was a columnDelim is actually a right border
                break
            }
        }

        if (!lineHasMarker && this.bodyRowAtLeastOneMarker === 'yes') {
            console.groupEnd()
            return null
        }

        console.debug(`new row: ${JSON.stringify(row, stringifyReplacerForDebugLogging())}`)
        console.groupEnd()
        return row
    }

    interruption (line: Line, lineHasMarker: boolean) {
        //! we only need to check for gfmQuirks table interruption
        //  when there is no left border
        if (!lineHasMarker && this.gfmQuirks) {
            return line.checkStyleMatch(this.interruptibleBy)
        } else {
            return false
        }
    }

}

