/*
See also:

by magic bytes:
 - https://www.npmjs.com/package/mmmagic
 - https://www.npmjs.com/package/magic-bytes.js
 - https://www.npmjs.com/package/buffer-signature

by file ext:
 0 https://www.npmjs.com/package/mimer
 - https://www.npmjs.com/package/mrmime

mimetype info and parsing
 - https://www.npmjs.com/package/whatwg-mimetype
 - https://www.npmjs.com/package/@contentful/mimetype

 */


export type Type
    = 'text'
    | 'media'
    | 'resource'
    | 'unknown'

export type Role
    = 'content'
    | 'resource'
    | 'assembly meta'
    | 'unknown'

export type FileType = {
    name: string
    type: Type
    role: Role
    mimetype: string | undefined
    userDefined: boolean
}

const extMap: Record<string, FileType> = {}

function registerFileType (name: string, type: Type, role: Role, mimetype: string | undefined, userDefined: boolean, ...extensions: string[]) {
    extensions.forEach(ext => {
        ext = ext.toLowerCase()
        if (extMap[ext] !== undefined) {
            throw new Error(`duplicate filetype registration for extension: ${ext}`)
        }
        extMap[ext] = {name, type, role, userDefined, mimetype}
    })
}

function registerBuiltinFileType (name: string, type: Type, role: Role, mimetype: string | undefined, ...extensions: string[]) {
    registerFileType(name, type, role, mimetype, false, ...extensions)
}

export function registerUserFileType (name: string, type: Type, role: Role, mimetype: string | undefined, ...extensions: string[]) {
    registerFileType(name, type, role, mimetype, true, ...extensions)
}


const UNKNOWN: FileType = {name: 'unknown', type: 'unknown', role: 'unknown', userDefined: false, mimetype: undefined}

function ext (f: string): string {
    // https://stackoverflow.com/a/12900504/8910547
    return f.slice((f.lastIndexOf('.') - 1 >>> 0) + 2)
}

export function fileTypeByFilename (name: string): FileType {
    const e = ext(name)
    return extMap[e] || extMap['$' + name] || UNKNOWN
}


// text
registerBuiltinFileType('HTML', 'text', 'content', 'text/html', 'html', 'htm')
registerBuiltinFileType('MARKDOWN', 'text', 'content', 'text/markdown', 'md', 'markdown')
registerBuiltinFileType('STIR (JSON)', 'text', 'content', 'text/json+stir', 'stir')
registerBuiltinFileType('TEXT', 'text', 'content', 'text/plain', 'txt')
// todo![renderAssembly] should we register the extension '$[[GENERATED]]' for registerBuiltinFileType('STIR', 'text', 'content', undefined, '$[[GENERATED]]')

// media
registerBuiltinFileType('JPEG', 'media', 'content', 'image/jpeg', 'jpeg', 'jpg')
registerBuiltinFileType('PNG', 'media', 'content', 'image/png', 'png')
registerBuiltinFileType('GIF', 'media', 'content', 'image/gif', 'gif')
registerBuiltinFileType('SVG', 'media', 'content', 'image/svg+xml', 'svg')

// resources
registerBuiltinFileType('CSS', 'resource', 'resource', 'text/css', 'css')
registerBuiltinFileType('ICO', 'resource', 'resource', undefined, 'ico')
registerBuiltinFileType('JS', 'resource', 'resource', 'text/javascript', 'js')

// text assembly meta
registerBuiltinFileType('TextAssembly sort spec', 'resource', 'assembly meta', undefined, '$sort')
