import * as ptss from '../ptss/index.js'

/**
 * todo![functional test] I think the premises below
 *   are accurate anymore. Nail the premise for this
 *   test style and then rename it, as I have done for
 * 
 * style sheet premises
 * ====================
 *  Indentation and basic structure:
 *   - NO SLOPPINESS
 *   - 0-3 space indent is ignored (Writer Prerogative)
 *     - But both containers and multiline chunks must
 *       still have proper left alignment. Differences
 *       in alignment are interpreted as block boundaries.
 *   - 4 spaces triggers an indented block
 *   - 8 spaces triggers an indented code block
 *   - But headings can be indented as much as you want.
 *     - one consequence of this is that headings can't
 *       be the first element in an indented block. But if
 *       you look at the plain text, such an interpretation
 *       makes perfect sense. Indented blocks ARE NOT
 *       intended to be perfect recursive nesting structures,
 *       meaning "any valid root content is also valid
 *       nested content if indented". They are intended for
 *       simple quotations, or other bits of content meant
 *       that the main flow "is talking about" or refers to.
 *     - Another still, utilizing borders, is available for
 *       such open-ended generalized nesting.
 *  Headings:
 *   - Indent as much as you want, center even. See above.
 */
export function newTestPtssOneSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = new ptss.Stylesheet('PTSS Test ONE Stylesheet')

    // test default defaults
    // ptss.lazyContinuation = false
    // ptss.sloppyMargin = 0
    // ptss.sloppyPadding = false

    //todo[spec] just copied CommonMark's. Haven't even thought about natural escapes.
    stylesheet.escapeSyntax.escapeChar = '\\'
    stylesheet.escapeSyntax.escapeLiterals = '!"#$%&\'()*+,-./:;<=>?@[\\]^_`{|}~'

    // this style shows a few things:
    // - a chunk style with both head and tails that are full lines
    // - the definition of an overall style for headings is divvied up
    //   between two lower level styles.
    //todo[spec] need to assert that the tail chars match the head chars?
    const setextOneTwo = new ptss.ChunkStyle('Level 1/2 Setext Heading', stylesheet)
    setextOneTwo.indent = 'optional'
    setextOneTwo.headPattern = /(=+|-+)[ \t]*$/ym
    setextOneTwo.tailPattern = /(=+|-+)[ \t]*$/ym
    setextOneTwo.canInterruptParagraph = false
    setextOneTwo.maxLines = 6
    setextOneTwo.irNode.type = new ptss.Expression('heading')
    setextOneTwo.irNode.level = new ptss.Expression(['map', ['at', ['head', 1], 0], {'=': 1, '-': 2}])
    setextOneTwo.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body']]]) //todo[perf] trim may be redundant with latter handling. clean up.

    const setextThreeFour = new ptss.ChunkStyle('Level 3/4 Setext Heading', stylesheet)
    setextThreeFour.indent = 'optional'
    setextThreeFour.tailPattern = /(=+|-+)[ \t]*$/ym
    setextThreeFour.canInterruptParagraph = false
    setextThreeFour.maxLines = 5
    setextThreeFour.irNode.type = new ptss.Expression('heading')
    setextThreeFour.irNode.level = new ptss.Expression(['map', ['at', ['tail', 1], 0], {'=': 3, '-': 4}])
    setextThreeFour.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body']]]) //todo[perf] trim may be redundant with latter handling. clean up.

    // note how if it were not for precedence, this style would consume the
    // lines that otherwise belong to Setext headings and the Asterisk Box Quote
    const thematicBreak = new ptss.ChunkStyle('Thematic Break', stylesheet)
    thematicBreak.indent = 'optional'
    thematicBreak.headPattern = /(?:(?:\*[ \t]*){3,}|(?:_[ \t]*){3,}|(?:-[ \t]*){3,})[ \t]*$/ym
    thematicBreak.maxLines = 1
    thematicBreak.canInterruptParagraph = true
    thematicBreak.irNode.type = new ptss.Expression('thematic_break')

    const warningLine = new ptss.ChunkStyle('Warning Line', stylesheet)
    warningLine.indent = 'yes'
    warningLine.headPattern = /Warning:\s+/y
    warningLine.maxLines = 1
    warningLine.canInterruptParagraph = false
    warningLine.irNode.type = new ptss.Expression('interjection')
    warningLine.irNode.subType = new ptss.Expression('warning')
    //todo[spec] should this be the default, i.e. if content defaults to body?
    warningLine.irNode.body = new ptss.Expression(['parseInlines', ['body']])

    // todo[spec] While in this case a multiline head+tail
    //   chunk makes total sense, There may be a legitimate
    //   case for allowing maxLines = 1; but the current
    //   code disallows it. REVIEW.
    // todo[spec] This is a great example of a use case for
    //   supporting more than just `content` for blockStyles,
    //   but things like `title`. See the test case for this
    //   style. One should be able to prefix the content with
    //   "Note:", "Special note:", "Announcement:", etc
    //   without having to hard code new styles for each case.
    //   The value should just go into a title element.
    const note = new ptss.ChunkStyle('Note Chunk', stylesheet)
    note.headPattern = /^>>\s+/ym
    note.tailPattern = /\s+<<\s*$/gm
    note.canInterruptParagraph = false
    note.irNode.type = new ptss.Expression('interjection')
    note.irNode.subType = new ptss.Expression('info')
    note.irNode.body = new ptss.Expression(['parseInlines', ['body']])

    // this style intentionally uses `+` to test precedence. `+` is also:
    //  - a bullet char for unordered lists
    //  - the topBorder char for Fenced(+/) Block Quote
    const leftBorderBQ = new ptss.ContainerStyle('Left(+) Border Block Quote', stylesheet)
    leftBorderBQ.irNode.type = new ptss.Expression('block_quote')
    //todo[spec] add '>' so I can make tests to compare this stylesheet with CommonMark?
    //todo[spec] add '>' to test and demonstrate how precedence resolves ambiguity between
    //  this style and Note Chunk.
    leftBorderBQ.leftBorder = '+'
    leftBorderBQ.leftPadding = 'per first line'
    leftBorderBQ.indent = 'optional'

    const fencedCode = new ptss.ContainerStyle('Fenced($) Code Block', stylesheet)
    fencedCode.irNode.type = new ptss.Expression('code_block')
    fencedCode.topBorder = '$'
    //fencedBQ.topBorderMinLength = 13
    fencedCode.indent = 'optional'
    fencedCode.body = 'RAW_NL'
    fencedCode.canInterruptParagraph = true

    const leftBorderCode = new ptss.ContainerStyle('Left(|) Border Code Block', stylesheet)
    leftBorderCode.irNode.type = new ptss.Expression('code_block')
    leftBorderCode.leftBorder = '|'
    leftBorderCode.leftPadding = 'per first line'
    leftBorderCode.indent = 'optional'
    leftBorderCode.body = 'RAW_NL'

    // while this style isn't good stylesheet design, it is here to demonstrate
    // indentation behavior under many different contexts. See the test case "container: three different indents"
    const indentedCode = new ptss.ContainerStyle('Indent(8) Code Block', stylesheet)
    indentedCode.irNode.type = new ptss.Expression('code_block')
    indentedCode.indent = 'markdown(8,8)'
    indentedCode.body = 'RAW_NL'

    // exists to test proper interpretation of `+`, which is used by three different styles.
    const fencedBQ = new ptss.ContainerStyle('Fenced(+/) Block Quote', stylesheet)
    fencedBQ.irNode.type = new ptss.Expression('block_quote')
    fencedBQ.topBorder = '+/'
    //fencedBQ.topBorderMinLength = 13
    fencedBQ.indent = 'optional'
    fencedBQ.canInterruptParagraph = true

    // this style exists to show how precedence works. In this case a line of `*` which would
    // otherwise be interpreted as a thematic break instead gets interpreted as a top border
    // of this style.
    const boxAsteriskBQ = new ptss.ContainerStyle('Asterisk Box Quote', stylesheet)
    boxAsteriskBQ.irNode.type = new ptss.Expression('block_quote')
    boxAsteriskBQ.topBorder = '*'
    //boxBQ.topBorderMinLength = 13
    boxAsteriskBQ.leftBorder = '*'
    boxAsteriskBQ.leftPadding = 'per first line'
    boxAsteriskBQ.indent = 'optional'
    boxAsteriskBQ.canInterruptParagraph = true

    const boxAsciiLineBQ = new ptss.ContainerStyle('Box(–|) Block Quote', stylesheet)
    boxAsciiLineBQ.irNode.type = new ptss.Expression('block_quote')
    boxAsciiLineBQ.topBorder = '–'
    //boxBQ.topBorderMinLength = 13
    boxAsciiLineBQ.leftBorder = '|'
    boxAsciiLineBQ.leftPadding = 'per first line'
    boxAsciiLineBQ.indent = 'optional'
    boxAsciiLineBQ.canInterruptParagraph = true

    const list = new ptss.ListStyle('List', stylesheet)
    list.unorderedMarkers = '-*+'
    list.orderedMarkers = '.)'

    // todo[spec]  should we have something fall through to this in tests to test the concept?
    //   OR should a test for that go in another simpler text ptss?
    // this represents the built-in default indentation container.
    const defaultIndentContainer = new ptss.ContainerStyle('Default Indent Block', stylesheet)
    defaultIndentContainer.irNode.type = new ptss.Expression('block_quote')
    defaultIndentContainer.indent = 'yes'

    stylesheet.blockStyles.push(
        setextOneTwo, // must be above setextThreeFour which would otherwise match with same bottom border
        setextThreeFour,
        boxAsteriskBQ, // requires precedence over thematicBreak
        thematicBreak,
        warningLine,
        note,
        boxAsciiLineBQ,
        fencedBQ,
        leftBorderBQ,
        fencedCode,
        leftBorderCode,
        indentedCode,
        list,
        defaultIndentContainer)

    stylesheet.inlineStyles = [
        'autolink',
        'backticks',
        'emphasis',
        'entity',
        'escape',
        'html_inline',
        'image',
        'reference',
        'newline',
        'html'
    ]

    return stylesheet
}
