import type {Pipe, PipeProvider} from '../pipe.js'
import {registerPipe} from '../registry.js'
import type {Config} from '../config.js'
import {simplePipelineFromConfigFile} from '../pipeline.js'
import * as ta from '../textAssembly/index.js'
import * as ir from '../../stir/index.js'
import type {STIR, TunnelingSTIR} from '../stir.js'
import htmlnorm from 'htmlnorm'

import JSON5 from 'json5'


type CaseBlockRenderingBranch = ta.Folder & {
    renderCaseBlocks: (caseFileRoot: ir.Block) => number
}

// todo[api] THIS TRANSFORM DOESN'T BELONG AS A BUILT-IN
// - it is specific to case-file.ptss, tightly coupled to it
//
// IDEALLY:
// - it should be colocated with case-file.ptss

/**
 * Renderers and inserts output for all cases that at the root of the document
 * or any of its sections and subsections. Cases nested in other elements are
 * skipped.
 */
const provider: PipeProvider = {

    async configure (config?: Config): Promise<Pipe> {
        if (config === undefined) {
            throw new Error('missing RenderCaseBlockTransform configuration')
        }
        const textAssembly = config.textAssembly()
        if (textAssembly === undefined) {
            //todo: CBRB (this Pipe class) COULD work in a non TextAssembly context.
            // 🌈 See how stir2template.ts does it. 🌈
            // The CBRB config would determine which form it takes. For example:
            //  1. support either of the following settings:
            //            pipelineConfigName
            //            pipelineConfigPath
            //  2. The former implies a TextAssembly context, within
            //     which it will search for files of the specified *name* and
            //     instantiate Textplain render pipelines using the config in
            //     the found files for the assembly branch at which it was found.
            //  3. The latter implies a non TextAssembly context. It will
            //     instantiate Textplain render pipelines using the config at
            //     the given *path*.
            throw new Error('This pipe only works in a TextAssembly context')
        }

        const normalize = config.value('normalize') === true
        const htmlnormConfigForSpecFiles = (() => {
            if (normalize) {
                return JSON5.parse(config.requiredReadFileSyncAt('htmlnormConfigPath')) as Record<string, unknown>
            } else {
                return {}
            }
        })()

        console.group('configure Branch Specified Caseblock Render Rigs...')
        const pipelineConfigName = config.requiredValue('pipelineConfigName')
        if (typeof pipelineConfigName !== 'string') {
            throw `Invalid pipelineConfigName: ${pipelineConfigName}`
        }

        async function configurationVisitor (n: ta.Node): Promise<ta.PreorderVisitReturn> {
            if (n.isFile()) {
                if (n.name === pipelineConfigName) {
                    console.group(`${n.parent.crossAssemblyPath()}`)

                    const pipeline = await simplePipelineFromConfigFile({vfs: n.assemblyRoot().srcFS(), path: n.innerAssemblyPath()})
                    const transformSection = (sectionRoot: ir.Block): number => {
                        let fails = 0
                        const body = sectionRoot.body
                        if (!ir.isBlockSequence(body)) {
                            return 0
                        }
                        for (const b of body) {
                            if (b.type === 'section') {
                                fails += transformSection(b)
                            } else if (b.type === 'case_block') {
                                b.actual = pipeline.pump((b.input as string)
                                    // CommonMark case blocks treat the input as files
                                    // so must strip any UTF-8 Bom.
                                    // see https://github.com/commonmark/cmark/blob/7195c6735f29be947ddc41f86c9ddfc8621d33b9/test/regression.txt#L3-L12
                                    // todo[test] TEMP HACK: i don't like the idea of assuming that case blocks are whole files.
                                    // todo[hack] i haven't implemented this in textplain/bin, only in the test drivers
                                    //   just to pass the test. Because I haven't decided how Textplain should handle it.
                                    //   In any case, it is a corner case so punt for now.
                                    .replace(/^\uFEFF/, '')
                                    // case blocks represent tabs with placeholder symbols
                                    //   INPUT: transform the symbols into tabs
                                    .replace(/→/g, '\t'))
                                    //   OUTPUT: transform the tabs into symbols
                                    .replace(/\t/g, '→')
                                if (normalize) {
                                    b.actual = htmlnorm(b.actual as string)
                                    b.expected = htmlnorm(b.expected as string, htmlnormConfigForSpecFiles)
                                }
                                if (b.actual !== b.expected) {
                                    b.failed = true
                                    fails++
                                }
                            }
                        }
                        sectionRoot.fails = fails
                        return fails
                    }

                    (n.parent as CaseBlockRenderingBranch).renderCaseBlocks
                        = (caseFileRoot: ir.Block) => transformSection(caseFileRoot)

                    console.groupEnd()
                }
            }
            return 'continue'
        }

        await ta.traverseAsync(textAssembly, configurationVisitor, undefined)
        console.groupEnd()

        function transform (stir: STIR) {
            try {
                const tStir = stir as TunnelingSTIR
                const r = tStir._tunnel.srcFile.searchUpForProperty('renderCaseBlocks', true)
                if (r !== undefined) {
                    (r.value as (caseFileRoot: STIR) => number)(stir)
                }
            } catch (e) {
                console.error('--------------------- ERROR ---------------------')
                console.dir(stir, {depth: null})
                console.error()
                console.error(e)
                // todo[errors] should i call exit here, or throw a new error
                //  with this error as the cause, as i do in pipeline.ts ?
                process.exit(-1)
            }
        }

        return transform
    }

}

registerPipe('RenderCaseActuals', provider)

export default provider

