import type * as ptss from '../ptss/index.js'
import {newCommonMarkSheet} from './commonmark.ptss.js'

export function newCommonMarkFWDSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    const stylesheet = newCommonMarkSheet(options)
    stylesheet.name = 'CommonMark-FWD'

    /**
     * Setext has lowest precedence per CommonMark spec.
     * For example, the spec says:
     * ```
     * > Foo
     * ---
     * ```
     * is a block quote followed by a thematic break
     */
    const setext = stylesheet.remove('Setext Heading')
    setext.consumesBackChunk = false
    stylesheet.blockStyles.push(setext)

    return stylesheet
}
