import * as ta from '../../textAssembly/index.js'
import * as ir from '../../../stir/index.js'
import {AssemblyPipeline, assemblyPipelineFromConfig} from '../../pipeline.js'
import {SandboxFS, VfsPath} from '../../../vfs/index.js'
import {newConfigForVfsPath} from '../../config.js'
import {fileTypeByFilename} from '../../filetypes.js'
import type {RenderStats} from './AssemblyRenderer.js'

// todo![renderAssembly, arch] strange place to define this. Maybe will go away as I evolve this
// code so 🏈
export type PipelineForFileMapper = (f: ta.File) => AssemblyPipeline

/**
 * call this to initiate an assembly rendering, the details of which are
 * governed by the passed config.
 *
 * Path Arguments
 * --------------
 * We create the SandboxFS instances internally rather than ask
 * the caller for these reasons:
 * - We must have access to the source FS to resolve subassembly references
 *   at graft point
 * - To be sure we are using a Sandbox whose root path is the source path.
 * - To be sure it is set to readonly.
 *
 * todo[perf] 🏈 We can make it clear in the documentation that this is what
 * we'll do, but they may redundantly create and pass SandboxFS anyway. We can
 * check that the Vfs is readonly and that VfsPath.path === '/' (i.e. bounded
 * as we need it to be) and if so, just use it as-is.
 *
 * @param {VfsPath} src
 * @param {VfsPath} dest output will be written at the root of this FS. As
 *                         a SandboxFS nothing can be written outside of it.
 * @param {VfsPath} configPath to pipeline config. If none given, it will be
 *                         expected that the root of the assembly src has one.
 * @return {Promise<void>}
 */
export async function renderAssembly (src: VfsPath,
                                      dest: VfsPath,
                                      configPath?: VfsPath) {
    //todo![RepoDoc] RepoDoc should support code comment to doc links as
    //  below. When todos are rendered to a todo webpage, the links should work.
    //  When TSDoc is rendered, the links should also work.
    //todo![site scheme] this info message should summarize all the param
    //  options whether explicit, implied or default as described in
    //  [Zero-Config vs CLI vs Config file vs Project folder].
    console.info(`TextAssembly rendering parameters:
    source:  ${src.path} (${src.vfs})
    output:  ${dest.path} (${dest.vfs})
    config:  ${configPath?.path} (${configPath?.vfs})`)

    const destFS = new SandboxFS(dest, 'empty')

    console.log()
    console.debug('\n\n==================================== 📚 READ 📚 ====================================')
    console.info('reading text assembly...')
    console.group()
    // read assembly tree structure
    // - this will include any textplain configurations
    const assembly = ta.readAssembly(src)
    if (configPath === undefined && assembly.textplainConfig === undefined) {
        const msg = 'missing pipeline config -- zero config not yet implemented'
        process.stderr.write(msg)
        throw new Error(msg)
    }
    console.groupEnd()


    // todo![arch] should prepare be moved to the textassembly module, alongside
    //   read.ts, or even incorporated into readAssembly? Is there any case where
    //   we'd read an assembly but not prepare it?
    //   - IN FACT, i could easily integrate this into the read logic, thereby
    //      eliminating an extra tree traversal.
    console.log()
    console.debug('\n\n==================================== 📚 PREPARE  📚 ====================================')
    console.info('preparing text assembly...')
    console.group()
    prepareFolder(assembly)
    console.groupEnd()

    console.log()
    console.debug('\n\n=============================== ⚙️  CONFIGURE PIPELINES ⚙️  ===============================')

    //! even when a config is given, it's inlet and pipes can be overridden by
    //  branch-specific pipelines (including one defined at the assembly root!).
    //  BUT the given config DOES determine the outlet (there can only be one
    //  assembly output).
    if (configPath !== undefined) {
        const config = newConfigForVfsPath(configPath, assembly)
        assembly.pipeline = await assemblyPipelineFromConfig(assembly, destFS, config)
    }

    await configureBranchSpecificPipelines(assembly, destFS)

    const pipelineMapper: PipelineForFileMapper = (f: ta.File) => {
        const r = f.searchUpForProperty('pipeline', true)
        if (r === undefined) {
            throw new Error(`no pipeline config found for ${f.crossAssemblyPath()}.`)
        }
        const pipeline = r.value as AssemblyPipeline
        console.debug(`using pipeline configured at ${r.node.crossAssemblyPath()}`)
        return pipeline
    }

    if (assembly.pipeline === undefined) {
        throw Error('internal error: at this point we should have a root pipeline, falling back on a zero-config default')
    }

    console.log()
    console.debug('\n\n==================================== 🚧 PRIME 🚧 ====================================')
    console.info('priming pipeline...')
    console.group()
    assembly.pipeline.prime()
    console.groupEnd()

    console.debug('\n\n=============================== 📕 PRE-RENDER STATE 📕 ===============================')
    console.debug(buildStateAsString(assembly))

    console.log()
    console.debug('\n\n==================================== 📕 RENDER 📕 ====================================')
    console.info('rendering...')
    console.group()
    pumpFolder(assembly, pipelineMapper)
    console.groupEnd()

    console.log()
    console.debug('\n\n==================================== 🏁 FINISH 🏁 ====================================')
    console.info('flushing pipeline...')
    console.group()
    assembly.pipeline.flush()
    console.groupEnd()

    console.debug('\n\n======================================================================================')
    printSummary(assembly.pipeline.stats())
    console.info('\n\n')
}

function pumpFolder (folder: ta.Folder, pipelineMapper: PipelineForFileMapper): void {
    for (const n of folder.content) {
        if (n.isFolder()) {
            if (n.hasIncludedContent) {
                //todo[vfs, api] TEMP HACK: until we implement Vpath.relative(...)
                console.log(n.outerAssemblyPath().slice(folder.outerAssemblyPath().length))
                console.group()
                pumpFolder(n, pipelineMapper)
                console.groupEnd()
            }
        } else {
            if (n.status === 'sText: waiting' || n.status === 'resource: waiting') {
                console.log(n.outerAssemblyPath().slice(folder.outerAssemblyPath().length))
                pumpFile(n, pipelineMapper)
            }
        }
    }

    pumpFile(folder.openingContent as ta.File, pipelineMapper)
}

function pumpFile (file: ta.File, pipelineMapper: PipelineForFileMapper) {
    //todo[arch] ditch this assertion later? else ditch from the caller and let this handle it?
    if (file.status !== 'sText: waiting' && file.status !== 'resource: waiting') {
        throw new Error(`unexpected status for ${file.outerAssemblyPath()}: ${file.status}`)
    }

    console.debug(`♒️♒️♒️ PIPELINE PUSH ♒️♒️♒️ ${file.name}`)
    console.group()
    console.debug(`srcFS:    ${file.parent.assemblyRoot().srcFS()}`)
    console.debug(`inner:    ${file.innerAssemblyPath()}`)
    console.debug(`cross:    ${file.crossAssemblyPath()}`)
    if (file.status === 'sText: waiting') {
        file.status = 'sText: in pipeline'
    } else if (file.status === 'resource: waiting') {
        file.status = 'resource: in pipeline'
    }
    pipelineMapper(file).pump(file)
    console.debug(`✅ ${file.name}`)
    console.groupEnd()
}

function prepareFolder (folder: ta.Folder): boolean {
    let hasContent = false

    for (const n of folder.content) {
        if (n.isFolder()) {
            hasContent = prepareFolder(n) || hasContent
        } else {
            const type = fileTypeByFilename(n.name)
            if (type.name === 'MARKDOWN' ||
                type.name === 'HTML' ||
                (type.name === 'TEXT' && folder.name === 'spec/')) {
                n.status = 'sText: waiting'
                n.isIndexable = true
                n.title = ta.defaultFileTitle(n)
                hasContent = true
            } else if (type.type === 'media') {
                n.status = 'resource: waiting'
                n.title = ta.defaultFileTitle(n)
                hasContent = true
            } else {
                n.status = 'excluded'
            }
        }
    }

    if (folder.openingContent !== undefined) {
        hasContent = true
        folder.openingContent.title = ta.defaultFileTitle(folder.openingContent)
        folder.openingContent.status = 'sText: waiting'
    } else if (hasContent) {
        console.log(`generating opening content for ${folder.outerAssemblyPath()}`)
        //todo![assembly2website, sitescheme] TEMP HACK: the generate page
        // should be determined by the AssemblyRenderer implementation, e.g for
        //  assembly2website it should be provided by the Site Scheme, and
        //  might include things like boiler plate content.
        //  ---
        //  Below is a hardcoded hack that assumes sections rather than
        //  flat headings:
        const generated = new ta.File(undefined, folder)
        folder.openingContent = generated
        generated.title = ta.defaultFileTitle(generated)
        // generate content
        const content = new ir.Block('structured_text_intermediate_representation')
        const section = new ir.Block('section')
        section.level = 1
        section.title = generated.title
        content.appendToBody(section)
        generated.stir = content
        // end generate content
        generated.status = 'sText: waiting'
        generated.isIndexable = true
    }

    folder.hasIncludedContent = hasContent

    if (!hasContent) console.log(`skipping ${folder.outerAssemblyPath()}`)
    return hasContent
}

/**
 * todo[api]: is the placement of a config file at a node
 * how we support "the content at this node and below will
 * be output to a single PDF, or a single Book"?
 * That seems like a nice idea, but what if I want to process
 * the assembly this way:
 *  - nodes at level two down have a custom HTML pipeline,
 *    overriding the HTML pipeline defined at the root.
 *  - each node at level three down are configured to be rendered
 *    as a single aggregate PDF, but this does not affect that these
 *    nodes and their children continue to be rendered as separate
 *    HTML pages. IN OTHER WORDS, the level three configs for PDF
 *    output DOES NOT OVERRIDE the level two configs, but augments them.
 *  - the index nodes at level two point to the child level three
 *    nodes. *In addition*, they also link to the single PDF produced
 *    for each.
 * In addition:
 *  - If i want all level three branches to aggregate into one output
 *    node, i don't want to have to place a custom config at each of
 *    those branch roots.
 *  - should be able to use glob patterns or something to declare such
 *    an outcome at some common parent node, to be applied to branches
 *    at some level below that node, or matching some glob pattern, or
 *    something.
 *
 * Pipeline Config Inheritance
 * ---------------------------
 * - each tree node where a config is defined results in a new
 *   pipeline being configured
 * - parts of the config that aren't specified will be inherited
 *   from the ancestor pipeline(s).
 * - the pipe parts can be keyed as 'additive_pipes', in which case
 *   the pipes for this node are the pipes from the ancestor
 *   pipeline + the additive pipes defined on this node.
 *
 * THIS GIVES US (in order of importance)
 * - we can define the template mappings at the root just once,
 *   with still the option to override at lower levels if needed.
 *   (though is that mute since our plan is to use GLOB matching to
 *   assign mappings?)
 * - we can eliminate all the redundancy in the doc_gen case
 *   (this isn't a priority, but a nice-to-have)
 * - we'd only have to add a RenderCaseActuals pipe for the stylesheet
 *   directory rather than redeclare the entire config redundantly and
 *   unDRYly.
 * - if we added support for a new path prefix (e.g. $/caseblock-pipeline.json5'),
 *   we could even eliminate the latter, having only a root config,
 *   but since the semantics of `$/...` means "if said path exists,
 *   construct a new pipeline with the resource at this path but
 *   everything else inherited. 🏈
 */
async function configureBranchSpecificPipelines (assembly: ta.Assembly,
                                                 dest: SandboxFS): Promise<void> {
    console.info('mapping branch-specific pipelines...')
    console.group()

    async function configurationVisitor (n: ta.Node): Promise<ta.PreorderVisitReturn> {
        if (n.isFolder()) {
            if (n.textplainConfig !== undefined) {
                console.info(`${n.crossAssemblyPath()}`)
                console.group()

                let heritablePipeline
                const r = n.searchUpForProperty('pipeline', true)
                if (r !== undefined) {
                    console.info(`overriding/inheriting pipeline defined for ${r.node.crossAssemblyPath()}`)
                    heritablePipeline = r.value as AssemblyPipeline
                }

                n.pipeline = await assemblyPipelineFromConfig(
                    assembly, dest, n.textplainConfig, heritablePipeline)
                console.groupEnd()
            }
        }
        return 'continue'
    }

    await ta.traverseAsync(assembly, configurationVisitor, undefined)
    console.groupEnd()
}

function buildStateAsString (fromNode: ta.Folder): string {
    let s = ''
    let level = 0
    ta.traverse(fromNode,
        (n: ta.Node) => {
            if (n.isFolder() && !n.hasIncludedContent) {
                return 'abort branch'
            }
            s += '    '.repeat(level)
            s += n.name
            s += ' --> '
            s += n.outNode
            if (n.isFolder()) {
                if (n.openingContent) {
                    s += ` (${n.openingContent.outNode})`
                } else {
                    s += ' ❌ missing opening page'
                }
                level++
            } else {
                s += ` (${n.status})`
            }
            s += '\n'
            return 'continue'
        },
        (n: ta.Node) => {
            if (n.isFolder()) {
                level--
            }
            return 'continue'
        })
    return s
}

function printSummary (stats: { [key: string]: RenderStats }) {
    for (const [key, val] of Object.entries(stats)) {
        if (typeof val === 'number') {
            console.info(`${key}: ${val}`)
        } else {
            console.info(key + ':')
            console.group()
            printSummary(val)
            console.groupEnd()
        }
    }
}
