import type {Provider} from './provider.js'
import type {Outlet} from './outlet.js'
import type {Config} from './config.js'
import type {STIR} from './stir.js'
import {lookupSTIRRenderer} from './registry.js'

// Maybe rename to (Never)STIRender 😂

export type TOC = (TOCHeading | TOCEmptyHeading)[]
export type TOCHeading = {
    heading: string,
    //todo![internal links, references] Targetable elements must have a referencable
    // unique string identifier (unique at least within a STIR). PTSS should
    // populate its corresponding STIR element with it, and STIR elements
    // that link to it will use the same reference. Such an identifier should
    // be included in the TOC entry, so that the rendered TOC entry can be
    // linked to its corresponding rendered content element.
    //synonym/targetingRef: ???,
    subheadings?: TOC,
    [key: string]: unknown
}
export type TOCEmptyHeading = {
    heading: undefined
    subheadings: TOC
}

/**
 * A subtype of {@link Outlet}, RenderSTIR returns a string rendering of the
 * input.
 */
export type RenderSTIR = Outlet<string> //todo[api] Buffer? {toString():sting?}

export type RenderSTIRProvider = Provider<RenderSTIR>

export async function configRenderSTIRProvider (config: Config): Promise<RenderSTIR> {
    const providerName = config.requiredValue('provider')
    console.group(`configure outlet: ${providerName}`)
    const provider = lookupSTIRRenderer(providerName as string)
    if (provider === undefined || provider.configure === undefined) {
        throw new Error(`invalid outlet: ${providerName as string}`)
    }
    const providerConfig = config.subConfig('config')
    const outlet = await provider.configure(providerConfig)
    console.groupEnd()
    return (stir: STIR) => {
        console.debug(`♒️ OUTLET ${providerName} ${providerConfig ? JSON.stringify(providerConfig.data()) : ''}`)
        console.group()
        const out = outlet(stir)
        console.groupEnd()
        return out
    }
}
