/**
 * A stack of ParseContainers (connected via the `parent` field) represents
 * a nested container hierarchy parsed from the plain text.
 *
 * A hierarchy of ParseContainers, representing *concrete containers*, and any
 * open child *virtual containers* (list or sections) that they reference is
 * used to represent the nested container hierarchy of any point in the plain
 * text.
 *
 *
 * concrete containers
 * -------------------
 * The concrete participants are explicitly defined by lexical
 * elements such as a left border of `>` for blockquotes or the leading
 * `12. <body>` of a list item. Both their beginning and end are defined by
 * their lexical structure.
 *
 * They have a `continues` function that determines if the container ends at
 * the current parse point.
 *
 *
 * virtual containers
 * ------------------
 * Lists and Sections are *virtual containers*: they don't have an independent
 * concrete lexical manifestation but exist by virtue of other elements. Lists
 * come into being at the start of a sequence of list items and end at the end
 * of that sequence. Sections come into being at the occurrence of a
 * section head and end at the occurrence of another section head, or the end
 * of their containing sectioning root (their containing ParseContainer).
 *
 * They don't have a `continues` function.
 *
 *
 * the hierarchy
 * -------------
 * A ParseContainer's`parent` ParseContainer is not necessarily its direct
 * parent container in the abstract nesting hierarchy. This is simply a parser
 * implementation detail.
 *
 * For example, if the curParseContainer for a line points at `B` in the
 * following:
 *
 * ```
 *   ParseContainer A:
 *     openList: L1
 *
 *   List L1:
 *     body: [B]
 *
 *   ParseContainer B:
 *     parent: A
 * ```
 *
 * It means the nested container hierarchy for the line is `A > L1 > B` and `B`
 * is the tip.
 *
 * The `parent` property of a ParseContainer simply points back to the closest
 * *concrete* ancestor, which may or may not be its direct parent. In the above
 * example, if container `B` terminates, the line's curParseContainer is moved
 * to `A`, but `A` is not the tip. Since List L1 is still open, L1 represents
 * the current tip.
 *
 * See the implementation of the `append___` methods to see the logic for
 * determining the current *tip*.
 *
 * Likewise:
 * ```
 *   ParseContainer A:
 *     sectionStack: [A1, A1.2, A1.2.1]
 *     openList: L1
 *
 *   Section A1 > Section A1.2 > Section A1.2.1 > List L1 > ParseContainer B
 *
 *   ParseContainer B:
 *     parent: A
 * ```
 *
 * means the actual nesting structure is:
 * ```
 * A > A1 > A1.2 > A1.2.1 > L1 > B
 * ```
 *
 *
 * how elements are correctly inserted into the hierarchy
 * ------------------------------------------------------
 * See rules under {@link append}.
 */
export class ParseContainer {
    parent: ParseContainer | null

    /** the ir.Block associated with this ParseContainer */
    irContainer: ir.Block

    /**
     * Lists are "retroactive, virtual" containers in plain text: We can't
     * parse them; instead we parse list items and then retroactively create
     * a List container to wrap adjacent list items of the same style. If the
     * last item parsed in a container is a list item, the `openList` will
     * point to that item's containing list. Otherwise it will be null.
     *
     * An open list is closed in one of two ways:
     *  - we encounter a non-list item. The parser must set this to null
     *  - this ParseContainer ends (e.g. a block quote ends, or the parent
     *    list item for a nested list ends). When this happens, the parser
     *    will no longer point to this ParseContainer, so its openList value
     *    is no longer relevant.
     */
    openList: ir.Block | null = null

    sectionStack: ir.Block[] | undefined = undefined


    constructor (irContainer: ir.Block, parent: ParseContainer | null = null) {
        this.irContainer = irContainer
        this.parent = parent
    }

    public toString (): string {
        let hierarchy = this.tip().toHierarchyString()
        if (hierarchy.length > 50) {
            const i1 = hierarchy.indexOf('/') + 1
            const i2 = hierarchy.slice(0, hierarchy.lastIndexOf('/')).lastIndexOf('/')
            hierarchy = hierarchy.slice(0, i1) + '...' + hierarchy.slice(i2)
        }
        return hierarchy
    }

    private hierarchy (tip: boolean): string {
        let s
        if (this.parent === null) {
            s = ''
        } else {
            s = this.parent.hierarchy(false) + '/' + this.irContainer.type
            if (tip && this.irContainer.source?.lineStart) {
                s += '(ln ' + this.irContainer.source?.lineStart + ')'
            }
        }

        if (this.sectionStack?.length) {
            let section
            for (section of this.sectionStack) {
                s += '/section-L' + section.level
            }
            if (tip && section && section.source?.lineStart) {
                s += '(ln ' + section.source?.lineStart + ')'
            }
        }

        if (this.openList) {
            if (tip) {
                // parenthesis to indicate that the list being open is
                // contingent on the type of next item encountered
                s += '/*list'
                if (this.openList.source?.lineStart) {
                    s += '(ln ' + this.openList.source?.lineStart + ')'
                }
            } else {
                s += '/list'
            }
        }

        return s === '' ? '/' : s
    }

    public root (): ParseContainer {
        let root = this as ParseContainer
        while (root.parent !== null) {
            root = root.parent
        }
        return root
    }

    public tip (): ir.Block {
        if (this.openList !== null) {
            return this.openList
        }
        if (this.sectionStack !== undefined && this.sectionStack.length > 0) {
            return this.sectionStack.at(-1) as ir.Block
        }
        return this.irContainer
    }

    /**
     * See rules under {@link append}.
     *
     * *! This method assumes section structure is created from heading levels.
     *    If we decide to support a an implicit [[hierarchy syntax]], then
     *    sections would not have numbered levels (currently taken from the
     *    heading. Arguably, levels numbers are redundant in the STIR given
     *    that sections are nested (unless it is important to support skipped
     *    levels, e.g. an H4 child of an H2). In any case,
     *     1. this method would simple add the new section to the current
     *        section, and
     *     2. we'd need to add a `closeSection` method that gets called when
     *        we parse the explicit termination of the section as defined by
     *        the syntax.
     *
     * @param newSection
     */
    appendSection (newSection: ir.Block): void {
        if (this.openList != null) {
            console.debug(`❌${this.toString()}`)
            this.openList = null
        }

        const stack = this.sectionStack
        if (stack === undefined || stack.length === 0) {
            console.debug(`🌱 opening new section within ${this.toString()}`)
            this.irContainer.appendToBody(newSection)
            this.sectionStack = [newSection]
        } else {
            let curSection: ir.Block
            for (let i = stack.length - 1; i >= 0; i--) {
                curSection = stack[i]
                if ((curSection.level as number) < (newSection.level as number)) {
                    console.debug(`🌱 opening new section within ${this.toString()}`)
                    curSection.appendToBody(newSection)
                    stack.push(newSection)
                    return //todo[arch] i don't like this return here, or this whole if/else. can do better?
                }
                console.debug(`❌ closing section ${this.toString()}`)
                stack.pop()
            }
            console.debug(`🌱 opening new section within ${this.toString()}`)
            this.irContainer.appendToBody(newSection)
            this.sectionStack = [newSection]
        }
    }

    /**
     * See rules under {@link append}.
     *
     * @param item
     * @param newList if given, this list will first be appended, then the item
     *                appended to it. if non is given, the item will be added to
     *                the existing open list.
     */
    appendListItem (item: ir.Block, newList: ir.Block | undefined): void {
        //todo[doc]: Move this to docs:
        // Why List is not handled as a container on the stack:
        //  1. While STIR/HTML etc have an explicit List container...
        //  2. In plain text the container is only *implied*. A list is the set
        //     of consecutive list items (of the same style/kind)
        //  3. The ContainerStack represents the *physical PTSS container* structure,
        //     while the STIR tree represents the IR's structure. They are not
        //     necessarily identical.
        //  4. It makes sense that PTSS parsing mimics #2, not creating a container stack
        //     item for List (since when parsing continuation, there is nothing to parse
        //     for the List), and just creating IR List containers on the fly when a new
        //     list is detected, and adding items to the previous item's list if it matches
        //     that item's style. [should we move the style source info to the item?]
        // Or as I put it before: WE PARSE LISTS JUST AS THE HUMAN EYE DOES.
        //    The style governs list **items** and are so associated in the IR. When
        //    list items with identical styles are adjacent in the text, they are
        //    naturally collected together into the same list. (doesn't matter whether
        //    that's determined on the fly as the text stream is processed or as a
        //    post-facto step).
        let list
        if (newList !== undefined) {
            this.append(newList)
            list = newList
        } else {
            console.debug('➕ adding to existing list')
            list = this.openList as ir.Block

            if ((this.irContainer.source as ptss.Block).lastLineBlank) {
                console.debug('  set commonmark.list.tight to FALSE for list starting on line',
                    this.irContainer.source?.lineStart)
                list.hints['commonmark.list.tight'] = false;
                (this.irContainer.source as ptss.Block).lastLineBlank = false
            }
        }

        console.debug(`🌱 appending ${item.type} to ${list.toDebugString()}`)
        const ptContainer = list.source as ptss.Block

        // if last line was blank AND this is not the first item in the list
        if (ptContainer.lastLineBlank && list.body) {
            list.hints['hints.commonmark.list.tight'] = false
            console.debug('  set commonmark.list.tight to FALSE for list starting on line', ptContainer.lineStart)
        }

        ptContainer.lastLineBlank = false
        console.debug(`  CLEARED LAST BLANK for ${list.type}`)

        list.appendToBody(item)
    }


    /**
     * Rules for determining where exactly new block elements are added into
     * the nested container hierarchy managed by ParseContainer:
     *
     *   cpc = curParseContainer
     *
     *   The tip to add new elements in precedence order:
     *     - cpc.openList  (if list item, else close list)
     *     - section at head of cpc.sectionStack
     *     - cpc.irContainer
     *
     *   When a List is added:
     *     - sets cpc.openList to List
     *        (it will have been null since a nested List isn't
     *         ever a direct child of a List, but of a List Item)
     *     - list is appended as child of tip (see above)
     *        (and it thus becomes the tip)
     *
     *   When a List Item is added:
     *     - if this is the first item in a new list, that will have already
     *       been determined and the new List will be added first.
     *     - item added to cpc.openList
     *     - since it is a container, it will be associated
     *       with a new ParseContainer whose parent is cpc, and
     *       become the new cpc and tip
     *     - since the call site for adding list items is dedicated to
     *       list items, and since the add item logic is very unique, purely
     *       for performance purposes we have a separate {@link appendListItem}
     *       method. We could easily have merged that logic into this method
     *       inside of an if-else, but that doesn't make much sense.
     *
     *   When a new Section is encountered:
     *     - sets cpc.openList to null
     *     - pops sections off the cpc.sectionStack that are of same level or higher
     *       (THIS or closing of parent ParseContainer is how a Section terminates)
     *     - section is appended as child of tip (see above)
     *     - section pushed onto cpc.sectionStack
     *       (and thus is the new tip)
     *     - since the call site for adding sections items is dedicated to
     *       sections, and since the add section logic is very unique, purely
     *       for performance purposes we have a separate {@link appendSection}
     *       method. We could easily have merged that logic into this method
     *       inside of an if-else, but that doesn't make much sense.
     *
     *   When any other element is added:
     *     - sets cpc.openList to null
     *       (THIS or closing of parent ParseContainer is how a List terminates)
     *     - element appended as child of tip (see above)
     *     - if it is a container, it will be associated
     *       with a new ParseContainer whose parent is pc, and
     *       become the new cpc and tip
     *
     */
    append (content: ir.Block): void {
        // any open list closes since we know we are not adding a list item
        // (that has a dedicated appendItemToList method)
        if (this.openList != null) {
            // because list items are added via appendItemToList not this method,
            // we know `content` is not a list item. Thus any open list is closed.
            console.debug(`❌${this.toString()}`)
            this.openList = null
        }

        const irContainer = this.irContainer
        const ptContainer = irContainer.source as ptss.Block

        if (content.type === 'list') {
            console.debug(`🌱 opening new list within ${this.toString()}`)
            this.openList = content
        } else {
            console.debug(`🌱 appending ${content.type} to ${this.toString()}`)
        }

        let tip = this.sectionStack?.at(-1)
        if (tip === undefined) {
            tip = irContainer

            // if appending to list item: if last line was blank AND this is not the first content
            if (tip.type === 'list_item' && ptContainer.lastLineBlank && tip.body) {
                const parent = this.parent as ParseContainer
                const enclosingContainer = parent.openList ? parent.openList : parent?.irContainer as ir.Block
                enclosingContainer.hints['commonmark.list.tight'] = false
                console.debug('  set commonmark.list.tight to FALSE for list starting on line',
                    enclosingContainer.source?.lineStart)
            }
        }

        ptContainer.lastLineBlank = false
        console.debug(`  CLEARED LAST BLANK for ${irContainer.type}`)

        // todo[spec] doing it here sometimes doesn't always do the right thing.
        //  Perhaps moving it to BlockTreeParser's code that executes right after
        //  the call to this method, where it has more info available.
        setLineEndUpTree(this, content.source!.lineEnd as number)
        tip.appendToBody(content)
    }
}

// set lineEnd all the way up the container stack
function setLineEndUpTree (pContainer: ParseContainer, lineEnd: number) {
    let c: ParseContainer | null = pContainer
    while (c !== null) {
        c.irContainer.source!.lineEnd = lineEnd
        if (c.openList !== null) {
            c.openList.source!.lineEnd = lineEnd
        }
        c = c.parent
    }
    if (pContainer.sectionStack !== undefined) {
        for (const block of pContainer.sectionStack) {
            block.source!.lineEnd = lineEnd
        }
    }
}

/**
 * Given a line and the deepest container from the previous line,
 * this function determines whether the container or any of its
 * parents continues on this line. It updates the line's state
 * as follows:
 *   - `line.pContainer` is set to the deepest ParseContainer from the given
 *      ParseContainer that continues on this line.
 *   - if a bottom border occurs on this line:
 *     - `line.pContainer` is set to the container to which the
 *        bottom border belongs. This could be *any* of the
 *        containers in the stack.
 *     - `line.pContainerBottomBorder` is set to the pattern match results
 *        from the border pattern
 *     - if the bottom border spans multiple lines, line.pContainerBottomBorder
 *       is set for each of these lines.
 *     By definition, the parent container of the one terminated by
 *     a bottom border can now be assumed to be the "current container".
 *     But we can't close the just terminated container quite yet: There
 *     may be unresolved lines from before the bottom border that first
 *     need to be resolved; that is the responsibility of the caller.
 *   - the character parse state of the line will be updated
 *     to reflect the consumption of container elements (indentation,
 *     borders).
 *
 *   todo[spec] If the bottom border spans multiple lines, the state of
 *       each line should be updated. Most often it will be one line, and
 *       the next most often will be two, and at the very least the first
 *       and last need to be updated (the first because this call might
 *       be done for lookahead (e.g. table parser), meaning it will be
 *       called again later on the first line when regular parsing
 *       resumes and arrives at it, and the last line of course because
 *       it represents the farthest reach of parsing so far that we
 *       don't want to redo redundantly. Since 3 or more will be rare,
 *       don't sweat it and update all lines for consistency.
 *
 * @param pContainer ContainerStack node
 * @param line
 */
export function parseContinuation (pContainer: ParseContainer, line: Line): void {
    // previously determined to this level of nesting.
    // This will be the path for lines at the root container, the most common case
    if (line.pContainer === pContainer) {
        console.debug(`previously parsed${line.pContainerBottomBorder ? ' 🅱 bottom border' : ''}`)
        return
    }

    // first check enclosing container continuation (recursive)
    if (line.pContainer !== pContainer.parent) {
        parseContinuation(pContainer.parent as ParseContainer, line)
        if (line.pContainer !== pContainer.parent || line.pContainerBottomBorder !== null) {
            console.debug('❌' + pContainer.toString())
            return
        }
    }

    if ((pContainer.irContainer.source as ptss.Block).continues(line)) {
        line.pContainer = pContainer
        console.debug(pContainer.toString())
        console.debug('  ' + line.toString())
    } else {
        console.debug('❌' + pContainer.toString())
    }
}
