import * as ptss from '../ptss/index.js'
import {newGFMStylesheet} from './gfm.ptss.js'


export function newGracedownSheet (options?: Record<string, unknown>): ptss.Stylesheet {
    //todo[RepoDoc] allow user to pass in custom interjection icon-to-type mappings via `options`
    const stylesheet = newGFMStylesheet(options)
    stylesheet.name = 'Gracedown'

    // override inherited CommonMark, restore PTSS default
    stylesheet.linkDestAllowSpaces = true

    const interjectionPara= new ptss.ChunkStyle('Simple Interjection', stylesheet)
    //todo: use ES9 *Unicode property escapes in regular expressions*
    // https://pawelgrzybek.com/whats-new-in-ecmascript-2018/#unicode-property-escapes-in-regular-expressions-by-mathias-bynens
    interjectionPara.headPattern = /‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|❓|💡|✅|❌/y
    // todo[RepoDoc] hack: this regex doesn't ensure the emphasis/strong markers are properly paired
    // todo![ptss composition] ideally we use the plan where this style in terms
    //  of the other ptss defined elements, e.g. ATX heading.
    interjectionPara.bodyPattern = / *(?:([^\n]+)(?:\\| {2})\s*\n|[*_]{1,3}([^\n]+?)[*_]{1,3} *\n)?([\s\S]*)/y
    interjectionPara.canInterruptParagraph = false
    interjectionPara.canInterruptLazy = false
    interjectionPara.irNode.type = new ptss.Expression('interjection')
    // todo[stir] settle on first cut of interjection subtypes supported natively by the IR
    //    OR:
    // todo![stir] decide whether the IR is neutral and just passes the subtype through? Would that
    //   work for other output formats if they don't have a notion like classes ???
    //   **Should there instead be a predefined *base* set of interjection types that all writers
    //   can rely on without worrying about whether a particular implementation supports it?**
    // See also https://rdmd.readme.io/docs/callouts
    // See also https://github.com/jgm/djot/issues/196#issuecomment-1529691779
    // Also my post: on talk.commonmark.org about github admonitions + my post on the github announcement
    // DocBook: danger, error, hint, important, note, tip, warning
    interjectionPara.irNode.subType = new ptss.Expression(
        ['map', ['head', 0], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '❓': 'question',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    interjectionPara.irNode.icon = new ptss.Expression(['head', 0])
    interjectionPara.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['first', ['body', 1], ['body', 2]]]])
    interjectionPara.irNode.body = new ptss.Expression(['parseInlines', ['trim', ['body', 3]]])
    stylesheet.insertAbove('Block Quote', interjectionPara)

    const interjectionHanging = new ptss.ContainerStyle('Hanging Interjection', stylesheet)
    interjectionHanging.irNode.type = new ptss.Expression('interjection')
    interjectionHanging.indent = 'no'
    interjectionHanging.hangingIndent = 'marker'
    // todo[ptss composition] TEMP HACK until body composition is supported, we
    //  are forced to include the interjection symbol in the marker, forcing
    //  the hanging indent to be greater than it needs to be.
    interjectionHanging.marker = /\* (‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|💡|✅|❌)/y
    interjectionHanging.bottomBorder = null
    interjectionHanging.canInterruptParagraph = true
    interjectionHanging.canInterruptLazy = true
    interjectionHanging.irNode.subType = new ptss.Expression(
        ['map', ['marker', 1], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    interjectionHanging.irNode.icon = new ptss.Expression(['marker', 1])
    // todo![ptss composition] we can't support a title without it.
    //interjectionHanging.irNode.title = ???
    stylesheet.insertAbove('List', interjectionHanging)

    const interjectionIndented = new ptss.ContainerStyle('Indented Interjection', stylesheet)
    interjectionIndented.irNode.type = new ptss.Expression('interjection')
    interjectionIndented.indent = 'yes'
    // todo![ptss composition] does composition make more sense than mimicking ATX as I'm doing below?
    // todo[spec] TEMP HACK until ContainerStyle `header` is implemented.
    interjectionIndented.topBorder = '#'
    interjectionIndented.topBorderMinLength = 1
    interjectionIndented.righter = /\s*(‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|💡|✅|❌)\s*([^\n]*)(?:\n|$)/y
    // END hack
    interjectionIndented.bottomBorder = null
    interjectionIndented.canInterruptParagraph = true
    interjectionIndented.canInterruptLazy = true
    interjectionIndented.irNode.subType = new ptss.Expression(
        ['map', ['righter', 1], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    interjectionIndented.irNode.icon = new ptss.Expression(['righter', 1])
    interjectionIndented.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['righter', 2]]])
    stylesheet.insertAbove('Block Quote', interjectionIndented)

    const interjectionBirdTracked = new ptss.ContainerStyle('Bird-Tracked Interjection', stylesheet)
    interjectionBirdTracked.irNode.type = new ptss.Expression('interjection')
    interjectionBirdTracked.indent = 'no'
    // todo![ptss composition] does composition make more sense than mimicking ATX as I'm doing below?
    // todo[spec] TEMP HACK until ContainerStyle `header` is implemented.
    interjectionBirdTracked.topBorder = '>'
    interjectionBirdTracked.topBorderMinLength = 1
    interjectionBirdTracked.righter = /\s*#{0,6}\s*(‼️|⚠️|🚫|⛔|☠️|ℹ️|🚩|📌|💡|✅|❌)\s*([^\n]*)(?:\n|$)/y
    // END hack
    interjectionBirdTracked.bottomBorder = null
    interjectionBirdTracked.leftBorder = '>'
    interjectionBirdTracked.leftPadding = 'sloppy'
    interjectionBirdTracked.canInterruptParagraph = true
    interjectionBirdTracked.canInterruptLazy = true
    interjectionBirdTracked.irNode.subType = new ptss.Expression(
        ['map', ['righter', 1], {
            '‼️': 'important',
            '🚩': 'important',
            '⚠️': 'warning', // aka caution
            '🚫': 'danger', // todo[RepoDoc] what makes danger different from warning? just more severe?
            '⛔': 'danger',
            '☠️': 'danger',
            'ℹ️': 'info',  // aka note
            '📌': 'info',
            '💡': 'tip',
            '✅': 'positive',
            '❌': 'negative'
        }])
    interjectionBirdTracked.irNode.icon = new ptss.Expression(['righter', 1])
    interjectionBirdTracked.irNode.title = new ptss.Expression(['parseInlines', ['trim', ['righter', 2]]])
    stylesheet.insertAbove('Block Quote', interjectionBirdTracked)

    const table = stylesheet.get('GFM Table') as ptss.TableStyle
    // todo![spec] figure out a generic way to support Gracedown's need to manipulate
    //   the cell body?
    // todo![composition] this Expression should be applied to irNode, not table.
    //  - 🫥forgot why i wrote the preceding sentence, and thus what it means.
    //    anyway, think about how composition might make this better? thoughts:
    //    - i think composition by definition will be a feature for containers,
    //      not leaves. But it also applies to nested containers, for which
    //      composition is a sub-type mechanism. It doesn't make sense for the
    //      doc root container, and i think similarly it won't make sense for
    //      cell, which a sort of doc root itself ??? 🤔  Doesn't composition
    //      only apply to a nested container within a cell? Is there a use case
    //      for a table cell whose content is prescribed?
    table.cell = new ptss.Expression(
        ['if',
            ['match', ['body'], /<br *\/?>/],
            ['parseBlocks',
                ['replace', ['body'], /<br *\/?>/g, '\n'],
                stylesheet,
                'cell'],
            ['parseInlines', ['body']]
        ]
    )

    //! first case of making node type conditional!
    const tickFence = stylesheet.get('Backtick Fenced Code Block') as ptss.ContainerStyle
    tickFence.body = 'RAW'
    tickFence.irNode.type = new ptss.Expression(
        ['if',
            ['match', ['righter'], /\s*pre\s*/],
            'pre',
            'code_block']
    )
    tickFence.irNode.info = new ptss.Expression(
        ['if',
            ['match', ['righter'], /\s*pre\s*/],
            undefined,
            ['unescape', ['trim', ['righter', 0]]]]
    )
    tickFence.irNode.body = new ptss.Expression(
        ['if',
            ['match', ['righter'], /\s*pre\s*/],
            ['parseInlines', ['body'], 'preserveNewlines', 'preserveWhitespace'],
            ['body']]
    )

    stylesheet.inlineStyles.push('text_highlight')

    return stylesheet
}
