import posix from 'path-browserify'

/**
 * All Vfs paths are POSIX-style, web URL-style, even on Windows.
 * The root path is ALWAYS '/'.
 * 
 * - The Vfs API, regardless of underlying file system, is supposed
 *   to have uniform same path syntax and semantics.
 * - We've settled on POSIX.
 * - It's only the concrete backends that need to do translations
 *   to/from POSIX when making underlying fs calls (when underlying fs isn't
 *   POSIX):
 *   - SystemFS (which handles Windows, etc)
 *   - WebFS or whatever we call it will also need some translation, probably.
 *     Because WebFS has paths with protocols and host names at the head. Also,
 *     some characters in the path may need to be escaped/swapped/URL-safe.
 */
export const Vpath = {
    parse (path: string): { dir: string, base: string, name: string, ext: string } {
        return posix.parse(path)
    },

    parseDir (path: string): string {
        return posix.dirname(path)
    },

    parseBase (path: string): string {
        return posix.basename(path)
    },

    parseName (path: string): string {
        return posix.parse(path).name
    },

    parseExt (path: string): string {
        return posix.extname(path)
    },

    normalize (path: string): string {
        return posix.normalize(path)
    },

    join (...paths: string[]): string {
        return posix.join(...paths)
    },

    // todo[api] not sure we need these two. If we do, they DO NOT need to have
    // same signature as Nodejs.fs API. For example i think the Node resolve
    // method is odd, in that it doesn't necessarily use all the args,
    // and relative I believe only works if the two paths given are absolute
    // (need to confirm this); if so can/should it work for non absolute paths?
    // if one or two relative paths are passed, what are the results?

    resolve (...pathSegments: string[]): never {
        throw new Error(`path.resolve not implemented -- see note in vfs.ts`)
    },

    relative (fromPath: string, toPath: string): never {
        throw new Error(`path.relative not implemented -- see note in vfs.ts`)
    }
}
