import type StateInline from 'markdown-it/lib/rules_inline/state_inline.js'
import markdownItUtils from 'markdown-it/lib/common/utils.js'

/**
 * Modified version of markdown-it/lib/rules_inline/state_inline.js
 * Mods annotated with "//!" comments.
 *
 *! Modified with parameterized behavior
 */
export function tokenize (state: StateInline, silent: boolean) {
    let ws, pos = state.pos

    if (state.src.charCodeAt(pos) !== 0x0A/* \n */) { return false }

    const pmax = state.pending.length - 1
    const max = state.posMax

    // '  \n' -> hardbreak
    // Lookup in pending chars is bad practice! Don't copy to other rules!
    // Pending string is stored in concat mode, indexed lookups will cause
    // conversion to flat mode.
    if (!silent) {
        if (state.env.preserveNewlines) { //! PTSS inline parse function 'preserveNewlines' option
            state.pending += '\n'
        } else if (state.env.newlineIsHardBreak) { //! PTSS inline parse function 'newlineIsHardBreak' option
            state.push('hardbreak', 'br', 0)
        } else if (state.env.twoSpaceNewlineIsHardBreak //! PTSS inline parse function 'twoSpaceNewlineIsHardBreak' option
                   && pmax >= 0 && state.pending.charCodeAt(pmax) === 0x20) {
            if (pmax >= 1 && state.pending.charCodeAt(pmax - 1) === 0x20) {
                // Find whitespaces tail of pending chars.
                ws = pmax - 1
                while (ws >= 1 && state.pending.charCodeAt(ws - 1) === 0x20) ws--

                state.pending = state.pending.slice(0, ws)
                state.push('hardbreak', 'br', 0)
            } else {
                state.pending = state.pending.slice(0, -1)
                state.push('softbreak', 'br', 0)
            }

        } else {
            state.push('softbreak', 'br', 0)
        }
    }

    pos++

    //! inline parse function 'preserveWhitespace' option
    if (!state.env.preserveWhitespace) {
        // skip spaces after newline (i.e. skip leading spaces on next line)
        while (pos < max && markdownItUtils.isSpace(state.src.charCodeAt(pos))) { pos++ }
    }

    state.pos = pos
    return true
}

